import React, { useMemo } from 'react';
import styles from './BurgerMenu.module.scss';

import { List, Menu, MenuItem } from '../Menu';
import HeaderSearch from './HeaderSearch';

import Icon from '../Icon';

import CollapsedDimensions from './CollapsedDimensions';

import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import Indicators from '../../containers/Indicators';
import { dimensionsMap } from '../../utils/getDimensions';
import CollapsedDamage from './CollapsedDamage';
import CollapsedSearchFiled from './CollapsedSearchField';
import ExtendedDamage from './ExtendedDamage';
import IndicatorsContainer from './IndicatorsContainer/IndicatorsContainer';
import { ReactComponent as FileDownloadIcon } from './icons/file-download.svg';
import { ReactComponent as MoneyIcon } from './icons/money.svg';
import { ReactComponent as PrinterIcon } from './icons/printer.svg';

interface BurgerMenuProps {
  /** Показывает раскрыто ли меню */
  extended: boolean;
  /** Направление графа  */
  direction: number;
  /** Функция "Передать изменения" (схема) */
  sendChanges: () => void;
  /** Функция "Передать изменения" (РАВП) */
  sendChangesMDAB: () => void;
  /** Функция "Передать изменения" (граф) */
  sendGraph: () => void;
  /** Функция сохранения */
  onSave: () => void;
  /** Показывает пуст ли список графов пользователя */
  userGraphsEmpty: boolean;
  /** Список графов пользователя */
  userGraphsList: any[];
  /** Свободный поиск */
  freeSearch: () => void;
  /** Начало работы */
  onBeginWork: () => void;
  /** Функция выбора графа */
  onSelectGraph: (id: string) => void;
  /** Функция клика по моим графам */
  onUserGraphsClick: () => void;
  /** Режим показа скрытых узлов */
  isHideNode: boolean;
  /** Флаг показывает состояние периода */
  periodIsActive: boolean;
  /** Функция поиска */
  onSearch?: (v: string) => void;
  /** Callback для выбора периода */
  pickPeriodCallback?: () => void;
  /** Callback для фильтров */
  filtersCallback?: () => void;
  /** Callback для настроек тултипа НП */
  taxpayerSettingsCallback?: () => void;
  chainsCallback?: () => void;
  /** Callback для графа от продавца / от покупателя */
  transferCallback?: () => void;
  /** Callback для добавления нового узла */
  addNodeCallback?: () => void;
  /** Callback вертикального направления просмотра */
  verticalViewCallback?: () => void;
  /** Callback горизонтального направления просмотра */
  horizontalViewCallback?: () => void;
  /** Callback для легенды */
  legendCallback?: () => void;
  /** Callback для интегральной оценки */
  integralAssessmentCallback?: () => void;
  /** Callback для скрытых узлов */
  hiddenNodesCallback?: () => void;
  /** Callback для Проекта акта */
  referenceCallback?: () => void;
  /** Callback для поиска узла на графе */
  searchNodeGraphCallback?: () => void;
  /** Callback для Отчёта "Дерево связей" */
  treeReportCallback?: () => void;
  /** Цветовая тема приложения */
  theme?: string;
  /** Доступность переключения режима раскладки */
  showTransformMode?: boolean;
  /** Раскладка графа */
  graphTransformMode?: string;
  /** Доступность отчёта Дерево связей*/
  showTreeReport?: boolean;
  /** От продавца / от покупателя */
  currentDrawingMode?: string;
  /** Доступность Свободго поиска */
  showFreeSearch?: boolean;
  /** Передать изменения */
  showSendChanges?: boolean;
  /** Передать изменения (схема) */
  showCreateScheme?: boolean;
  /** Номер схемы */
  schemeId?: any;
  /** Создание схем КАО */
  showCreateKAO?: boolean;
  /** Создание схем КНП */
  showCreateKNP?: boolean;
  /** Добавить НП */
  showAddNP?: boolean;
  /** Поиск НП */
  showSearchNP?: boolean;
  /** Фильтры */
  showFilters?: boolean;
  /** Формализованная справка */
  showFormalReport?: boolean;
  /** Просмотр списка графов */
  showListGraphs?: boolean;
  /** Создание графа */
  showCreateGraph?: boolean;
  /** Изменение фильтров */
  showChangeFilters?: boolean;
  /** Список ошибок для "Передать изменения" */
  listErrors?: any;
  /** Ошибка "Не заполнены аналитические роли" для потенциального ущерба*/
  listErrorsType?: any;
  /** Единица измерения */
  dimensions: string;
  /** Режим показа скрытых узлов */
  showHideNP?: boolean;
  /** Callback для фильтра на индикаторы */
  indicatorsCallback?: () => void;
  /** Поиск графа */
  showAllScheme?: boolean;
  /** Является ли граф тестовым */
  isTestGraph?: boolean;
  /** Вернуться в Досье проверки */
  backToDossier?: () => void;
  /** Вернуться в РАВП */
  backToRAVP?: () => void;
  /** Владелец графа */
  graphOwner?: string;
  /** Режим Схема */
  scheme?: boolean;
  /** Переключатель Схема - Текущее состояние*/
  setSchemeCallback?: () => void;
  /** Показать переключатель "Схема - Текущий режим", если был совершён переход */
  isFromScheme?: boolean;
  /** Возможность редактирования */
  ableEdit?: boolean;
  /** Активный период */
  isPeriod?: boolean;
  /** Индикаторы (операции особого контроля)*/
  SpecialOperationFilter?: any;
  /** Индикаторы (результат по НД)*/
  ResultNDFilter?: any;
  /** Индикаторы (разрывы и задолженности)*/
  GapsDebtFilter?: any;
  /** Индикаторы (индикаторы групп)*/
  LinksFilter?: any;
  /** Фильтры (подконтрольности) */
  AccountabilityFilter?: any;
  /** Фильтры (согласованность) */
  CoherenceFilter?: any;
  /** Фильтры (кооперация) */
  CooperationFilter?: any;
  /** Фильтры (пользовательские связи) */
  CustomLinksFilter?: any;
  /** Фильтры (изменения графа) */
  GraphChangeFilter?: any;
  /** Фильтры (связи по схеме) */
  TypologyFilter?: any;
  /** Фильтры (Налогополательщики) */
  TaxpayersFilter?: any;
  /** Открыто окно по смене единицы измерения */
  isCollapsedDimensions?: boolean;
  /** Действие по открытию окна по смене единицы измерения */
  dimensionsChange?: () => void;
  /** Callback для ввода потенциального ущерба */
  potentialDamageCallback?: () => void;
  /** Callback для Отмены последнего изменения */
  undoCallback?: () => void;
  /** Признак наличия предыдущей версии */
  isPreviousVersion?: boolean;
  /** Взаимодействие с РАВП */
  sourceSystem?: any;
  /** Вернуться в ИСА КН */
  backToITAIS?: () => void;
  /** Доступность кнокпи "Передача изменений в Досье" */
  enableDossierButton?: boolean;
  /** Callback для Напечатать схему */
  printSchemeCallback?: () => void;
  /** Callback для меню */
  menuCallback: () => void;
  /** Функция ввода суммы потенциального ущерба */
  onInputDamage?: (v: string) => void;
  /** Сумма потенциального ущерба */
  amountOfDamage?: any;
  nodesReportCallback?: () => void;
  showNodesReport?: boolean;
}

const BurgerMenu = ({
  onSearch,
  extended,
  direction,
  pickPeriodCallback,
  filtersCallback,
  transferCallback,
  addNodeCallback,
  verticalViewCallback,
  horizontalViewCallback,
  legendCallback,
  hiddenNodesCallback,
  referenceCallback,
  taxpayerSettingsCallback,
  chainsCallback,
  sendChanges,
  sendChangesMDAB,
  sendGraph,
  onSave,
  userGraphsEmpty,
  userGraphsList,
  onSelectGraph,
  onBeginWork,
  onUserGraphsClick,
  searchNodeGraphCallback,
  isHideNode,
  treeReportCallback,
  theme,
  showTransformMode,
  indicatorsCallback,
  graphTransformMode,
  showTreeReport,
  currentDrawingMode,
  showFreeSearch,
  showSendChanges,
  showCreateScheme,
  schemeId,
  showCreateKAO,
  showCreateKNP,
  showAddNP,
  showSearchNP,
  showFilters,
  showFormalReport,
  showListGraphs,
  showCreateGraph,
  showChangeFilters,
  listErrors,
  dimensions,
  showHideNP,
  showAllScheme,
  isTestGraph,
  backToDossier,
  backToRAVP,
  graphOwner,
  scheme,
  setSchemeCallback,
  isFromScheme,
  ableEdit,
  isPeriod,
  SpecialOperationFilter,
  ResultNDFilter,
  GapsDebtFilter,
  LinksFilter,
  AccountabilityFilter,
  CoherenceFilter,
  CooperationFilter,
  CustomLinksFilter,
  TaxpayersFilter,
  GraphChangeFilter,
  TypologyFilter,
  isCollapsedDimensions,
  listErrorsType,
  undoCallback,
  isPreviousVersion,
  sourceSystem,
  backToITAIS,
  enableDossierButton,
  printSchemeCallback,
  menuCallback,
  onInputDamage,
  amountOfDamage,
  nodesReportCallback,
  showNodesReport,
}: BurgerMenuProps) => {
  const {
    modalStore: { openSearchListModal },
    searchListStore: { mainStatus },
  } = useStores();

  const isGraphLoaded = schemeId !== '0';

  const items = useMemo<any[]>(
    () => [
      {
        label: 'Поиск',
        iconComponent: () => <Icon name="searchMenu" size={25} theme={theme} />,
        hideInCollapsed: !showAllScheme,
        hideInExtended: true,
        hasActiveState: true,
        collapsedMenuExtendableComponent: () => <CollapsedSearchFiled onSearch={onSearch} />,
        separateNextWithLineCollapse: true,
      },
      {
        label: 'Разрядность',
        iconComponent: () => (
          <span className={styles.dimensions}>{dimensionsMap.get(dimensions)}</span>
        ),
        hideInCollapsed: false,
        hideInExtended: true,
        hasActiveState: true,
        collapsedMenuExtendableComponent: () => <CollapsedDimensions />,
        separateNextWithLineCollapse: true,
      },
      {
        label: 'Построить новую схему',
        parentLabel: 'Общая информация',
        iconComponent: () => <Icon name="buildScheme" size={27} theme={theme} />,
        hideInCollapsed: !(showFreeSearch && showCreateGraph && sourceSystem !== 'ITAIS'),
        callback: onBeginWork, // freeSearch,
        extendedClassName: styles.option,
        title: 'Построение новой схемы',
        isShow: showFreeSearch && showCreateGraph && sourceSystem !== 'ITAIS',
      },
      {
        label: 'Мои схемы',
        iconComponent: () => <Icon name="star" size={30} theme={theme} />,
        hideInCollapsed: !(showListGraphs && showCreateGraph),
        callback: onUserGraphsClick,
        title: 'Отображение перечня схем, переименование, удаление',
        isShow: showListGraphs && showCreateGraph,
      },
      {
        label: 'Основные фильтры',
        iconComponent: () => <Icon name="filters" size={28} theme={theme} />,
        isActive:
          AccountabilityFilter.keep ||
          CoherenceFilter.keep ||
          CooperationFilter.keep ||
          CustomLinksFilter.keep ||
          GraphChangeFilter.keep ||
          TypologyFilter.keep ||
          TaxpayersFilter.keep,
        hideInCollapsed: !(isGraphLoaded && showFilters && sourceSystem !== 'ITAIS'),
        callback: filtersCallback,
        title: 'Фильтрация узлов, связей и кластеров схемы по критериям',
        isShow: isGraphLoaded && showFilters && sourceSystem !== 'ITAIS',
        separateNextWithLine: schemeId === '0' || !schemeId,
        separateNextWithLineCollapse: schemeId === '0' || !schemeId,
      },
      {
        label: 'Вернуться в Досье проверки',
        iconComponent: () => <Icon name="back" size={27} theme={theme} />,
        hideInCollapsed: !(
          isGraphLoaded &&
          schemeId &&
          !isTestGraph &&
          sourceSystem !== 'MDAB' &&
          sourceSystem !== 'ITAIS'
        ),
        callback: backToDossier,
        title: 'Вернуться в Досье проверки',
        isShow:
          isGraphLoaded &&
          schemeId &&
          !isTestGraph &&
          sourceSystem !== 'MDAB' &&
          sourceSystem !== 'ITAIS',
        separateNextWithLine:
          schemeId && !isTestGraph && sourceSystem !== 'MDAB' && sourceSystem !== 'ITAIS',
        separateNextWithLineCollapse:
          schemeId && !isTestGraph && sourceSystem !== 'MDAB' && sourceSystem !== 'ITAIS',
      },
      {
        label: 'Вернуться в РАВП',
        iconComponent: () => <Icon name="returnRAVP" size={30} theme={theme} />,
        hideInCollapsed: !(isGraphLoaded && schemeId && !isTestGraph && sourceSystem === 'MDAB'),
        callback: backToRAVP,
        title: 'Вернуться в РАВП',
        isShow: isGraphLoaded && schemeId && !isTestGraph && sourceSystem === 'MDAB',
        separateNextWithLine: schemeId && !isTestGraph && sourceSystem === 'MDAB',
        separateNextWithLineCollapse: schemeId && !isTestGraph && sourceSystem === 'MDAB',
      },
      {
        label: 'Вернуться в ИСА КН',
        iconComponent: () => <Icon name="returnITAIS" size={40} theme={theme} />,
        hideInCollapsed: !(isGraphLoaded && schemeId && sourceSystem === 'ITAIS'),
        callback: backToITAIS,
        title: 'Вернуться в ИСА КН',
        isShow: isGraphLoaded && schemeId && sourceSystem === 'ITAIS',
        separateNextWithLine: schemeId && sourceSystem === 'ITAIS',
        separateNextWithLineCollapse: schemeId && sourceSystem === 'ITAIS',
      },
      {
        label: 'От продавца / покупателя',
        parentLabel: 'Схемы',
        iconComponent: () => <Icon name="buyersSellers" size={27} theme={theme} />,
        hideInCollapsed: !(isGraphLoaded && ableEdit),
        callback: transferCallback,
        title: 'Изменение направление построения схемы от продавца/ от покупателя',
        isShow: isGraphLoaded && ableEdit,
      },
      {
        label: 'Вертикальный режим',
        iconComponent: () => <Icon name="graph_view_vertical" size={27} theme={theme} />,
        isActive: direction === 90,
        hideInCollapsed: !(isGraphLoaded && ableEdit),
        hasActiveState: true,
        callback: verticalViewCallback,
        title: 'Изменение ориентации построения схемы на Вертикальный',
        isShow: isGraphLoaded && ableEdit,
      },
      {
        label: 'Горизонтальный режим',
        iconComponent: () => <Icon name="graph_view_horizontal" size={27} theme={theme} />,
        isActive: direction === 0,
        hasActiveState: true,
        hideInCollapsed: !(isGraphLoaded && ableEdit),
        callback: horizontalViewCallback,
        title: 'Изменение ориентации построения схемы Горизонтальный',
        isShow: isGraphLoaded && ableEdit,
      },
      {
        label: 'Индикаторы',
        iconComponent: () => <Icon name="indicatorsFilter" size={27} theme={theme} />,
        isActive:
          SpecialOperationFilter.keep ||
          ResultNDFilter.keep ||
          GapsDebtFilter.keep ||
          LinksFilter.keep,
        hideInCollapsed: !(isGraphLoaded && sourceSystem !== 'ITAIS'),
        extendedMenuExtendableComponent: () => <Indicators />,
        collapsedMenuExtendableComponent: () => <IndicatorsContainer />,
        callback: indicatorsCallback,
        title: 'Настройка отображения индикаторов',
        isShow: isGraphLoaded && sourceSystem !== 'ITAIS',
        idExtendedComponent: 'Indicators',
      },
      {
        label: 'Передать изменения',
        key: 'sendChanges',
        iconComponent: () => <Icon name="sendChanges" size={27} theme={theme} />,
        hideInCollapsed: !(
          isGraphLoaded &&
          schemeId &&
          showSendChanges &&
          ableEdit &&
          !isHideNode &&
          sourceSystem !== 'MDAB' &&
          sourceSystem !== 'ITAIS' &&
          enableDossierButton
        ),
        callback: sendChanges,
        title: listErrors?.length > 0 ? listErrors : 'Передать изменения',
        isShow:
          isGraphLoaded &&
          schemeId &&
          showSendChanges &&
          ableEdit &&
          !isHideNode &&
          sourceSystem !== 'MDAB' &&
          sourceSystem !== 'ITAIS' &&
          enableDossierButton,
        isDisabled: listErrors?.length > 0,
      },
      {
        label: 'Передать изменения',
        key: 'sendGraph',
        iconComponent: () => <Icon name="sendChanges" size={27} theme={theme} />,
        hideInCollapsed: !(
          isGraphLoaded &&
          !schemeId &&
          showCreateScheme &&
          (showCreateKAO || showCreateKNP) &&
          !isHideNode &&
          sourceSystem !== 'MDAB' &&
          sourceSystem !== 'ITAIS' &&
          enableDossierButton
        ),
        callback: sendGraph,
        title: listErrors?.length > 0 ? listErrors : 'Передать изменения',
        isShow:
          isGraphLoaded &&
          !schemeId &&
          showCreateScheme &&
          (showCreateKAO || showCreateKNP) &&
          sourceSystem !== 'MDAB' &&
          sourceSystem !== 'ITAIS',
        isDisabled: listErrors?.length > 0,
      },
      {
        label: 'Передать изменения в РАВП',
        iconComponent: () => <Icon name="sendChangesRAVP" size={30} theme={theme} />,
        hideInCollapsed: !(
          isGraphLoaded &&
          schemeId &&
          showSendChanges &&
          ableEdit &&
          !isHideNode &&
          sourceSystem === 'MDAB'
        ),
        callback: sendChangesMDAB,
        title: 'Передача изменений схемы в РАВП',
        isShow:
          isGraphLoaded &&
          schemeId &&
          showSendChanges &&
          ableEdit &&
          !isHideNode &&
          sourceSystem === 'MDAB',
      },
      {
        label: 'Режим показа скрытых узлов',
        iconComponent: () => <Icon name="no_hidden_nodes" size={27} theme={theme} />,
        activeIconComponent: () => <Icon name="show_hidden_nodes" size={27} theme={theme} />,
        isActive: isHideNode,
        hideInCollapsed: !(isGraphLoaded && showHideNP && ableEdit && sourceSystem !== 'ITAIS'),
        hasActiveState: true,
        callback: hiddenNodesCallback,
        title: 'Перерисовка схемы с учетом скрытых узлов и его связей',
        isShow: isGraphLoaded && showHideNP && ableEdit && sourceSystem !== 'ITAIS',
      },
      {
        label: 'Поиск узла на схеме',
        iconComponent: () => <Icon name="searchNode" size={27} theme={theme} />,
        hideInCollapsed: !(isGraphLoaded && showSearchNP),
        callback: searchNodeGraphCallback,
        title: 'Поиск узла на схеме по Наименованию или ИНН НП',
        isShow: isGraphLoaded && showSearchNP,
        separateNextWithLine: sourceSystem === 'ITAIS',
        separateNextWithLineCollapse: sourceSystem === 'ITAIS',
      },
      {
        label: 'Сумма потенциального ущерба',
        iconComponent: MoneyIcon,
        hideInCollapsed: !(isGraphLoaded && !schemeId && !isHideNode && sourceSystem !== 'ITAIS'),
        extendedMenuExtendableComponent: () => (
          <ExtendedDamage
            onInput={onInputDamage}
            id="ExtendedDamage"
            amountOfDamage={amountOfDamage}
          />
        ),
        collapsedMenuExtendableComponent: () => (
          <CollapsedDamage onInput={onInputDamage} amountOfDamage={amountOfDamage} />
        ),
        title:
          listErrorsType?.length === 0
            ? 'Ввод суммы потенциального ущерба'
            : 'Необходимо указать аналитические роли по всем налогоплательщикам',
        isShow: isGraphLoaded && !schemeId && !isHideNode && sourceSystem !== 'ITAIS',
        isDisabled: listErrorsType?.length > 0,
        separateNextWithLine: isGraphLoaded && !schemeId,
        separateNextWithLineCollapse: isGraphLoaded && !schemeId,
        idExtendedComponent: 'ExtendedDamage',
      },
      {
        label: 'Режим отображения схемы',
        iconComponent: () => <Icon name="scheme" size={27} theme={theme} />,
        isActive: scheme,
        hasActiveState: true,
        hideInCollapsed: !(
          isGraphLoaded &&
          ((graphOwner && schemeId) || isFromScheme) &&
          sourceSystem !== 'ITAIS'
        ),
        callback: setSchemeCallback,
        isShow:
          isGraphLoaded && ((graphOwner && schemeId) || isFromScheme) && sourceSystem !== 'ITAIS',
        title: scheme
          ? 'Режим отображения схемы (схема)'
          : 'Режим отображения схемы (текущее состояние)',
        separateNextWithLine:
          ((graphOwner && schemeId) || isFromScheme) && sourceSystem !== 'ITAIS',
        separateNextWithLineCollapse:
          ((graphOwner && schemeId) || isFromScheme) && sourceSystem !== 'ITAIS',
      },
      {
        label: 'Легенда',
        parentLabel: 'Справочная информация',
        iconComponent: () => <Icon name="legend" size={27} theme={theme} />,
        hideInCollapsed: false,
        callback: legendCallback,
        title: 'Отображение окна легенды',
        isShow: true,
      },
      {
        label: 'Справка к проекту акта',
        iconComponent: () => <Icon name="reference" size={27} theme={theme} />,
        hideInCollapsed: !(isGraphLoaded && showFormalReport && sourceSystem !== 'ITAIS'),
        callback: referenceCallback,
        title: 'Генерация справки к проекту акта',
        separateNextWithLine: !showTreeReport,
        isShow: isGraphLoaded && !isHideNode && showFormalReport && sourceSystem !== 'ITAIS',
      },
      {
        label: 'Отчет "Дерево связей"',
        iconComponent: () => <Icon name="printReport" size={27} theme={theme} />,
        hideInCollapsed: !(isGraphLoaded && showTreeReport),
        callback: treeReportCallback,
        title: 'Генерация табличной формы схемы Дерево связей',
        isShow: isGraphLoaded && !isHideNode && showTreeReport,
      },
      {
        label: 'Отчет "Выгрузка узлов"',
        iconComponent: FileDownloadIcon,
        hideInCollapsed: !(showNodesReport && sourceSystem !== 'ITAIS'),
        callback: nodesReportCallback,
        title: 'Отчет "Выгрузка узлов"',
        isShow: showNodesReport && sourceSystem !== 'ITAIS',
      },
      {
        label: 'Напечатать схему',
        iconComponent: PrinterIcon,
        hideInCollapsed: false,
        callback: printSchemeCallback,
        title: 'Генерация печатной формы схемы',
        isShow: isGraphLoaded && !isHideNode && showTreeReport,
      },
      {
        label: `Всплывающее окно узла`,
        iconComponent: () => <Icon name="settings" size={27} theme={theme} />,
        hideInCollapsed: !isGraphLoaded,
        callback: taxpayerSettingsCallback,
        title: 'Всплывающее окно узла',
        isShow: isGraphLoaded,
      },
      {
        label: `Поиск цепочек между НП`,
        iconComponent: () => <Icon name="chains" size={27} theme={theme} />,
        hideInCollapsed: false,
        callback: chainsCallback,
        title: 'Поиск цепочек между НП',
        isShow: true,
      },
      {
        label: `Очередь поиска`,
        iconComponent: mainStatus
          ? () => <div className={`${styles.status} ${styles[mainStatus.name]}`} />
          : null,
        callback: openSearchListModal,
        title: 'Очередь поиска',
        isShow: true,
      },
    ],
    [
      direction,
      userGraphsList,
      isHideNode,
      onSelectGraph,
      userGraphsEmpty,
      theme,
      showTransformMode,
      graphTransformMode,
      showTreeReport,
      currentDrawingMode,
      showFreeSearch,
      showSendChanges,
      showCreateScheme,
      schemeId,
      showCreateKAO,
      showCreateKNP,
      showAddNP,
      showSearchNP,
      showFilters,
      showFormalReport,
      showListGraphs,
      showCreateGraph,
      showChangeFilters,
      listErrors,
      dimensions,
      showHideNP,
      showAllScheme,
      isTestGraph,
      graphOwner,
      scheme,
      isFromScheme,
      ableEdit,
      isPeriod,
      SpecialOperationFilter,
      ResultNDFilter,
      GapsDebtFilter,
      LinksFilter,
      AccountabilityFilter,
      CoherenceFilter,
      CooperationFilter,
      CustomLinksFilter,
      GraphChangeFilter,
      TypologyFilter,
      isCollapsedDimensions,
      listErrorsType,
      enableDossierButton,
      amountOfDamage,
      mainStatus?.name,
    ],
  );

  return (
    <div className={`${styles.wrapper} ${extended && styles.extended}`} id="BurgerMenu">
      <Menu extended={extended} extendedMenuContainerClassName={styles.extendedMenu}>
        {extended && (
          <HeaderSearch
            onSearch={onSearch!}
            theme={theme}
            menuCallback={menuCallback}
            undoCallback={undoCallback}
            onSave={onSave}
            showAllScheme={showAllScheme}
            canUndo={
              isGraphLoaded &&
              showListGraphs &&
              showCreateGraph &&
              ableEdit &&
              isPreviousVersion &&
              !isHideNode
            }
            canSave={
              isGraphLoaded &&
              showListGraphs &&
              showCreateGraph &&
              ableEdit &&
              !isHideNode &&
              sourceSystem !== 'ITAIS'
            }
          />
        )}
        <List
          className={extended ? styles.extendedList : styles.collapsedWrapper}
          isExtend={extended}
        >
          {items.map(({ key, ...item }) => (
            <MenuItem
              key={'burger-menu-items-' + (key || item.label)}
              {...item}
              extendedParentLabelClassName={styles.extendedItemParentLabel}
              extendedLabelClassName={`${styles.extendedItemLabel} ${
                item.isActive ? styles.activeLabel : ''
              }`}
              extendedMenuExtendedContainerClassName={styles.extendedContainer}
              extendedClassName={styles.extendedItem}
              collapsedIconClassName={styles.collapsedItem}
              collapsedIconContainerClassName={styles.collapsedIconContainer}
              collapsedClassName={styles.collapsedItem}
            />
          ))}
        </List>
      </Menu>
    </div>
  );
};

BurgerMenu.defaultProps = {
  direction: 0,
  isHideNode: true,
  periodIsActive: false,
  freeSearch: () => {},
  onSearch: () => {},
  pickPeriodCallback: () => {},
  filtersCallback: () => {},
  transferCallback: () => {},
  addNodeCallback: () => {},
  verticalViewCallback: () => {},
  horizontalViewCallback: () => {},
  legendCallback: () => {},
  integralAssessmentCallback: () => {},
  hiddenNodesCallback: () => {},
  referenceCallback: () => {},
  searchNodeGraphCallback: () => {},
  menuCallback: () => {},
};

export default observer(BurgerMenu);
