import { TaxpayerType } from 'DataTypes';
import Search from 'components/Search';
import Spinner from 'components/Spinner/Spinner';
import { inject, observer } from 'mobx-react';
import React, { Component, RefObject, createRef } from 'react';
import CommonStore from 'stores/common';
import ApiServices from '../../../../utils/services/api/ApiServices';
import SearchResult from '../SearchResult';
import styles from './SearchNp.module.scss';

interface IProps {
  onSelectNp: (np: any) => void;
  commonStore?: CommonStore;
  onOpenFilter?: () => void;
  filterIsOpen?: boolean;
  filterIsFill?: boolean;
}

interface IState {
  searchValue: string;
  listIsOpen: boolean;
}

@inject('commonStore')
@observer
export class SearchNp extends Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);

    this.state = { searchValue: '', listIsOpen: false };
    this.innerRef = createRef();
  }
  innerRef: RefObject<HTMLDivElement>;

  /**
   * Метод изменяет значение поля поиска
   */
  handleChangeSearchValue = (searchValue: string): void => {
    this.setState({ searchValue });

    if (searchValue.length === 0) {
      const { commonStore } = this.props;
      commonStore?.resetNodesOrder();
    }
  };

  /**
   * Метод отправляет запрос на поиск
   */
  handleSearch = (): void => {
    const {
      state: { searchValue },
      props: { commonStore },
    } = this;

    commonStore?.resetNodesOrder();

    if (commonStore?.nodes.pending) {
      ApiServices.source.cancel();
      commonStore?.nodesPendingFalse();
    }

    if (searchValue) {
      commonStore?.searchNodesByText(searchValue);
      commonStore?.nodesPendingTrue();
    }
  };

  /**
   * Метод очищает поле поиска
   */
  handleResetSearchValue = (): void => {
    this.setState({ searchValue: '' });

    const { commonStore } = this.props;

    if (!commonStore?.nodes.pending) {
      this.setState({ searchValue: '' });
      commonStore?.resetNodesOrder();
    }
  };

  /**
   * Метод очищает поле поиска
   */
  handleSelectNp = (np: TaxpayerType) => {
    const { commonStore } = this.props;

    if (commonStore?.nodes.pending) {
      ApiServices.source.cancel();
      commonStore.nodesPendingFalse();
    }
    this.props.onSelectNp(np);
  };

  render() {
    const { searchValue } = this.state;
    const { commonStore, onOpenFilter, filterIsOpen, filterIsFill } = this.props;
    const { error, pending, noResults, order } = commonStore?.nodes || {};

    return (
      <div className={styles.container} ref={this.innerRef}>
        <Search
          wrapperStyle={styles.search}
          value={searchValue}
          onChange={this.handleChangeSearchValue}
          onSearch={this.handleSearch}
          autoSearchTimeout={700}
          placeholder={'Введите ИНН или наименования налогоплательщика для поиска'}
          autoFocus
          withReset
          onReset={this.handleResetSearchValue}
          withButton
          theme={commonStore?.appTheme}
          withFilter
          onOpenFilter={onOpenFilter}
          isLarge
          filterIsFill={filterIsFill}
          filterIsOpen={filterIsOpen}
        />
        {(error || pending || noResults || !!order?.length) && !filterIsOpen && (
          <>
            {error ? (
              <div className={styles.error}>
                <span className={styles.errorText}>
                  {typeof error === 'string' ? error : error?.message}
                </span>
              </div>
            ) : pending ? (
              <Spinner className={styles.spinner} text="Поиск результатов..." />
            ) : (
              <SearchResult onSelectNp={this.handleSelectNp} items={order as TaxpayerType[]} />
            )}
          </>
        )}
      </div>
    );
  }
}
