import React, { useState } from 'react';
import { ILinkProps } from './Link.props';

import { ReactComponent as ArrowIcon } from 'assets/icons/arrow.svg';
import { ReactComponent as LinkIcon } from 'assets/icons/link.svg';

import styles from './Link.module.scss';

const Link = ({ className, link, number, ...props }: ILinkProps) => {
  const [opened, setOpened] = useState<boolean>(true);

  const date = String(-link.year_quarter).split('');
  const quarter = date.pop() || '';
  const year = date.join('');

  return (
    <div className={`${className} ${styles.link}`} {...props}>
      <div className={styles.body}>
        <div className={styles.date}>
          {quarter} кв. {year}
        </div>

        <div className={styles.taxpayers}>
          <div className={`${styles.taxpayer} ${styles.seller}`}>
            <div className={`${styles.line} ${styles.line__top}`}></div>
            <div className={`${styles.line} ${styles.line__left}`}></div>
            <div className={`${styles.line} ${styles.line__bottom}`}>
              <div className={styles.triangle}></div>
            </div>

            <span className={styles.inn}>{link.seller_inn}</span>
            <span className={styles.name}>{link.seller_name}</span>
          </div>

          <div className={`${styles.taxpayer} ${styles.buyer}`}>
            <span className={styles.inn}>{link.buyer_inn}</span>
            <span className={styles.name}>{link.buyer_name}</span>
          </div>
        </div>

        <div className={styles.params}>
          <div
            className={`${styles.params__header} ${opened && styles.opened}`}
            onClick={() => setOpened(!opened)}
          >
            <div className={styles.params__arrow}>
              <ArrowIcon />
            </div>
            Параметры связи
          </div>

          {opened && (
            <div className={styles.params__list}>
              {(link.vat_calculated_total || link.vat_calculated_total === 0) && (
                <div className={styles.params__item}>
                  <span className={styles.params__label}>Сумма НДС, исчисленная, ₽:</span>
                  <span>{link.vat_calculated_total.toLocaleString('ru-RU')}</span>
                  <span>
                    ({Math.round((link.vat_calculated_total / link.vatCalculatedSumm) * 100)}%)
                  </span>
                </div>
              )}

              {(link.vat_deduction_total || link.vat_deduction_total === 0) && (
                <div className={styles.params__item}>
                  <span className={styles.params__label}>Сумма НДС, к вычету, ₽:</span>
                  <span>{link.vat_deduction_total.toLocaleString('ru-RU')}</span>
                  <span>
                    ({Math.round((link.vat_deduction_total / link.vatDeductionSumm) * 100)}%)
                  </span>
                </div>
              )}

              {(link.credit_sum || link.credit_sum === 0) && (
                <div className={styles.params__item}>
                  <span className={styles.params__label}>Сумма прихода, ₽:</span>
                  {link.credit_sum.toLocaleString('ru-RU')}
                  <span>({Math.round((link.credit_sum / link.creditSumm) * 100)}%)</span>
                </div>
              )}

              {(link.debit_sum || link.debit_sum === 0) && (
                <div className={styles.params__item}>
                  <span className={styles.params__label}>Сумма расхода, ₽:</span>
                  {link.debit_sum.toLocaleString('ru-RU')}
                  <span>({Math.round((link.debit_sum / link.debitSumm) * 100)}%)</span>
                </div>
              )}

              {(link.goods_sum || link.goods_sum === 0) && (
                <div className={styles.params__item}>
                  <span className={styles.params__label}>Сумма, ₽:</span>
                  {link.goods_sum.toLocaleString('ru-RU')}
                  <span>({Math.round((link.goods_sum / link.goodsSumm) * 100)}%)</span>
                </div>
              )}

              {(link.contract_sum || link.contract_sum === 0) && (
                <div className={styles.params__item}>
                  <span className={styles.params__label}>Сумма контракта, ₽:</span>
                  {link.contract_sum.toLocaleString('ru-RU')}
                  <span>({Math.round((link.contract_sum / link.contractSumm) * 100)}%)</span>
                </div>
              )}
            </div>
          )}
        </div>
      </div>

      <div className={styles.icon}>
        {number}
        <LinkIcon />
      </div>
    </div>
  );
};

export default Link;
