import React, { PureComponent } from 'react';
import { gapsDebtPropTypes } from './GapsDebt.props';
import styles from './GapsDebt.module.scss';
import filterStyles from '../common/Filter.module.scss';

import Icon from '../../../components/Icon';

import Option from '../Option';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'stores/common/commonStore';

import { ReactComponent as Debt13 } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/debt_one_third.svg';
import { ReactComponent as Debt13Dark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/debt_one_thirdDark.svg';
import { ReactComponent as Debt23 } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/debt_two_third.svg';
import { ReactComponent as Debt23Dark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/debt_two_thirdDark.svg';
import { ReactComponent as Debt100 } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/debt_full.svg';
import { ReactComponent as Debt100Dark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/debt_fullDark.svg';
import { ReactComponent as VatGap } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/vat_gap.svg';
import { ReactComponent as VatGapDark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/vat_gapDark.svg';
import { ReactComponent as TaxGap } from '../assets/TG.svg';
import { GraphStoreType } from '../../../stores/graph/graphStore';

interface GapsDebtProps {
  isActive: boolean;
  onToggleCheck: () => void;
  isOpen?: boolean;
  toggleOpenState?: () => void;
  search?: any;

  gapsDebtFilter?: any;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;

  isOpenSearch?: boolean;
}

/**
 * Компонент блока Расхождение и задолженность для фильтра Индикаторов
 */
@inject('gapsDebtFilter', 'commonStore', 'graphStore')
@observer
class GapsDebt extends PureComponent<GapsDebtProps> {
  static propTypes = gapsDebtPropTypes;

  render() {
    const {
      isOpen,
      toggleOpenState,
      gapsDebtFilter,
      commonStore,
      search,
      graphStore,
      isOpenSearch,
    } = this.props;

    return (
      <li className={`${filterStyles.wrapper} ${styles.wrapper}`}>
        <div className={filterStyles.header}>
          <span className={filterStyles.headerLabel} onClick={toggleOpenState}>
            Расхождение и задолженность
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {(isOpen || isOpenSearch) && (
          <div className={styles.container}>
            {'1/3 от суммы задолженности'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.sectionOption}
                  isActive={gapsDebtFilter.options?.Debt13}
                  onToggleCheck={() => gapsDebtFilter.toggleIndicator('Debt13')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          gapsDebtFilter.options?.Debt13 ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'Debt13' in graphStore?.indicatorsCount ||
                        'Krsb_Indicator13' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount.Debt13 ??
                            0 + graphStore?.indicatorsCount.Krsb_Indicator13 ??
                            0
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <Debt13 className={styles.icon} />
                      ) : (
                        <Debt13Dark className={styles.icon} />
                      )}
                      <span>1/3 от суммы задолженности</span>
                    </div>
                  }
                />
              </div>
            )}
            {'2/3 от суммы задолженности'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.sectionOption}
                  isActive={gapsDebtFilter.options?.Debt23}
                  onToggleCheck={() => gapsDebtFilter.toggleIndicator('Debt23')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          gapsDebtFilter.options?.Debt23 ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'Debt23' in graphStore?.indicatorsCount ||
                        'Krsb_Indicator23' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount.Debt23 ??
                            0 + graphStore?.indicatorsCount.Krsb_Indicator23 ??
                            0
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <Debt23 className={styles.icon} />
                      ) : (
                        <Debt23Dark className={styles.icon} />
                      )}
                      <span>2/3 от суммы задолженности</span>
                    </div>
                  }
                />
              </div>
            )}
            {'100% задолженности'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.sectionOption}
                  isActive={gapsDebtFilter.options?.Debt100}
                  onToggleCheck={() => gapsDebtFilter.toggleIndicator('Debt100')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          gapsDebtFilter.options?.Debt100 ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'Debt100' in graphStore?.indicatorsCount ||
                        'Krsb_Indicator100' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount.Debt100 ??
                            0 + graphStore?.indicatorsCount.Krsb_Indicator100 ??
                            0
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <Debt100 className={styles.icon} />
                      ) : (
                        <Debt100Dark className={styles.icon} />
                      )}
                      <span>100% задолженности</span>
                    </div>
                  }
                />
              </div>
            )}
            {'Разрыв по НДС'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.sectionOption}
                  isActive={gapsDebtFilter.options?.Break_NDS}
                  onToggleCheck={() => gapsDebtFilter.toggleIndicator('Break_NDS')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          gapsDebtFilter.options?.Break_NDS ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'Break_NDS' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount['Break_NDS']
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <VatGap className={styles.icon} />
                      ) : (
                        <VatGapDark className={styles.icon} />
                      )}
                      <span>Разрыв по НДС</span>
                    </div>
                  }
                />
              </div>
            )}
            {'Доля разрыва'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.sectionOption}
                  isActive={gapsDebtFilter.options?.tax_gap}
                  onToggleCheck={() => gapsDebtFilter.toggleIndicator('tax_gap')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          gapsDebtFilter.options?.tax_gap ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'tax_gap' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount['tax_gap']
                          : 0}
                        )
                      </span>
                      <TaxGap className={styles.icon} />
                      <span>Доля разрыва</span>
                    </div>
                  }
                />
              </div>
            )}
          </div>
        )}
      </li>
    );
  }
}

export default GapsDebt;
