import React, { PureComponent } from 'react';
import { typologyPropTypes } from './Taxpayers.props';
import filterStyles from '../common/Filter.module.scss';
import styles from './Taxpayers.module.scss';

import Icon from '../../../components/Icon';
import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import MoneySpreadPicker from '../../../components/MoneySpreadPicker';

import Dropdown from '../../../components/Dropdown';

interface TaxpayersProps {
  isActive: boolean;
  onToggleCheck: () => void;
  toggleOpenState?: () => void;
  taxpayersFilter?: any;
  isOpen?: boolean;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент блока пользовательских связей для фильтров
 */
@inject('taxpayersFilter', 'commonStore', 'graphStore')
@observer
class Taxpayers extends PureComponent<TaxpayersProps> {
  static propTypes = typologyPropTypes;

  render() {
    const { isOpen, toggleOpenState, taxpayersFilter, commonStore, graphStore } = this.props;

    return (
      <li className={`${filterStyles.wrapper} ${styles.wrapper}`}>
        <div className={filterStyles.header}>
          <span className={filterStyles.headerLabel} onClick={toggleOpenState}>
            Налогоплательщики
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {isOpen && (
          <div className={styles.container}>
            <Dropdown
              dropdownStyle={styles.dropdown}
              labelStyle={styles.dropdownLabel}
              containerStyle={styles.dropdownContainer}
              valueStyle={styles.dropdownValue}
              itemsArray={taxpayersFilter.integralsArray}
              onSelect={taxpayersFilter.changeIntegrals}
              onSearch={(search) => taxpayersFilter.onSearch(search, 'integrals')}
              searchValue={taxpayersFilter.searchValue.integrals}
              onChangeOptionsVisible={(value?: boolean) =>
                taxpayersFilter.onChangeOptionsVisible('integrals', value)
              }
              showOnlySelected={taxpayersFilter.showOnlySelected.integrals}
              checked={taxpayersFilter.options.integrals}
              label={'Уровень риска АСК НДС-2'}
              theme={commonStore?.appTheme}
              isAll
              isCheckbox
              isCheckedSum
              isNewStyle
            />
            <Dropdown
              dropdownStyle={styles.dropdown}
              labelStyle={styles.dropdownLabel}
              containerStyle={styles.dropdownContainer}
              valueStyle={styles.dropdownValue}
              itemsArray={taxpayersFilter.analyticalRolesArray}
              onSelect={taxpayersFilter.changeAnalyticalRoles}
              onSearch={(search) => taxpayersFilter.onSearch(search, 'analyticalRoles')}
              searchValue={taxpayersFilter.searchValue.analyticalRoles}
              onChangeOptionsVisible={(value?: boolean) =>
                taxpayersFilter.onChangeOptionsVisible('analyticalRoles', value)
              }
              showOnlySelected={taxpayersFilter.showOnlySelected.analyticalRoles}
              checked={taxpayersFilter.options.analytical_roles}
              label={'Аналитическая роль'}
              theme={commonStore?.appTheme}
              isAll
              isCheckbox
              isCheckedSum
              isNewStyle
            />
            <Dropdown
              dropdownStyle={styles.dropdown}
              labelStyle={styles.dropdownLabel}
              containerStyle={styles.dropdownContainer}
              valueStyle={styles.dropdownValue}
              itemsArray={taxpayersFilter.businessRolesArray}
              onSelect={taxpayersFilter.changeBusinessRoles}
              onSearch={(search) => taxpayersFilter.onSearch(search, 'businessRoles')}
              searchValue={taxpayersFilter.searchValue.businessRoles}
              onChangeOptionsVisible={(value?: boolean) =>
                taxpayersFilter.onChangeOptionsVisible('businessRoles', value)
              }
              showOnlySelected={taxpayersFilter.showOnlySelected.businessRoles}
              checked={taxpayersFilter.options.business_roles}
              label={'Бизнес-роль'}
              theme={commonStore?.appTheme}
              isAll
              isCheckbox
              isCheckedSum
              isNewStyle
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={taxpayersFilter.options.ook_from ? Number(taxpayersFilter.options.ook_from)?.toLocaleString('ru') : '0'}
              toValue={
                taxpayersFilter.options.ook_to
                  ? Number(taxpayersFilter.options.ook_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={taxpayersFilter.changeOOKFrom}
              onChangeToValue={taxpayersFilter.changeOOKTo}
              label={'Сумма ООК, ₽'}
              title={'Диапазон сумм ООК'}
              disabled={!graphStore?.showChangeFilters}
              inputStyle={styles.input}
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(taxpayersFilter.options.tax_gap_from)?.toLocaleString('ru')}
              toValue={
                taxpayersFilter.options.tax_gap_to
                  ? Number(taxpayersFilter.options.tax_gap_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={taxpayersFilter.changeTaxGapFrom}
              onChangeToValue={taxpayersFilter.changeTaxGapTo}
              label={'TG, %'}
              title={'Диапазон доли вычета с несформированным источником'}
              disabled={!graphStore?.showChangeFilters}
              isPercent
              isSmall
            />
          </div>
        )}
      </li>
    );
  }
}

export default Taxpayers;
