import React, { PureComponent } from 'react';
import styles from './ToolTipVirtualGroup.module.scss';

import { inject, observer } from 'mobx-react';
import { GraphStoreType } from '../../stores/graph/graphStore';

type ToolTipVirtualGroupProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
};

type ToolTipVirtualGroupState = {
  graphID: any;
};

/**
 * Компонент всплывающей подсказки по наименованию НП
 */
@inject('graphStore')
@observer
class ToolTipVirtualGroup extends PureComponent<
  ToolTipVirtualGroupProps,
  ToolTipVirtualGroupState
> {
  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { graphStore, id } = this.props;

    return (
      <>
        <div className={styles.container} id={id}>
          <div className={styles.data_container}>
            Количество НП в виртуальной группе: {graphStore?.countVirtualGroup}. Показывается{' '}
            {graphStore?.realCountVirtualGroup} по техническим ограничениям.
          </div>
        </div>
      </>
    );
  }
}

export default ToolTipVirtualGroup;
