import React, { PureComponent } from 'react';
import styles from './ToolTipAnalytic.module.scss';

import { inject, observer } from 'mobx-react';
import { GraphStoreType } from '../../stores/graph/graphStore';

type ToolTipAnalyticProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
};

type ToolTipAnalyticState = {
  graphID: any;
};

/**
 * Компонент всплывающей подсказки по аналитической роли
 */
@inject('graphStore')
@observer
class ToolTipAnalytic extends PureComponent<ToolTipAnalyticProps, ToolTipAnalyticState> {
  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { graphStore, id } = this.props;

    return (
      <>
        <div className={graphStore?.analyticName ? styles.container : styles.hidden} id={id}>
          <div className={styles.data_container}>{graphStore?.analyticName}</div>
        </div>
      </>
    );
  }
}

export default ToolTipAnalytic;
