import { observer } from 'mobx-react';
import React from 'react';
import { useStores } from '../../../../hooks';
import styles from './SearchResult.module.scss';
import { ISearchResultProps } from './SearchResult.props';

const SearchResult = observer(
  ({
    className,
    items = [],
    count,
    warning = '',
    onSelectNp,
    onSearchReset,
    ...props
  }: ISearchResultProps) => {
    const {
      beginningWorkStore: { selectedNp, isEqualNp },
    } = useStores();

    return (
      <div className={`${className} ${styles.result}`} {...props}>
        {onSearchReset && (
          <>
            <div className={styles.bar}>
              <div className={styles.total}>
                Результаты поиска: {items.length} / {count}
                <span className={styles.warning}>{warning}</span>
              </div>

              <div className={styles.reset} onClick={onSearchReset}>
                Сбросить поиск
              </div>
            </div>
            <p className={styles.tip}>
              Результаты поиска удаляются через 1 день после завершения поиска
            </p>
          </>
        )}
        <div className={styles.table}>
          <div className={styles.header}>
            <div>ИНН</div>
            <div>КПП</div>
            <div>Наименование</div>
            <div>Период</div>
            <div>Код НО</div>
          </div>
          <div className={styles.list}>
            {items.length === 0 ? (
              <div className={styles.noresult}>Поиск не дал результатов</div>
            ) : (
              items.map((item) => {
                return (
                  <div
                    key={item.inn}
                    className={`${styles.item} ${isEqualNp(selectedNp, item) && styles.active} ${
                      !onSelectNp && styles.disabled
                    }`}
                    onClick={() => onSelectNp && onSelectNp(item)}
                  >
                    <div>{item.inn || '-'}</div>
                    <div>{item.kpp || '-'}</div>
                    <div>{item.name || '-'}</div>
                    <div>{item.quarter ? `${item.quarter} кв. ${item.year}` : '-'}</div>
                    <div>{item.ufns_code || '-'}</div>
                  </div>
                );
              })
            )}
          </div>
        </div>
      </div>
    );
  },
);

export default SearchResult;
