import { ContextMenuTool } from 'gojs';

import { Context } from './ContextMenu';
import { DiagramStoreType } from '../../../stores/graph/diagramStore';
import { GraphStoreType } from '../../../stores/graph/graphStore';

/**
 * Реализация контекстное меню узла
 */
class Cluster implements Context {
  DiagramStore: DiagramStoreType;
  GraphStore: GraphStoreType;

  // ID узла в HTML
  nodeId: string;

  constructor(DiagramStore: DiagramStoreType, nodeId: string, GraphStore: GraphStoreType) {
    this.DiagramStore = DiagramStore;
    this.nodeId = nodeId;
    this.GraphStore = GraphStore;
  }

  /**
   * Метод по отмене предыдущего действия (закрытие контекст меню)
   */
  hideCX = () => {
    if (this.DiagramStore?.diagram?.currentTool instanceof ContextMenuTool) {
      this.DiagramStore?.diagram.currentTool.doCancel();
    }
  };

  /**
   * Метод открытия контекст меню
   */
  showContextMenu = (obj: any, diagram: any, tool: any) => {
    const cxElement: HTMLElement | null = document.getElementById(this.nodeId);
    let hasMenuItem = false;
    const objectsAt = diagram.findObjectsAt(diagram.lastInput.documentPoint);
    let clusterArray: any = [];
    objectsAt.each((element: any) => {
      if (element.part.data.category === 'Cluster') {
        clusterArray.push(element.part.data);
      }
    });
    const resultArray = clusterArray.filter(
      (item: any, index: any, arrayCluster: any) =>
        arrayCluster.findIndex((elem: any) => elem.key === item.key) === index,
    );
    this.DiagramStore.setClusterInfo(resultArray);

    function maybeShowItem(elt: any) {
      if ((elt.style.display = 'none')) {
        elt.style.display = 'block';
        hasMenuItem = true;
      }
    }
    maybeShowItem(document.getElementById('detailCluster'));

    const showCluster = this.GraphStore.showCluster;
    const showLinksCluster = this.GraphStore.showLinksCluster;

    if (hasMenuItem && showCluster && showLinksCluster) {
      cxElement?.classList.add('show-menu');
      const mousePt = diagram.lastInput.viewPoint;
      const leftCoordinate = mousePt.x + 7 + 'px';
      const topCoordinate = mousePt.y + 60 + 'px';
      cxElement?.setAttribute('style', 'left:' + leftCoordinate + ';top:' + topCoordinate);
    }
    window.addEventListener('click', this.hideCX, true);
  };

  /**
   * Метод закрытия контекст меню
   */
  hideContextMenu = () => {
    const cxElement = document.getElementById(this.nodeId);
    cxElement?.classList.remove('show-menu');
    window.removeEventListener('click', this.hideCX, true);
  };
}

export default Cluster;
