import { CommonStoreType, DiagramStoreType, GraphStoreType, ZoomStoreType } from 'StoreTypes';
import Diagram from 'components/Diagram';
import { inject, observer } from 'mobx-react';
import React from 'react';
import FilterGraphCluster from '../../components/FilterGraphCluster';
import ClusterStore from '../../stores/graph/graphStore/clusterStore';
import './DiagramContainer.scss';
import DiagramMap from './DiagramMap';

interface DiagramContainerStoresProps {
  /** Хранилище диаграммы */
  diagramStore?: DiagramStoreType;

  /** Хранилище графа */
  graphStore?: GraphStoreType;

  /** Хранилище масштаба */
  zoomStore?: ZoomStoreType;

  /** Хранилище общей информации для приложения */
  commonStore?: CommonStoreType;

  clusterStore?: ClusterStore;
}

interface DiagramContainerProps extends DiagramContainerStoresProps {}

@inject('diagramStore', 'graphStore', 'zoomStore', 'commonStore', 'clusterStore')
@observer
export default class DiagramContainer extends React.PureComponent<DiagramContainerProps> {
  render() {
    const { diagram, initDiagram } = this.props.diagramStore!;
    const { appVersion } = this.props.commonStore!;
    const { zoom, setZoom, initOverview } = this.props.zoomStore!;
    const { pending, isLoadingDiagram, currentGraph } = this.props.graphStore!;

    const { isFilterCluster } = this.props.clusterStore!;

    return (
      <>
        <Diagram
          diagram={diagram || {}}
          isGraphLoading={pending}
          stateLoading={isLoadingDiagram}
          isDictsLoading={this.props.commonStore!.pending}
          initDiagram={initDiagram}
          nodeDataArray={currentGraph.nodes}
          linkDataArray={currentGraph.links}
        />
        {diagram && (
          <DiagramMap
            restricted
            zoom={zoom}
            setZoom={setZoom}
            diagram={diagram}
            initOverview={initOverview}
            wrapperClassName="diagram-controls"
            toggleMapClassName="diagram-controls__toggle_open_icon"
            closedMapClassName="diagram-controls__toggle_closed_icon"
            mapContainerClassName="diagram-controls__map__container"
            mapClassName="diagram-controls__map"
            zoomContainerClassName="diagram-controls__zoom__container"
            zoomIconClassName="diagram-controls__zoom__icon"
          />
        )}
        <div className="app-versions">
          {appVersion.ppvsaVersion && <div>Версия ППВСА: {appVersion.ppvsaVersion}</div>}
          {appVersion.webVersion && <div>Версия Web: {appVersion.webVersion}</div>}
          {appVersion.matrixVersion && <div>Версия матрицы: {appVersion.matrixVersion}</div>}
        </div>
        {isFilterCluster && <FilterGraphCluster />}
      </>
    );
  }
}
