import { computed, observable, autorun, action } from 'mobx';
import Filter from '../utils/Filter';

import { FilterStoreType } from './filterStore';
import { GraphStoreType } from '../graph/graphStore';

/**
 * Хранилище фильтров связей, выявленных типологией
 */
class TypologyFilter extends Filter {
  FilterStore: FilterStoreType;
  GraphStore: GraphStoreType;

  constructor(FilterStore: FilterStoreType, GraphStore: GraphStoreType) {
    super();
    this.FilterStore = FilterStore;
    this.GraphStore = GraphStore;

    // функция срабатывает, когда изменяется одна из переданных зависимостей,
    // в данным случае this.FilterStore.options.
    autorun(() => {
      const {
        is_from_typology,
        is_control_from_typology,
        is_coherence_from_typology,
        is_cooperation_from_typology,
        is_dependence_from_typology,
      } = this.FilterStore.options;
      this.updateOptions(
        {
          is_from_typology,
          is_control_from_typology,
          is_coherence_from_typology,
          is_cooperation_from_typology,
          is_dependence_from_typology,
        },
        'is_from_typology',
      );
      this.checkAllTrue();
    });
  }

  @observable keep = false;

  @observable isOptionsLevelOneFalse: boolean = false;
  @observable isAllFalse: boolean = false;
  @observable isAllTrue: boolean = false;

  // опции фильтра
  initialOptions = {
    /** Подконтрольность из Типологии */
    is_control_from_typology: false,
    /** Согласованность из Типологии */
    is_coherence_from_typology: false,
    /** Кооперация из Типологии */
    is_cooperation_from_typology: false,
    /** Взаимозависимость в схеме */
    is_dependence_from_typology: false,
  };

  @action checkLevelOneOptionsFalse = () => {
    this.isOptionsLevelOneFalse = false;
    if (
      !this.options['is_coherence_from_typology'] &&
      !this.options['is_control_from_typology'] &&
      !this.options['is_cooperation_from_typology'] &&
      !this.options['is_dependence_from_typology']
    ) {
      this.isOptionsLevelOneFalse = true;
    }
  };

  @action checkAllFalse = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_from_typology') {
        othersOptions[item] = this.options[item];
      } else {
        othersOptions[item] = false;
      }
    });
    this.isAllFalse = Object.keys(othersOptions).every(
      (item: any) => othersOptions[item] === false,
    );
  };

  @action checkAllTrue = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    delete othersOptions['is_from_typology'];
    this.isAllTrue = Object.keys(othersOptions).every((item: any) => othersOptions[item] === true);
  };

  @action resetFilters = (): void => {
    this.reset();
    this.setOptionsToFalseExpandType();
  };

  /** Метод переключает состояние фильтра (Общий) */
  @action toggleFilter = () => {
    this.keep = !this.keep;
    if (this.keep === false) {
      this.resetFilters();
    } else {
      this.setOptionsToTrue();
    }
    this.isChangeStatic();
    this.checkAllTrue();
    this.FilterStore.checkChanges();
  };

  @action setOptionsToFalseExpandType = () => {
    const { options } = this;
    for (const key in options) {
      if (typeof options[key] === 'boolean') {
        if (
          this.GraphStore.showNDS &&
          this.GraphStore.graphExpandType === 'Nds' &&
          this.GraphStore.schemeId
        ) {
          options['is_cooperation_from_typology'] = true;
          this.keep = true;
        } else {
          this.keep = false;
        }
      }
    }
  };

  /** Метод переключает состояние фильтра (Подконтрольность) */
  @action toggleFilterControl = () => {
    this.options.is_control_from_typology = !this.options.is_control_from_typology;
    this.checkLevelOneOptionsFalse();
    this.keepFilter(this.isOptionsLevelOneFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /** Метод переключает состояние фильтра (Согласованность) */
  @action toggleFilterCoherence = () => {
    this.options.is_coherence_from_typology = !this.options.is_coherence_from_typology;
    this.checkLevelOneOptionsFalse();
    this.keepFilter(this.isOptionsLevelOneFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  // Переключение опции "Кооперация в схеме"
  @action.bound
  toggleCooperationNdsFromTypology = () => {
    this.changeOption('is_cooperation_from_typology', !this.options.is_cooperation_from_typology);
    this.checkLevelOneOptionsFalse();
    this.keepFilter(this.isOptionsLevelOneFalse);
    this.isChangeStatic();
    this.checkAllTrue();
    this.FilterStore.checkChanges();
  };

  // Переключение опции "Взаимозависимость в схеме"
  @action.bound
  toggleDependenceFromTypology = () => {
    this.changeOption('is_dependence_from_typology', !this.options.is_dependence_from_typology);
    this.checkLevelOneOptionsFalse();
    this.keepFilter(this.isOptionsLevelOneFalse);
    this.isChangeStatic();
    this.checkAllTrue();
    this.FilterStore.checkChanges();
  };

  // Изменение опций в "Согласованность"
  @action.bound
  toggleCoherenceCommon = (option: string) => {
    this.changeOption(option, !this.options[option]);
    this.checkAllFalse();
    this.checkAllTrue();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  // Изменение опций в "Подконтрольность"
  @action.bound
  toggleControlCommon = (option: string) => {
    this.changeOption(option, !this.options[option]);
    this.checkAllFalse();
    this.checkAllTrue();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  // Происходили ли изменения среди фильтров
  @action isChangeStatic = () => {
    this.FilterStore.isTypologyFilter = false;
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_from_typology') {
        if (
          this.options[item] !=
          (this.FilterStore.options[item] == undefined ? '' : this.FilterStore.options[item])
        ) {
          this.FilterStore.isTypologyFilter = true;
        }
      }
    });
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
      is_from_typology: this.keep,
    };
  }
}

export default TypologyFilter;
