import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import React from 'react';
import Draggable from 'react-draggable';
import Icon from '../Icon';
import styles from './Legend.module.scss';
import './Legend.scss';
import Group from './components/Group';

const Legend = observer(() => {
  const {
    commonStore: { legendInfo, appTheme },
    uiStore: { toggleLegendState },
  } = useStores();

  return (
    <Draggable handle=".legendDrag" axis="x" bounds={{ left: 0 }}>
      <div className={styles.wrapper}>
        <div className={`${styles.header} legendDrag`}>
          <span className={styles.title}>Легенда</span>
          <Icon
            className={styles.close}
            name="close"
            onClick={toggleLegendState}
            theme={appTheme}
          />
        </div>
        <div className={styles.body}>
          {legendInfo.map((item: any, i: number) => (
            <Group key={'legend' + item.name} group={item} number={i} />
          ))}
        </div>
      </div>
    </Draggable>
  );
});

export default Legend;
