import React, { PureComponent } from 'react';
import { accountabilityPropTypes } from './Accountability.props';
import styles from './Accountability.module.scss';
import filterStyles from '../common/Filter.module.scss';

import Icon from '../../../components/Icon';

import Option from '../Option';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import { FilterStoreType } from '../../../stores/filters/filterStore';

interface AccountabilityProps {
  isActive: boolean;
  onToggleCheck: () => void;
  accountabilityFilter?: any;
  isOpen?: boolean;
  toggleOpenState?: () => void;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
  filterStore?: FilterStoreType;
}

/**
 * Компонент блока подконтрольности для фильтров
 */
@inject('accountabilityFilter', 'commonStore', 'graphStore', 'filterStore')
@observer
class Accountability extends PureComponent<AccountabilityProps> {
  static propTypes = accountabilityPropTypes;

  render() {
    const { isOpen, toggleOpenState, accountabilityFilter, commonStore, graphStore, filterStore } =
      this.props;

    return (
      <li className={`${styles.wrapper} ${filterStyles.wrapper}`}>
        <div className={filterStyles.header}>
          <span className={filterStyles.headerLabel} onClick={toggleOpenState}>
            Подконтрольность
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {isOpen && (
          <ul className={styles.container}>
            {filterStore?.controlDynamicOptions?.length > 0 && (
              <>
                {filterStore?.controlDynamicOptions.map((item: any) => (
                  <li
                    className={`${styles.optionContainer} 
                       ${'is_disabled' in item && item.is_disabled ? styles.disabled : ''}`}
                  >
                    <Option
                      className={styles.option}
                      isActive={item.is_checked}
                      onToggleCheck={() => {
                        'is_disabled' in item
                          ? graphStore?.showChangeFilters &&
                            !item.is_disabled &&
                            accountabilityFilter.toggleControlDynamicOptions(item.link_subtype_id)
                          : graphStore?.showChangeFilters &&
                            accountabilityFilter.toggleControlDynamicOptions(item.link_subtype_id);
                      }}
                      value={item.link_subtype_name}
                      title={'title' in item ? item.title : item.link_subtype_name}
                      isAllCheck
                    />
                  </li>
                ))}
              </>
            )}
          </ul>
        )}
      </li>
    );
  }
}

export default Accountability;
