import { observable, action, computed, runInAction } from 'mobx';

import { IChecboxData } from '../components/CheckboxGroup/CheckboxGroup.props';
import { formatDate } from '../utils/ui/formatDate';
import { formatIntegralAssessment } from '../utils/ui/formatIntegralAssessment';
import { formatMoneyDetailedNode } from '../utils/ui/formatMoneyDetailedNode';

const savedTaxpayerSetting = localStorage.getItem('taxpayerSetting') || '{}';
const taxpayerSetting = JSON.parse(savedTaxpayerSetting);

let initialSettingsItems: IChecboxData[] = [
  {
    checked: false,
    name: 'registration_date',
    label: 'Дата регистрации НП',
    prepare: (data) => {
      return data ? formatDate(data) : '-';
    },
  },
  {
    checked: false,
    name: 'director',
    label: 'Руководитель НП',
    prepare: (data) => {
      return data || '-';
    },
  },
  {
    checked: false,
    name: 'status',
    label: 'Статус НП',
    prepare: (data) => {
      return data || '-';
    },
  },
  {
    checked: false,
    name: 'score',
    label: 'СУР АСК НДС-2',
    prepare: (data) => {
      return data ? formatIntegralAssessment(data) : '-';
    },
  },
  {
    checked: false,
    name: 'ufns_code',
    label: 'Код НО, постановки на учет',
    prepare: (data) => {
      return data || '-';
    },
  },
  {
    checked: false,
    name: 'tax_regime',
    label: 'Применяемый режим НО',
    prepare: (data) => {
      return data || '-';
    },
  },
  {
    checked: false,
    name: 'avg_headcount',
    label: 'Среднесписочная численность персонала по РСВ, чел',
    prepare: (data) => {
      return data || '-';
    },
  },
  {
    checked: false,
    name: 'avgHeadcountPeriod',
    label: 'Период выведения информации (РСВ)',
  },
  {
    checked: false,
    name: 'avg_headcount_6ndfl',
    label: 'Среднесписочная численность персонала по 6-НДФЛ, чел',
    prepare: (data) => {
      return data || '-';
    },
  },
  {
    checked: false,
    name: 'avgHeadcountPeriodNDFL',
    label: 'Период выведения информации (6-НДФЛ)',
  },
  {
    checked: false,
    name: 'liquidation_date',
    label: 'Дата ликвидации',
    prepare: (data) => {
      return data ? formatDate(data) : '-';
    },
  },
  {
    checked: false,
    name: 'is_property_available',
    label: 'Признак наличия имущества',
    prepare: (data) => {
      return data ? 'Да' : 'Нет';
    },
  },
  {
    checked: false,
    name: 'property_amount',
    label: 'Стоимость имущества, руб.',
    prepare: (data) => {
      return data ? formatMoneyDetailedNode(data) : '-';
    },
  },
  {
    checked: false,
    parentName: 'falsity',
    name: 'unreliability_code',
    label: 'Признак наличия недостоверности',
    prepare: (data) => {
      return data?.unreliability_code ? 'Да' : 'Нет';
    },
  },
  {
    checked: false,
    parentName: 'falsity',
    name: 'unreliability_name',
    label: 'Недостоверность',
    prepare: (data) => {
      return data?.unreliability_name || '-';
    },
  },
  {
    checked: false,
    name: 'pay_debit',
    label: 'Входящие платежи, руб.',
    prepare: (data) => {
      return data ? formatMoneyDetailedNode(data) : '-';
    },
  },
  {
    checked: false,
    name: 'pay_credit',
    label: 'Исходящие платежи, руб.',
    prepare: (data) => {
      return data ? formatMoneyDetailedNode(data) : '-';
    },
  },
  {
    checked: false,
    name: 'vat_calc',
    label: 'Сумма НДС исчисленная, руб.',
    prepare: (data) => {
      return data ? formatMoneyDetailedNode(data) : '-';
    },
  },
  {
    checked: false,
    name: 'vat_pay',
    label: 'Сумма НДС к уплате, руб.',
    prepare: (data) => {
      return data ? formatMoneyDetailedNode(data) : '-';
    },
  },
  {
    checked: false,
    name: 'vat_deduct',
    label: 'Сумма НДС к вычету, руб.',
    prepare: (data) => {
      return data ? formatMoneyDetailedNode(data) : '-';
    },
  },
  {
    checked: false,
    name: 'deduction_vat_sum',
    label: 'Доля "чистых" вычетов по НДС, %',
    prepare: (data) => {
      return data?.length > 0
        ? data.reduce((acc: number, item: any) => (acc += item.value), 0)
        : '-';
    },
  },
  {
    checked: false,
    name: 'scheme_deduction_sum',
    label: 'Доля "схемных" вычетов по НДС, %',
    prepare: (data) => {
      return data?.length > 0
        ? data.reduce((acc: number, item: any) => (acc += item.value), 0)
        : '-';
    },
  },
];

initialSettingsItems = initialSettingsItems.map((item) => ({
  ...item,
  checked: !!taxpayerSetting[item.name],
}));

/**
 * Хранилище состояния интерфейса приложения
 */
class TooltipStore {
  constructor() {
    this.taxpayerSettingItems = initialSettingsItems;
    this.unsavedSettingsItems = initialSettingsItems;
  }

  @observable taxpayerSettingRequiredItems: IChecboxData[] = [
    {
      checked: true,
      disabled: true,
      name: 'inn',
      label: 'ИНН',
      prepare: (data) => {
        return data || '-';
      },
    },
    {
      checked: true,
      disabled: true,
      name: 'kpp',
      label: 'КПП',
      prepare: (data) => {
        return data || '-';
      },
    },
    {
      checked: true,
      disabled: true,
      name: 'short_name',
      label: 'Наименование',
      prepare: (data) => {
        return data || '-';
      },
    },
    {
      checked: true,
      disabled: true,
      name: 'vat10total',
      label: 'НДС по журналу учета выставленных сч/ф продавца, руб.',
      prepare: (data) => {
        return data ? formatMoneyDetailedNode(data) : '-';
      },
    },
    {
      checked: true,
      disabled: true,
      name: 'vat11total',
      label: 'НДС по журналу учета полученных сч/ф покупателя, руб.',
      prepare: (data) => {
        return data ? formatMoneyDetailedNode(data) : '-';
      },
    },
  ];

  @observable taxpayerSettingItems: IChecboxData[] = [];

  @observable unsavedSettingsItems: IChecboxData[] = [];

  @observable saveButtonText: string = 'Сохранить';

  @computed get hasChanges() {
    return this.unsavedSettingsItems.some(
      ({ checked }, i) => checked !== this.taxpayerSettingItems[i].checked,
    );
  }

  @action changeTaxpayerSettings = (name: string): void => {
    this.unsavedSettingsItems = this.unsavedSettingsItems.map((item) =>
      item.name === name
        ? {
            ...item,
            checked: !item.checked,
          }
        : item,
    );
  };

  @action changeAllTaxpayerSettings = (): void => {
    const isAllChecked = !this.unsavedSettingsItems.some(({ checked }) => !checked);

    this.unsavedSettingsItems = this.unsavedSettingsItems.map((item) => ({
      ...item,
      checked: !isAllChecked,
    }));
  };

  @action saveChanges = (): void => {
    this.taxpayerSettingItems = this.unsavedSettingsItems;

    const savedItems = this.taxpayerSettingItems.reduce(
      (acc, item) => ({
        ...acc,
        [item.name]: item.checked,
      }),
      {} as Record<string, boolean | undefined>,
    );

    localStorage.setItem('taxpayerSetting', JSON.stringify(savedItems));

    this.saveButtonText = 'Сохранено';
    setTimeout(() => runInAction(() => (this.saveButtonText = 'Сохранить')), 1000);
  };

  @action cancelChanges = (): void => {
    this.unsavedSettingsItems = this.taxpayerSettingItems;
  };

  @observable currentLink: go.Link | null = null;

  @action setCurrentLink = (obj: go.GraphObject) => {
    this.currentLink = obj.part as go.Link;
  };
}

export type TooltipStoreType = TooltipStore;
export default TooltipStore;
