import { flow, action } from 'mobx';
import AsyncStore from '../../utils/AsyncStore';

import {
  hideNodes,
  showNodes,
  createNode,
  setVisibilityMode,
  createGroup,
  outGroup,
  addToGroup,
  getDetailedNodeData,
  postFocusOnNode,
  ungroupVirtual,
  postAddIndividualPersonal,
} from '../../../utils/services/api/graph';

import { GraphStoreType } from 'StoreTypes';

/** Хранилище для узлов графа */
export default class GraphNodesStore extends AsyncStore {
  constructor(private readonly GraphStore: GraphStoreType) {
    super();
  }

  /**
   * Метод делает POST запрос на создание узла
   *
   * @param {string} id - идентификатор графа
   * @param {any} nodeData информация узла
   */
  @action.bound
  createNode = flow(function* (
    this: GraphNodesStore,
    nodeData: any,
    createLinkModeV3: boolean = false,
    id: string = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      yield createNode(id, nodeData).then(({ graph_id, node_id }) => {
        this.GraphStore.nodeIDMoveV3 = node_id;
        if (!createLinkModeV3) {
          this.GraphStore.fetchGraph(graph_id).then(() => {
            // Позиционирование к узлу
            let targetNode: go.Part | null | undefined;

            targetNode = this.GraphStore.DiagramStore.diagram
              ?.findNodesByExample({ key: node_id })
              .first()?.part;

            if (targetNode) {
              this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
              targetNode.isSelected = true;
              this.GraphStore.DiagramStore.setSelectedNodes([node_id]);
            }
          });
        }
      });

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на скрытие узлов
   *
   * @param { string[] } orIds - идентификаторы узлов для создания группы
   * @param {string} id - идентификатор графа
   */
  @action.bound
  hideNodes = flow(function* (
    this: GraphNodesStore,
    orIds: string[],
    id: string = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield hideNodes(id, orIds);

      this.GraphStore.state = 'done';
      this.GraphStore.updateGraphId(GraphId);
      this.GraphStore.updateGraph();
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на показ узлов
   *
   * @param { string[] } orIds - идентификаторы узлов для создания группы
   * @param {string} id - идентификатор графа
   */
  @action.bound
  showNodes = flow(function* (
    this: GraphNodesStore,
    orIds: string[],
    id: string = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield showNodes(id, orIds);

      this.GraphStore.state = 'done';
      this.GraphStore.updateGraphId(GraphId);
      this.GraphStore.updateGraph();
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос для смены режима отображения скрытых узлов
   *
   * @param {boolean} visible - флаг режима
   * @param {string} id - идентификатор графа
   */
  @action.bound
  setHiddenNodesMode = flow(function* (this: GraphNodesStore, visible: boolean, id: string) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield setVisibilityMode(id, visible);

      this.GraphStore.hiddenNodesVisible = visible;
      this.GraphStore.state = 'done';

      this.GraphStore.updateGraphId(GraphId);
      this.GraphStore.updateGraph();
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /** Метод устанавливает режим скрытых узлов */
  @action toggleVisibleNodesMode = async (id = this.GraphStore.previouslyFetchedGraphId) => {
    this.setHiddenNodesMode(!this.GraphStore.hiddenNodesVisible, id);
  };

  /**
   * Метод делает POST запрос на создание группы
   *
   * @param {string} id - идентификатор графа
   * @param {string} orId - идентификатор узла группы
   */
  @action.bound
  createGroupFromNode = flow(function* (
    this: GraphNodesStore,
    orId: string[],
    id = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield createGroup(id, orId);

      this.GraphStore.updateGraphId(GraphId);

      this.GraphStore.fetchGraph(GraphId);

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.fetchGraph(this.GraphStore.previouslyFetchedGraphId, err);
    }
  });

  /**
   * Метод GET запрос для выхода узла из группы
   *
   * @param {string} id - идентификатор графа
   * @param {string} orId - идентификатор узла группы
   */
  @action.bound
  nodeOutOfGroup = flow(function* (
    this: GraphNodesStore,
    orId: string,
    id = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield outGroup(id, orId);

      this.GraphStore.updateGraphId(GraphId);
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /** Метод удаления узлов из группы */
  @action.bound
  removeFromVirtualGroup = flow(function* (
    this: GraphNodesStore,
    groupId: string,
    id = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield ungroupVirtual(id, groupId);

      this.GraphStore.updateGraphId(GraphId);
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на добавление узла в группу
   *
   * @param {string} id - идентификатор графа
   * @param {string} orId - идентификатор узла группы
   * @param {string} groupId - идентификатор группы
   */
  @action.bound
  addNodeToGroup = flow(function* (
    this: GraphNodesStore,
    orId: string[],
    groupId: string,
    id = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield addToGroup(id, orId, groupId);

      this.GraphStore.updateGraphId(GraphId);
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод запрашивает детальную информацию о узле
   *
   * @param {string} nodeId - идентификатор узла
   * @param {string} [graphId] - идентификатор графа
   */
  @action.bound
  fetchDetailedNodeData = flow(function* (
    this: GraphNodesStore,
    nodeId: string,
    graphId = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const result = yield getDetailedNodeData(graphId, nodeId);
      this.GraphStore.fetchedNodesDetails = [...this.GraphStore.fetchedNodesDetails, { ...result }];

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод по нахождению узла, если он находится внутри группы
   *
   * @param {string} node - информация об узле
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound
  focusNode = flow(function* (
    this: GraphNodesStore,
    node: object,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      yield postFocusOnNode(graph_id, { ...node }).then((result) => {
        this.GraphStore.fetchGraph(graph_id).then(() => {
          // Позиционирование к узлу
          let targetNode: go.Part | null | undefined;
          targetNode = this.GraphStore.DiagramStore.diagram
            ?.findNodesByExample({ key: result.node_id })
            .first()?.part;
          if (targetNode) {
            this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
            targetNode.isSelected = true;
            this.GraphStore.DiagramStore.setSelectedNodes([result.node_id]);
          }
        });
      });
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на создание ФЛ
   *
   * @param {string} id - идентификатор графа
   * @param {any} nodeData информация узла
   */
  @action.bound
  createNodeFL = flow(function* (
    this: GraphNodesStore,
    nodeData: any,
    createLinkModeV3: boolean = false,
    id: string = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      yield postAddIndividualPersonal(id, { ...nodeData }).then(({ graph_id, node_id }) => {
        this.GraphStore.nodeIDMoveV3 = node_id;
        if (!createLinkModeV3) {
          this.GraphStore.fetchGraph(graph_id).then(() => {
            // Позиционирование к узлу
            let targetNode: go.Part | null | undefined;

            targetNode = this.GraphStore.DiagramStore.diagram
              ?.findNodesByExample({ key: node_id })
              .first()?.part;

            if (targetNode) {
              this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
              targetNode.isSelected = true;
              this.GraphStore.DiagramStore.setSelectedNodes([node_id]);
            }
          });
        }
      });
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });
}
