import Button from 'components/Button';
import Checkbox from 'components/Checkbox';
import Dropdown from 'components/Dropdown';
import Input from 'components/Input';
import MoneySpreadPicker from 'components/MoneySpreadPicker';
import Radio from 'components/Radio';
import TimePeriodPicker from 'components/TimePeriodPicker';
import { QUARTERS, YEARS } from 'components/constants';
import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import React from 'react';
import { fractionalNumberMask } from 'utils/formRules';
import { KeysOfType } from 'utils/types/utils/keysOfType';
import styles from './Form.module.scss';
import { IFormProps } from './Form.props';

const Form = observer(({ className, disabled, ...props }: IFormProps) => {
  const {
    chainsStore: {
      formData,
      changeFormData,
      changeBooleanFormData,
      changeNumberFormData,
      changePeriod,
      searchState,
      resetSearch,
      submitForm,
    },
  } = useStores();

  const changeInn = (
    value: string,
    fieldName: KeysOfType<{ seller_inn: string; buyer_inn: string }, string>,
  ) => {
    const formatValue = fractionalNumberMask(value);
    if (formatValue.length > 12) return;
    changeFormData(fieldName, formatValue);
  };

  const changeLinkType = (value: string) => {
    changeFormData('link_type', value);
    changeNumberFormData('vat_calc_from', null);
    changeNumberFormData('vat_calc_to', null);
    changeNumberFormData('vat_deduct_from', null);
    changeNumberFormData('vat_deduct_to', null);
  };

  return (
    <div
      className={`${className} ${styles.form} ${
        (searchState === 2 || searchState === 3) && styles.borderBottom
      }`}
      {...props}
    >
      <div className={styles.row}>
        <div className={`${styles.col} ${styles.inn}`}>
          <Input
            label="ИНН продавца"
            labelPosition="top"
            placeholder="1234567890"
            onChange={({ target: { value } }) => changeInn(value, 'seller_inn')}
            value={formData.seller_inn}
            disabled={disabled}
          />
        </div>
        <div className={`${styles.col} ${styles.inn}`}>
          <Input
            label="ИНН покупателя"
            labelPosition="top"
            placeholder="1234567890"
            onChange={({ target: { value } }) => changeInn(value, 'buyer_inn')}
            value={formData.buyer_inn}
            disabled={disabled}
          />
        </div>
        <div className={`${styles.col} ${styles.date}`}>
          <TimePeriodPicker
            containerStyle={styles.picker}
            fromQuarterValue={formData.quarter_from}
            fromYearValue={formData.year_from}
            toQuarterValue={formData.quarter_to}
            toYearValue={formData.year_to}
            onFromValueChange={(value) => changePeriod('from', value)}
            onToValueChange={(value) => changePeriod('to', value)}
            quarters={QUARTERS}
            years={YEARS}
            label={'Период учета данных'}
            disabled={disabled}
            labelFrom={'С'}
            labelTo={'по'}
            placeholderFrom="Кв."
            placeholderTo="Год"
          />
        </div>
      </div>
      <div className={styles.row}>
        <div className={`${styles.col} ${styles.deep}`}>
          <Radio
            label="Глубина поиска"
            items={[
              { value: '2', label: '2' },
              { value: '3', label: '3' },
            ]}
            value={formData.steps}
            onChange={(value) => changeFormData('steps', value)}
            mode="buttons2"
            disabled={disabled}
            tooltipText="Количество связей в цепочке"
            tooltipId="steps_tooltip"
          />
        </div>
        <div className={`${styles.col} ${styles.type}`}>
          <Dropdown
            dropdownStyle={styles.dropdown}
            mainContainerStyle={styles.dropdownMainContainer}
            items={['АСК НДС', 'АБД', 'Товарный поток', 'Госконтракты']}
            onSelect={(value) => changeLinkType(value)}
            value={formData.link_type}
            label={'Вид связи'}
            placeholder="Выберите значение"
            disabled={disabled}
          />
        </div>
        {formData.link_type === 'АСК НДС' && (
          <>
            <div className={`${styles.col} ${styles.summ}`}>
              <MoneySpreadPicker
                fromValue={formData.vat_calc_from}
                toValue={formData.vat_calc_to}
                onChangeFromValue={(value) => changeNumberFormData('vat_calc_from', value)}
                onChangeToValue={(value) => changeNumberFormData('vat_calc_to', value)}
                label="НДС исчисленный, ₽"
                disabled={disabled}
              />
            </div>
            <div className={`${styles.col} ${styles.summ}`}>
              <MoneySpreadPicker
                fromValue={formData.vat_deduct_from}
                toValue={formData.vat_deduct_to}
                onChangeFromValue={(value) => changeNumberFormData('vat_deduct_from', value)}
                onChangeToValue={(value) => changeNumberFormData('vat_deduct_to', value)}
                label="НДС к вычету, ₽"
                disabled={disabled}
              />
            </div>
          </>
        )}
        {formData.link_type === 'АБД' && (
          <>
            <div className={`${styles.col} ${styles.summ}`}>
              <MoneySpreadPicker
                fromValue={formData.debit_min}
                toValue={formData.debit_max}
                onChangeFromValue={(value) => changeNumberFormData('debit_min', value)}
                onChangeToValue={(value) => changeNumberFormData('debit_max', value)}
                label="Сумма прихода, ₽"
                disabled={disabled}
              />
            </div>
            <div className={`${styles.col} ${styles.summ}`}>
              <MoneySpreadPicker
                fromValue={formData.credit_min}
                toValue={formData.credit_max}
                onChangeFromValue={(value) => changeNumberFormData('credit_min', value)}
                onChangeToValue={(value) => changeNumberFormData('credit_max', value)}
                label="Сумма расхода, ₽"
                disabled={disabled}
              />
            </div>
          </>
        )}
        {formData.link_type === 'Товарный поток' && (
          <div className={`${styles.col} ${styles.summ}`}>
            <MoneySpreadPicker
              fromValue={formData.cost_min}
              toValue={formData.cost_max}
              onChangeFromValue={(value) => changeNumberFormData('cost_min', value)}
              onChangeToValue={(value) => changeNumberFormData('cost_max', value)}
              label="Сумма, ₽"
              disabled={disabled}
            />
          </div>
        )}
        {formData.link_type === 'Госконтракты' && (
          <div className={`${styles.col} ${styles.summ}`}>
            <MoneySpreadPicker
              fromValue={formData.contract_min}
              toValue={formData.contract_max}
              onChangeFromValue={(value) => changeNumberFormData('contract_min', value)}
              onChangeToValue={(value) => changeNumberFormData('contract_max', value)}
              label="Сумма, ₽"
              disabled={disabled}
            />
          </div>
        )}
      </div>
      {formData.link_type === 'Товарный поток' && (
        <div className={styles.row}>
          <div className={`${styles.col} ${styles.trace}`}>
            <Checkbox
              checked={formData.is_trace}
              disabled={disabled}
              onChange={() => changeBooleanFormData('is_trace')}
            >
              Прослеживаемость
            </Checkbox>
            <Checkbox
              checked={formData.is_property}
              disabled={disabled}
              onChange={() => changeBooleanFormData('is_property')}
            >
              Собственность
            </Checkbox>
            <Checkbox
              checked={formData.is_mark}
              disabled={disabled}
              onChange={() => changeBooleanFormData('is_mark')}
            >
              ГИС МТ
            </Checkbox>
          </div>
        </div>
      )}
      <div className={`${styles.row} ${styles.tip}`}>
        Результаты поиска удаляются через 1 день после завершения поиска
      </div>
      {(searchState === 2 || searchState === 3) && (
        <div className={`${styles.row} ${styles.buttons}`}>
          <Button mode="secondary" onClick={resetSearch}>
            Очистить
          </Button>
          <Button onClick={submitForm} disabled={true}>
            Найти цепочки НП
          </Button>
        </div>
      )}
    </div>
  );
});

export default Form;
