import React, { PureComponent } from 'react';
import styles from './ToolTipIndicators.module.scss';

import { inject, observer } from 'mobx-react';
import { GraphStoreType } from '../../stores/graph/graphStore';
import { ReactComponent as DebtFullIcon } from './assets/debt_full.svg';
import { ReactComponent as DebtOneThirdIcon } from './assets/debt_one_third.svg';
import { ReactComponent as DebtTwoThirdIcon } from './assets/debt_two_third.svg';
import { ReactComponent as CloseIcon } from './assets/close.svg';
import { ReactComponent as OOKIcon } from './assets/ook.svg';
import { ReactComponent as Traceability } from './assets/traceability_red.svg';

import { IndicatorsStoreType } from '../../stores/indicators/indicatorsStore';
import { formatMoneyDimensions } from '../../utils/ui/formatMoneyDimensions';
import { toNumber } from '../../utils';

type ToolTipIndicatorsProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
  indicatorsStore?: IndicatorsStoreType;
  gapsDebtFilter?: any;
  specialOperationFilter?: any;
};

type ToolTipIndicatorsState = {
  graphID: any;
};

/**
 * Компонент всплывающей подсказки по индикаторам для узла
 */
@inject('graphStore', 'indicatorsStore', 'gapsDebtFilter', 'specialOperationFilter')
@observer
class ToolTipIndicators extends PureComponent<ToolTipIndicatorsProps, ToolTipIndicatorsState> {
  /**
   * Метод отрисовывает элементы на странице
   */

  componentDidMount() {
    const { graphStore } = this.props;
    let result: any = 0;
    graphStore?.indicatorsInfo?.forEach((item: any) => {
      if (item.value) {
        result += parseInt(item.value.replace(/\s+/g, ''));
      }
    });
    graphStore?.setIndicatorsResult(result);
  }

  _onClose = (): void => {
    let toolTipDiv = document.getElementById('toolTipIndicators');
    let toolTipBackGround = document.getElementById('toolTipBackGround');

    if (toolTipDiv && toolTipBackGround) {
      toolTipDiv.style.display = 'none';
      toolTipBackGround.style.display = 'none';
    }
  };

  render() {
    const { graphStore, id, gapsDebtFilter, specialOperationFilter } = this.props;
    const ook = [
      'Ook_Mis11Amount',
      'Ook_KsVatAmount',
      'Ook_DoubtfulAmount',
      'Ook_Inv2015Ch8Amount',
      'Ook_Oc17Amount',
      'Ook_Oc23Amount',

      'Ook_MisAfterKnpAmount',
      'Ook_Oc45Amount',
      'Ook_SplittingAmount',

      'Ook_Inv2015Ch11Amount',
      'Ook_JournalsNotInDeclAmount',

      'Ook_JournalExposedWithoutReceivedInvoices',
      'Ook_UnmatchedAmountsInSecondPart',
      'Ook_NegativeOperationsInChapter9',
      'Ook_OperationCode33Amount',
      'Ook_OperationCode34Amount',
      'Ook_OperationCode36Amount',
    ];

    let result: any = 0;
    graphStore?.indicatorsInfo?.forEach((item: any) => {
      if (item.value) {
        if (item.name === 'Ook_Mis11Amount' && specialOperationFilter.options?.Ook_Mis11Amount) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (item.name === 'Ook_KsVatAmount' && specialOperationFilter.options?.Ook_KsVatAmount) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_DoubtfulAmount' &&
          specialOperationFilter.options?.Ook_DoubtfulAmount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_Inv2015Ch8Amount' &&
          specialOperationFilter.options?.Ook_Inv2015Ch8Amount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (item.name === 'Ook_Oc17Amount' && specialOperationFilter.options?.Ook_Oc17Amount) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (item.name === 'Ook_Oc23Amount' && specialOperationFilter.options?.Ook_Oc23Amount) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }

        if (
          item.name === 'Ook_MisAfterKnpAmount' &&
          specialOperationFilter.options?.Ook_MisAfterKnpAmount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_Inv2015Ch11Amount' &&
          specialOperationFilter.options?.Ook_Inv2015Ch11Amount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (item.name === 'Ook_Oc45Amount' && specialOperationFilter.options?.Ook_Oc45Amount) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_SplittingAmount' &&
          specialOperationFilter.options?.Ook_SplittingAmount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_JournalsNotInDeclAmount' &&
          specialOperationFilter.options?.Ook_JournalsNotInDeclAmount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_JournalExposedWithoutReceivedInvoices' &&
          specialOperationFilter.options?.Ook_JournalExposedWithoutReceivedInvoices
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_UnmatchedAmountsInSecondPart' &&
          specialOperationFilter.options?.Ook_UnmatchedAmountsInSecondPart
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_NegativeOperationsInChapter9' &&
          specialOperationFilter.options?.Ook_NegativeOperationsInChapter9
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_OperationCode33Amount' &&
          specialOperationFilter.options?.Ook_OperationCode33Amount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_OperationCode34Amount' &&
          specialOperationFilter.options?.Ook_OperationCode34Amount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
        if (
          item.name === 'Ook_OperationCode36Amount' &&
          specialOperationFilter.options?.Ook_OperationCode36Amount
        ) {
          result += parseInt(item.value.replace(/\s+/g, ''));
        }
      }
    });
    graphStore?.setIndicatorsResult(result);

    const ookActual = graphStore?.indicatorsInfo?.filter(
      (item: any) =>
        ook.some((indicator: any) => indicator === item.name) && !!toNumber(item.value),
    );

    return (
      <>
        <div className={styles.background} id="toolTipBackGround" onClick={this._onClose} />
        <div className={styles.container} id={id}>
          <div className={styles.header} data-draggable>
            <span className={styles.header_text} data-draggable>
              Индикаторы узла
            </span>
            <div onClick={this._onClose}>
              <CloseIcon className={styles.close_icon} />
            </div>
          </div>
          <div className={styles.data_container}>
            {graphStore?.indicatorsInfo?.some((item: any) =>
              ook.some((indicator: any) => indicator === item.name),
            ) &&
              graphStore?.indicatorsInfo
                ?.map((item: any) => {
                  return (
                    item.name in specialOperationFilter.options &&
                    specialOperationFilter.options[item.name] &&
                    item.value !== '0'
                  );
                })
                .some((element: any) => {
                  return element === true;
                }) && (
                <div className={styles.subtitle}>
                  <div className={styles.iconTitle}>
                    <span className={styles.icon}>
                      <OOKIcon className={styles.icon} />
                    </span>
                    <span className={styles.title}>Операции особого контроля</span>
                  </div>
                </div>
              )}
            {graphStore?.indicatorsInfo?.map(
              (item: any) =>
                ook.some((indicator: any) => indicator === item.name) &&
                specialOperationFilter.options?.[item.name] &&
                item.value !== '0' && (
                  <>
                    <div className={styles.containerDebt}>
                      <div className={styles.iconTitle}>
                        <div className={styles.title}>
                          {item.name === 'Ook_Mis11Amount' &&
                            specialOperationFilter.options?.Ook_Mis11Amount &&
                            'Расхождения по разделу 11: '}
                          {item.name === 'Ook_KsVatAmount' &&
                            specialOperationFilter.options?.Ook_KsVatAmount &&
                            'Контрольные соотношения: '}
                          {item.name === 'Ook_DoubtfulAmount' &&
                            specialOperationFilter.options?.Ook_DoubtfulAmount &&
                            'Сомнительные начисления: '}
                          {item.name === 'Ook_Inv2015Ch8Amount' &&
                            specialOperationFilter.options?.Ook_Inv2015Ch8Amount &&
                            'СФ до 2015г. в разделе 8: '}
                          {item.name === 'Ook_Oc17Amount' &&
                            specialOperationFilter.options?.Ook_Oc17Amount &&
                            'КВО 17: '}
                          {item.name === 'Ook_Oc23Amount' &&
                            specialOperationFilter.options?.Ook_Oc23Amount &&
                            'КВО 23: '}
                          {item.name === 'Ook_MisAfterKnpAmount' &&
                            specialOperationFilter.options?.Ook_MisAfterKnpAmount &&
                            'Расхождения после завершения КНП: '}
                          {item.name === 'Ook_Inv2015Ch11Amount' &&
                            specialOperationFilter.options?.Ook_Inv2015Ch11Amount &&
                            'СФ до 2015г. в разделе 11: '}
                          {item.name === 'Ook_Oc45Amount' &&
                            specialOperationFilter.options?.Ook_Oc45Amount &&
                            'КВО 45: '}
                          {item.name === 'Ook_SplittingAmount' &&
                            specialOperationFilter.options?.Ook_SplittingAmount &&
                            'Дробление СФ: '}
                          {item.name === 'Ook_JournalsNotInDeclAmount' &&
                            specialOperationFilter.options?.Ook_JournalsNotInDeclAmount &&
                            'Журналы, в том числе не в составе НД: '}
                          {item.name === 'Ook_JournalExposedWithoutReceivedInvoices' &&
                            specialOperationFilter.options
                              ?.Ook_JournalExposedWithoutReceivedInvoices &&
                            'Журнал выставленных СФ без полученных СФ: '}
                          {item.name === 'Ook_UnmatchedAmountsInSecondPart' &&
                            specialOperationFilter.options?.Ook_UnmatchedAmountsInSecondPart &&
                            'Несопоставленные суммы в части 2 Журнала: '}
                          {item.name === 'Ook_NegativeOperationsInChapter9' &&
                            specialOperationFilter.options?.Ook_NegativeOperationsInChapter9 &&
                            'Отрицательные операции в разделе 9 НД по НДС: '}
                          {item.name === 'Ook_OperationCode33Amount' &&
                            specialOperationFilter.options?.Ook_OperationCode33Amount &&
                            'КВО 33: '}
                          {item.name === 'Ook_OperationCode34Amount' &&
                            specialOperationFilter.options?.Ook_OperationCode34Amount &&
                            'КВО 34: '}
                          {item.name === 'Ook_OperationCode36Amount' &&
                            specialOperationFilter.options?.Ook_OperationCode36Amount &&
                            'КВО 36: '}
                        </div>
                      </div>
                      <span className={styles.value}>
                        {item.name === 'Ook_Mis11Amount' &&
                          specialOperationFilter.options?.Ook_Mis11Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_KsVatAmount' &&
                          specialOperationFilter.options?.Ook_KsVatAmount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_DoubtfulAmount' &&
                          specialOperationFilter.options?.Ook_DoubtfulAmount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_Inv2015Ch8Amount' &&
                          specialOperationFilter.options?.Ook_Inv2015Ch8Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_Oc17Amount' &&
                          specialOperationFilter.options?.Ook_Oc17Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_Oc23Amount' &&
                          specialOperationFilter.options?.Ook_Oc23Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_MisAfterKnpAmount' &&
                          specialOperationFilter.options?.Ook_MisAfterKnpAmount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_Inv2015Ch11Amount' &&
                          specialOperationFilter.options?.Ook_Inv2015Ch11Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_Oc45Amount' &&
                          specialOperationFilter.options?.Ook_Oc45Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_JournalsNotInDeclAmount' &&
                          specialOperationFilter.options?.Ook_JournalsNotInDeclAmount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_SplittingAmount' &&
                          specialOperationFilter.options?.Ook_SplittingAmount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_JournalExposedWithoutReceivedInvoices' &&
                          specialOperationFilter.options
                            ?.Ook_JournalExposedWithoutReceivedInvoices &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_UnmatchedAmountsInSecondPart' &&
                          specialOperationFilter.options?.Ook_UnmatchedAmountsInSecondPart &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_NegativeOperationsInChapter9' &&
                          specialOperationFilter.options?.Ook_NegativeOperationsInChapter9 &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_OperationCode33Amount' &&
                          specialOperationFilter.options?.Ook_OperationCode33Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_OperationCode34Amount' &&
                          specialOperationFilter.options?.Ook_OperationCode34Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Ook_OperationCode36Amount' &&
                          specialOperationFilter.options?.Ook_OperationCode36Amount &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                      </span>
                    </div>
                  </>
                ),
            )}
            {ookActual.length > 1 && (
              <div className={styles.result}>
                <div>Итого:</div>
                <div>
                  {formatMoneyDimensions(graphStore?.indicatorsResult, graphStore?.dimensions)}
                </div>
              </div>
            )}
            {graphStore?.indicatorsInfo?.some(
              (item: any) => item.name === 'Debt' || item.name === 'Krsb_Indicator',
            ) &&
              graphStore?.indicatorsInfo
                ?.map((item: any) => {
                  if (item.name === 'Debt' && item.interval === '1') {
                    return (
                      'Debt13' in gapsDebtFilter.options &&
                      gapsDebtFilter.options.Debt13 &&
                      item.value !== '0'
                    );
                  }
                  if (item.name === 'Debt' && item.interval === '2') {
                    return (
                      'Debt23' in gapsDebtFilter.options &&
                      gapsDebtFilter.options.Debt23 &&
                      item.value !== '0'
                    );
                  }
                  if (item.name === 'Debt' && item.interval === '3') {
                    return (
                      'Debt100' in gapsDebtFilter.options &&
                      gapsDebtFilter.options.Debt100 &&
                      item.value !== '0'
                    );
                  }
                  if (item.name === 'Krsb_Indicator' && Number(item.value_months) === 1) {
                    return (
                      'Debt13' in gapsDebtFilter.options &&
                      gapsDebtFilter.options.Debt13 &&
                      item.value !== '0'
                    );
                  }
                  if (item.name === 'Krsb_Indicator' && Number(item.value_months) === 2) {
                    return (
                      'Debt23' in gapsDebtFilter.options &&
                      gapsDebtFilter.options.Debt23 &&
                      item.value !== '0'
                    );
                  }
                  if (item.name === 'Krsb_Indicator' && Number(item.value_months) === 3) {
                    return (
                      'Debt100' in gapsDebtFilter.options &&
                      gapsDebtFilter.options.Debt100 &&
                      item.value !== '0'
                    );
                  }
                })
                .some((element: any) => {
                  return element === true;
                }) && (
                <span className={styles.subtitle}>
                  Задолженность {graphStore.payMonth ? ' на ' + graphStore.payMonth : ''}
                </span>
              )}
            {graphStore?.indicatorsInfo?.map(
              (item: any) =>
                (item.name === 'Debt' || item.name === 'Krsb_Indicator') && (
                  <>
                    <div className={styles.containerDebt}>
                      <div className={styles.iconTitle}>
                        <div className={styles.icon}>
                          {item.name === 'Debt' &&
                            item.interval === '1' &&
                            gapsDebtFilter.options?.Debt13 && <DebtOneThirdIcon />}
                          {item.name === 'Debt' &&
                            item.interval === '2' &&
                            gapsDebtFilter.options?.Debt23 && <DebtTwoThirdIcon />}
                          {item.name === 'Debt' &&
                            item.interval === '3' &&
                            gapsDebtFilter.options?.Debt100 && <DebtFullIcon />}

                          {item.name === 'Krsb_Indicator' &&
                            Number(item.value_months) === 1 &&
                            gapsDebtFilter.options?.Debt13 && <DebtOneThirdIcon />}
                          {item.name === 'Krsb_Indicator' &&
                            Number(item.value_months) === 2 &&
                            gapsDebtFilter.options?.Debt23 && <DebtTwoThirdIcon />}
                          {item.name === 'Krsb_Indicator' &&
                            Number(item.value_months) === 3 &&
                            gapsDebtFilter.options?.Debt100 && <DebtFullIcon />}
                        </div>
                        <div className={styles.title}>
                          {item.name === 'Debt' &&
                            item.interval === '1' &&
                            gapsDebtFilter.options?.Debt13 &&
                            '1/3 от части суммы задолженности: '}
                          {item.name === 'Debt' &&
                            item.interval === '2' &&
                            gapsDebtFilter.options?.Debt23 &&
                            '2/3 от части суммы задолженности: '}
                          {item.name === 'Debt' &&
                            item.interval === '3' &&
                            gapsDebtFilter.options?.Debt100 &&
                            '100% задолженности: '}
                          {item.name === 'Krsb_Indicator' &&
                            Number(item.value_months) === 1 &&
                            gapsDebtFilter.options?.Debt13 &&
                            '1/3 от части суммы задолженности: '}
                          {item.name === 'Krsb_Indicator' &&
                            Number(item.value_months) === 2 &&
                            gapsDebtFilter.options?.Debt23 &&
                            '2/3 от части суммы задолженности: '}
                          {item.name === 'Krsb_Indicator' &&
                            Number(item.value_months) === 3 &&
                            gapsDebtFilter.options?.Debt100 &&
                            '100% задолженности: '}
                        </div>
                      </div>
                      <span className={styles.value}>
                        {item.name === 'Debt' &&
                          item.interval === '1' &&
                          gapsDebtFilter.options?.Debt13 &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Debt' &&
                          item.interval === '2' &&
                          gapsDebtFilter.options?.Debt23 &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Debt' &&
                          item.interval === '3' &&
                          gapsDebtFilter.options?.Debt100 &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Krsb_Indicator' &&
                          Number(item.value_months) === 1 &&
                          gapsDebtFilter.options?.Debt13 &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Krsb_Indicator' &&
                          Number(item.value_months) === 2 &&
                          gapsDebtFilter.options?.Debt23 &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                        {item.name === 'Krsb_Indicator' &&
                          Number(item.value_months) === 3 &&
                          gapsDebtFilter.options?.Debt100 &&
                          item.value &&
                          formatMoneyDimensions(item.value, graphStore.dimensions)}
                      </span>
                    </div>
                  </>
                ),
            )}
            {graphStore?.isTraceRisk && (
              <div className={styles.containerDebt}>
                <div className={styles.iconTitle}>
                  <div className={styles.icon}>
                    <Traceability />
                  </div>
                  <div className={styles.title}>Наличие рисков по ПП 'Прослеживаемость'</div>
                </div>
              </div>
            )}
          </div>
        </div>
      </>
    );
  }
}

export default ToolTipIndicators;
