import { CrossIcon } from '@fdc/vsa-react-lib';
import { ReactComponent as ArrowIcon } from 'assets/icons/arrow.svg';
import { observer } from 'mobx-react';
import React, { HTMLAttributes } from 'react';
import { ReactComponent as MainSearchIcon } from '../../assets/icons/search.svg';
import styles from './Search.module.scss';
import { searchPropTypes } from './Search.props';
import { ReactComponent as ClearIcon } from './assets/clearIcon.svg';
import { ReactComponent as FilterNoIcon } from './assets/filterNo.svg';
import { ReactComponent as SearchIcon } from './assets/searchIcon.svg';

interface SearchProps extends HTMLAttributes<HTMLDivElement> {
  value: string;
  onChange: (value: any) => void;
  onSearch?: (value: string) => void;
  onReset?: () => void;
  placeholder?: string;

  autoFocus?: boolean;
  autoSearchTimeout?: number;
  withSearchIcon?: boolean;
  withReset?: boolean;
  withResetRight?: boolean;
  withButton?: boolean;
  buttonText?: string;

  wrapperStyle?: string;
  inputStyle?: string;
  searchIconStyle?: string;
  resetIconStyle?: string;
  buttonStyle?: string;

  activeButton?: boolean;
  theme?: string;

  withTags?: boolean;
  selectedList?: any;
  onCloseTag?: (value: string) => void;
  withArrow?: boolean;
  onOpenList?: () => void;
  isOpenArrow: boolean;

  id?: string;
  withFilter?: boolean;

  onOpenFilter?: () => void;
  isLarge?: boolean;
  filterIsFill?: boolean;
  filterIsOpen?: boolean;
}

/**
 * Компонент поля поиска
 *
 * @param {string} value - значение поля поиска
 * @param {func} onChange - обработчик ввода
 * @param {func} [onSearch] - функция, вызывающяяся при поиске
 * @param {func} [onReset] - функция для сброса поиска
 * @param {string} [placeholder] - заполнитель
 * @param {number} [autoSearchTimeout] - значение в милисекундах в течении которого
 * если не вводятся новые символы будет отправлен запрос на поиск. Если не указано
 * запрос автоматически отправлен не будет.
 * @param {boolean} [autoFocus] - авто фокус на поле поиска
 * @param {boolean} [withSearchIcon] - флаг показывающий нужна ли иконка поиска
 * @param {boolean} [withReset] - флаг показывающий нужна ли опция сброса ввода
 * @param {boolean} [withButton] - флаг показывающий нужна ли кнопка
 * @param {string} [buttonText] - текст кнопки
 * @param {boolean} [withTags] - с тэгами
 * @param {any} [selectedList] - выбранные элементы для поиска
 * @param {func} [onCloseTag] - удалить тэг
 * @param {func} [onOpenList] - открыть список
 * @param {boolean} [isOpenArrow] - форма галочки
 */
@observer
class Search extends React.PureComponent<SearchProps> {
  constructor(props: SearchProps) {
    super(props);
  }

  static propTypes = searchPropTypes;

  static defaultProps = {
    placeholder: '',
    autoSearchTimeout: 0,
    autoFocus: false,
    onSearch: () => {},
    onReset: () => {},
    withSearchIcon: false,
    withReset: false,
    withResetRight: false,
    withButton: false,
    buttonText: '',
    wrapperStyle: '',
    inputStyle: '',
    searchIconStyle: '',
    resetIconStyle: '',
    buttonStyle: '',
    buttonTextStyle: '',
    activeButton: false,
    withTags: false,
    selectedList: [],
    withArrow: false,
    isOpenArrow: false,
    id: '',
    withFilter: false,
  };

  searchTimeout: any;
  inputRef = React.createRef<HTMLInputElement>();

  /**
   * Метод обрабатывает ввод
   */
  _onChange = (event: React.BaseSyntheticEvent): void => {
    clearTimeout(this.searchTimeout);

    const { onChange } = this.props;
    onChange(event.target.value);
  };

  /**
   * Метод обнуляет значение поиска
   */
  _onReset = (): void => {
    const { onReset, onChange } = this.props;

    if (onReset) {
      onReset();
    } else {
      onChange('');
    }

    this.inputRef.current!.focus();
  };

  /**
   * Метод поиска
   */
  _onSearch = (event?: React.BaseSyntheticEvent): void => {
    if (this.props.filterIsOpen) return;

    if (event) {
      event.preventDefault();
    }

    const { onSearch, value } = this.props;
    clearTimeout(this.searchTimeout);

    if (onSearch) {
      onSearch(value);
    }
  };

  _onCloseTag = (inn: string): void => {
    const { onCloseTag } = this.props;
    if (onCloseTag) {
      onCloseTag(inn);
    }
  };

  _onOpenList = () => {
    const { onOpenList } = this.props;
    if (onOpenList) {
      onOpenList();
    }
  };

  openFilter = () => {
    const { onOpenFilter } = this.props;

    onOpenFilter && onOpenFilter();
  };

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const {
      value,
      autoFocus,
      placeholder,
      withSearchIcon,
      withReset,
      withResetRight,
      withButton,
      wrapperStyle,
      inputStyle,
      searchIconStyle,
      resetIconStyle,
      withTags,
      selectedList,
      withArrow,
      isOpenArrow,
      id,
      withFilter,
      isLarge,
      filterIsFill,
      filterIsOpen,
      className,
    } = this.props;

    return (
      <div className={`${styles.searchContainer} ${className}`}>
        <form onSubmit={this._onSearch} className={`${styles.wrapper} ${wrapperStyle}`}>
          {withSearchIcon && (
            <MainSearchIcon
              className={`${styles.searchIcon} ${styles.iconLeft} ${searchIconStyle}`}
              onClick={this._onSearch}
            />
          )}
          {withReset && (
            <ClearIcon
              className={`${styles.reset} ${styles.iconLeft} ${resetIconStyle}`}
              onClick={this._onReset}
            />
          )}
          {withTags && selectedList.length
            ? selectedList.map((item: any) => (
                <div className={styles.tag} key={`inn_tag_${item}`}>
                  <span>{item}</span>
                  <CrossIcon className={styles.crossIcon} onClick={() => this._onCloseTag(item)} />
                </div>
              ))
            : null}
          <input
            className={`${styles.input} ${inputStyle}`}
            type="text"
            ref={this.inputRef}
            autoFocus={autoFocus}
            onChange={this._onChange}
            value={value}
            placeholder={filterIsOpen ? 'При расширенном поиске поле недоступно' : placeholder}
            id={id}
            disabled={filterIsOpen}
          />
          {withArrow && (
            <button
              className={`${styles.arrow} ${isOpenArrow && styles.opened}`}
              onClick={this._onOpenList}
            >
              <ArrowIcon />
            </button>
          )}
          {withFilter && (
            <div
              className={`${styles.button} ${isLarge && styles.large} ${
                filterIsOpen && styles.active
              }`}
              onClick={this.openFilter}
            >
              {filterIsFill && <div className={styles.label}></div>}
              <FilterNoIcon className={`${styles.filterIcon} ${isLarge && styles.large}`} />
            </div>
          )}
          {withResetRight && (
            <ClearIcon
              className={`${styles.reset} ${styles.iconRight} ${resetIconStyle}`}
              onClick={this._onReset}
            />
          )}
          {withButton && (
            <div
              className={`${styles.button} ${isLarge && styles.large} ${
                filterIsOpen && styles.disabled
              }`}
              onClick={this._onSearch}
            >
              <SearchIcon className={`${styles.searchIconButton} ${isLarge && styles.large}`} />
            </div>
          )}
        </form>
      </div>
    );
  }
}

export default Search;
