import React, { PureComponent } from 'react';
import filterStyles from '../../common/Filter.module.scss';
import styles from './Abd.module.scss';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';
import { GraphStoreType } from '../../../../stores/graph/graphStore';

import Icon from '../../../../components/Icon';
import MoneySpreadPicker from '../../../../components/MoneySpreadPicker';

interface AbdProps {
  isActive: boolean;
  onToggleCheck?: () => void;
  toggleOpenState?: () => void;
  cooperationFilter?: any;
  isOpen?: boolean;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент "Ask Nds" в блоке "Кооперация"
 */
@inject('cooperationFilter', 'commonStore', 'graphStore')
@observer
class Abd extends PureComponent<AbdProps> {
  render() {
    const { isOpen, toggleOpenState, commonStore, cooperationFilter, graphStore } = this.props;

    return (
      <li className={`${filterStyles.wrapper} ${styles.wrapper}`}>
        <div className={filterStyles.header}>
          <span
            className={filterStyles.headerLabel}
            onClick={toggleOpenState}
            title={
              graphStore?.graphExpandType === 'Abd' && graphStore?.sourceSystem !== 'MDAB'
                ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                : 'Признак отображения связей по данным анализа банковской деятельности'
            }
          >
            АБД (движение ДС)
          </span>
          {graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Abd' && (
            <Icon
              className={filterStyles.toggle}
              name={isOpen ? 'arrow_up' : 'arrow_down'}
              onClick={toggleOpenState}
              theme={commonStore?.appTheme}
            />
          )}
        </div>
        {graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Abd' && isOpen && (
          <div className={styles.container}>
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(cooperationFilter.options.credit_from)?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.credit_to
                  ? Number(cooperationFilter.options.credit_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeCreditFrom}
              onChangeToValue={cooperationFilter.changeCreditTo}
              label={'Входящие платежи, руб.'}
              title={'Диапазон сумм Входящих платежей к вычету для отображаемых связей'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Abd')}
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(cooperationFilter.options.debet_from)?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.debet_to
                  ? Number(cooperationFilter.options.debet_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeDebetFrom}
              onChangeToValue={cooperationFilter.changeDebetTo}
              label={'Исходящие платежи, руб.'}
              title={'Диапазон сумм Исходящих платежи для отображаемых связей'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Abd')}
            />
          </div>
        )}
      </li>
    );
  }
}

export default Abd;
