import { INodeData } from 'DataTypes';
import { ReactComponent as ArrowIcon } from 'assets/icons/arrow.svg';
import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import React, { useEffect, useState } from 'react';
import styles from './Group.module.scss';
import { IGroupProps, IItemProps } from './Group.props';

const Item = ({ item, isSelected, ...props }: IItemProps) => (
  <div {...props} className={`${styles.item} ${isSelected && styles.selected}`}>
    <div className={`${styles.icon} ${item.options.className}`}>
      <img
        alt=""
        src={item.icon}
        className={`${styles.image}`}
        onError={(e) => ((e.target as HTMLImageElement).style.display = 'none')}
      />
    </div>
    <span className={styles.label}>{item.value}</span>
  </div>
);

const Group = observer(({ className, group, number, ...props }: IGroupProps) => {
  const {
    diagramStore: { diagram, selectedItem, setSelectedItem },
  } = useStores();

  const [opened, setOpened] = useState<boolean>(number === 0);

  useEffect(() => () => unHighlight(), []);

  function roleIsHighlighted(node: go.Node, item: any) {
    const nodeData = node.data as INodeData;
    const nodeInfo = nodeData.node;
    return (
      item?.role_id &&
      (nodeInfo?.business_role_id === item?.role_id || nodeInfo?.analytic_role_id === item?.role_id)
    );
  }

  const highlightItem = (item: any) => {
    if (diagram) diagram.animationManager.isEnabled = true;

    let highlightedNode: go.Part | undefined = undefined;
    let highlightedLink: go.Part | undefined = undefined;

    diagram?.nodes.each((node) => {
      if (
        !node.data?.isHighlightedFromLegend &&
        (roleIsHighlighted(node, item) || item?.highlightCondition?.(node))
      ) {
        diagram.model.setDataProperty(node.data, 'isHighlightedFromLegend', true);
        highlightedNode = !highlightedNode ? node : highlightedNode;
      }
    });

    diagram?.links.each((link) => {
      if (item?.highlightLinkCondition?.(link)) {
        diagram.model.setDataProperty(link.data, 'isHighlightedFromLegend', true);
        highlightedLink = !highlightedLink ? link : highlightedLink;
      }
    });

    if (highlightedNode) {
      diagram?.commandHandler.scrollToPart(highlightedNode);
    } else if (highlightedLink) {
      diagram?.commandHandler.scrollToPart(highlightedLink);
    }
  };

  const unHighlight = () => {
    if (diagram) diagram.animationManager.isEnabled = false;

    diagram?.nodes.each((node) => {
      if (node.data?.isHighlightedFromLegend) {
        diagram.model.setDataProperty(node.data, 'isHighlightedFromLegend', false);
      }
    });

    diagram?.links.each((link) => {
      if (link.data?.isHighlightedFromLegend) {
        diagram.model.setDataProperty(link.data, 'isHighlightedFromLegend', false);
      }
    });
  };

  const onItemClick = (item: any) => {
    unHighlight();

    if (selectedItem !== item?.value) {
      highlightItem(item);
      setSelectedItem(item.value);
    } else {
      setSelectedItem('');
    }
  };

  return (
    <div className={`${className} ${styles.group}`} data-name={group.name} {...props}>
      <header
        className={`${styles.header} ${opened && styles.opened}`}
        onClick={() => setOpened(!opened)}
      >
        <span className={styles.title}>{group.name}</span>
        <span className={styles.arrow}>
          <ArrowIcon />
        </span>
      </header>
      {opened && (
        <>
          {group.order.map((item: any) => (
            <Item
              key={`legend_item_${item.value}`}
              item={item}
              onClick={() => onItemClick(item)}
              isSelected={item.value === selectedItem}
            />
          ))}
          {group.subgroups &&
            group.subgroups.map((subgroup: any) => (
              <div key={`legend_subgroup_${subgroup.name}`} className={styles.subgroup}>
                <div className={styles.subgroup__header}>{subgroup.name}</div>
                {subgroup.order.map((item: any) => (
                  <Item
                    key={`legend_subitem_${item.value}`}
                    item={item}
                    onClick={() => onItemClick(item)}
                    isSelected={item.value === selectedItem}
                  />
                ))}
              </div>
            ))}
        </>
      )}
    </div>
  );
});

export default Group;
