import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { GraphStoreType } from '../../stores/graph/graphStore';
import { formatMoneyDimensions } from '../../utils/ui/formatMoneyDimensions';
import styles from './ToolTipMultipleFull.module.scss';
import { ReactComponent as CloseIcon } from './assets/close.svg';

type ToolTipMultipleFullProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
};

/**
 * Компонент всплывающей подсказки по отложенным вычетам (полная информация)
 */
@inject('graphStore')
@observer
class ToolTipMultipleFull extends PureComponent<ToolTipMultipleFullProps> {
  /**
   * Метод отрисовывает элементы на странице
   */

  _onClose = (): void => {
    let toolTipDiv = document.getElementById('toolTipMultipleFull');
    let toolTipBackGround = document.getElementById('toolTipBackGroundMultipleFull');

    if (toolTipDiv && toolTipBackGround) {
      toolTipDiv.style.display = 'none';
      toolTipBackGround.style.display = 'none';
    }
  };

  render() {
    const { graphStore, id } = this.props;

    return (
      <>
        <div
          className={styles.background}
          id="toolTipBackGroundMultipleFull"
          onClick={this._onClose}
        />
        <div className={styles.container} id={id}>
          <div className={styles.header} data-draggable>
            <span className={styles.header_text} data-draggable>
              Отложенные вычеты
            </span>
            <div onClick={this._onClose}>
              <CloseIcon className={styles.close_icon} />
            </div>
          </div>
          {graphStore?.coopLinkPeriods?.map((element: any) => (
            <div className={styles.item}>
              <div className={styles.title}>
                В <span className={styles.quarter}>{element.periodStr} </span>
                приняты к вычету:
              </div>

              <div className={styles.data_container}>
                {graphStore?.interQuarterPeriods.map((item: any) => (
                  <div className={styles.containerMultiple}>
                    <span className={styles.quarter}>{item.periodStr + ':'}</span>
                    <span>{formatMoneyDimensions(item.vatDeduct, graphStore.dimensions)}</span>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      </>
    );
  }
}

export default ToolTipMultipleFull;
