import React from 'react';
import { customLinksPropTypes } from './CustomLinks.props';
import filterStyles from '../common/Filter.module.scss';
import styles from './CustomLinks.module.scss';

interface CustomLinksProps {
  isActive: boolean;
  onToggleCheck: () => void;
  title?: string;
}

/**
 * Компонент блока пользовательских связей для фильтров
 */
const CustomLinks = (props: CustomLinksProps) => {
  return (
    <li className={filterStyles.wrapper} title={props.title}>
      <span className={styles.headerLabel}>Пользовательские связи</span>
    </li>
  );
};

CustomLinks.propTypes = customLinksPropTypes;

export default CustomLinks;
