import React, { useState } from 'react';
import { collapsedSearchFieldPropTypes } from './CollapsedSearchField.props';
import styles from './CollapsedSearchField.module.scss';

import Search from '../../Search';

interface CollapsedSearchFiledProps {
  /** Функция поиска */
  onSearch: (v: string) => void;
  theme?: string;
}

const CollapsedSearchFiled = ({ onSearch, theme }: CollapsedSearchFiledProps) => {
  const [value, setValue] = useState('');

  const onChange = (v: string) => {
    setValue(v);
  };

  const searchRequest = () => {
    onSearch(value);
  };

  return (
    <Search
      autoFocus
      withSearchIcon
      wrapperStyle={styles.wrapper}
      value={value}
      onChange={onChange}
      onSearch={searchRequest}
      theme={theme}
      placeholder={'Найти схему'}
    />
  );
};

CollapsedSearchFiled.propTypes = collapsedSearchFieldPropTypes;

CollapsedSearchFiled.defaultProps = {
  onSearch: () => {},
};

export default CollapsedSearchFiled;
