import { configure, observable, action, decorate } from 'mobx';
import { RootStoreType } from 'StoreTypes';

configure({ enforceActions: 'observed' });

/**
 * Хранилище информации о связи
 *
 * @param editLinkTotal - сумма
 * @param editLinkSource - источник информации
 * @param editLinkComment - комментарий
 * @param editLinkChangeReason - причина изменения связи
 * @param editLinkInvalidReason - причина невалидности связи
 */
class EditLinkStore {
  RootStore: RootStoreType;

  constructor(RootStore: RootStoreType) {
    this.RootStore = RootStore;
  }

  linkDataStore: any = [];

  editLinkTotal: any = null;
  editLinkSource: any = null;
  editLinkComment: any = null;
  editLinkChangeReason: any = null;
  editLinkInvalidReason: any = null;
  isValidFile: boolean = false;

  setIsValidFile = (data: any): void => {
    this.isValidFile = data;
  };

  setLinkDataStore = (data: any): void => {
    this.linkDataStore = data;
  };
}

decorate(EditLinkStore, {
  linkDataStore: observable,

  editLinkTotal: observable,
  editLinkSource: observable,
  editLinkComment: observable,
  editLinkChangeReason: observable,
  editLinkInvalidReason: observable,
  isValidFile: observable,

  setIsValidFile: action,
  setLinkDataStore: action,
});

export default EditLinkStore;
