import { action, computed, observable, reaction } from 'mobx';
import { v4 } from 'uuid';

import { INotification } from 'DataTypes';
import { CommonStoreType } from './common/commonStore';
import { FilterStoreType } from './filters/filterStore';
import { GraphStoreType } from './graph/graphStore';
import { RootStoreType } from './rootStore';
import { UserGraphsStoreType } from './userGraphsStore';

export interface messageResponse {
  code: string;
  description: string;
  message: string;
  type: 'info' | 'error';
  request_id: string;
  exec_time: any;
  status: any;
}

/**
 * Хранилище для оповещений, которые нужно отобразить для пользователя
 */
class NotificationStore {
  CommonStore: CommonStoreType;
  GraphStore: GraphStoreType;
  FilterStore: FilterStoreType;
  UserGraphsStore: UserGraphsStoreType;

  constructor(RootStore: RootStoreType) {
    this.CommonStore = RootStore.CommonStore;
    this.GraphStore = RootStore.GraphStore;
    this.FilterStore = RootStore.FilterStore;
    this.UserGraphsStore = RootStore.UserGraphsStore;

    reaction(() => this.errorsFromStores, this.saveErrors);
  }

  // массив нотификаций
  @observable notifications: INotification[] = [];

  /**
   * Метод удаляет нотификации из хранилища
   */
  @action removeNotifications = (ids: string[]): void => {
    this.notifications = this.notifications.filter((e) => !ids.includes(e.id));
  };

  /**
   * Метод возвращает нужные ошибки
   */
  @computed get errorsFromStores(): any[] {
    let errors: any[] = [];

    if (this.GraphStore.error) {
      errors.push({
        ...this.GraphStore.requestError,
        id: v4(),
      });
    }

    if (this.CommonStore.error) {
      errors.push({
        ...this.CommonStore.requestError,
        id: v4(),
      });
    }

    if (this.FilterStore.error) {
      errors.push({
        ...this.FilterStore.requestError,
        id: v4(),
      });
    }

    if (this.UserGraphsStore.error) {
      errors.push({
        ...this.UserGraphsStore.requestError,
        id: v4(),
      });
    }

    return errors;
  }

  /**
   * Метод сохраняет ошибки
   */
  @action saveErrors = () => {
    if (this.errorsFromStores[0] && 'config' in this.errorsFromStores[0]) {
      if (typeof this.errorsFromStores[0].response === 'undefined') {
        this.notifications.push({
          id: v4(),
          message: 'Таймаут',
          url: this.errorsFromStores[0].config.url,
          payload: this.errorsFromStores[0].config.data,
        });
      } else {
        if ('response' in this.errorsFromStores[0]) {
          this.notifications.push({
            id: v4(),
            request_id: this.errorsFromStores[0].config?.headers['X-Request-ID'],
            status: this.errorsFromStores[0].response?.status,
          });
        } else {
          this.notifications.push({
            id: v4(),
            request_id: this.errorsFromStores[0].config?.headers['X-Request-ID'],
          });
        }
      }
    } else {
      if (this.notifications.length === 0) {
        this.notifications = [...this.errorsFromStores];
      }
    }
  };

  /**
   * Метод сохраняет ошибки
   */
  @action saveMessage = (message: INotification) => {
    this.notifications = [...this.notifications, message];
  };

  @action saveStatusCode = (status: any) => {
    this.notifications = [status];
  };
}

export type NotificationStoreType = NotificationStore;
export default NotificationStore;
