import React from 'react';
import { IRowProps } from './Row.props';
import styles from './Row.module.scss';

const Row = ({ children, className, aligment = 'start', ...props }: IRowProps) => {
  return (
    <div className={`${styles.row} ${styles[aligment]} ${className} `} {...props}>
      {children}
    </div>
  );
};

export { Row };
