/**
 * Функция считывает все параметры из URL и возвращает их в объекте вида { параметр : значение }
 *
 * @param {string} url - URL
 */
export const getForScreenGraphURL = (url: string) => {
  const searchValue = 'uploadGraphImage';
  if (url.includes(searchValue)) {
    const paramsStartsAt = url.indexOf(searchValue);
    const searchURL = url.slice(paramsStartsAt + (searchValue.length + 1), url.length);
    return searchURL;
  } else {
    return false;
  }
};
