import { action, computed, observable, autorun } from 'mobx';
import Filter from '../utils/Filter';

import { FilterStoreType } from './filterStore';

/**
 * Хранилище периода учёта данных
 */
class PeriodDataFilter extends Filter {
  FilterStore: FilterStoreType;

  constructor(FilterStore: FilterStoreType) {
    super();
    this.FilterStore = FilterStore;

    autorun(() => {
      const { nds3_period_from, nds3_period_to } = this.FilterStore.options;
      this.updateOptions(
        {
          nds3_period_from,
          nds3_period_to,
        },
        'is_period_data',
      );
    });
  }

  @observable keep = true;

  @observable quarter = () => {
    const currentDate = new Date();
    const currentYear = currentDate.getFullYear();
    let quarter = 0;
    let quarterIndex = 'I';
    let month = currentDate.getMonth();

    if (currentDate.getDate() < 25) {
      month = month - 1;
    }
    quarter = Math.floor(month / 3);
    const defaultQuarterIndex = quarter === 0 ? 4 : quarter;
    switch (defaultQuarterIndex) {
      case 1:
        quarterIndex = 'I';
        break;
      case 2:
        quarterIndex = 'II';
        break;
      case 3:
        quarterIndex = 'III';
        break;
      case 4:
        quarterIndex = 'IV';
        break;
    }
    const defaultYear = quarter === 0 ? currentYear - 1 : currentYear;
    return quarterIndex + ';' + defaultYear;
  };

  // опции фильтра
  initialOptions = {
    // Период учета данных от
    nds3_period_from: 'I;2015',
    // Период учета данных до
    nds3_period_to: this.quarter(),
  };

  /**
   * Метод изменяет период учета данных от
   *
   * @param {string} value - значение для записи
   */
  @action changePeriodFrom = (value: string) => {
    this.changeOption('nds3_period_from', value);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет период учета данных до
   *
   * @param {string} value - значение для записи
   */
  @action changePeriodTo = (value: string) => {
    this.changeOption('nds3_period_to', value);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  // Происходили ли изменения среди фильтров
  @action isChangeStatic = () => {
    this.FilterStore.isPeriodDataChange = false;
    Object.keys(this.options).forEach((item: any) => {
      if (this.options[item] !== this.FilterStore.options[item]) {
        this.FilterStore.isPeriodDataChange = true;
      }
    });
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
    };
  }
}

export default PeriodDataFilter;
