import { inject, observer } from 'mobx-react';
import React, { useEffect, useState } from 'react';
import ReactTooltip from 'react-tooltip';
import { MenuItem } from '../../Menu.props';
import styles from './ExtendedMenuItem.module.scss';

import DropdownItem from './DropdownItem';

import { UIStoreType } from 'StoreTypes';
import { KEY_CODES } from '../../../constants/keyCodes';

const ITEMS_MENU_CLOSE = ['Построить новую схему', 'Поиск цепочек между НП'];

interface ExtendedMenuItemProps extends MenuItem {
  /** Хранилище состояние интерфейса */
  uiStore?: UIStoreType;
}

const ExtendedMenuItem = inject('uiStore')(
  observer(
    ({
      uiStore,
      label,
      iconComponent,
      activeIconComponent,
      hideInExtended,
      isActive,
      callback,
      separateNextWithLine,
      parentLabel,
      extendedMenuExtendableComponent,
      extendedClassName,
      extendedIconClassName,
      extendedLabelClassName,
      extendedParentLabelClassName,
      extendedMenuExtendedContainerClassName,
      extendedHorizontalLineClassName,
      extendedActiveIconClassName,
      dropdownItems,
      dropdownSubLabel,
      isShow,
      title,
      isActiveText,
      isDisabled,
      idExtendedComponent,
    }: ExtendedMenuItemProps) => {
      /** Состояние наведения на элемент */
      const [hovered, toggleHover] = useState(false);
      /** Состояние активности элемента */
      const [isActiveState, toggleActive] = useState(isActive);
      /** Состояние выпадающего списка */
      const [dropdownOpen, toggleDropdownOpenState] = useState(true);
      /** Координаты top для выпадающего пункта меню */
      const [topcoordinate, toggleTopCoordinate] = useState('0px');

      /** Ref-ссылка на элемент */
      const elementRef = React.createRef<HTMLLIElement>();
      /** Ref-ссылка на расширенный компонент */
      const extendedComponentRef = React.createRef<HTMLDivElement>();

      const Icon = iconComponent;
      const ActiveIcon = activeIconComponent;
      const ExtendedComponent = extendedMenuExtendableComponent;

      /**
       * Функция обрабатывает нажаните клавиш
       *
       * При нажатии ESC - закрывается расширенный компонент
       */
      const handleKeyDown = (e: KeyboardEvent) => {
        if (e.which === KEY_CODES.ESC || e.keyCode === KEY_CODES.ESC) {
          toggleHover(false);
        }
        if (e.which === KEY_CODES.ENTER || e.keyCode === KEY_CODES.ENTER) {
          if (idExtendedComponent === 'ExtendedDamage') {
            toggleHover(false);
          }
        }
      };

      /**
       * Функция обрабатывает нажатие мышки
       *
       * Закрывает развернутый компонент, если клик мышкой произошел не в его области
       */
      const handleMouseClick = (e: any) => {
        if (idExtendedComponent === 'Indicators' && hovered) {
          if (e.target.innerText === 'Индикаторы') {
            toggleHover(false);
          }
        } else {
          if (idExtendedComponent && e.target.id !== idExtendedComponent) {
            toggleHover(false);
          }
        }
      };

      /** Установка обработчиков событий */
      useEffect(() => {
        const onWheel = (event: MouseEvent) => {
          event.stopPropagation();
        };

        const onScroll = () => {
          toggleHover(false);
        };

        const elExtendedMenuItem = document.getElementById('extended_menuItem');
        const elExtendedMenu = document.getElementById('extendedMenu');

        if (elExtendedMenuItem) {
          const innerElements = Array.prototype.slice.call(elExtendedMenuItem?.childNodes);

          if (innerElements.length) {
            innerElements.forEach((el) => {
              el.addEventListener('wheel', onWheel);
            });
          }

          if (elExtendedMenu) {
            elExtendedMenu.addEventListener('scroll', onScroll);
          }
        }

        if (hovered) {
          window.addEventListener('keydown', handleKeyDown);
          window.addEventListener('click', handleMouseClick);
        } else {
          window.removeEventListener('keydown', handleKeyDown);
          window.removeEventListener('click', handleMouseClick);
        }

        return () => {
          window.removeEventListener('keydown', handleKeyDown);
          window.removeEventListener('click', handleMouseClick);

          if (elExtendedMenuItem) {
            const innerElements = Array.prototype.slice.call(elExtendedMenuItem?.childNodes);

            if (innerElements.length) {
              innerElements.forEach((el) => {
                el.removeEventListener('wheel', onWheel);
              });
            }

            if (elExtendedMenu) {
              elExtendedMenu.removeEventListener('scroll', onScroll);
            }
          }
        };
      });

      /** Функция обрабатывает клик на элемент */
      const handleClick = (e: any) => {
        const { toggleMenuState } = uiStore;

        if (idExtendedComponent === 'Indicators') {
          if (e.target.innerText === 'Индикаторы' && !hovered) {
            toggleTopCoordinate(e.clientY + 'px');
            toggleHover(true);
            callback?.();
          }
        } else {
          if (ExtendedComponent && idExtendedComponent && e.target.id !== idExtendedComponent) {
            if (!hovered) {
              toggleTopCoordinate(e.clientY + 'px');
              toggleHover(true);
              callback?.();
            }
          } else {
            callback?.();
            toggleActive(!isActiveState);

            if (ITEMS_MENU_CLOSE.some((i) => i === e.target.innerText)) {
              toggleMenuState();
            }
          }
        }
      };

      if (hideInExtended) {
        return null;
      }

      return (
        <>
          {dropdownItems && dropdownItems.length > 0 ? (
            <>
              <li
                key={'extended-menu-item' + label}
                className={`${styles.dropdownItem} ${styles.extendedClassName}`}
              >
                <div className={styles.divIcon}>
                  {Icon && (
                    <Icon className={`${styles.dropdownItemIcon} ${extendedIconClassName}`} />
                  )}
                </div>
                <span
                  className={styles.dropdownLabel}
                  onClick={() => toggleDropdownOpenState(!dropdownOpen)}
                  title={title}
                >
                  {label}
                </span>
                <span
                  className={styles.dropdownSubLabel}
                  onClick={() => toggleDropdownOpenState(!dropdownOpen)}
                  title={dropdownSubLabel}
                >
                  {dropdownSubLabel}
                </span>
                <ul className={styles.dropdownList}>
                  {dropdownOpen &&
                    dropdownItems.map(
                      (item, index) =>
                        item.label && (
                          <DropdownItem
                            key={'dropdown-item' + index.toString() + item.label}
                            {...item}
                          />
                        ),
                    )}
                </ul>
              </li>
            </>
          ) : (
            isShow && (
              <>
                {parentLabel && (
                  <li className={`${styles.parentLabel} ${extendedParentLabelClassName}`}>
                    {parentLabel}
                  </li>
                )}
                <li
                  key={'extended-menu-item-' + label}
                  className={`${styles.wrapper} ${extendedClassName} ${
                    isDisabled ? `${styles.disabled}` : ''
                  }`}
                  onClick={(e) => {
                    !isDisabled && handleClick(e);
                  }}
                >
                  {ExtendedComponent && hovered && (
                    <div
                      id="extended_menuItem"
                      style={{ top: topcoordinate }}
                      className={`${styles.extended} ${extendedMenuExtendedContainerClassName}`}
                      ref={extendedComponentRef}
                    >
                      {<ExtendedComponent />}
                    </div>
                  )}
                  <div className={styles.divIcon}>
                    {isActive ? (
                      ActiveIcon ? (
                        <ActiveIcon
                          className={`${styles.icon} ${styles.activeIcon} ${extendedIconClassName} ${extendedActiveIconClassName}`}
                          stroke="white"
                          color="white"
                        />
                      ) : (
                        <Icon
                          className={`${styles.icon} ${styles.activeIcon} ${extendedIconClassName}`}
                          stroke="white"
                          color="white"
                        />
                      )
                    ) : (
                      <Icon className={`${styles.icon} ${extendedIconClassName}`} />
                    )}
                  </div>
                  <div
                    className={
                      isActiveText
                        ? `${styles.label} ${extendedLabelClassName} ${styles.label__active}`
                        : `${styles.label} ${extendedLabelClassName}`
                    }
                    data-tip={'React-tooltip'}
                    data-for={'extended-menu-item-tooltip-' + label}
                  >
                    {label}
                  </div>
                  <ReactTooltip
                    place="right"
                    type="dark"
                    effect="solid"
                    id={'extended-menu-item-tooltip-' + label}
                    delayShow={400}
                    className={styles.reactTooltip}
                  >
                    {typeof title === 'string'
                      ? title
                      : Object.keys(title).map((item, i) => (
                          <li key={i} className={styles.liTooltip}>
                            <span>{title[item]}</span>
                          </li>
                        ))}
                  </ReactTooltip>
                </li>
                {separateNextWithLine && (
                  <li className={`${styles.line} ${extendedHorizontalLineClassName}`} />
                )}
              </>
            )
          )}
        </>
      );
    },
  ),
);

export default ExtendedMenuItem;
