import go, { GraphObject, Point } from 'gojs';

export class DraggingTool extends go.DraggingTool {
  constructor() {
    super();
  }

  public doDropOnto(pt: Point, obj: GraphObject) {
    const targetNode = obj?.part;
    const canDrop =
      (targetNode?.category === 'Node' || targetNode?.category === 'Group') &&
      targetNode?.data?.node?.is_visible &&
      !targetNode?.data?.node?.is_special;

    if (!canDrop) {
      this.draggedParts?.each((obj) => {
        const node = obj.key;

        node.location = new go.Point(
          node.location.x - (pt.x - this.startPoint.x),
          node.location.y - (pt.y - this.startPoint.y),
        );
      });
    }
  }
}
