import { Context } from '../types/utils/UnifiedClientFacade';

/**
 * Класс-фасад для работы с unifiedClientIntegration
 */
export default abstract class UnifiedClientFacade {
  /**
   * Метод устанавливает объект unifiedClientIntegration если клиент - ЕКП
   */
  static async getUnifiedClientIntegration() {
    if (!window.unifiedClientIntegration) {
      await window?.CefSharp?.BindObjectAsync('unifiedClientIntegration');
    }
    return window.unifiedClientIntegration;
  }

  /**
   * Метод вызывает функцию перехода из ЕКП или переход в браузере
   */
  static async executeFunction(
    moduleName: string,
    funcName: string,
    context: Context,
    browserLink?: string,
  ) {
    const unifiedClientIntegration = await this.getUnifiedClientIntegration();

    if (!unifiedClientIntegration) {
      browserLink
        ? window.location.assign(browserLink)
        : console.log(moduleName, funcName, context);
      return 'isBorwser';
    }

    return unifiedClientIntegration.executeFunction(
      moduleName,
      funcName,
      typeof context === 'string' ? context : JSON.stringify(context),
    );
  }
}
