import React from 'react';
import styles from './styles/Checkbox.module.scss';

import { ReactComponent as CheckboxAll } from './assets/checkboxAll.svg';
import { ReactComponent as CheckboxSome } from './assets/checkboxSome.svg';
import { ReactComponent as CheckboxNone } from './assets/checkboxNone.svg';

interface WithCheckboxProps {
  isActive: boolean;
  onToggleCheck?: () => void;
  title?: string;
  isIndicators?: boolean;
  isAllCheck?: boolean;
}

/**
 * Компонент высшего порядка, добавляющий чекбокс
 *
 * @param {bool} isActive - флаг, показывающий состояние чекбокса
 * @param {func} onToggleCheck - функция переключающая состояние чекбокса
 */
const withCheckbox =
  <P extends object>(Component: React.ComponentType<P>): React.FC<P & WithCheckboxProps> =>
  ({ ...props }: WithCheckboxProps) => {
    const { isActive, onToggleCheck, title, isAllCheck } = props;

    return (
      <li className={styles.wrapper} title={title}>
        <div className={styles.checkboxContainer} onClick={onToggleCheck}>
          {isActive && isAllCheck ? (
            <CheckboxAll className={styles.checkboxIcon} />
          ) : isActive ? (
            <CheckboxSome className={styles.checkboxIcon} />
          ) : (
            <CheckboxNone className={styles.checkboxIcon} />
          )}
        </div>
        <div className={styles.container}>
          <Component {...(props as P)} />
        </div>
      </li>
    );
  };

export default withCheckbox;
