import React from 'react';
import styles from './Radio.module.scss';
import Tooltip from 'react-tooltip';

export type RadioTtemType = {
  value: string;
  label: string;
  isDisabled?: boolean;
};

interface IProps {
  items: RadioTtemType[];
  value?: string;
  onChange: (value: string) => void;
  iconFirst?: boolean;
  className?: string;
  disabled?: boolean;
  mode?: 'default' | 'buttons' | 'buttons2';
  label?: string;
  tooltipText?: string;
  tooltipId?: string;
}

/**
 * Компонент отрисовывающий радио кнопки
 *
 * @param {RadioTtemType[]} items - элементы для выбора
 * @param {string} value - выбранный элемент
 * @param {func} onChange - обработчик события выбора
 * @param {boolean} iconFirst - отрисовывать иконку слева от текста
 * @param {string} className - имя класса для стиля
 */
export const Radio = ({
  items,
  iconFirst,
  className,
  value,
  onChange,
  disabled,
  label,
  tooltipText,
  tooltipId,
  mode = 'default',
}: IProps) => {
  return (
    <>
      <div
        className={`${className} ${styles.radio} ${styles[mode]}`}
        data-tip="React-tooltip"
        data-for={tooltipId}
      >
        {label && <div className={styles.label}>{label}</div>}
        <div className={`${styles.items} ${styles[mode]}`}>
          {items.map((item) => (
            <div
              key={item.value}
              onClick={() => (disabled ? false : onChange(item.value))}
              className={`${styles.item} ${item.isDisabled || disabled ? styles.disabled : ''} ${
                item.value === value && styles.checked
              } ${styles[mode]}`}
              title={item.isDisabled ? 'Функциональность временно не доступна' : ''}
            >
              {iconFirst && <div className={styles.icon}></div>}
              <span>{item.label}</span>
              {!iconFirst && <div className={styles.icon}></div>}
            </div>
          ))}
        </div>
      </div>

      {tooltipText && tooltipId && (
        <Tooltip
          place="top"
          type="dark"
          effect="solid"
          delayShow={200}
          offset={{ top: -8, left: 0 }}
          id={tooltipId}
        >
          <span className={styles.tooltip}>{tooltipText}</span>
        </Tooltip>
      )}
    </>
  );
};
