// Кварталы года
export const QUARTERS = ['I', 'II', 'III', 'IV'];
// Кварталы года с надписями
export const QUARTERS_LABELS = ['I кв.', 'II кв.', 'III кв.', 'IV кв.'];
// Кварталы года с полными надписями
export const QUARTERS_FULL_LABELS = ['I квартал', 'II квартал', 'III квартал', 'IV квартал'];

// Текущий год
export const currentYear = new Date().getFullYear();
// Количество учитываемых будущих лет от текущего года
export const PERIOD_YEAR_UP_CURRENT = 0;
// Количество учитываемых прошлых лет от текущего года
export const PERIOD_YEAR_DOWN_CURRENT = 5 + (currentYear - 2020);

// Массив будущих лет
export const YEARS_UP_CURRENT: string[] = Array.from(
  new Array(PERIOD_YEAR_UP_CURRENT),
  (value: string, index: number) => (index + 1 + currentYear).toString(),
);
// Массив прошлых лет
export const YEARS_DOWN_CURRENT: string[] = Array.from(
  new Array(PERIOD_YEAR_DOWN_CURRENT),
  (value: string, index: number) => (currentYear - (index + 1)).toString(),
);

const years: string[] = [];
export const YEARS = years.concat(YEARS_UP_CURRENT, currentYear.toString(), YEARS_DOWN_CURRENT);
