import * as go from 'gojs';
import { computed, observable } from 'mobx';
import { inject, observer } from 'mobx-react';
import React from 'react';
import { DiagramStoreType } from '../../stores/graph/diagramStore';
import { ModalStoreType } from '../../stores/modalStore';
import { DETAILED_LINK_NDS_MODAL } from '../modals/constants/modalNames';
import { ReactComponent as DetailIcon } from './assets/detail.svg';

interface ContextMenuLinkProps {
  [key: string]: any;
  modalStore?: ModalStoreType;
  diagramStore?: DiagramStoreType;
}

/**
 * Компонент контекстное меню связи
 */
@inject('modalStore', 'diagramStore')
@observer
class ContextMenuLink extends React.Component<any> {
  // Объект пользовательского контекст меню
  @observable CustomContextMenuLink = go.GraphObject.make(go.HTMLInfo, {
    show: null,
    hide: null,
  });

  /**
   * Метод по отмене предыдущего действия (закрытие контекст меню)
   */
  hideCX = () => {
    const { diagramStore } = this.props;
    if (diagramStore?.diagram?.currentTool instanceof go.ContextMenuTool) {
      diagramStore.diagram.currentTool.doCancel();
    }
  };

  /**
   * Метод открытия контекст меню
   */
  showContextMenu = (obj: any, diagram: any, tool: any) => {
    const cxElement: HTMLElement | null = document.getElementById('contextMenuLink');
    let hasMenuItem = false;

    if (hasMenuItem) {
      cxElement?.classList.add('show-menu');
      const mousePt = diagram.lastInput.viewPoint;
      const leftCoordinate = mousePt.x + 7 + 'px';
      let topCoordinate;

      if (window.innerWidth > 1745) {
        topCoordinate = mousePt.y + 100 + 'px';
      } else {
        topCoordinate = mousePt.y + 55 + 'px';
      }
      cxElement?.setAttribute('style', 'left:' + leftCoordinate + ';top:' + topCoordinate);
    }
    window.addEventListener('click', this.hideCX, true);
  };

  /**
   * Метод закрытия контекст меню
   */
  hideContextMenu = () => {
    const cxElement = document.getElementById('contextMenuLink');
    cxElement?.classList.remove('show-menu');
    window.removeEventListener('click', this.hideCX, true);
  };

  @computed get getContextMenu() {
    this.CustomContextMenuLink = go.GraphObject.make(go.HTMLInfo, {
      show: this.showContextMenu,
      hide: this.hideContextMenu,
    });
    return this.CustomContextMenuLink;
  }

  /**
   * Метод открытия связи для детализации
   */
  _onLinkDetails = (diagramEvent: any) => {
    const { diagramStore, modalStore } = this.props;
    const linkID = diagramStore.diagram?.selection?.first()?.data.orig_id;
    modalStore.openModal(DETAILED_LINK_NDS_MODAL, { linkID });
  };

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const { diagramStore } = this.props;

    return (
      <>
        <ul id={diagramStore.vsaLinkContextMenuId} className="menu">
          <li className="menu__header">Детализация</li>
          <li id="detailLink" className="menu-item" onClick={this._onLinkDetails}>
            <span className="menu__item">
              <span className="menu__icon">
                <DetailIcon />
              </span>
              Детальная информация
            </span>
          </li>
        </ul>
      </>
    );
  }
}

export default ContextMenuLink;
