import React from 'react';
import * as go from 'gojs';
import { inject, observer } from 'mobx-react';

import { observable, computed } from 'mobx';
import { DETAILED_CLUSTER_MODAL } from '../modals/constants/modalNames';

import { ModalStoreType, DiagramStoreType, GraphStoreType } from 'StoreTypes';

interface ContextMenuClusterProps {
  [key: string]: any;
  modalStore?: ModalStoreType;
  diagramStore?: DiagramStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент контекстное меню кластера
 */
@inject('modalStore', 'diagramStore', 'graphStore')
@observer
class ContextMenuCluster extends React.Component<any> {
  // Объект пользовательского контекст меню
  @observable CustomContextMenuCluster = go.GraphObject.make(go.HTMLInfo, {
    show: null,
    hide: null,
  });

  /**
   * Метод открытия карточки кластера для детализации
   */
  _onClusterDetails = (clusterId: any) => {
    const { modalStore, graphStore } = this.props;

    modalStore?.openModal(DETAILED_CLUSTER_MODAL, {
      clusterId,
      disabled: true,
      fetchDetailedCluster: (clusterId: string, graphId?: string) =>
        graphStore.fetchDetailedCluster(clusterId, graphId),
      fetchNPCluster: (clusterId: string, graphId?: string) =>
        graphStore.fetchNPCluster(clusterId, graphId),
    });
  };

  /**
   * Метод по отмене предыдущего действия (закрытие контекст меню)
   */
  hideCX = () => {
    const { diagramStore } = this.props;
    if (diagramStore?.diagram?.currentTool instanceof go.ContextMenuTool) {
      diagramStore.diagram.currentTool.doCancel();
    }
  };

  /**
   * Метод открытия контекст меню
   */
  showContextMenu = (obj: any, diagram: any, tool: any) => {
    const cxElement: HTMLElement | null = document.getElementById('contextMenuCluster');
    let hasMenuItem = false;

    function maybeShowItem(elt: any) {
      if ((elt.style.display = 'none')) {
        elt.style.display = 'block';
        hasMenuItem = true;
      }
    }
    maybeShowItem(document.getElementById('detailCluster'));
    if (hasMenuItem) {
      cxElement?.classList.add('show-menu');
      const mousePt = diagram.lastInput.viewPoint;
      const leftCoordinate = mousePt.x + 7 + 'px';
      let topCoordinate;

      if (window.innerWidth > 1745) {
        topCoordinate = mousePt.y + 100 + 'px';
      } else {
        topCoordinate = mousePt.y + 55 + 'px';
      }
      cxElement?.setAttribute('style', 'left:' + leftCoordinate + ';top:' + topCoordinate);
    }
    window.addEventListener('click', this.hideCX, true);
  };

  /**
   * Метод закрытия контекст меню
   */
  hideContextMenu = () => {
    const cxElement = document.getElementById('contextMenuCluster');
    cxElement?.classList.remove('show-menu');
    window.removeEventListener('click', this.hideCX, true);
  };

  @computed get getContextMenu() {
    this.CustomContextMenuCluster = go.GraphObject.make(go.HTMLInfo, {
      show: this.showContextMenu,
      hide: this.hideContextMenu,
    });
    return this.CustomContextMenuCluster;
  }

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const { diagramStore } = this.props;

    return (
      <>
        <ul id={diagramStore.vsaClusterContextMenuId} className="menu">
          <li className="menu__header" id="detailInfoCluster">
            Детализация
          </li>
          {diagramStore?.clusterInfo?.map((cluster: any, index: number) => (
            <li
              key={index}
              id="detailCluster"
              className="menu-item"
              onClick={() => this._onClusterDetails(cluster.key)}
            >
              {cluster.text + ' ' + (cluster.cluster_index ? cluster.cluster_index + 1 : 1)}
            </li>
          ))}
        </ul>
      </>
    );
  }
}

export default ContextMenuCluster;
