import React, { PureComponent } from 'react';
import styles from './ToolTipBusiness.module.scss';

import { inject, observer } from 'mobx-react';
import { GraphStoreType } from '../../stores/graph/graphStore';

type ToolTipBusinessProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
};

type ToolTipBusinessState = {
  graphID: any;
};

/**
 * Компонент всплывающей подсказки по бизнес-роли
 */
@inject('graphStore')
@observer
class ToolTipBusiness extends PureComponent<ToolTipBusinessProps, ToolTipBusinessState> {
  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { graphStore, id } = this.props;

    return (
      <>
        <div className={graphStore?.businessName ? styles.container : styles.hidden} id={id}>
          <div className={styles.data_container}>{graphStore?.businessName}</div>
        </div>
      </>
    );
  }
}

export default ToolTipBusiness;
