import { observable, computed, action } from 'mobx';

import AsyncStore from '../utils/AsyncStore';

import SpecialOperationFilter from './specialOperationFilter';
import ResultNDFilter from './resultNDFilter';
import GapsDebtFilter from './gapsDebtFilter';
import LinksFilter from './linksFilter';

import { GraphStoreType } from '../graph/graphStore';
import { RootStoreType } from '../rootStore';

/**
 * Хранилище фильтров
 */
class IndicatorsStore extends AsyncStore {
  GraphStore: GraphStoreType;
  SpecialOperationFilter: SpecialOperationFilter;
  ResultNDFilter: ResultNDFilter;
  GapsDebtFilter: GapsDebtFilter;
  LinksFilter: LinksFilter;

  /**
   * Конструктор собирает все фильтры
   */
  constructor(RootStore: RootStoreType) {
    super();

    this.GraphStore = RootStore.GraphStore;

    this.SpecialOperationFilter = new SpecialOperationFilter(this);
    this.ResultNDFilter = new ResultNDFilter(this);
    this.GapsDebtFilter = new GapsDebtFilter(this);
    this.LinksFilter = new LinksFilter(this);
  }

  @observable options: any = {
    // 1/3 от суммы задолженности
    Debt13: true,
    // 2/3 от суммы задолженности
    Debt23: true,
    // 100% задолженности
    Debt100: true,
    // Разрыв по НДС
    Break_NDS: true,
    // Сумма по НДС
    Group_Type_Nds: true,
    // Сумма по АБД
    Group_Type_Abd: true,
    // Сумма НДС + АБД
    Group_Type_Nds_Abd: true,
    // Декларация к уплате
    DeclState_1: true,
    // Декларация к возмещению
    DeclState_2: true,
    // Нулевая декларация
    DeclState_3: true,
    // Декларация не представлена
    DeclState_0: true,
    // Расхождения по разделу 11
    Ook_Mis11Amount: true,
    // Контрольные соотношения
    Ook_KsVatAmount: true,
    // Сомнительные начисления
    Ook_DoubtfulAmount: true,
    // СФ до 2015г.
    Ook_Inv2015Ch8Amount: true,
    // КВО 17
    Ook_Oc17Amount: true,
    // КВО 23
    Ook_Oc23Amount: true,
    // Дробление СФ
    Ook_SplittingAmount: true,

    is_links: true,
    is_gapsDebt: true,
    is_resultND: true,
    is_special_operation: true,
  };

  // Показать все
  @observable showAll: boolean = true;

  // Изменения по Разрывы и задолженности
  @observable isGabsDebtFilter: boolean = false;
  // Изменения по Индикаторы связи
  @observable isLinksFilter: boolean = false;
  // Изменения по Результат по НД
  @observable isResultNDFilter: boolean = false;
  // Изменения по Операции особого контроля
  @observable isSpecialOperationFilter: boolean = false;

  @observable isChangeFilter: boolean = false;

  @action
  setShowAll = () => {
    this.showAll = !this.showAll;
  };

  /**
   * Метод возвращает опции всех фильтров для запроса на фильтрацию
   */
  @action getFiltersOptions = () => {
    const { SpecialOperationFilter, ResultNDFilter, GapsDebtFilter, LinksFilter } = this;

    let options = {};

    if (SpecialOperationFilter.keep) {
      const SpecialOperationFilterOptions = SpecialOperationFilter.getFilterOptions;
      options = {
        ...options,
        ...SpecialOperationFilterOptions,
      };
    }

    if (ResultNDFilter.keep) {
      const ResultNDFilterOptions = ResultNDFilter.getFilterOptions;
      options = {
        ...options,
        ...ResultNDFilterOptions,
      };
    }

    if (GapsDebtFilter.keep) {
      const GapsDebtFilterOptions = GapsDebtFilter.getFilterOptions;
      options = {
        ...options,
        ...GapsDebtFilterOptions,
      };
    }

    if (LinksFilter.keep) {
      const LinksFilterOptions = LinksFilter.getFilterOptions;
      options = {
        ...options,
        ...LinksFilterOptions,
      };
    }

    return options;
  };

  /**
   * Метод сбрасывает настройки фильтров
   */
  @action resetFilters = () => {
    const { SpecialOperationFilter, ResultNDFilter, GapsDebtFilter, LinksFilter } = this;
    this.showAll = false;
    SpecialOperationFilter.resetFilters();
    ResultNDFilter.resetFilters();
    GapsDebtFilter.resetFilters();
    LinksFilter.resetFilters();
  };

  /**
   * Метод показывает все настройки фильтров
   */
  @action showAllFilters = () => {
    const { SpecialOperationFilter, ResultNDFilter, GapsDebtFilter, LinksFilter } = this;
    this.showAll = true;
    SpecialOperationFilter.setOptionsToTrue();
    ResultNDFilter.setOptionsToTrue();
    GapsDebtFilter.setOptionsToTrue();
    LinksFilter.setOptionsToTrue();
  };

  @action filter = () => {
    this.options = this.getFiltersOptions();
    this.GraphStore.setOptions(this.options);
    this.isChangeFilter = false;
    this.isSpecialOperationFilter = false;
    this.isResultNDFilter = false;
    this.isLinksFilter = false;
    this.isGabsDebtFilter = false;
  };

  @action checkAllTrue = () => {
    const { SpecialOperationFilter, ResultNDFilter, GapsDebtFilter, LinksFilter } = this;
    let othersOptions: any = {};
    othersOptions = this.getFiltersOptions();
    this.showAll = Object.keys(othersOptions).every((item: any) => othersOptions[item] === true);
    if (
      !SpecialOperationFilter.keep ||
      !ResultNDFilter.keep ||
      !GapsDebtFilter.keep ||
      !LinksFilter.keep
    ) {
      this.showAll = false;
    }
  };

  filterGraph = () => {
    this.filter();
    this.GraphStore.fetchGraph(this.GraphStore.previouslyFetchedGraphId, null, true);
  };

  @action.bound
  checkChanges = () => {
    this.isChangeFilter =
      this.isGabsDebtFilter ||
      this.isLinksFilter ||
      this.isResultNDFilter ||
      this.isSpecialOperationFilter;
  };

  /**
   * Метод возвращает список опций
   */
  @computed get publicOptions() {
    return this.options;
  }
}

export type IndicatorsStoreType = IndicatorsStore;
export default IndicatorsStore;
