export interface ParseCooperationDateParams {
  nds3_period_from: string;
  nds3_period_to: string;
}

/** Функция для обработки периода */
export const parseCooperationDate = ({
  nds3_period_from,
  nds3_period_to,
}: ParseCooperationDateParams) => {
  const nds3PeriodFrom = nds3_period_from?.split(';') || ['', ''];
  const nds3PeriodTo = nds3_period_to?.split(';') || ['', ''];

  return {
    quarterFrom: nds3PeriodFrom[0] || '',
    quarterTo: nds3PeriodTo[0] || '',
    yearFrom: nds3PeriodFrom[1] || '',
    yearTo: nds3PeriodTo[1] || '',
  };
};
