import { observable, decorate, action } from 'mobx';
import { IRole } from './components/RoleForm/RoleForm.props';
/**
 * Хранилище информации о детализированной информации узла
 *
 * @param businessRoleName - наименование (бизнес роль)
 * @param businessSource - источник информации (бизнес роль)
 * @param businessReason - причина изменения (бизнес роль)
 * @param businessComment - комментарий (бизнес роль)
 * @param analiticRoleName - наименование (аналитическая роль)
 * @param analiticSource - источник информации (аналитическая роль)
 * @param analiticReason - причина изменения (аналитическая роль)
 * @param analiticComment - комментарий (аналитическая роль)
 * @param analiticDocument - документы (аналитическая роль)
 * @param businessDocument - документы (бизнес роль)
 * @param isChange - наличие изменения
 */

class DetailedNodeStore {
  businessRoleName: any = null;
  businessSource: any = null;
  businessReason: any = null;
  businessComment: any = null;

  analiticRoleName: any = null;
  analiticSource: any = null;
  analiticReason: any = null;
  analiticComment: any = null;

  analiticDocument: any[] = [];
  businessDocument: any[] = [];

  businessRoles: any[] = [];

  analyticRoleID: any = null;
  businessRoleID: any = null;

  businessReasonID: any = null;
  analiticReasonID: any = null;

  isChangeAnalitical: boolean = false;
  isChangeBusiness: boolean = false;

  isValidFile: boolean = false;
  fileName: string = '';

  businessRole: IRole = {
    name: '',
    source: '',
    reason: '',
    comment: '',
    files: [],
  };

  analiticalRole: IRole = {
    name: '',
    source: '',
    reason: '',
    comment: '',
    files: [],
  };

  setBusinessRole = (role: IRole) => {
    this.businessRole = role;
  };

  setAnaliticalRole = (role: IRole) => {
    this.analiticalRole = role;
  };

  // Произошли ли изменения (аналитическая роль)
  setIsChangeAnalitical = (condition: boolean): void => {
    this.isChangeAnalitical = condition;
  };

  // Произошли ли изменения (аналитическая роль)
  setIsChangeBusiness = (condition: boolean): void => {
    this.isChangeBusiness = condition;
  };

  // Установка значения Наименование бизнес-роли
  setBusinessRoleName = (businessRoleName: any): void => {
    this.businessRoleName = businessRoleName;
  };

  // Установка значения Прикрепленные документы аналитической роли
  setAnaliticDocument = (documents: any[]): void => {
    this.analiticDocument = documents;
  };

  // Установка значения Прикрепленные бизнес-роли
  setBusinessDocument = (documents: any[]): void => {
    this.businessDocument = documents;
  };

  // Установка значения Источник бизнес-роли
  setBusinessSource = (businessSource: any): void => {
    this.businessSource = businessSource;
  };

  // Установка значения Причина изменения бизнес-роли
  setBusinessReason = (businessReason: any): void => {
    this.businessReason = businessReason;
  };

  // Установка значения Комментарий в бизнес-роли
  setBusinessComment = (businessComment: any): void => {
    this.businessComment = businessComment;
  };

  // Установка значения Наименование аналитической роли
  setAnaliticRoleName = (analiticRoleName: any): void => {
    this.analiticRoleName = analiticRoleName;
  };

  // Установка значения Источник аналитической роли
  setAnaliticSource = (analiticSource: any): void => {
    this.analiticSource = analiticSource;
  };

  // Установка значения Причина изменения аналитической роли
  setAnaliticReason = (analiticReason: any): void => {
    this.analiticReason = analiticReason;
  };

  // Установка значения Комментарий в аналитической роли
  setAnaliticComment = (analiticComment: any): void => {
    this.analiticComment = analiticComment;
  };

  // Установка значения ID аналитической роли
  setAnalyticRoleID = (analyticRoleID: any): void => {
    this.analyticRoleID = analyticRoleID;
  };

  // Установка значения ID в бизнес-роли
  setBusinessRoleID = (businessRoleID: any): void => {
    this.businessRoleID = businessRoleID;
  };

  // Валидация файлов
  setIsValidFile = (data: any): void => {
    this.isValidFile = data;
  };

  // Установить имя файла
  setFileName = (data: any): void => {
    this.fileName = data;
  };

  // Установка значения ID причины изменения аналитической роли
  setAnaliticReasonID = (analiticReasonID: any): void => {
    this.analiticReasonID = analiticReasonID;
  };

  // Установка значения ID причины изменения бизнес-роли
  setBusinessReasonID = (businessReasonID: any): void => {
    this.businessReasonID = businessReasonID;
  };

  // Установка истории Бизнес-ролей
  setBusinessRolesArray = (array: any): void => {
    this.businessRoles = array;
  };
}

decorate(DetailedNodeStore, {
  businessRole: observable,
  businessRoleName: observable,
  businessSource: observable,
  businessReason: observable,
  businessComment: observable,

  analiticRoleName: observable,
  analiticSource: observable,
  analiticReason: observable,
  analiticComment: observable,

  analiticDocument: observable,
  businessDocument: observable,

  analyticRoleID: observable,
  businessRoleID: observable,

  businessReasonID: observable,
  analiticReasonID: observable,

  isChangeAnalitical: observable,
  isChangeBusiness: observable,

  isValidFile: observable,
  fileName: observable,

  businessRoles: observable,
  analiticalRole: observable,

  setBusinessRole: action,
  setAnaliticalRole: action,

  setBusinessRoleName: action,
  setBusinessSource: action,
  setBusinessReason: action,
  setBusinessComment: action,

  setAnaliticDocument: action,
  setBusinessDocument: action,

  setAnalyticRoleID: action,
  setBusinessRoleID: action,

  setAnaliticRoleName: action,
  setAnaliticSource: action,
  setAnaliticReason: action,
  setAnaliticComment: action,

  setIsChangeAnalitical: action,
  setIsChangeBusiness: action,

  setIsValidFile: action,
  setFileName: action,

  setAnaliticReasonID: action,
  setBusinessReasonID: action,

  setBusinessRolesArray: action,
});

export type DetailedNodeStoreType = DetailedNodeStore;
export default new DetailedNodeStore();
