import React from 'react';
import { IToolTipLayersProps } from './ToolTipLayers.props';
import styles from './ToolTipLayers.module.scss';
import { observer } from 'mobx-react';
import { useStores } from 'hooks';
import { ILinkData } from 'DataTypes';
import { formatMoneyWithText } from 'utils/ui/formatMoney';

const ToolTipLayers = observer(({ className, ...props }: IToolTipLayersProps) => {
  const {
    tooltipStore: { currentLink },
    graphStore: { dimensions, graphExpandType },
  } = useStores();

  const linkData = (currentLink?.data as ILinkData) || undefined;

  return (
    <div className={`${styles.tooltip} ${className}`} {...props} id="ToolTipLayers">
      <div className={styles.header}>Детализация</div>
      {graphExpandType !== 'Contract' && linkData?.contract_price && (
        <div className={styles.item}>
          Госконтракты: {formatMoneyWithText(linkData.contract_price, dimensions)}
        </div>
      )}
      {graphExpandType !== 'Nds' && linkData?.vat_calc && (
        <div className={styles.item}>
          Сумма НДС исчисленная: {formatMoneyWithText(linkData.vat_calc, dimensions)}
        </div>
      )}
      {graphExpandType !== 'Nds' && linkData?.vat_deduct && (
        <div className={styles.item}>
          Сумма НДС к вычету: {formatMoneyWithText(linkData.vat_deduct, dimensions)}
        </div>
      )}
      {graphExpandType !== 'Nds' && linkData?.discrepancy_total && (
        <div className={styles.item}>
          Сумма расхождений по НДС: {formatMoneyWithText(linkData.discrepancy_total, dimensions)}
        </div>
      )}
      {graphExpandType !== 'Abd' && linkData?.debit_sum && (
        <div className={styles.item}>
          Приход: {formatMoneyWithText(linkData.debit_sum, dimensions)}
        </div>
      )}
      {graphExpandType !== 'Abd' && linkData?.credit_sum && (
        <div className={styles.item}>
          Расход: {formatMoneyWithText(linkData.credit_sum, dimensions)}
        </div>
      )}
      {!graphExpandType.includes('Trace') && (linkData?.trace_cost || linkData?.trace_count) && (
        <div className={styles.item}>
          ПП "Прослеживаемость":{' '}
          {linkData?.trace_cost
            ? formatMoneyWithText(linkData.trace_cost, dimensions)
            : 'стоимость не указана'}
        </div>
      )}
      {!graphExpandType.includes('Property') &&
        (linkData?.property_cost || linkData?.property_count) && (
          <div className={styles.item}>
            ПП "Собственность":{' '}
            {linkData?.property_cost
              ? formatMoneyWithText(linkData.property_cost, dimensions)
              : 'стоимость не указана'}
          </div>
        )}
      {!graphExpandType.includes('Mark') && (linkData?.mark_cost || linkData?.mark_count) && (
        <div className={styles.item}>
          ГИС МТ:{' '}
          {linkData?.mark_cost
            ? formatMoneyWithText(linkData.mark_cost, dimensions)
            : 'стоимость не указана'}
        </div>
      )}
    </div>
  );
});

export default ToolTipLayers;
