import axios, { AxiosResponse, AxiosRequestConfig, ResponseType } from 'axios';
import { API_URL, NEW_API_URL, API_TYPOLOGY, API_ACCOUNT, API_DNP } from '../constants';
import rootStore from 'stores/rootStore';

import { v4 } from 'uuid';

/**
 * Класс для обработки запросов к API
 */
class ApiService {
  public source: any = axios.CancelToken.source();

  /**
   * Метод возвращает заголовки для запроса
   */
  get commonRequestHeaders() {
    const headers = {
      'X-Request-ID': v4(),
    };

    return headers;
  }

  /**
   * Функция, делающая GET запрос
   *
   * Устанавливает Reqest ID для запроса
   *
   * @param {string} path - Путь API
   * @returns {Promise<object|error>} - Возвращает промис ответа(Объект или ошибка)
   */
  get = async <T>(
    path: string,
    _isNewController: boolean = false,
    responseType: ResponseType = 'json',
    headers: any = {},
    _isTypology: boolean = false,
    _isUserInfo: boolean = false,
    _isDNP: boolean = false,
    withRequestId?: boolean,
  ): Promise<T> => {
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      console.log('%c GET: ', 'color: #29acf2;', path);
    }

    this.source = axios.CancelToken.source();

    const config: AxiosRequestConfig = {
      cancelToken: this.source.token,
      headers: { ...this.commonRequestHeaders, ...headers },
      responseType,
    };

    return await axios
      .get<T>(
        (_isNewController
          ? NEW_API_URL
          : _isTypology
          ? API_TYPOLOGY
          : _isUserInfo
          ? API_ACCOUNT
          : _isDNP
          ? API_DNP
          : API_URL) + path,
        config,
      )
      .then<T>((response) => this.onSuccess(response, responseType, withRequestId))
      .catch(this.handleError);
  };

  /**
   * Функция, делающая POST запрос
   *
   * Устанавливает Reqest ID для запроса
   *
   * @param {string} path - Путь API
   * @param {object} requestBody - Тело POST запроса
   * @returns {Promise<object|error>} - Возвращает промис ответа(Объект или ошибка)
   */
  post = async <T>(
    path: string,
    requestBody?: any,
    headers: any = {},
    _isNewController: boolean = false,
    _isTypology: boolean = false,
  ): Promise<T> => {
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      console.log('%c POST: ', 'color: #ffe552;', path, '\n BODY: ', requestBody);
    }

    const config = {
      headers: { ...this.commonRequestHeaders, ...headers },
    };

    return await axios
      .post<T>(
        (_isNewController ? NEW_API_URL : _isTypology ? API_TYPOLOGY : API_URL) + path,
        requestBody,
        config,
      )
      .then<T>(this.onSuccess)
      .catch(this.handleError);
  };

  /**
   * Функция, делающая PUT запрос
   *
   * Устанавливает Reqest ID для запроса
   *
   * @param {string} path - Путь API
   * @param {object} requestBody - Тело PUT запроса
   * @returns {Promise<object|error>} - Возвращает промис ответа(Объект или ошибка)
   */
  put = async <T>(path: string, requestBody: any): Promise<T | AxiosResponse | null> => {
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      console.log('%c PUT: ', 'color: #8e49fc;', path, '\n BODY: ', requestBody);
    }

    const config = {
      headers: this.commonRequestHeaders,
    };

    return await axios
      .put(API_URL + path, requestBody, config)
      .then<T>(this.onSuccess)
      .catch(this.handleError);
  };

  /**
   * Функция, делающая DELETE запрос
   *
   * Устанавливает Reqest ID для запроса
   *
   * @param {string} path - Путь API
   * @returns {Promise<object|error>} - Возвращает промис ответа(Объект или ошибка)
   */
  delete = async <T>(path: string): Promise<T | AxiosResponse | null> => {
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      console.log('%c DELETE: ', 'color: #ed1111;', path);
    }

    const config = {
      headers: {
        'X-Request-ID': v4(),
      },
    };

    return await axios
      .delete(API_URL + path, config)
      .then<T>((response) => this.onSuccess(response))
      .catch(this.handleError);
  };

  /**
   * Функция, обрабатывающая успешный запрос
   *
   * @param {object} response - Объект ответа от сервера, обернутый в AxiosResponse
   * @returns {object} - Возвращает ответ от сервера
   */
  onSuccess = <T>(
    response: AxiosResponse,
    responseType?: ResponseType,
    withRequestId?: boolean,
  ): T | any | null => {
    const { data, status } = response;
    const result =
      responseType === 'arraybuffer'
        ? data
        : withRequestId
        ? { ...data.result, request_id: data.request_id }
        : data.result;

    const { error, request_id, message, is_error, exec_time } = data;

    if (is_error || !result) {
      if (message) {
        this.handleMessage({ ...message, request_id, exec_time, status });
      }

      this.handleError({
        ...error,
        ...message,
        request_id,
        exec_time,
        status,
      });

      return null;
    }

    if (message) {
      this.handleMessage({ ...message, request_id, exec_time, status });
    }

    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      console.log('%c SUCCESS', 'color: #3ecf65;');
    }

    return result;
  };

  /**
   * Функция, обрабатывающая запросы с ошибкой
   *
   * @param {error} err - Объект ошибки
   */
  handleError = (err: Error): never => {
    if (axios.isCancel(err)) {
      console.log('Request cancelled');
    } else {
      if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        console.warn('Failed request: ', err);
      }
    }
    throw err;
  };

  /**
   * Функция, обрабатывающая запросы с ошибкой
   *
   * @param {error} err - Объект ошибки
   */
  handleMessage = (message: any) => {
    rootStore.NotificationStore.saveMessage(message);
  };
}

export default new ApiService();
