export const toSnakeCase = (string: string): string => {
  return string.replace(
    /[A-Z]/g,
    (match, offset) => (offset === 0 ? '' : '_') + match.toLowerCase(),
  );
};

export const toNumber = (value: any, ifNaNValue: number | null = null) =>
  value && !isNaN(Number(value.replaceAll(' ', '').replaceAll(String.fromCharCode(160), '')))
    ? Number(value.replaceAll(' ', '').replaceAll(String.fromCharCode(160), ''))
    : ifNaNValue !== undefined
    ? ifNaNValue
    : null;
