import { action, observable } from 'mobx';
import * as modalNames from '../components/modals/constants/modalNames';

import { RootStoreType } from 'StoreTypes';

/**
 * Тип для всплывающего окна
 *
 * @param {string} modalName - идентификатор всплывающего окна
 * @param {any} modalProps - свойства(данные) для всплывающего окна
 */
interface ModalType {
  modalName: string;
  modalProps: any;
}

/**
 * Хранилище всплывающиъ окон
 */
class ModalStore {
  RootStore: RootStoreType;

  constructor(RootStore: RootStoreType) {
    this.RootStore = RootStore;
  }

  @observable modals: Array<ModalType> = [];

  @observable activeModal: any = '';

  /**
   * Метод добавляет всплывающее окно в массив modals
   *
   * @param {string} modalName - идентификатор всплывающего окна
   * @param {any} modalProps - свойства(данные) для всплывающего окна
   */
  @action openModal = (modalName: string, modalProps?: any): void => {
    const currentModals = this.modals.map(({ modalName }) => modalName);

    if (!currentModals.includes(modalName)) {
      this.modals.push({ modalName, modalProps });
      this.setActiveModal(modalName);
    }
  };

  @action setActiveModal = (modalName: string) => {
    this.activeModal = modalName;
  };

  /**
   * Метод удаляет всплывающее окно из массива modals
   *
   * @param {string} modalName - идентификатор всплывающего окна
   */
  @action closeModal = (modalName: string): void => {
    this.modals = this.modals.filter((modal) => modal.modalName !== modalName);
  };

  /** Метод закрывает все всплывающие окна */
  @action closeModals = (): void => {
    this.modals = [];
  };

  /** Метод открывает окно добавления узла */
  @action openAddNewNodeModal = (options = {}) => {
    this.openModal(modalNames.ADD_NEW_NODE, options);
  };

  /** Метод открывает окно Проекта акта */
  @action openReferenceModal = (options = {}) => {
    this.openModal(modalNames.MODAL_SAVE_REFERENCE, options);
  };

  /** Метод открытия модального окна по поиску узла на графе*/
  @action openSearchNodeGraph = (options = {}) => {
    this.openModal(modalNames.MODAL_SEARCH_NODE_IN_GRAPH, options);
  };

  /** Метод открывает окно передачи изменений */
  @action openSendChangesModal = (options = {}) => {
    this.openModal(modalNames.MODAL_SEND_CHANGES, options);
  };

  /** Метод открывает окно сохранения графа */
  @action openSaveGraphModal = (options = {}) => {
    this.openModal(modalNames.MODAL_SAVE_GRAPH, options);
  };

  /** Метод открывает окно графов пользователя */
  @action openUserGraphsModal = (options = {}) => {
    this.openModal(modalNames.MODAL_USER_GRAPHS, options);
  };

  @action openSearchListModal = (options = {}) => {
    this.openModal(modalNames.MODAL_SEARCH_LIST, options);
  };

  @action.bound
  closeSearchListModal = () => {
    this.closeModal(modalNames.MODAL_SEARCH_LIST);
  };

  /** Метод открывает окно выбора периода */
  @action.bound
  openPickPeriodModal = (options = {}) => {
    this.openModal(modalNames.MODAL_PICK_PERIOD, options);
  };

  /** Метод закрывает окно выбора периода */
  @action.bound
  closePickPeriodModal = () => {
    this.closeModal(modalNames.MODAL_PICK_PERIOD);
  };

  /** Метод открывает окно начала работы */
  @action.bound
  openBeginingWorkModal = (options = {}) => {
    this.openModal(modalNames.MODAL_BEGINNING_WORK, options);
  };

  /** Метод закрывает окно начала работы */
  @action.bound
  closeBeginingWorkModal = () => {
    this.closeModal(modalNames.MODAL_BEGINNING_WORK);
  };

  /** Метод открывает окно Отчёт "Дерево связей" */
  @action.bound
  openTreeReportCallback = (options = {}) => {
    this.openModal(modalNames.MODAL_TREE_REPORT, options);
  };

  /** Метод открывает окно Отчёт "Дерево связей" */
  @action.bound
  openCreateSchemeCallback = (options = {}) => {
    this.openModal(modalNames.MODAL_CREATE_SCHEME, options);
  };

  /** Метод открывает окно Отчёт "Дерево связей" */
  @action.bound
  openPotentialDamageModal = (options = {}) => {
    this.openModal(modalNames.MODAL_POTENTIAL_DAMAGE, options);
  };

  /** Метод открывает окно Отчёт "Дерево связей" */
  @action.bound
  openDifferentSumCallback = (options = {}) => {
    this.openModal(modalNames.MODAL_DIFFERENT_SUM, options);
  };

  /** Метод открывает окно Напечатать схему */
  @action.bound
  openPrintSchemeCallback = (options = {}) => {
    this.openModal(modalNames.MODAL_PRINT_SCHEME, options);
  };
}

export type ModalStoreType = ModalStore;
export default ModalStore;
