import ApiServices from './ApiServices';

import { RoleResponse, ReasonInvalid, Dictionary, ChangeReason } from 'ApiModel';

/**
 * Функция делает GET запрос для получения списка аналитических ролей
 *
 * @return {Promise} - список аналитических ролей
 */
export const getAnalyticalRoles = async (): Promise<RoleResponse> => {
  return await ApiServices.get<RoleResponse>(`/VSA2DictionaryApiV1/analyticalroles`, true);
};

