import { observer } from 'mobx-react';
import React, { useRef } from 'react';
import Button from '../../components/Button';
import CheckboxGroup from '../../components/CheckboxGroup';
import { Panel, PanelGroup } from '../../components/PanelGroup';
import { useOutsideClick, useStores } from '../../hooks';
import styles from './TaxpayerSettings.module.scss';
import { ITaxpayerSettingsProps } from './TaxpayerSettings.props';

const TaxpayerSettings = observer(({ className, ...props }: ITaxpayerSettingsProps) => {
  const {
    tooltipStore: {
      unsavedSettingsItems,
      taxpayerSettingRequiredItems,
      changeTaxpayerSettings,
      changeAllTaxpayerSettings,
      hasChanges,
      saveChanges,
      cancelChanges,
      saveButtonText,
    },
    uiStore: { toggleTaxpayerSettingsState },
  } = useStores();

  const ref = useRef(null);
  useOutsideClick(ref, toggleTaxpayerSettingsState, 'BurgerMenu');

  const menu = document.querySelector('#BurgerMenu') as HTMLDivElement;

  return (
    <div
      className={`${className} ${styles.settings}`}
      style={{ left: Math.max(36, menu.offsetWidth - 24) }}
      ref={ref}
      {...props}
    >
      <PanelGroup>
        <Panel header="Обязательные параметры">
          <CheckboxGroup
            items={taxpayerSettingRequiredItems}
            onCheckboxChange={(_, name) => changeTaxpayerSettings(name)}
          />
        </Panel>

        <Panel header="Настраиваемые параметры">
          <CheckboxGroup
            items={unsavedSettingsItems}
            onCheckboxChange={(_, name) => changeTaxpayerSettings(name)}
            onAllCheckboxChange={changeAllTaxpayerSettings}
            itemsClassName={styles.scroll}
          />
        </Panel>
      </PanelGroup>

      <div className={styles.buttons}>
        <Button size="small" disabled={!hasChanges} onClick={saveChanges}>
          {saveButtonText}
        </Button>

        <Button size="small" disabled={!hasChanges} onClick={cancelChanges} mode="transparent">
          Отменить
        </Button>
      </div>
    </div>
  );
});

export default TaxpayerSettings;
