import { ISearchListParams, ISearchListResponse } from 'ApiModel';
import { action, configure, decorate, flow, observable } from 'mobx';
import { RootStoreType } from 'stores/rootStore';
import { flowTypes } from 'utils/flowTypes';
import { fetchSearchList } from 'utils/services/api/common';
import GraphStore from '../graph/graphStore/graphStore';

configure({ enforceActions: 'observed' });

class SearchListStore {
  graphStore: GraphStore;
  rootStore: RootStoreType;

  constructor(rootStore: RootStoreType) {
    this.rootStore = rootStore;
    this.graphStore = rootStore.GraphStore;
  }

  statusMap = {
    '0': {
      name: 'queue',
      label: 'В очереди',
    },
    '1': {
      name: 'process',
      label: 'В процессе',
    },
    '2': {
      name: 'success',
      label: 'Выполнен',
    },
    '3': {
      name: 'error',
      label: 'Ошибка',
    },
  };

  mainStatus: {
    name: string;
    label: string;
  } | null = null;

  searchList: ISearchListResponse[] = [];
  isLoading: boolean = false;

  goToChains = (search: ISearchListResponse) => {
    const params = JSON.parse(search.parameters) as ISearchListParams;

    this.rootStore.ChainsStore.setSearchIsComplited(
      search.job_id,
      search.search_id,
      search.status,
      params,
    );

    this.rootStore.ModalStore.closeSearchListModal();
    this.rootStore.UIStore.setChainsIsOpen(true);
  };

  goToBeginning = (search: ISearchListResponse) => {
    const params = JSON.parse(search.parameters) as ISearchListParams;

    this.rootStore.BeginningWorkStore.setSearchIsComplited(
      search.job_id,
      search.search_id,
      search.status,
      params,
    );

    this.rootStore.ModalStore.closeSearchListModal();
    this.rootStore.UIStore.setChainsIsOpen(false);
    this.rootStore.CommonStore.openBeginningWork();
  };

  checkActiveChainsSearch = () => {
    const activeSearch = this.searchList.find(
      ({ status, description }) =>
        (status === 0 || status === 1) && description === 'Поиск кратчайших цепочек',
    );

    if (activeSearch) {
      const params = JSON.parse(activeSearch.parameters) as ISearchListParams;

      this.rootStore.ChainsStore.setSearchIsActive(
        activeSearch.job_id,
        activeSearch.search_id,
        activeSearch.status,
        params,
      );
    }
  };

  checkActiveNpSearch = () => {
    const activeSearch = this.searchList.find(
      ({ status, description }) =>
        (status === 0 || status === 1) && description === 'Поиск налогоплательщиков',
    );

    if (activeSearch) {
      const params = JSON.parse(activeSearch.parameters) as ISearchListParams;

      this.rootStore.BeginningWorkStore.setSearchIsActive(
        activeSearch.job_id,
        activeSearch.search_id,
        activeSearch.status,
        params,
      );
    }
  };

  fetchSearchList = flow(function* (this: SearchListStore) {
    this.isLoading = true;

    try {
      this.searchList = (yield* flowTypes(fetchSearchList())).reverse();

      if (this.searchList.length > 0) {
        this.mainStatus = this.statusMap?.[this.searchList[0].status] || {
          name: 'undefined',
          label: 'Не определён',
        };
      }
    } catch (e) {
      this.graphStore.onError(e);
    } finally {
      this.isLoading = false;
    }
  });
}

decorate(SearchListStore, {
  searchList: observable,
  isLoading: observable,
  mainStatus: observable,

  checkActiveChainsSearch: action,
  goToChains: action,
  goToBeginning: action,
  fetchSearchList: action.bound,
});

export default SearchListStore;
