import React from 'react';
import { IButtonProps } from './Button.props';
import styles from './Button.module.scss';

const Button = ({
  className,
  children,
  leftIcon,
  rightIcon,
  size = 'medium',
  mode = 'primary',
  isActive,
  isIcon,
  ...props
}: IButtonProps) => {
  return (
    <button
      className={`${styles.button} ${styles[mode]} ${styles[size]} ${isActive && styles.active} ${
        isIcon && styles.isIcon
      } ${className}`}
      {...props}
    >
      {leftIcon && <span className={`${styles.icon} ${styles.leftIcon}`}>{leftIcon}</span>}
      <span className={styles.label}>{children}</span>
      {rightIcon && <span className={`${styles.icon} ${styles.rightIcon}`}>{rightIcon}</span>}
    </button>
  );
};

export default Button;
