import { action, computed, observable, autorun } from 'mobx';
import Filter from '../utils/Filter';

import { FilterStoreType } from './filterStore';
import { CommonStoreType } from '../common/commonStore';
import { GraphStoreType } from '../graph/graphStore';
import { TaxpayerFilterItem, TaxpayerFilterField, TaxpayerFilteredField } from 'FilterTypes';
import { IntegralsIdentities, Role } from 'DataTypes';
import { toSnakeCase } from 'utils';

/**
 * Хранилище фильтра Налогоплательщики
 */
class TaxpayersFilter extends Filter {
  FilterStore: FilterStoreType;
  CommonStore: CommonStoreType;
  GraphStore: GraphStoreType;

  constructor(
    FilterStore: FilterStoreType,
    CommonStore: CommonStoreType,
    GraphStore: GraphStoreType,
  ) {
    super();
    this.FilterStore = FilterStore;
    this.CommonStore = CommonStore;
    this.GraphStore = GraphStore;

    autorun(() => {
      const {
        is_taxpayers,
        ook_to,
        ook_from,
        tax_gap_to,
        tax_gap_from,
        analytical_roles,
        business_roles,
        integrals,
      } = this.FilterStore.options;
      this.updateOptions(
        {
          is_taxpayers,
          ook_to,
          ook_from,
          tax_gap_to,
          tax_gap_from,
          analytical_roles,
          business_roles,
          integrals,
        },
        'is_taxpayers',
      );
    });

    autorun(this.setFiltersOptions);
  }

  @observable keep = false;

  @observable isAllFalse = false;

  @observable analyticalRoles: TaxpayerFilterItem[] = [];
  @observable analyticalRolesArray: TaxpayerFilterItem[] = [];

  @observable businessRoles: TaxpayerFilterItem[] = [];
  @observable businessRolesArray: TaxpayerFilterItem[] = [];

  @observable integrals: TaxpayerFilterItem[] = [];
  @observable integralsArray: TaxpayerFilterItem[] = [];

  /** Настройки "показывать только выбранные" */
  @observable showOnlySelected = {
    analyticalRoles: false,
    businessRoles: false,
    integrals: false,
  };

  /** Значения поиска */
  @observable searchValue = {
    analyticalRoles: '',
    businessRoles: '',
    integrals: '',
  };

  // опции фильтра
  initialOptions = {
    // Сумма ООК от
    ook_from: 0,
    // Сумма ООК до
    ook_to: '',
    // TG от
    tax_gap_from: 0,
    // TG до
    tax_gap_to: '',
    // Аналитические роли
    analytical_roles: [],
    // Бизнес-роли
    business_roles: [],
    // Уровень риска
    integrals: [],
  };

  /** Метод сбрасывает фильтры и текст поиска */
  @action resetSearch = (): void => {
    this.searchValue = {
      analyticalRoles: '',
      businessRoles: '',
      integrals: '',
    };
    this.filterVisibleOptions('analyticalRoles');
    this.filterVisibleOptions('businessRoles');
    this.filterVisibleOptions('integrals');
  };

  /** Метод сбрасывает фильтры и текст поиска */
  @action resetFiltersAndSearch = (): void => {
    this.resetSearch();
    this.reset();
  };

  /** Метод переключает состояние фильтра */
  @action toggleFilter = () => {
    this.keep = !this.keep;
    if (!this.keep) {
      this.resetFiltersAndSearch();
    }
    this.FilterStore.isTaxpayersChange = this.keep !== this.GraphStore.filtersGraph.is_taxpayers;
    this.FilterStore.checkChanges();
  };

  @action checkValue = (value: number) => {
    let valueTransform = value;
    if (value.toString().includes('.')) {
      // @ts-ignore
      valueTransform = valueTransform.toString().replace('.', '');
    }
    if (isNaN(valueTransform)) {
      // @ts-ignore
      valueTransform = valueTransform.toString().replace(/[^\d]/g, '');
    }
    return valueTransform;
  };

  /**
   * Метод изменяет сумму ООК от
   *
   * @param {number} value - значение для записи
   */
  @action changeOOKFrom = (value: number) => {
    this.changeOption('ook_from', this.checkValue(value));
    this.checkAllFalse();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму ООК до
   *
   * @param {number} value - значение для записи
   */
  @action changeOOKTo = (value: number) => {
    this.changeOption('ook_to', this.checkValue(value));
    this.checkAllFalse();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму ООК от
   *
   * @param {number} value - значение для записи
   */
  @action changeTaxGapFrom = (value: number) => {
    this.changeOption('tax_gap_from', this.checkValue(value));
    this.checkAllFalse();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму ООК до
   *
   * @param {number} value - значение для записи
   */
  @action changeTaxGapTo = (value: number) => {
    this.changeOption('tax_gap_to', this.checkValue(value));
    this.checkAllFalse();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет аналитические роли
   *
   * @param {number} value - значение для записи
   */
  @action changeAnalyticalRoles = (value: string) => {
    if (value === 'RESET_ACTION') {
      this.options['analytical_roles'] = [];
      this.showOnlySelected.analyticalRoles = false;
    } else if (value === 'ALL_CHECK_ACTION') {
      //@ts-ignore
      this.options['analytical_roles'] = this.analyticalRolesArray.map(({ value }) => {
        return value;
      });
    } else {
      this.changeOptionArray('analytical_roles', value);
      if (this.options.analytical_roles.length === 0) {
        this.showOnlySelected.analyticalRoles = false;
      }
    }
    this.checkAllFalse();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
    this.filterVisibleOptions('analyticalRoles');
  };

  /**
   * Метод изменяет бизнес-роли
   *
   * @param {number} value - значение для записи
   */
  @action changeBusinessRoles = (value: string) => {
    if (value === 'RESET_ACTION') {
      this.options['business_roles'] = [];
      this.showOnlySelected.businessRoles = false;
    } else if (value === 'ALL_CHECK_ACTION') {
      // @ts-ignore
      this.options['business_roles'] = this.businessRolesArray.map(({ value }) => {
        return value;
      });
    } else {
      this.changeOptionArray('business_roles', value);
      if (this.options.business_roles.length === 0) {
        this.showOnlySelected.businessRoles = false;
      }
    }
    this.checkAllFalse();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
    this.filterVisibleOptions('businessRoles');
  };

  /**
   * Метод изменяет список уровней риска
   *
   * @param {number} value - значение для записи
   */
  @action changeIntegrals = (value: string) => {
    if (value === 'RESET_ACTION') {
      this.options['integrals'] = [];
      this.showOnlySelected.integrals = false;
    } else if (value === 'ALL_CHECK_ACTION') {
      // @ts-ignore
      this.options['integrals'] = this.integralsArray.map(({ value }) => {
        return value;
      });
    } else {
      this.changeOptionArray('integrals', value);
      if (this.options.integrals.length === 0) {
        this.showOnlySelected.integrals = false;
      }
    }
    this.checkAllFalse();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
    this.filterVisibleOptions('integrals');
  };

  // Происходили ли изменения среди фильтров
  @action isChangeStatic = () => {
    this.FilterStore.isTaxpayersChange = false;
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_taxpayers') {
        if (typeof this.options[item] === 'object') {
          if (
            !(
              this.options[item]?.length === this.FilterStore.options[item]?.length &&
              this.options[item].every(
                (value: any, index: any) => value === this.FilterStore.options[item][index],
              )
            )
          ) {
            this.FilterStore.isTaxpayersChange = true;
          }
        } else {
          if (
            this.options[item] !=
            (this.FilterStore.options[item] == undefined ? '' : this.FilterStore.options[item])
          ) {
            this.FilterStore.isTaxpayersChange = true;
          }
        }
      }
    });
  };

  @action checkAllFalse = () => {
    const isFalse = (key: string) =>
      !this.options[key] ||
      this.options[key]?.length === 0 ||
      this.options[key] === '0' ||
      key === 'is_taxpayers';

    this.isAllFalse = Object.keys(this.options).every((key: string) => isFalse(key));
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
      is_taxpayers: this.keep,
    };
  }

  /**
   * Метод фильтрует массивы возможных значенй фильтра в зависимости от поиска и выбранных занченйи
   *
   * @param {TaxpayerFilterField} field - тип фильтра по которому выполняется поиск
   */
  @action filterVisibleOptions = (field: TaxpayerFilterField) => {
    const filteredField = (field + 'Array') as TaxpayerFilteredField;

    this[filteredField] = this[field]?.filter((option: TaxpayerFilterItem) => {
      return (
        (!this.searchValue[field] ||
          option.name.toLowerCase().includes(this.searchValue[field].toLowerCase())) &&
        (!this.showOnlySelected[field] || this.options[toSnakeCase(field)].includes(option.value))
      );
    });
  };

  /**
   * Метод выполняет посик
   */
  @action onSearch = (search: string, field: TaxpayerFilterField) => {
    this.searchValue[field] = search;
    this.filterVisibleOptions(field);
  };

  /**
   * Метод изменянет значение "показать только выбранные"
   */
  @action onChangeOptionsVisible = (field: TaxpayerFilterField, value?: boolean) => {
    this.showOnlySelected[field] = value === undefined ? !this.showOnlySelected[field] : value;
    this.filterVisibleOptions(field);
  };

  /**
   * Метод устанавливает стартовые значения опций фильтра
   *
   * @param {Role[] | IntegralsIdentities[]} filterList - поле из которого брать данные
   * @param {TaxpayerFilterField} field - тип фильтра длязаполнения значений
   * @param {boolean} isIntegrals - для фильтра "integrals"
   */
  @action setFilterItems = (
    filterList: Role[] | IntegralsIdentities[],
    field: TaxpayerFilterField,
    isIntegrals?: boolean,
  ) => {
    const list = filterList as (IntegralsIdentities & Role)[];

    const listForFilter: TaxpayerFilterItem[] = list?.map((item) => ({
      name: isIntegrals ? item.description : item.value,
      value: isIntegrals ? item.id : item.role_id,
    }));

    !isIntegrals && listForFilter.push({ name: 'Не определена', value: 0 });

    const filteredField = (field + 'Array') as TaxpayerFilteredField;
    this[field] = this[filteredField] = listForFilter;
  };

  setFiltersOptions = () => {
    this.setFilterItems(this.CommonStore.analyticalRolesList, 'analyticalRoles');
    this.setFilterItems(this.CommonStore.businessRolesList, 'businessRoles');
    this.setFilterItems(this.CommonStore.integralsList, 'integrals', true);
  };
}

export default TaxpayersFilter;
