import React from 'react';
import { observer } from 'mobx-react';

import { useStores } from '../../../hooks';
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs';

import { FreeSearchTab } from './FreeSearchTab/FreeSearchTab';
import { CreateScheme } from './CreateScheme/CreateScheme';

import styles from './BeginningWork.module.scss';

interface IBeginningWorkProps {
  paramsDNP?: string;
}

const BeginningWork = observer(({ paramsDNP }: IBeginningWorkProps) => {
  const {
    beginningWorkStore: { tabIndex },
  } = useStores();

  return (
    <div className={styles.wrapper}>
      <div className={`${styles.header} ${styles.row}`}>
        <div className={styles.container}>Построить новую схему</div>
      </div>

      <div className={styles.row}>
        <div className={styles.container}>
          <Tabs className={styles.buttons} selectedIndex={tabIndex}>
            <TabList>
              <Tab>Поиск по налогоплательщикам</Tab>
              <Tab>Построение схемы</Tab>
            </TabList>
          </Tabs>
        </div>
      </div>

      <Tabs className={styles.panels} selectedIndex={tabIndex}>
        <TabPanel className={styles.panel}>
          <FreeSearchTab paramsDNP={paramsDNP} />
        </TabPanel>

        <TabPanel className={styles.panel}>
          <CreateScheme paramsDNP={paramsDNP} />
        </TabPanel>
      </Tabs>
    </div>
  );
});

export default BeginningWork;
