import React, { PureComponent } from 'react';
import Switch from 'react-switch';
import styles from './IndicatorsContainer.module.scss';

import { inject, observer } from 'mobx-react';

import { GraphStoreType } from '../../../stores/graph/graphStore';
import { IndicatorsStoreType } from '../../../stores/indicators/indicatorsStore';

type IndicatorsContainerProps = {
  [key: string]: any;
  indicatorsStore?: IndicatorsStoreType;
  gapsDebtFilter?: any;
  specialOperationFilter?: any;
  resultNDFilter?: any;
  linksFilter?: any;

  graphStore?: GraphStoreType;
};

/**
 * Отображение окошка для индикаторов рядом с бургер-меню
 */
@inject(
  'graphStore',
  'gapsDebtFilter',
  'specialOperationFilter',
  'resultNDFilter',
  'linksFilter',
  'indicatorsStore',
)
@observer
class IndicatorsContainer extends PureComponent<IndicatorsContainerProps> {
  _onFilterGapsDebt = async () => {
    const { indicatorsStore, graphStore, gapsDebtFilter } = this.props;
    gapsDebtFilter.toggleFilter();
    indicatorsStore?.filter();
    await graphStore?.fetchGraph();
  };

  _onFilterOperation = async () => {
    const { indicatorsStore, graphStore, specialOperationFilter } = this.props;
    specialOperationFilter.toggleFilter();
    indicatorsStore?.filter();
    await graphStore?.fetchGraph();
  };

  _onFilterND = async () => {
    const { indicatorsStore, graphStore, resultNDFilter } = this.props;
    resultNDFilter.toggleFilter();
    indicatorsStore?.filter();
    await graphStore?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, true);
  };

  _onFilterLink = async () => {
    const { indicatorsStore, graphStore, linksFilter } = this.props;
    linksFilter.toggleFilter();
    indicatorsStore?.filter();
    await graphStore?.fetchGraph();
  };

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { gapsDebtFilter, specialOperationFilter, resultNDFilter } = this.props;

    return (
      <div className={styles.containerExtended}>
        <div className={styles.indicatorsContainerBig}>
          <div className={styles.addInformation}>
            <div className={styles.additionalLabel}>
              <span>Расхождение и задолженность</span>
              <Switch
                onChange={this._onFilterGapsDebt}
                checked={gapsDebtFilter.keep}
                onColor="#3E72A3"
                onHandleColor="#3E72A3"
                handleDiameter={18}
                uncheckedIcon={false}
                checkedIcon={false}
                boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                height={13}
                width={33}
              />
            </div>
            <div className={styles.additionalLabel}>
              <span>Операции особого контроля</span>
              <Switch
                onChange={this._onFilterOperation}
                checked={specialOperationFilter.keep}
                onColor="#3E72A3"
                onHandleColor="#3E72A3"
                handleDiameter={18}
                uncheckedIcon={false}
                checkedIcon={false}
                boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                height={13}
                width={33}
              />
            </div>
            <div className={styles.additionalLabel}>
              <span>Результат по НД</span>
              <Switch
                onChange={this._onFilterND}
                checked={resultNDFilter.keep}
                onColor="#3E72A3"
                onHandleColor="#3E72A3"
                handleDiameter={18}
                uncheckedIcon={false}
                checkedIcon={false}
                boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                height={13}
                width={33}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default IndicatorsContainer;
