import React, { PureComponent } from 'react';
import filterStyles from '../../common/Filter.module.scss';
import styles from './AskNds.module.scss';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';
import { GraphStoreType } from '../../../../stores/graph/graphStore';

import Icon from '../../../../components/Icon';
import MoneySpreadPicker from '../../../../components/MoneySpreadPicker';

interface AskNdsProps {
  isActive: boolean;
  onToggleCheck?: () => void;
  toggleOpenState?: () => void;
  cooperationFilter?: any;
  isOpen?: boolean;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент "Ask Nds" в блоке "Кооперация"
 */
@inject('cooperationFilter', 'commonStore', 'graphStore')
@observer
class AskNds extends PureComponent<AskNdsProps> {
  render() {
    const { isOpen, toggleOpenState, commonStore, cooperationFilter, graphStore } = this.props;

    return (
      <li className={`${filterStyles.wrapper} ${styles.wrapper}`}>
        <div className={filterStyles.header}>
          <span
            className={filterStyles.headerLabel}
            onClick={toggleOpenState}
            title={
              graphStore?.graphExpandType === 'Nds' && graphStore?.sourceSystem !== 'MDAB'
                ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                : 'Признак отображения связей по данным АСК НДС-2'
            }
          >
            АСК НДС-2
          </span>
          {graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds' && (
            <Icon
              className={filterStyles.toggle}
              name={isOpen ? 'arrow_up' : 'arrow_down'}
              onClick={toggleOpenState}
              theme={commonStore?.appTheme}
            />
          )}
        </div>
        {graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds' && isOpen && (
          <div className={styles.container}>
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(cooperationFilter.options.vat_gap_from)?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.vat_gap_to
                  ? Number(cooperationFilter.options.vat_gap_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeVatGapFrom}
              onChangeToValue={cooperationFilter.changeVatGapTo}
              label={'Сумма расхождений по НДС, ₽'}
              title={'Диапазон сумм расхождений по НДС'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds')}
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(cooperationFilter.options.purchases_from)?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.purchases_to
                  ? Number(cooperationFilter.options.purchases_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changePurchasesFrom}
              onChangeToValue={cooperationFilter.changePurchasesTo}
              label={'НДС к вычету, ₽'}
              title={'Диапазон сумм НДС к вычету для отображаемых связей'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds')}
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(cooperationFilter.options.sales_from)?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.sales_to
                  ? Number(cooperationFilter.options.sales_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeSalesFrom}
              onChangeToValue={cooperationFilter.changeSalesTo}
              label={'НДС исчисленный, ₽'}
              title={'Диапазон сумм НДС исчисленный для отображаемых связей'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds')}
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(cooperationFilter.options.shareVatDeduct_from)?.toLocaleString(
                'ru',
              )}
              toValue={
                cooperationFilter.options.shareVatDeduct_to
                  ? Number(cooperationFilter.options.shareVatDeduct_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeShareVatDeductFrom}
              onChangeToValue={cooperationFilter.changeShareVatDeductTo}
              label={'Доля НДС к вычету, %'}
              title={'Диапазон доли НДС к вычету для отображаемых связей'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds')}
              isPercent
              isSmall
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(cooperationFilter.options.shareVatCalc_from)?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.shareVatCalc_to
                  ? Number(cooperationFilter.options.shareVatCalc_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeShareVatCalcFrom}
              onChangeToValue={cooperationFilter.changeShareVatCalcTo}
              label={'Доля НДС исчисленный, %'}
              title={'Диапазон доли НДС исчисленного для отображаемых связей'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds')}
              isPercent
              isSmall
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(
                cooperationFilter.options.clear_deduction_ratio_from,
              )?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.clear_deduction_ratio_to
                  ? Number(cooperationFilter.options.clear_deduction_ratio_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeClearDeductionRatioFrom}
              onChangeToValue={cooperationFilter.changeClearDeductionRatioTo}
              label={'Доля "чистых" вычетов по НДС, %'}
              title={'Диапазон доли "чистых" вычетов по НДС'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds')}
              isPercent
              isSmall
            />
            <MoneySpreadPicker
              wrapperStyle={styles.picker}
              fromValue={Number(
                cooperationFilter.options.schema_deduction_ratio_from,
              )?.toLocaleString('ru')}
              toValue={
                cooperationFilter.options.schema_deduction_ratio_to
                  ? Number(cooperationFilter.options.schema_deduction_ratio_to).toLocaleString('ru')
                  : ''
              }
              onChangeFromValue={cooperationFilter.changeSchemaDeductionRatioFrom}
              onChangeToValue={cooperationFilter.changeSchemaDeductionRatioTo}
              label={'Доля "схемных" вычетов по НДС, %'}
              title={'Диапазон доли "схемных" вычетов по НДС'}
              disabled={!(graphStore?.showChangeFilters && graphStore?.graphExpandType === 'Nds')}
              isPercent
              isSmall
            />
          </div>
        )}
      </li>
    );
  }
}

export default AskNds;
