import React from 'react';
import { observer } from 'mobx-react';
import { useStores } from 'hooks';

import { IResultProps } from './Result.props';

import { IPagination, Pagination } from '@fdc/vsa-react-lib';
import Chain from '../Chain';
import Loader from '../Loader';

import styles from './Result.module.scss';

const Result = ({ className, ...props }: IResultProps) => {
  const {
    chainsStore: { searchResult, pagination, changePagination, searchState },
  } = useStores();

  const { nds_chains, abd_chains, goods_chains, contract_chains, link_type } = searchResult;

  const getNumber = (i: number) => pagination.page * pagination.count + (i + 1);

  const typesMap = {
    nds: nds_chains.map((chain, i) => ({ ...chain, number: getNumber(i) })),
    abd: abd_chains.map((chain, i) => ({ ...chain, number: getNumber(i) })),
    goods: goods_chains.map((chain, i) => ({ ...chain, number: getNumber(i) })),
    contract: contract_chains.map((chain, i) => ({
      ...chain,
      number: getNumber(i),
    })),
  };

  const typesNamesMap = {
    nds: 'АСК',
    abd: 'АБД',
    goods: 'ТП',
    contract: 'ГК',
  };

  const chains = typesMap[link_type || 'nds'];

  const startResult = pagination.page * pagination.count + 1;
  const endResult = (pagination.page + 1) * pagination.count;

  const onPaginationChange = (pagination: IPagination) => {
    console.log(pagination);
  };

  return (
    <div className={`${className} ${styles.result}`} {...props}>
      {searchState === 1 ? (
        <Loader />
      ) : chains.length === 0 ? (
        <div className={styles.noresult}>Данные не найдены</div>
      ) : (
        <>
          <div className={styles.header}>
            <div></div>
            <div></div>
            <div className={styles.titles}>
              <div className={styles.title}>Продавец (начало цепочки)</div>
              <div className={styles.title}>Покупатель (конец цепочки)</div>
            </div>
            <div></div>
          </div>

          {chains.map((chain, i) => (
            <Chain
              key={`chain${chain.number}`}
              chain={chain}
              type={typesNamesMap[link_type || 'nds']}
            />
          ))}

          <div className={styles.paginationWrapper}>
            <span className={styles.total}>{`Результаты: с ${startResult} по ${
              endResult > pagination.total ? pagination.total : endResult
            } из ${pagination.total}`}</span>
            <Pagination
              className={styles.pagination}
              pagination={pagination}
              showPerPageOptions
              onChange={changePagination}
            />
          </div>
        </>
      )}
    </div>
  );
};

export default observer(Result);
