import React from 'react';
import * as go from 'gojs';
import { inject, observer } from 'mobx-react';

import { observable, computed } from 'mobx';

import { DiagramStoreType } from '../../stores/graph/diagramStore';
import { MODAL_DETAILED_GROUP } from '../modals/constants/modalNames';

import { ModalStoreType } from 'StoreTypes';

interface ContextMenuGroup {
  diagramStore?: DiagramStoreType;
  graphStore?: DiagramStoreType;
  modalStore?: ModalStoreType;
}

/**
 * Компонент контекстное меню группы
 */
@inject('graphStore', 'diagramStore', 'modalStore')
@observer
class ContextMenuGroup extends React.Component<any> {
  // Объект пользовательского контекст меню
  @observable CustomContextMenuGroup = go.GraphObject.make(go.HTMLInfo, {
    show: null,
    hide: null,
  });

  /**
   * Метод выхода узла из группы.
   *
   * @param {any} diagramEvent - событие диаграммы
   */
  _nodeOutGroup = async (): Promise<void> => {
    const { diagramStore } = this.props;
    const selectedNode = diagramStore?.diagram?.selection?.first();
    const orId = selectedNode?.data.orig_id;
    if (orId) {
      const { graphStore } = this.props;

      await graphStore.nodeOutOfGroup(orId);
      graphStore.fetchGraph();
    }
  };

  /**
   * Просмотр детализированной информации по группе
   *
   */
  _detailedGroup = async (): Promise<void> => {
    const { diagramStore, modalStore, graphStore } = this.props;
    const selectedNode = diagramStore?.diagram?.selection?.first();
    const groupID = selectedNode?.data.orig_id;
    const category = selectedNode?.data.category;
    if (groupID) {
      modalStore.openModal(MODAL_DETAILED_GROUP, {
        groupID,
        category,
      });
    }
  };

  /**
   * Метод создания группы
   */
  _createGroup = async (): Promise<void> => {
    const { diagramStore, graphStore } = this.props;
    const selectedNode = diagramStore?.diagram?.selection.first();

    const zombie = selectedNode.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orId =
      graphStore?.graphTransformMode === 'ModeV3' && zombie
        ? selectedNode.data.key
        : selectedNode.data.orig_id;

    const groupOrIds = diagramStore.selectedNodes.length > 1 ? diagramStore.selectedNodes : [orId];

    if (orId) {
      await graphStore?.createGroupFromNode(groupOrIds);
    }
  };

  /**
   * Метод по отмене предыдущего действия (закрытие контекст меню)
   */
  hideCX = () => {
    const { diagramStore } = this.props;
    if (diagramStore?.diagram?.currentTool instanceof go.ContextMenuTool) {
      diagramStore.diagram.currentTool.doCancel();
    }
  };

  /**
   * Метод открытия контекст меню
   */
  showContextMenu = (obj: any, diagram: any, tool: any) => {
    const cxElement: HTMLElement | null = document.getElementById('contextMenuGroup');
    let hasMenuItem = false;

    function maybeShowItem(elt: any) {
      if ((elt.style.display = 'none')) {
        elt.style.display = 'block';
        hasMenuItem = true;
      }
    }
    maybeShowItem(document.getElementById('outGroup'));
    if (hasMenuItem) {
      cxElement?.classList.add('show-menu');
      const mousePt = diagram.lastInput.viewPoint;
      const leftCoordinate = mousePt.x + 7 + 'px';
      let topCoordinate;

      if (window.innerWidth > 1745) {
        topCoordinate = mousePt.y + 100 + 'px';
      } else {
        topCoordinate = mousePt.y + 55 + 'px';
      }
      cxElement?.setAttribute('style', 'left:' + leftCoordinate + ';top:' + topCoordinate);
    }
    window.addEventListener('click', this.hideCX, true);
  };

  /**
   * Метод закрытия контекст меню
   */
  hideContextMenu = () => {
    const cxElement = document.getElementById('contextMenuGroup');
    cxElement?.classList.remove('show-menu');
    window.removeEventListener('click', this.hideCX, true);
  };

  @computed get getContextMenu() {
    this.CustomContextMenuGroup = go.GraphObject.make(go.HTMLInfo, {
      show: this.showContextMenu,
      hide: this.hideContextMenu,
    });
    return this.CustomContextMenuGroup;
  }

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const { diagramStore } = this.props;
    return (
      <>
        <ul id={diagramStore.vsaGroupContextMenuId} className="menu">
          <li id="detailedGroup" className="menu-item" onClick={this._detailedGroup}>
            Детальная информация
          </li>
          <li id="inGroup" className="menu-item" onClick={this._createGroup}>
            Группировать
          </li>
          <li id="outGroup" className="menu-item" onClick={this._nodeOutGroup}>
            Разгруппировать
          </li>
        </ul>
      </>
    );
  }
}

export default ContextMenuGroup;
