import { inject, observer } from 'mobx-react';
import React, { ComponentType, PureComponent, Suspense, lazy } from 'react';
import { ModalStoreType } from '../../../stores/modalStore';
import * as modalNames from '../constants/modalNames';
import styles from './ModalManager.module.scss';

/**
 * Константа для хранения типов модальных окон
 * DETAILED_NODE_MODAL - модальное окно детализации узла
 * DETAILED_LINK_NDS_MODAL - модальное окно детализации связи для движения НДС
 * DETAILED_CLUSTER_MODAL - модальное окно детализации кластера
 * ADD_NEW_NODE - модальное окно добавления узла
 * MODAL_SAVE_GRAPH - модальное окно сохранения графа
 * MODAL_CREATE_LINK - модальное окно по созданию связи
 * MODAL_CREATE_LINK_DETAIL - модальное окно по созданию связи (детализированная информация)
 * MODAL_EDIT_LINK - модальное окно по редактированию связи
 * MODAL_USER_GRAPHS - модальное окно графов пользователя
 * MODAL_SEARCH_NODE_IN_GRAPH - модальное окно поиска узла в графе
 * MODAL_SAVE_REFERENCE - модальное окно по сохранению Проекта акта
 * MODAL_SEND_CHANGES - модальное окно по передаче изменений
 * MODAL_DETAILED_LINK_CLUSTER - модальное окно по изменению связи через кластер
 * MODAL_DETAILED_GROUP - модальное окно по детализации группы
 * MODAL_BEGINNING_WORK - Модальное окно начала работы
 * MODAL_DETAILED_INDIVIDUAL_PERSON - Модальное окно по детализации ФЛ
 * MODAL_TREE_REPORT - Отчёт "Дерево связей"
 * MODAL_CREATE_SCHEME - Создание схемы
 * MODAL_POTENTIAL_DAMAGE - Ввод потенциального ущерба
 * MODAL_DIFFERENT_SUM - Наличие расхождения в суммах НДС между графом и витриной
 * MODAL_PRINT_SCHEME - Напечатать схему
 */
//@ts-ignore
const modalsMap = new Map([
  [modalNames.DETAILED_NODE_MODAL, lazy(() => import('../DetailedNode'))],
  [modalNames.DETAILED_LINK_NDS_MODAL, lazy(() => import('../DetailedLinkNDS'))],
  [modalNames.DETAILED_CLUSTER_MODAL, lazy(() => import('../DetailedCluster'))],
  [modalNames.ADD_NEW_NODE, lazy(() => import('../AddNewNode'))],
  [modalNames.MODAL_SAVE_GRAPH, lazy(() => import('../ModalSaveGraph'))],
  [modalNames.MODAL_CREATE_LINK, lazy(() => import('../CreateLink'))],
  [modalNames.MODAL_CREATE_LINK_DETAIL, lazy(() => import('../CreateLinkDetail'))],
  [modalNames.MODAL_EDIT_LINK, lazy(() => import('../EditLink'))],
  [modalNames.MODAL_USER_GRAPHS, lazy(() => import('../../../containers/UserGraphs'))],
  [modalNames.MODAL_SEARCH_NODE_IN_GRAPH, lazy(() => import('../SearchNode'))],
  [modalNames.MODAL_SAVE_REFERENCE, lazy(() => import('../GetReference'))],
  [modalNames.MODAL_SEND_CHANGES, lazy(() => import('../SendChanges'))],
  [modalNames.MODAL_DETAILED_LINK_CLUSTER, lazy(() => import('../DetailedLinkCluster'))],
  [modalNames.MODAL_PICK_PERIOD, lazy(() => import('../PickPeriod'))],
  [modalNames.MODAL_DETAILED_GROUP, lazy(() => import('../DetailedGroup'))],
  [modalNames.MODAL_BEGINNING_WORK, lazy(() => import('../BeginningWork'))],
  [modalNames.MODAL_DETAILED_INDIVIDUAL_PERSON, lazy(() => import('../DetailedIndividualPerson'))],
  [modalNames.MODAL_TREE_REPORT, lazy(() => import('../TreeReport'))],
  [modalNames.MODAL_CREATE_SCHEME, lazy(() => import('../CreateScheme'))],
  [modalNames.MODAL_POTENTIAL_DAMAGE, lazy(() => import('../PotentialDamage'))],
  [modalNames.MODAL_DIFFERENT_SUM, lazy(() => import('../DifferentSum'))],
  [modalNames.MODAL_PRINT_SCHEME, lazy(() => import('../PrintScheme'))],
  [modalNames.MODAL_SEARCH_LIST, lazy(() => import('../SearchList'))],
]);

const newModals: Record<string, boolean> = {
  MODAL_SAVE_GRAPH: true,
  MODAL_USER_GRAPHS: true,
  MODAL_CREATE_LINK: true,
  MODAL_SEARCH_LIST: true,
  MODAL_TREE_REPORT: true,
  MODAL_PRINT_SCHEME: true,
  MODAL_DIFFERENT_SUM: true,
  DETAILED_NODE_MODAL: true,
  MODAL_DETAILED_GROUP: true,
  MODAL_SAVE_REFERENCE: true,
  DETAILED_CLUSTER_MODAL: true,
  DETAILED_LINK_NDS_MODAL: true,
  MODAL_SEARCH_NODE_IN_GRAPH: true,
  MODAL_DETAILED_LINK_CLUSTER: true,
};

type ModalManagerProps = {
  modalStore?: ModalStoreType;
};

/**
 * Компонент контролирует отрисовку всплывающих окон
 */
@inject('modalStore')
@observer
class ModalManager extends PureComponent<ModalManagerProps> {
  _onMove = (e: React.MouseEvent<HTMLDivElement>, modalName: string) => {
    const { modalStore } = this.props;
    const targetElement: HTMLElement = e.target as HTMLElement;
    const isGrabbable = targetElement.getAttribute('data-draggable');

    if (!isGrabbable) {
      return;
    }

    modalStore?.setActiveModal(modalName);

    const clientWidth = window.innerWidth;
    const clientHeight = window.innerHeight;

    const topPadding = (clientWidth <= 1745 ? 60 : 95) / 2;

    let element = document.getElementById(modalName);

    const startPositionX = e.clientX;
    const startPositionY = e.clientY;

    const currentPositionX = parseInt(
      getComputedStyle(element!, null).getPropertyValue('transform').split(',')[4],
    );
    const currentPositionY = parseInt(
      getComputedStyle(element!, null).getPropertyValue('transform').split(',')[5],
    );

    const width = element!.getBoundingClientRect().width;

    moveAt(e.pageX, e.pageY);

    // Переносим на координаты (pageX, pageY),
    // дополнительно учитывая изначальный сдвиг относительно указателя мыши
    function moveAt(pageX: number, pageY: number) {
      targetElement!.style.cursor = 'grabbing';

      const newX = pageX - startPositionX + currentPositionX;
      const newY = pageY - startPositionY + currentPositionY;

      const isInHorCorner =
        clientWidth / 2 + newX + width - 200 > 0 && newX + width < clientWidth / 2;
      const isInVerCorner =
        clientHeight / 2 + newY - topPadding > 0 && newY + 200 < clientHeight / 2;

      if (isInHorCorner && isInVerCorner) {
        element!.style.transform = `translateX(${newX}px) translateY(${newY}px)`;
      } else finishDrag();
    }

    function onMouseMove(e: any) {
      e.preventDefault();
      moveAt(e.pageX, e.pageY);
    }

    // Отпустить, удалить ненужные обработчики
    function onMouseUp() {
      finishDrag();
    }

    // Следим за перемещением
    function finishDrag() {
      document.removeEventListener('mousemove', onMouseMove);
      element!.removeEventListener('mouseup', onMouseUp);
      targetElement!.style.cursor = 'grab';
    }

    document.addEventListener('mousemove', onMouseMove);
    element!.addEventListener('mouseup', onMouseUp);

    // Отключаем D'n'D браузера по умолчанию
    element!.ondragstart = function () {
      return false;
    };
  };

  render() {
    const { modalStore } = this.props;

    return modalStore?.modals.map(({ modalName, modalProps }) => {
      // @ts-ignore
      const ModalComponent: ComponentType = modalsMap.get(modalName);

      return newModals[modalName] ? (
        <div
          className={styles.overlay}
          style={{ zIndex: modalStore?.activeModal === modalName ? 32 : 31 }}
          key={modalName + '_OVERLAY'}
          id={modalName + '_OVERLAY'}
        >
          <Suspense fallback={<></>}>
            <ModalComponent {...modalProps} onClose={() => modalStore.closeModal(modalName)} />
          </Suspense>
        </div>
      ) : (
        <div
          key={modalName + '_WRAPPER'}
          id={modalName as string}
          className={`${styles.modal} ${styles.withoutOverlay}`}
          onMouseDown={(e) => this._onMove(e, modalName as string)}
          style={{ zIndex: modalStore?.activeModal === modalName ? 32 : 31 }}
        >
          <div key={modalName} className={styles.scroll_container}>
            <div className={styles.wrapper}>
              <Suspense fallback={<></>}>
                <ModalComponent {...modalProps} onClose={() => modalStore.closeModal(modalName)} />
              </Suspense>
            </div>
          </div>
        </div>
      );
    });
  }
}

export default ModalManager;
