import React from 'react';
import { timePeriodPickerPropTypes } from './TimePeriodPicker.props';
import styles from './TimePeriodPicker.module.scss';

import Dropdown from '../Dropdown';

interface TimePeriodPickerProps {
  fromQuarterValue: string;
  fromYearValue: string;
  toQuarterValue: string;
  toYearValue: string;
  onFromValueChange: (value: string) => void;
  onToValueChange: (value: string) => void;
  quarters: string[];
  years: string[];
  label?: string;
  wrapperStyle?: string;
  containerStyle?: string;
  isError?: boolean;
  theme?: string;
  title?: string;
  disabled?: boolean;

  labelTo?: string;
  labelFrom?: string;
  placeholderTo?: string;
  placeholderFrom?: string;
}

/**
 * Компонент выбора периода времени
 *
 * @param {string} fromQuarterValue - значение квартала от
 * @param {string} fromYearValue - значение года от
 * @param {string} toQuarterValue - значение квартала до
 * @param {string} toYearValue - значение года до
 * @param {func} onFromValueChange - обработчик ввода значения от
 * @param {func} onToValueChange - обработчик ввода значения до
 * @param { string[] } quarters - массик кварталов
 * @param { string[] } years - массив лет
 * @param {string} [label] - подпись
 * @param {string} title - всплывающая подсказка
 * @param {boolean} disabled - разрешить редактирование
 */
const TimePeriodPicker = (props: TimePeriodPickerProps) => {
  const {
    fromQuarterValue,
    fromYearValue,
    toQuarterValue,
    toYearValue,
    onFromValueChange,
    onToValueChange,
    quarters,
    years,
    label,
    wrapperStyle,
    theme,
    title,
    disabled,
    labelTo,
    labelFrom,
    placeholderFrom,
    placeholderTo,
    containerStyle,
  } = props;

  return (
    <div className={`${styles.wrapper} ${wrapperStyle}`}>
      {label && (
        <span className={styles.label} title={title}>
          {label}
        </span>
      )}
      <div className={`${styles.container} ${containerStyle}`}>
        <div className={styles.picker}>
          <Dropdown
            dropdownStyle={styles.quarter}
            containerStyle={styles.leftContainer}
            value={fromQuarterValue}
            label={labelFrom || 'От'}
            items={quarters}
            onSelect={(value: string) => onFromValueChange(value + ';' + fromYearValue)}
            isPeriod={true}
            theme={theme}
            title={title}
            disabled={disabled}
            labelStyle={styles.labelStyle}
            placeholder={placeholderFrom}
          />
          <Dropdown
            dropdownStyle={`${styles.year}`}
            containerStyle={styles.rightContainer}
            value={fromYearValue}
            items={years}
            onSelect={(value: string) => onFromValueChange(fromQuarterValue + ';' + value)}
            isPeriod={true}
            theme={theme}
            title={title}
            disabled={disabled}
            placeholder={placeholderTo}
          />
        </div>
        <div className={styles.picker}>
          <Dropdown
            dropdownStyle={`${styles.quarter}
              ${
                fromQuarterValue > toQuarterValue && fromYearValue === toYearValue
                  ? styles.quarter__error
                  : ''
              }
            `}
            containerStyle={styles.leftContainer}
            value={toQuarterValue}
            label={labelTo || 'до'}
            items={quarters}
            onSelect={(value: string) => onToValueChange(value + ';' + toYearValue)}
            isPeriod={true}
            theme={theme}
            title={title}
            disabled={disabled}
            labelStyle={styles.labelStyle}
            placeholder={placeholderFrom}
          />
          <Dropdown
            dropdownStyle={`${styles.year}
              ${
                fromYearValue > toYearValue ||
                (fromQuarterValue > toQuarterValue && fromYearValue > toYearValue)
                  ? styles.year__error
                  : ''
              }
            `}
            containerStyle={styles.rightContainer}
            value={toYearValue}
            items={years}
            onSelect={(value: string) => onToValueChange(toQuarterValue + ';' + value)}
            isPeriod={true}
            theme={theme}
            title={title}
            disabled={disabled}
            placeholder={placeholderTo}
          />
        </div>
      </div>
    </div>
  );
};

TimePeriodPicker.propTypes = timePeriodPickerPropTypes;

export default TimePeriodPicker;
