import { action, flow } from 'mobx';
import AsyncStore from '../../utils/AsyncStore';

import { uploadDoc } from 'utils/services/api/common';
import { postCreateLink, postEditLink, postLinkSubtype } from 'utils/services/api/graph';
import * as modalNames from 'components/modals/constants/modalNames';

import { GraphStoreType } from 'StoreTypes';

/** Хранилище для узлов графа */
export default class GraphLinksStore extends AsyncStore {
  constructor(private readonly GraphStore: GraphStoreType) {
    super();
  }

  /**
   * Метод делает PUT запрос для создания пользовательской связи
   *
   * @param {string} body - информация о новой пользовательской связи
   * @param {string} id - идентификатор графа
   */
  @action.bound createNewLink = flow(function* (
    this: GraphLinksStore,
    body: object,
    id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const newData: any = { ...body };

      //Загрузка документов для аналитической роли
      if (newData.attach_documents?.length > 0) {
        let docIds = [];

        for (const doc of newData.attach_documents) {
          if (doc.stream) {
            try {
              yield uploadDoc(doc.file_name, doc.stream).then((response: any) => {
                docIds.push(response.document_id);
              });
            } catch (err) {
              this.GraphStore.onError(err);
            }
          } else {
            docIds.push(doc.document_id);
          }
        }

        newData.attach_documents = docIds;
      }

      yield postCreateLink(id, newData).then(({ graph_id, edge_id }) => {
        this.GraphStore.fetchGraph(graph_id).then(() => {
          // Позиционирование к конечному узлу, к которому направлена новая связь
          let newLink: go.Part | null | undefined;
          let targetNode: go.Part | null | undefined;

          let nodeId = this.GraphStore.DiagramStore.diagram
            ?.findLinksByExample({ orig_id: edge_id })
            .first()?.toNode?.data.key;

          newLink = this.GraphStore.DiagramStore.diagram
            ?.findLinksByExample({ orig_id: edge_id })
            .first()?.part;
          targetNode = this.GraphStore.DiagramStore.diagram
            ?.findNodesByExample({ key: nodeId })
            .first()?.part;

          if (targetNode) {
            this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
          }

          if (newLink) {
            newLink.isSelected = true;
          }
        });
      });

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на изменение информации о связи
   *
   * @param {string} changes - изменённая информация о связи
   * @param {string} id - идентификатор графа
   */
  @action.bound editLink = flow(function* (
    this: GraphLinksStore,
    changes: any,
    checkCluster?: boolean,
    graph_id = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const newData = [...changes];

      for (const item of newData) {
        //Загрузка документов
        if (!checkCluster && item.attach_documents.length > 0) {
          let docIds = [];

          for (const doc of item.attach_documents) {
            if (doc.stream) {
              try {
                yield uploadDoc(doc.file_name, doc.stream).then((response: any) => {
                  docIds.push(response.document_id);
                });
              } catch (err) {
                this.GraphStore.onError(err);
              }
            } else {
              docIds.push(doc.document_id);
            }
          }

          item.attach_documents = docIds;
        }
      }

      const res = yield postEditLink(graph_id, newData);
      yield this.GraphStore.updateGraphId(res.GraphId);
      yield this.GraphStore.fetchGraph(res.GraphId);

      if (!res?.edge_id) {
        this.GraphStore.RootStore.ModalStore.closeModal(modalNames.DETAILED_LINK_NDS_MODAL);
      } else {
        // Позиционирование к редактируемой связи
        let editLink: go.Part | null | undefined;

        editLink = this.GraphStore.DiagramStore.diagram
          ?.findLinksByExample({ orig_id: res.edge_id })
          .first()?.part;

        if (editLink && !checkCluster) {
          this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(editLink);
          editLink.isSelected = true;
        }

        yield this.GraphStore.fetchDetailedLink(res.edge_id);
      }

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на изменение информации о кластере
   *
   * @param {string} nodeId - идентификатор узла
   * @param {string} subtypeId - идентификатор подтипа
   * @param {string} graph_id - идентификатор графа
   * @param {string} changes - изменённая информация о связи
   */
  @action.bound editCluster = flow(function* (
    this: GraphLinksStore,
    nodeId: string,
    subtypeId: string,
    changes: any,
    graph_id = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const newData = [...changes];

      for (const item of newData) {
        //Загрузка документов
        if (item.attach_documents?.length > 0) {
          let docIds = [];

          for (const doc of item.attach_documents) {
            if (doc.stream) {
              try {
                yield uploadDoc(doc.file_name, doc.stream).then((response: any) => {
                  let attachs = {
                    document_id: null,
                  };
                  attachs['document_id'] = response.document_id;
                  docIds.push(attachs);
                });
              } catch (err) {
                this.GraphStore.onError(err);
              }
            } else {
              let attachs = {
                document_id: null,
              };
              attachs['document_id'] = doc.document_id;
              docIds.push(attachs);
            }
          }

          item.attach_documents = docIds;
        }
      }

      yield postLinkSubtype(graph_id, nodeId, subtypeId, newData).then((res) => {
        this.GraphStore.updateGraphId(res.GraphId);
        this.GraphStore.fetchGraph(res.GraphId);
      });
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });
}
