import React from 'react';
import styles from './ExtendedMenu.module.scss';
import { extendedMenuPropTypes } from './ExtendedMenu.props';

interface ExtendedMenuProps {
  /** Элементы меню */
  children: React.ReactNode[] | React.ReactNode;
  /** CSS класс элемента */
  className?: string;
  /** Компонент для шапки */
  headerComponent?: React.ElementType;
  /** Компонент для футера */
  footerComponent?: React.ElementType;
  /** CSS класс списка */
  listClassName?: string;
  /** CSS класс элемента */
  itemClassName?: string;
}

const ExtendedMenu = ({ children, className }: ExtendedMenuProps) => {
  return (
    <div id="extendedMenu" className={`${styles.wrapper} ${className}`}>
      {children}
    </div>
  );
};

ExtendedMenu.propTypes = extendedMenuPropTypes;

ExtendedMenu.defaultProps = {
  headerComponent: null,
  footerComponent: null,
  listClassName: '',
  itemClassName: '',
  className: '',
};

export default ExtendedMenu;
