import React from 'react';
import styles from './Filters.module.scss';

import Cooperation from './Cooperation';
import Accountability from './Accountability';
import Coherence from './Coherence';
import CustomLinks from './CustomLinks';
import Typology from './Typology';
import Taxpayers from './Taxpayers';
import PeriodData from './PeriodData';

import ActionButton from '../../components/ActionButton';

import Icon from '../../components/Icon';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'stores/common/commonStore';
import { GraphStoreType } from '../../stores/graph/graphStore';
import { observable } from 'mobx';

interface FiltersProps {
  uiStore?: any;
  filterStore?: any;
  cooperationFilter?: any;
  accountabilityFilter?: any;
  coherenceFilter?: any;
  customLinksFilter?: any;
  graphChangeFilter?: any;
  typologyFilter?: any;
  periodDataFilter?: any;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
  taxpayersFilter?: any;
}

/**
 * Компонент фильтров
 */
@inject(
  'uiStore',
  'filterStore',
  'cooperationFilter',
  'accountabilityFilter',
  'coherenceFilter',
  'customLinksFilter',
  'graphChangeFilter',
  'typologyFilter',
  'periodDataFilter',
  'commonStore',
  'graphStore',
  'taxpayersFilter',
)
@observer
class Filters extends React.PureComponent<FiltersProps> {
  @observable isError: boolean = false;

  /**
   * Метод закрывающий фильтры
   */
  _toggleFilters = (): void => {
    const { uiStore } = this.props;
    uiStore?.toggleFiltersState();
  };

  /**
   * Метод фильтрации
   */
  _onFilter = (): void => {
    const { filterStore, cooperationFilter, periodDataFilter, taxpayersFilter } = this.props;

    const periodFrom = periodDataFilter.options?.nds3_period_from?.split(';') || ['', ''];
    const periodTo = periodDataFilter.options?.nds3_period_to?.split(';') || ['', ''];

    const quarterFrom = periodFrom[0];
    const quarterTo = periodTo[0];

    const yearFrom = periodFrom[1];
    const yearTo = periodTo[1];

    this.isError = false;

    if (
      !(
        (cooperationFilter.options.purchases_to
          ? Number(cooperationFilter.options.purchases_from) >
            Number(cooperationFilter.options.purchases_to)
          : false) ||
        (cooperationFilter.options.sales_to
          ? Number(cooperationFilter.options.sales_from) >
            Number(cooperationFilter.options.sales_to)
          : false) ||
        (cooperationFilter.options.credit_to
          ? Number(cooperationFilter.options.credit_from) >
            Number(cooperationFilter.options.credit_to)
          : false) ||
        (cooperationFilter.options.debet_to
          ? Number(cooperationFilter.options.debet_from) >
            Number(cooperationFilter.options.debet_to)
          : false) ||
        yearFrom > yearTo ||
        (quarterFrom > quarterTo && yearFrom === yearTo) ||
        (cooperationFilter.options.shareVatDeduct_to
          ? Number(cooperationFilter.options.shareVatDeduct_from) >
            Number(cooperationFilter.options.shareVatDeduct_to)
          : false) ||
        (cooperationFilter.options.shareVatCalc_to
          ? Number(cooperationFilter.options.shareVatCalc_from) >
            Number(cooperationFilter.options.shareVatCalc_to)
          : false) ||
        Number(cooperationFilter.options.clear_deduction_ratio_to) > 100 ||
        Number(cooperationFilter.options.clear_deduction_ratio_from) > 100 ||
        (cooperationFilter.options.clear_deduction_ratio_to
          ? Number(cooperationFilter.options.clear_deduction_ratio_from) >
            Number(cooperationFilter.options.clear_deduction_ratio_to)
          : false) ||
        Number(cooperationFilter.options.schema_deduction_ratio_to) > 100 ||
        Number(cooperationFilter.options.schema_deduction_ratio_from) > 100 ||
        (cooperationFilter.options.schema_deduction_ratio_to
          ? Number(cooperationFilter.options.schema_deduction_ratio_from) >
            Number(cooperationFilter.options.schema_deduction_ratio_to)
          : false) ||
        (cooperationFilter.options.vat_gap_to
          ? Number(cooperationFilter.options.vat_gap_from) >
            Number(cooperationFilter.options.vat_gap_to)
          : false) ||
        Number(cooperationFilter.options.shareVatDeduct_to) > 100 ||
        Number(cooperationFilter.options.shareVatDeduct_from) > 100 ||
        Number(cooperationFilter.options.shareVatCalc_to) > 100 ||
        Number(cooperationFilter.options.shareVatCalc_from) > 100 ||
        (taxpayersFilter.options.ook_to
          ? Number(cooperationFilter.options.ook_from) > Number(cooperationFilter.options.ook_to)
          : false) ||
        Number(taxpayersFilter.options.tax_gap_to) > 100 ||
        Number(taxpayersFilter.options.tax_gap_from) > 100 ||
        (taxpayersFilter.options.tax_gap_to
          ? Number(cooperationFilter.options.tax_gap_from) >
            Number(cooperationFilter.options.tax_gap_to)
          : false)
      )
    ) {
      typeof cooperationFilter.options.purchases_from === 'string' &&
        cooperationFilter.changeOption(
          'purchases_from',
          parseInt(cooperationFilter.options.purchases_from.replace(/\s/g, '')),
        );
      typeof cooperationFilter.options.purchases_to === 'string' &&
        cooperationFilter.changeOption(
          'purchases_to',
          parseInt(cooperationFilter.options.purchases_to.replace(/\s/g, '')),
        );

      typeof cooperationFilter.options.sales_from === 'string' &&
        cooperationFilter.changeOption(
          'sales_from',
          parseInt(cooperationFilter.options.sales_from.replace(/\s/g, '')),
        );
      typeof cooperationFilter.options.sales_to === 'string' &&
        cooperationFilter.changeOption(
          'sales_to',
          parseInt(cooperationFilter.options.sales_to.replace(/\s/g, '')),
        );

      typeof cooperationFilter.options.credit_from === 'string' &&
        cooperationFilter.changeOption(
          'credit_from',
          parseInt(cooperationFilter.options.credit_from.replace(/\s/g, '')),
        );
      typeof cooperationFilter.options.credit_to === 'string' &&
        cooperationFilter.changeOption(
          'credit_to',
          parseInt(cooperationFilter.options.credit_to.replace(/\s/g, '')),
        );

      typeof cooperationFilter.options.debet_from === 'string' &&
        cooperationFilter.changeOption(
          'debet_from',
          parseInt(cooperationFilter.options.debet_from.replace(/\s/g, '')),
        );
      typeof cooperationFilter.options.debet_to === 'string' &&
        cooperationFilter.changeOption(
          'debet_to',
          parseInt(cooperationFilter.options.debet_to.replace(/\s/g, '')),
        );
      typeof cooperationFilter.options.vat_gap_from === 'string' &&
        cooperationFilter.changeOption(
          'vat_gap_from',
          parseInt(cooperationFilter.options.vat_gap_from.replace(/\s/g, '')),
        );
      typeof cooperationFilter.options.vat_gap_to === 'string' &&
        cooperationFilter.changeOption(
          'vat_gap_to',
          parseInt(cooperationFilter.options.vat_gap_to.replace(/\s/g, '')),
        );
      typeof taxpayersFilter.options.ook_from === 'string' &&
        taxpayersFilter.changeOption(
          'ook_from',
          parseInt(taxpayersFilter.options.ook_from.replace(/\s/g, '')),
        );
      typeof taxpayersFilter.options.ook_to === 'string' &&
        taxpayersFilter.changeOption(
          'ook_to',
          parseInt(taxpayersFilter.options.ook_to.replace(/\s/g, '')),
        );
      filterStore.filter();
    } else {
      this.isError = true;
    }
  };

  /**
   * Метод сбрасывает настройки фильтров
   */
  _onReset = (): void => {
    const { filterStore, graphStore } = this.props;
    filterStore.resetFilters(graphStore?.showABD, graphStore?.showNDS, graphStore?.graphExpandType);
  };

  render() {
    const {
      cooperationFilter,
      accountabilityFilter,
      coherenceFilter,
      customLinksFilter,
      typologyFilter,
      commonStore,
      graphStore,
      filterStore,
      taxpayersFilter,
    } = this.props;

    return (
      <div className={styles.wrapper}>
        <div className={styles.header}>
          <div className={styles.headerContent}>
            <span className={styles.headerLabel}>Фильтры</span>
            <Icon
              className={styles.close}
              name="close"
              onClick={this._toggleFilters}
              theme={commonStore?.appTheme}
            />
          </div>
          <div className={styles.separateLine} />
        </div>
        <div className={styles.body}>
          <div className={styles.container}>
            <PeriodData />
          </div>
          <ul className={styles.container}>
            <Taxpayers
              onToggleCheck={graphStore?.showChangeFilters && taxpayersFilter.toggleFilter}
              isActive={taxpayersFilter.keep}
              isAllCheck
            />
            {(graphStore?.showNDS || graphStore?.showABD) && (
              <Cooperation
                onToggleCheck={graphStore?.showChangeFilters && cooperationFilter.toggleFilter}
                isActive={cooperationFilter.keep}
                title={
                  graphStore?.sourceSystem !== 'MDAB'
                    ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                    : ''
                }
                isAllCheck={cooperationFilter.isAllTrue}
              />
            )}
            <div className={styles.separateLine} />
            <Accountability
              onToggleCheck={graphStore?.showChangeFilters && accountabilityFilter.toggleFilter}
              isActive={accountabilityFilter.keep}
              isAllCheck={accountabilityFilter.isAllTrue}
            />
            <Coherence
              onToggleCheck={graphStore?.showChangeFilters && coherenceFilter.toggleFilter}
              isActive={coherenceFilter.keep}
              isAllCheck={coherenceFilter.isAllTrue}
            />
            <CustomLinks
              onToggleCheck={graphStore?.showChangeFilters && customLinksFilter.toggleFilter}
              isActive={customLinksFilter.keep}
              title={'Признак отображения связей, созданных пользователем'}
              isAllCheck
            />
            <Typology
              onToggleCheck={graphStore?.showChangeFilters && typologyFilter.toggleFilter}
              isActive={typologyFilter.keep}
              title={
                graphStore?.schemeId && graphStore?.sourceSystem !== 'MDAB'
                  ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                  : 'Связи, выявленные Типологией'
              }
              isAllCheck={typologyFilter.isAllTrue}
            />
          </ul>
        </div>
        {graphStore?.showChangeFilters && (
          <div className={styles.bottom}>
            <div className={`${styles.separateLine} ${styles.separateLineBottom}`} />
            {filterStore.isChangeFilter && (
              <span className={styles.labelFilterNotApply}>Фильтр не был применен</span>
            )}
            <>
              {(cooperationFilter.options.sales_to
                ? Number(cooperationFilter.options.sales_from) >
                  Number(cooperationFilter.options.sales_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  НДС исчисленный: значение "от" больше значения "до"
                </span>
              )}
              {(cooperationFilter.options.purchases_to
                ? Number(cooperationFilter.options.purchases_from) >
                  Number(cooperationFilter.options.purchases_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  НДС к вычету: значение "от" больше значения "до"
                </span>
              )}
              {(cooperationFilter.options.shareVatDeduct_to
                ? Number(cooperationFilter.options.shareVatDeduct_from) >
                  Number(cooperationFilter.options.shareVatDeduct_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Доля НДС к вычету: значение "от" больше значения "до"
                </span>
              )}
              {Number(cooperationFilter.options.shareVatDeduct_from) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля НДС к вычету: значение "от" больше 100
                </span>
              )}
              {Number(cooperationFilter.options.shareVatDeduct_to) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля НДС к вычету: значение "до" больше 100
                </span>
              )}
              {(cooperationFilter.options.shareVatCalc_to
                ? Number(cooperationFilter.options.shareVatCalc_from) >
                  Number(cooperationFilter.options.shareVatCalc_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Доля НДС исчисленный: значение "от" больше значения "до"
                </span>
              )}
              {Number(cooperationFilter.options.shareVatCalc_from) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля НДС исчисленный: значение "от" больше 100
                </span>
              )}
              {Number(cooperationFilter.options.shareVatCalc_to) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля НДС исчисленный: значение "до" больше 100
                </span>
              )}
              {(cooperationFilter.options.clear_deduction_ratio_to
                ? Number(cooperationFilter.options.clear_deduction_ratio_from) >
                  Number(cooperationFilter.options.clear_deduction_ratio_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Доля "чистых" вычетов по НДС: значение "от" больше значения "до"
                </span>
              )}
              {(cooperationFilter.options.schema_deduction_ratio_to
                ? Number(cooperationFilter.options.schema_deduction_ratio_from) >
                  Number(cooperationFilter.options.schema_deduction_ratio_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Доля "чистых" вычетов по НДС: значение "от" больше значения "до"
                </span>
              )}
              {Number(cooperationFilter.options.clear_deduction_ratio_to) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля "чистых" вычетов по НДС: значение "до" больше 100
                </span>
              )}
              {Number(cooperationFilter.options.clear_deduction_ratio_from) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля "чистых" вычетов по НДС: значение "от" больше 100
                </span>
              )}
              {Number(cooperationFilter.options.schema_deduction_ratio_to) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля "схемных" вычетов по НДС: значение "до" больше 100
                </span>
              )}
              {Number(cooperationFilter.options.schema_deduction_ratio_from) > 100 && (
                <span className={styles.labelFilterNotApply}>
                  Доля "схемных" вычетов по НДС: значение "от" больше 100
                </span>
              )}

              {(cooperationFilter.options.credit_to
                ? Number(cooperationFilter.options.credit_from) >
                  Number(cooperationFilter.options.credit_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Входящие платежи: значение "от" больше значения "до"
                </span>
              )}
              {(cooperationFilter.options.debet_to
                ? Number(cooperationFilter.options.debet_from) >
                  Number(cooperationFilter.options.debet_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Исходящие платежи: значение "от" больше значения "до"
                </span>
              )}

              {(cooperationFilter.options.vat_gap_to
                ? Number(cooperationFilter.options.vat_gap_from) >
                  Number(cooperationFilter.options.vat_gap_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Сумма разрыва по НДС: значение "от" больше значения "до"
                </span>
              )}
              {(taxpayersFilter.options.ook_to
                ? Number(taxpayersFilter.options.ook_from) > Number(taxpayersFilter.options.ook_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  Сумма ООК: значение "от" больше значения "до"
                </span>
              )}
              {(taxpayersFilter.options.tax_gap_to
                ? Number(taxpayersFilter.options.tax_gap_from) >
                  Number(taxpayersFilter.options.tax_gap_to)
                : false) && (
                <span className={styles.labelFilterNotApply}>
                  TG: значение "от" больше значения "до"
                </span>
              )}
              {Number(taxpayersFilter.options.tax_gap_from) > 100 && (
                <span className={styles.labelFilterNotApply}>TG: значение "от" больше 100</span>
              )}
              {Number(taxpayersFilter.options.tax_gap_to) > 100 && (
                <span className={styles.labelFilterNotApply}>TG: значение "до" больше 100</span>
              )}
            </>
            <div className={styles.buttons}>
              <ActionButton
                buttonStyle={styles.button}
                isSecondary
                label={'Сбросить все'}
                onClick={this._onReset}
              />
              <ActionButton
                buttonStyle={`${styles.button} ${styles.apply}`}
                disabled={!filterStore.isChangeFilter}
                label={'Применить'}
                onClick={this._onFilter}
              />
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default Filters;
