import moment from 'moment';

/**
 * Форматирование даты для правильного обображения
 */
export const formatDateDetailedLink = (date: string) => {
  const formattedDate = moment(date);
  const yearNumber = formattedDate.year();

  if (yearNumber >= 3000) {
    return 'по настоящее время';
  } else {
    return formattedDate.locale('ru').format('DD.MM.YYYY HH:mm:ss');
  }
};

export const formatDate = (date: string, format: string = 'DD.MM.YYYY') => {
  if (!date) return '-';

  const formattedDate = moment(date);
  const yearNumber = formattedDate.year();

  if (yearNumber >= 3000) {
    return 'по настоящее время';
  } else {
    return formattedDate.locale('ru').format(format);
  }
};
