import { action, computed, observable } from 'mobx';
import Filter from '../utils/Filter';

import { IndicatorsStoreType } from './indicatorsStore';

/**
 * Хранилище фильтра Результат по НД
 */
class ResultNDFilter extends Filter {
  IndicatorsStore: IndicatorsStoreType;

  constructor(IndicatorsStore: IndicatorsStoreType) {
    super();
    this.IndicatorsStore = IndicatorsStore;

    this.checkAllTrue();
  }

  @observable keep = true;

  @observable isAllTrue = false;

  @observable changeStatic: boolean = false;

  // опции фильтра
  initialOptions = {
    // Декларация к уплате
    DeclState_1: true,
    // Декларация к возмещению
    DeclState_2: true,
    // Нулевая декларация
    DeclState_3: true,
    // Декларация не представлена
    DeclState_0: true,
  };

  @observable isAllFalse: boolean = false;

  /** Метод переключает состояние фильтра */
  @action toggleFilter = () => {
    this.keep = !this.keep;
    if (this.keep === false) {
      this.setOptionsToFalse();
      this.IndicatorsStore.showAll = false;
    } else {
      this.setOptionsToTrue();
      this.IndicatorsStore.checkAllTrue();
    }

    this.isChange();
    this.checkAllTrue();
    this.IndicatorsStore.checkChanges();
    this.IndicatorsStore.filterGraph();
  };

  // Происходили ли изменения среди  фильтров
  @action isChange = () => {
    this.changeStatic = false;
    Object.keys(this.options).forEach((item: any) => {
      if (this.options[item] !== this.IndicatorsStore.options[item]) {
        this.changeStatic = true;
      }
    });
    this.IndicatorsStore.isResultNDFilter = this.changeStatic;
    this.checkAllTrue();
  };

  /**
   * Метод переключает опцию
   */
  @action toggleIndicator = (option: string) => {
    this.changeOption(option, !this.options[option]);
    this.checkAllFalse();
    if (!this.options[option]) {
      this.IndicatorsStore.showAll = false;
    }
    this.keepFilter(this.isAllFalse);
    this.IndicatorsStore.checkAllTrue();
    this.isChange();
    this.checkAllTrue();
    this.IndicatorsStore.checkChanges();
    this.IndicatorsStore.filterGraph();
  };

  @action checkAllFalse = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_resultND') {
        othersOptions[item] = this.options[item];
      } else {
        othersOptions[item] = false;
      }
    });
    this.isAllFalse = Object.keys(othersOptions).every(
      (item: any) => othersOptions[item] === false,
    );
  };

  @action checkAllTrue = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_resultND') {
        othersOptions[item] = this.options[item];
      } else {
        othersOptions[item] = false;
      }
    });
    this.isAllTrue = Object.keys(othersOptions).every((item: any) => othersOptions[item] === true);
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
      is_resultND: this.keep,
    };
  }
}

export default ResultNDFilter;
