/**
 * Функция сортирует массив по свойству объекта
 *
 * @param {Array} arr - массив объектов
 * @param {string} prop - свойство для сортировки
 * @param {number} sortOrder - направление сортировки
 */
export const sortByProp = <T>(arr: T[], prop: keyof T, sortOrder: number = 1): T[] => {
  /**
   * .slice() применяется для создания копии массива
   *
   * Подробнее: observable массив нельзя изменить без создания его копии,
   * иммутабельность данных.
   */
  return arr.slice().sort((a: T, b: T) => (a[prop] > b[prop] ? 1 * sortOrder : -1 * sortOrder));
};
