import { HTMLInfo, GraphObject } from 'gojs';

export interface Context {
  showContextMenu: (obj: any, diagram: any, tool: any) => void;
  hideContextMenu: () => void;
}

class ContextMenu {
  // контекстное меню
  private context: Context;

  constructor(contextMenu: Context) {
    this.context = contextMenu;
  }

  /**
   * Метод заменяет контекстное меню
   */
  setContextMenu(contextMenu: Context) {
    this.context = contextMenu;
  }

  get getContextMenu() {
    return GraphObject.make(HTMLInfo, {
      show: this.context.showContextMenu,
      hide: this.context.hideContextMenu,
    });
  }
}

export type ContextMenuType = ContextMenu;
export default ContextMenu;
