/**
 * Форматирование денежной суммы
 */
export const formatMoney = (amount: any, currency: string = 'RUB') => {
  if (typeof amount === 'string') {
    amount = amount.replace(/,/, '.');
  }
  const options = {
    style: 'currency',
    maximumFractionDigits: 0,
    minimumFractionDigits: 0,
    currency,
  };
  const numberFormat = new Intl.NumberFormat('ru-RU', options);

  if (amount == 0) {
    return null;
  } else {
    const str = numberFormat.format(amount);
    const formatedString = str.slice(0, str.length - 1);

    return formatedString;
  }
};

export const formatMoneyWithText = (v: any, dimensions: string): any => {
  switch (dimensions) {
    case 'rubles':
      return Number(Number(v).toFixed()).toLocaleString('ru');
    case 'thousand':
      if (Number((Number(v) / 1e3).toFixed(3)) === 0) {
        return Number((Number(v) / 1e3).toPrecision(1))
          .toFixed(10)
          .replace(/\.?0+$/, '')
          .replace(/\./g, ',');
      } else {
        return Number((Number(v) / 1e3).toFixed(3)).toLocaleString('ru');
      }
    case 'million':
      if (Number((Number(v) / 1e6).toFixed(3)) === 0) {
        return Number((Number(v) / 1e6).toPrecision(1))
          .toFixed(10)
          .replace(/\.?0+$/, '')
          .replace(/\./g, ',');
      } else {
        return Number((Number(v) / 1e6).toFixed(3)).toLocaleString('ru');
      }
    case 'billion':
      if (Number((Number(v) / 1e9).toFixed(3)) === 0) {
        return Number((Number(v) / 1e9).toPrecision(1))
          .toFixed(10)
          .replace(/\.?0+$/, '')
          .replace(/\./g, ',');
      } else {
        return Number((Number(v) / 1e9).toFixed(3)).toLocaleString('ru');
      }
    default:
      return Number(Number(v).toFixed()).toLocaleString('ru');
  }
};
