import { configure } from 'mobx';

import UIStore from './uiStore';
import TooltipStore from './tooltipStore';
import CommonStore from './common';
import ModalStore from './modalStore';
import GraphStore from './graph/graphStore';
import FilterStore from './filters';
import UserGraphsStore from './userGraphsStore';
import NotificationStore from './notificationStore';
import IndividualPersonStore from './modalData/individualPersonStore';
import EditLinkStore from './modalData/editLinkStore';
import BeginningWorkStore from './modalData/beginningWorkStore';
import ChainsStore from './modalData/chainsStore';
import SearchListStore from './modalData/searchListStore';
import IndicatorsStore from './indicators';
import DetailedNodeStore from '../components/modals/DetailedNode/detailedNodeStore';
import NodesReportStore from './nodesReportStore';

configure({ enforceActions: 'observed' });

/**
 * Корневое хранилище
 */
class RootStore {
  UIStore: UIStore;
  TooltipStore: TooltipStore;
  CommonStore: CommonStore;
  ModalStore: ModalStore;
  GraphStore: GraphStore;
  FilterStore: FilterStore;
  UserGraphsStore: UserGraphsStore;
  NotificationStore: NotificationStore;
  IndividualPersonStore: IndividualPersonStore;
  EditLinkStore: EditLinkStore;
  IndicatorsStore: IndicatorsStore;
  DetailedNodeStore: typeof DetailedNodeStore;
  BeginningWorkStore: BeginningWorkStore;
  ChainsStore: ChainsStore;
  SearchListStore: SearchListStore;
  NodesReportStore: NodesReportStore;

  constructor() {
    this.CommonStore = new CommonStore(this);

    this.ModalStore = new ModalStore(this);

    this.UserGraphsStore = new UserGraphsStore(this);
    this.GraphStore = new GraphStore(this);

    this.FilterStore = new FilterStore(this);

    this.NotificationStore = new NotificationStore(this);

    this.UIStore = new UIStore(this);
    this.TooltipStore = new TooltipStore();

    this.IndividualPersonStore = new IndividualPersonStore(this);
    this.EditLinkStore = new EditLinkStore(this);

    this.DetailedNodeStore = DetailedNodeStore;

    this.IndicatorsStore = new IndicatorsStore(this);
    this.BeginningWorkStore = new BeginningWorkStore(this);
    this.ChainsStore = new ChainsStore(this);
    this.SearchListStore = new SearchListStore(this);
    this.NodesReportStore = new NodesReportStore(this);
  }

  fetchGraphLayers = async () => {
    return await this.FilterStore.getGraphFilters();
  };

  fetchGraphIndicators = () => {
    this.IndicatorsStore.showAllFilters();
    this.IndicatorsStore.filter();
  };
}

export type RootStoreType = RootStore;
export default new RootStore();
