import React from 'react';
import Dropdown from '../../../Dropdown';
import styles from './PeriodPicker.module.scss';
import { YEARS, QUARTERS_FULL_LABELS } from '../../../constants';
import { getCurrentDate } from '../../../../utils/ui/getCurrentDate';

const { year, quarter } = getCurrentDate('roman');
const defaultValue = `${quarter} квартал;${year}`;

interface IProps {
  value?: string;
  onChange: (value: string) => void;
  theme?: string;
}

/**
 * Компонент выбора периода учёта данных
 *
 * @param {string} value - период
 * @param {func} onChange - обработчик ввода значения
 */
const PeriodPicker = (props: IProps) => {
  const { value = defaultValue, onChange, theme } = props;
  const [quarter, year] = value?.split(';');

  return (
    <div className={styles.container}>
      <div className={styles.title}>Период учета данных</div>

      <div className={styles.picker}>
        <div className={styles.quarter}>
          <Dropdown
            containerStyle={styles.dropdown}
            value={quarter}
            items={QUARTERS_FULL_LABELS}
            onSelect={(value: string) => onChange(`${value};${year}`)}
            theme={theme}
          />
        </div>

        <div className={styles.year}>
          <Dropdown
            containerStyle={styles.dropdown}
            value={year}
            items={YEARS}
            onSelect={(value: string) => onChange(`${quarter};${value}`)}
            theme={theme}
          />
        </div>
      </div>
    </div>
  );
};

export default PeriodPicker;
