import moment from 'moment';

/**
 * Форматирование даты для правильного обображения
 */
export const formatDateIndividualPerson = (date: string) => {
  if (!date) {
    return '';
  }

  const formattedDate = moment(date);
  const yearNumber = formattedDate.year();

  if (yearNumber >= 3000) {
    return 'по настоящее время';
  } else {
    return formattedDate.locale('ru').format('DD.MM.YYYY');
  }
};
