import React from 'react';
import { observer } from 'mobx-react';

import { useStores } from 'hooks';

import { ILoaderProps } from './Loader.props';
import Spinner from 'components/Spinner';

import styles from './Loader.module.scss';

const Loader = ({ className, ...props }: ILoaderProps) => {
  const {
    modalStore: { openSearchListModal },
    chainsStore: { searchState },
  } = useStores();

  return (
    <div className={`${className} ${styles.loader}`} {...props}>
      <Spinner
        className={styles.loader__spinner}
        text={`${searchState === 0 ? 'Поиск' : 'Загрузка'} результатов...`}
      />

      {searchState === 0 && (
        <div className={styles.loader__text}>
          Поиск добавлен в{' '}
          <span className={styles.link} onClick={openSearchListModal}>
            очередь
          </span>{' '}
          и будет выполнен в фоновом режиме. Можно покинуть страницу. Текущий статус поиска и его
          очередь можно отслеживать из основного меню.
          <br />
          По результатам поиска придет уведомление.
        </div>
      )}
    </div>
  );
};

export default observer(Loader);
