import { ContextMenuTool } from 'gojs';

import { Context } from './ContextMenu';
import { DiagramStoreType } from '../../../stores/graph/diagramStore';
import { FilterStoreType } from '../../../stores/filters/filterStore';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import { commodityFlow } from '../../commodityFlow';

/**
 * Реализация контекстное меню узла
 */
class Node implements Context {
  DiagramStore: DiagramStoreType;
  FilterStore: FilterStoreType;
  GraphStore: GraphStoreType;

  // ID узла в HTML
  nodeId: string;

  constructor(
    DiagramStore: DiagramStoreType,
    nodeId: string,
    FilterStore: FilterStoreType,
    GraphStore: GraphStoreType,
  ) {
    this.DiagramStore = DiagramStore;
    this.nodeId = nodeId;
    this.FilterStore = FilterStore;
    this.GraphStore = GraphStore;
  }

  /**
   * Метод по отмене предыдущего действия (закрытие контекст меню)
   */
  hideCX = () => {
    if (this.DiagramStore?.diagram?.currentTool instanceof ContextMenuTool) {
      this.DiagramStore?.diagram.currentTool.doCancel();
    }
  };

  /**
   * Метод открытия контекст меню
   */
  showContextMenu = async (obj: any, diagram: any, tool: any) => {
    const cxElement: HTMLElement | null = document.getElementById(this.nodeId);
    let hasMenuItem = false;
    function maybeShowItem(elt: any) {
      if ((elt.style.display = 'none')) {
        elt.style.display = 'block';
        hasMenuItem = true;
      }
    }
    maybeShowItem(document.getElementById('detailNode'));
    maybeShowItem(document.getElementById('createGroup'));

    const { node } = obj.data;

    const zombie = obj.data.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const is_special = obj.data.node.is_special;
    const transborder_vertex_type = obj.data.node.itais_node_info?.transborder_vertex_type;

    const showNodeItem = document.getElementById('showNode');
    const hideNodeItem = document.getElementById('hideNode');

    const ABDItem = document.getElementById('ABD');
    const ABDPayersItem = document.getElementById('ABDPayers');
    const ABDRecipientsItem = document.getElementById('ABDRecipients');

    const NDSItem = document.getElementById('NDS');
    const NDSSchemeForward = document.getElementById('schemeForward');
    const NDSSchemeBack = document.getElementById('schemeBack');

    const createGroup = document.getElementById('createGroup');

    const NDSOpenNP = document.getElementById('NDSOpenNP');
    const ABDOpenNP = document.getElementById('ABDOpenNP');

    const detailNode = document.getElementById('detailNode');

    const createTree = document.getElementById('createTree');
    const createTreeNode = document.getElementById('createTreeNode');

    const analyticRole = document.getElementById('analyticRole');

    const userLinkToNP = document.getElementById('userLinkToNP');
    const userLinkFromNP = document.getElementById('userLinkFromNP');

    const TraceItem = document.getElementById('Trace');
    const TraceOpenNP = document.getElementById('TraceOpenNP');
    const schemeBackTrace = document.getElementById('schemeBackTrace');
    const schemeForwardTrace = document.getElementById('schemeForwardTrace');

    const AksNDSItem = document.getElementById('AksNDS');
    const declarationByTaxPayer = document.getElementById('declarationByTaxPayer');
    const AskNdsByTaxPayer = document.getElementById('AskNdsByTaxPayer');
    const deductionsByBuyerCard = document.getElementById('deductionsByBuyerCard');
    const deductionsBySellerCard = document.getElementById('deductionsBySellerCard');

    const response = this.GraphStore.filtersGraph;

    const graphTransformMode = this.GraphStore.graphTransformMode;
    const mainVertexId = this.GraphStore.mainVertexId;
    const showNP = this.GraphStore.showNP;
    const showHideNP = this.GraphStore.showHideNP;
    const showOpenLinks = this.GraphStore.showOpenLinks;
    const showGroupNP = this.GraphStore.showGroupNP;
    const showCreateGraph = this.GraphStore.showCreateGraph;
    const showAnalyticRole = this.GraphStore.showAnalytic;
    const currentDrawingMode = this.GraphStore.currentDrawingMode;
    const informationMainVertexID = this.GraphStore.informationMainVertexID;
    const visibleNodes = this.GraphStore.visibleNodes;

    if (
      AksNDSItem &&
      declarationByTaxPayer &&
      AskNdsByTaxPayer &&
      deductionsByBuyerCard &&
      deductionsBySellerCard
    ) {
      if (!(this.DiagramStore.selectedNodes.length > 1) && obj.data.node.inn) {
        if (typeof window.CefSharp === 'object') {
          if (!response.is_ask_nds2) {
            AksNDSItem.style.display = 'block';
            declarationByTaxPayer.style.display = 'block';

            AskNdsByTaxPayer.style.opacity = '0.6';
            AskNdsByTaxPayer.style.display = 'block';
            deductionsByBuyerCard.style.opacity = '0.6';
            deductionsByBuyerCard.style.display = 'block';
            deductionsBySellerCard.style.opacity = '0.6';
            deductionsBySellerCard.style.display = 'block';
          } else {
            AksNDSItem.style.opacity = '1';
            AksNDSItem.style.display = 'block';
            declarationByTaxPayer.style.opacity = '1';
            declarationByTaxPayer.style.display = 'block';

            AskNdsByTaxPayer.style.opacity = '1';
            AskNdsByTaxPayer.style.display = 'block';
            deductionsByBuyerCard.style.opacity = '1';
            deductionsByBuyerCard.style.display = 'block';
            deductionsBySellerCard.style.opacity = '1';
            deductionsBySellerCard.style.display = 'block';
          }
        } else {
          AksNDSItem.style.opacity = '0.6';
          AksNDSItem.style.display = 'block';
          declarationByTaxPayer.style.opacity = '0.6';
          declarationByTaxPayer.style.display = 'block';

          AskNdsByTaxPayer.style.opacity = '0.6';
          AskNdsByTaxPayer.style.display = 'block';
          deductionsByBuyerCard.style.opacity = '0.6';
          deductionsByBuyerCard.style.display = 'block';
          deductionsBySellerCard.style.opacity = '0.6';
          deductionsBySellerCard.style.display = 'block';
        }
      } else {
        AksNDSItem.style.display = 'none';
        declarationByTaxPayer.style.display = 'none';
        AskNdsByTaxPayer.style.display = 'none';
        deductionsByBuyerCard.style.display = 'none';
        deductionsBySellerCard.style.display = 'none';
      }
    }

    if (TraceItem && TraceOpenNP && schemeForwardTrace && schemeBackTrace) {
      if (
        commodityFlow.some((item) => this.GraphStore.graphExpandType.includes(item)) &&
        showOpenLinks &&
        this.GraphStore.ableEdit &&
        !is_special &&
        !visibleNodes &&
        this.GraphStore.sourceSystem !== 'ITAIS'
      ) {
        if (obj.data.orig_id === mainVertexId) {
          TraceItem.style.display = 'block';
          schemeForwardTrace.style.display = 'block';
          schemeBackTrace.style.display = 'block';
          TraceOpenNP.style.display = 'none';
        } else {
          schemeForwardTrace.style.display = 'none';
          schemeBackTrace.style.display = 'none';
          if (zombie) {
            TraceItem.style.display = 'none';
            TraceOpenNP.style.display = 'none';
          } else if (node?.is_visible) {
            TraceItem.style.display = 'block';
            TraceOpenNP.style.display = 'block';
          } else {
            TraceItem.style.display = 'none';
            TraceOpenNP.style.display = 'none';
          }
        }
      } else {
        TraceItem.style.display = 'none';
        TraceOpenNP.style.display = 'none';
        schemeForwardTrace.style.display = 'none';
        schemeBackTrace.style.display = 'none';
      }
    }

    if (ABDItem && ABDPayersItem && ABDRecipientsItem && ABDOpenNP) {
      if (
        this.GraphStore.graphExpandType === 'Abd' &&
        showOpenLinks &&
        this.GraphStore.ableEdit &&
        !is_special &&
        !visibleNodes &&
        this.GraphStore.sourceSystem !== 'ITAIS'
      ) {
        if (obj.data.orig_id === mainVertexId) {
          ABDItem.style.display = 'block';
          ABDPayersItem.style.display = 'block';
          ABDRecipientsItem.style.display = 'block';
          ABDOpenNP.style.display = 'none';
        } else {
          ABDPayersItem.style.display = 'none';
          ABDRecipientsItem.style.display = 'none';
          if (zombie) {
            ABDItem.style.display = 'none';
            ABDOpenNP.style.display = 'none';
          } else if (node?.is_visible) {
            ABDItem.style.display = 'block';
            ABDOpenNP.style.display = 'block';
          } else {
            ABDItem.style.display = 'none';
            ABDOpenNP.style.display = 'none';
          }
        }
      } else {
        ABDItem.style.display = 'none';
        ABDPayersItem.style.display = 'none';
        ABDRecipientsItem.style.display = 'none';
        ABDOpenNP.style.display = 'none';
      }
    }

    if (NDSItem && NDSSchemeForward && NDSSchemeBack && NDSOpenNP) {
      if (
        (this.GraphStore.graphExpandType === 'Nds' ||
          this.GraphStore.graphExpandType === 'Contract') &&
        showOpenLinks &&
        this.GraphStore.ableEdit &&
        !is_special &&
        !visibleNodes &&
        this.GraphStore.sourceSystem !== 'ITAIS'
      ) {
        if (obj.data.key === mainVertexId) {
          NDSItem.style.display = 'block';
          if (this.GraphStore.interQuarterSearch) {
            NDSSchemeForward.style.display = 'none';
          } else {
            NDSSchemeForward.style.display = 'block';
          }
          NDSSchemeBack.style.display = 'block';
          NDSOpenNP.style.display = 'none';
        } else {
          NDSSchemeForward.style.display = 'none';
          NDSSchemeBack.style.display = 'none';
          if (zombie) {
            NDSItem.style.display = 'none';
            NDSOpenNP.style.display = 'none';
          } else if (node?.is_visible) {
            if (this.GraphStore.sourceSystem === 'MDAB' && !response.is_cooperation) {
              NDSItem.style.display = 'block';
              NDSOpenNP.style.opacity = '0.6';
              NDSOpenNP.style.display = 'block';
            } else {
              NDSItem.style.display = 'block';
              NDSOpenNP.style.display = 'block';
              NDSOpenNP.style.opacity = '1';
            }
          } else {
            NDSItem.style.display = 'none';
            NDSOpenNP.style.display = 'none';
          }
        }
      } else {
        NDSItem.style.display = 'none';
        NDSSchemeForward.style.display = 'none';
        NDSSchemeBack.style.display = 'none';
        NDSOpenNP.style.display = 'none';
      }
    }

    if (showNodeItem && hideNodeItem) {
      if (!this.GraphStore.ableEdit || this.GraphStore.sourceSystem === 'ITAIS') {
        showNodeItem.style.display = 'none';
        hideNodeItem.style.display = 'none';
      } else {
        if (showHideNP) {
          if (node?.is_visible) {
            showNodeItem.style.display = 'none';
            hideNodeItem.style.display = 'block';
          } else {
            showNodeItem.style.display = 'block';
            hideNodeItem.style.display = 'none';
          }
        } else {
          if (node?.is_visible) {
            showNodeItem.style.display = 'none';
            hideNodeItem.style.display = 'none';
          } else {
            showNodeItem.style.display = 'block';
            hideNodeItem.style.display = 'none';
          }
        }
      }
    }

    if (createGroup) {
      if (showGroupNP && !visibleNodes && !is_special && this.GraphStore.sourceSystem !== 'ITAIS') {
        if (node?.is_visible) {
          createGroup.style.display = 'block';
        } else {
          if (obj.category === 'Group') {
            createGroup.style.display = 'block';
          } else {
            createGroup.style.display = 'none';
          }
        }
      } else {
        createGroup.style.display = 'none';
      }
    }

    if (detailNode) {
      if (showNP && !is_special && !(this.DiagramStore.selectedNodes.length > 1)) {
        detailNode.style.display = 'block';
      } else {
        detailNode.style.display = 'none';
      }
    }

    if (createTree) {
      if (obj.isSelected && this.DiagramStore.selectedNodes.length > 1) {
        createTree.style.display = 'none';
      } else if (
        obj.data.orig_id === mainVertexId &&
        this.GraphStore.sourceSystem === 'ITAIS' &&
        transborder_vertex_type !== 'BY'
      ) {
        createTree.style.display = 'block';
      } else if (
        showCreateGraph &&
        this.GraphStore.ableEdit &&
        !is_special &&
        obj.data.orig_id !== mainVertexId &&
        !visibleNodes &&
        transborder_vertex_type !== 'BY'
      ) {
        createTree.style.display = 'block';
        if (!response.is_ask_nds2 && !response.is_abd) {
          createTree.style.opacity = '0.6';
        } else {
          createTree.style.opacity = '1';
        }
      } else {
        createTree.style.display = 'none';
      }
    }

    if (createTreeNode) {
      if (
        obj.isSelected &&
        this.DiagramStore.selectedNodes.length > 1 &&
        showCreateGraph &&
        this.GraphStore.ableEdit &&
        !is_special &&
        graphTransformMode === 'ModeV3' &&
        !visibleNodes &&
        this.GraphStore.sourceSystem !== 'ITAIS'
      ) {
        createTreeNode.style.display = 'block';
        if (!response.is_ask_nds2 && !response.is_abd) {
          createTreeNode.style.opacity = '0.6';
        } else {
          createTreeNode.style.opacity = '1';
        }
      } else {
        createTreeNode.style.display = 'none';
      }
    }

    if (analyticRole) {
      if (
        showAnalyticRole &&
        !is_special &&
        !(this.DiagramStore.selectedNodes.length > 1) &&
        this.GraphStore.sourceSystem !== 'ITAIS'
      ) {
        analyticRole.style.display = 'block';
      } else {
        analyticRole.style.display = 'none';
      }
    }

    if (userLinkToNP && userLinkFromNP) {
      if (
        graphTransformMode === 'ModeV3' &&
        this.GraphStore.ableEdit &&
        !is_special &&
        obj.isSelected &&
        !visibleNodes &&
        !(this.DiagramStore.selectedNodes.length > 1) &&
        this.GraphStore.sourceSystem !== 'ITAIS'
      ) {
        if (!response.is_user_link) {
          userLinkToNP.style.opacity = '0.6';
          userLinkFromNP.style.opacity = '0.6';
        } else {
          userLinkToNP.style.opacity = '1';
          userLinkFromNP.style.opacity = '1';
        }
        if (obj.data.orig_id === mainVertexId) {
          userLinkToNP.style.display = 'block';
          userLinkFromNP.style.display = 'block';
        } else {
          if (currentDrawingMode === 'from_seller') {
            if (obj.data.loc.y >= informationMainVertexID[0].loc.y) {
              userLinkToNP.style.display = 'block';
              userLinkFromNP.style.display = 'none';
            } else {
              userLinkToNP.style.display = 'none';
              userLinkFromNP.style.display = 'block';
            }
          } else {
            if (obj.data.loc.y >= informationMainVertexID[0].loc.y) {
              userLinkToNP.style.display = 'none';
              userLinkFromNP.style.display = 'block';
            } else {
              userLinkToNP.style.display = 'block';
              userLinkFromNP.style.display = 'none';
            }
          }
        }
      } else {
        userLinkToNP.style.display = 'none';
        userLinkFromNP.style.display = 'none';
      }
    }

    if (hasMenuItem && cxElement) {
      cxElement.classList.add('show-menu');

      const diagramHeight = (document.querySelector('.diagram-component') as HTMLDivElement)
        .offsetHeight;
      const ctxMenuHeight = cxElement.offsetHeight;
      const topMenuHeight = (document.querySelector('#topMenu') as HTMLDivElement).offsetHeight;
      const MENU_OFFSETS = 4;

      const mousePt = diagram.lastInput.viewPoint;
      const positionX = mousePt.x + 7;
      let positionY = mousePt.y + topMenuHeight - MENU_OFFSETS;

      if (diagramHeight - mousePt.y < ctxMenuHeight) {
        positionY = diagramHeight - ctxMenuHeight + topMenuHeight - MENU_OFFSETS;
      }

      const leftCoordinate = positionX + 'px';
      const topCoordinate = positionY + 'px';
      cxElement.setAttribute('style', 'left:' + leftCoordinate + ';top:' + topCoordinate);
    }
    window.addEventListener('click', this.hideCX, true);
  };

  /**
   * Метод закрытия контекст меню
   */
  hideContextMenu = () => {
    const cxElement = document.getElementById(this.nodeId);
    cxElement?.classList.remove('show-menu');
    window.removeEventListener('click', this.hideCX, true);
  };
}

export default Node;
