import React, { PureComponent } from 'react';
import './App.scss';

import RootContainer from 'containers/RootContainer';
import MenuContainer from 'containers/MenuContainer';
import DiagramContainer from 'containers/DiagramContainer';

import Filters from 'containers/Filters';
import ModalManager from '../modals/ModalManager';

import { inject, observer } from 'mobx-react';

import * as modalNames from '../modals/constants/modalNames';

import ContextMenuNode from '../ContextMenuNode';

import { DIRECTION_VERTICAL, DIRECTION_HORIZONTAL } from '@fdc/vsa-gojs-lib';
import { postChangeDirection } from 'utils/services/api/graph';
import ContextMenuCluster from '../ContextMenuCluster';
import ContextMenuGroup from '../ContextMenuGroup';
import ContextMenuVirtualGroup from '../ContextMenuVirtualGroup';
import {
  MODAL_SAVE_GRAPH,
  MODAL_SEARCH_NODE_IN_GRAPH,
  MODAL_SAVE_REFERENCE,
} from '../modals/constants/modalNames';
import { KEY_CODES } from '../constants';

import ContextMenuLink from '../ContextMenuLink';
import {
  GraphStoreType,
  UIStoreType,
  DiagramStoreType,
  CommonStoreType,
  ModalStoreType,
  UserGraphsStoreType,
  FilterStoreType,
} from 'StoreTypes';

import ToolTipIndicators from '../ToolTipIndicators';
import ToolTipDeclaration from '../ToolTipDeclaration';
import ToolTipJournal from '../ToolTipJournal';
import ToolTipAnalytic from '../ToolTipAnalytic';
import ToolTipBusiness from '../ToolTipBusiness';
import ToolTipName from '../ToolTipName';
import ToolTipMultipleShort from '../ToolTipMultipleShort';
import ToolTipLayers from '../ToolTipLayers';
import Indicators from '../../containers/Indicators';
import { IndicatorsStoreType } from '../../stores/indicators/indicatorsStore';
import ToolTipMultipleFull from '../ToolTipMultipleFull';
import ToolTipVirtualGroup from '../ToolTipVirtualGroup/ToolTipVirtualGroup';
import TaxpayerSettings from '../../containers/TaxpayerSettings';
import TooltipTaxpayer from '../TooltipTaxpayer';
import BeginningWork from '../modals/BeginningWork/BeginningWork';
import Chains from '../../containers/Chains';
import SearchListStore from 'stores/modalData/searchListStore';
import Dialog from 'components/Dialog';

type AppProps = {
  graphStore?: GraphStoreType;
  uiStore?: UIStoreType;
  diagramStore?: DiagramStoreType;
  commonStore?: CommonStoreType;
  modalStore?: ModalStoreType;
  userGraphsStore?: UserGraphsStoreType;
  filterStore?: FilterStoreType;
  indicatorsStore?: IndicatorsStoreType;
  searchListStore?: SearchListStore;
};

/**
 * Компонент приложения
 *
 * Компонент подключен к хранилищу графа
 */
@inject(
  'graphStore',
  'uiStore',
  'commonStore',
  'modalStore',
  'userGraphsStore',
  'diagramStore',
  'filterStore',
  'indicatorsStore',
  'searchListStore',
)
@observer
class App extends PureComponent<AppProps> {
  /**
   * Метод устанавливает обработчик события после появления компонента, запрашивает граф и
   * списки бизнес, аналитических ролей.
   */
  async componentDidMount() {
    const { uiStore, indicatorsStore, commonStore, graphStore, searchListStore } = this.props;

    commonStore?.setTheme('light');

    if (uiStore) {
      window.addEventListener('resize', uiStore.setScreenMode);
    }

    graphStore!.statePending();

    await Promise.all([
      graphStore!.edgeTypes(),
      commonStore?.fetchInternalParameters(),
      commonStore?.fetchBusinessRoles(),
      commonStore?.fetchAnalyticalRoles(),
      commonStore?.fetchInvalidReasons(),
      commonStore?.fetchChangeReason(),
      commonStore?.fetchDocumentIdentities(),
      commonStore?.fetchIntegrals(),
    ]);

    await graphStore!.fetchUserInfo();
    await searchListStore?.fetchSearchList();
    searchListStore?.checkActiveChainsSearch();
    searchListStore?.checkActiveNpSearch();

    if (graphStore!.showCountries) {
      await commonStore?.fetchCountries();
    }

    await graphStore!.fetchInitialGraph();

    await commonStore?.fetchVersion();

    indicatorsStore!.showAllFilters();
    indicatorsStore!.filter();

    graphStore!.stateDone();

    window.addEventListener('keydown', this.ctrlEventListener);
    window.addEventListener('keyup', this.ctrlEventListener);
  }

  /**
   * Метод удаляет обработчик события
   */
  componentWillUnmount() {
    const { uiStore } = this.props;

    if (uiStore) {
      window.removeEventListener('resize', uiStore.setScreenMode);
    }

    window.removeEventListener('keydown', this.ctrlEventListener);
    window.removeEventListener('keyup', this.ctrlEventListener);
  }

  /** Обработчик события нажатия клавиши Ctrl */
  ctrlEventListener = (event: KeyboardEvent) => {
    const { diagramStore } = this.props;

    if (
      !diagramStore.ctrlPressed &&
      (event.keyCode === KEY_CODES.CTRL ||
        event.which === KEY_CODES.CTRL ||
        event.keyCode === KEY_CODES.COMMAND ||
        event.which === KEY_CODES.COMMAND)
    ) {
      this.handleCtrlPress(true);
    } else {
      this.handleCtrlPress(false);
    }
  };

  /** Оработка нажатия клавиши Ctrl */
  handleCtrlPress = (pressed: boolean) => {
    const { diagramStore } = this.props;
    diagramStore?.setCtrlPressed(pressed);
  };

  /**
   * Метод вызывающийся при поиске графа
   *
   * @param {string} id - идентификатор графа
   */
  _onSearch = (id: string): void => {
    const { graphStore } = this.props;
    graphStore?.fetchScheme(id);
  };

  /**
   * Метод вызывающийся при клике на выбор периода
   */
  _onPickPeriod = (): void => {};

  /**
   * Метод вызывающийся при клике на фильтры
   */
  _onFilter = (): void => {
    const { uiStore } = this.props;
    uiStore?.toggleFiltersState();
  };

  /**
   * Метод вызывающийся при клике на граф от продавца / покупателя
   */
  _onTransfer = async (): Promise<void> => {
    const { graphStore } = this.props;

    if (graphStore!.currentDrawingMode === 'from_buyer' || null) {
      await graphStore!.changeDrawingMode('from_seller');
    } else {
      await graphStore!.changeDrawingMode('from_buyer');
    }
    await graphStore!.fetchGraph();
  };

  /**
   * Метод вызывающийся при добавлении нового узла
   */
  _onAddNode = (): void => {
    const { modalStore } = this.props;
    modalStore?.openModal(modalNames.ADD_NEW_NODE, {});
  };

  /**
   * Метод вызывающийся при переключении режима просмотра графа
   *
   * @param {0 | 90} viewMode - режим просмотра графа
   */
  _onSwitchGraphView = async (
    viewMode: typeof DIRECTION_VERTICAL | typeof DIRECTION_HORIZONTAL,
  ): Promise<void> => {
    const { graphStore } = this.props;
    if (viewMode === DIRECTION_HORIZONTAL) {
      await postChangeDirection(graphStore!.previouslyFetchedGraphId, DIRECTION_HORIZONTAL);
    } else {
      await postChangeDirection(graphStore!.previouslyFetchedGraphId, DIRECTION_VERTICAL);
    }
    await graphStore?.fetchGraph();
  };

  /**
   * Метод вызывающийся при открытии легенды
   */
  _onOpenLegend = (): void => {
    const { uiStore } = this.props;
    uiStore?.toggleLegendState();
  };

  /**
   * Метод вызывающийся при сохранении
   */
  _onSave = (): void => {
    const { modalStore } = this.props;
    modalStore?.openModal(MODAL_SAVE_GRAPH, null);
  };

  /**
   * Метод вызывающийся при сохранении как
   */
  _onSaveAs = (): void => {};

  /**
   * Метод вызывающийся при клике на "Развернуть все"
   */
  _onExpandAll = (): void => {};

  /**
   * Метод вызывающийся при клике на "Показать поток счетов"
   */
  _onShowBillingFlow = (): void => {};

  /**
   * Метод вызывающийся при скрытии пользовательских связей
   */
  _hideCustomConnections = (): void => {};

  /**
   * Метод переключения режима просмотра интегральной оценки
   */
  _toggleIntegralAssessment = (): void => {};

  /**
   * Метод переключение режима просмотра скрытых узлов
   */
  _toggleHiddenNodesView = (): void => {
    const { graphStore } = this.props;
    graphStore?.setVisibleNodesMode(!graphStore.hiddenNodesVisible);
  };

  /**
   * Метод создания пользовательской связи
   */
  _createLink = (): void => {};

  /**
   * Метод, вызывающийся при поиске узла в графе
   */
  _onSearchNode = (): void => {
    const { modalStore } = this.props;
    modalStore?.openModal(MODAL_SEARCH_NODE_IN_GRAPH, null);
  };

  /**
   * Метод, вызывающийся при получении Проекта акта
   */
  _onSaveReference = (): void => {
    const { modalStore } = this.props;
    modalStore?.openModal(MODAL_SAVE_REFERENCE, null);
  };

  /**
   * Метод, вызывающийся при передаче изменений
   */
  _sendChanges = (): void => {
    const { graphStore } = this.props;
    const { schemeId } = graphStore;
    // Если есть схема - передать изменения
    if (schemeId) {
      graphStore?.fetchChangeSchema();
    }
    // Если нет - передача графа в свободном поиске
    else {
      graphStore?.fetchSendGraph();
    }
  };

  /**
   * Метод свободного поиска
   */
  _freeSearch = (): void => {
    const { graphStore } = this.props;
    graphStore!.freeSearch();
  };

  /**
   * Метод отмены действия
   */
  _onUndo = (): void => {};

  /**
   * Метод повторения действия
   */
  _onRedo = (): void => {};

  /**
   * Метод отрисовывает элемент на странице
   */
  render() {
    const { uiStore, commonStore, graphStore } = this.props;

    return (
      <div className={`${commonStore.appTheme}-theme`}>
        <ModalManager />
        <DiagramContainer />
        {commonStore?.isShowBeginningWork && <BeginningWork />}
        {uiStore?.chainsIsOpen && <Chains />}
        <RootContainer />
        <MenuContainer />
        {uiStore?.filtersIsOpen && <Filters />}
        {uiStore?.indicatorsIsOpen && <Indicators />}
        {uiStore?.taxpayerSettingsIsOpen && <TaxpayerSettings />}
        <ContextMenuNode />
        <ContextMenuCluster />
        <ContextMenuGroup />
        <ContextMenuLink />
        <ContextMenuVirtualGroup />

        <ToolTipIndicators id={'toolTipIndicators'} />
        <ToolTipDeclaration id={'toolTipDeclaration'} />
        <ToolTipAnalytic id={'toolTipAnalytic'} />
        <ToolTipBusiness id={'toolTipBusiness'} />
        <ToolTipName id={'toolTipName'} />
        <ToolTipMultipleShort id={'toolTipMultipleShort'} />
        <ToolTipMultipleFull id={'toolTipMultipleFull'} />
        <ToolTipVirtualGroup id={'toolTipVirtualGroup'} />
        <ToolTipJournal id={'toolTipJournal'} />
        <ToolTipLayers />
        {graphStore?.taxpayerTooltipIsVisible && <TooltipTaxpayer />}
        <Dialog options={uiStore?.dialog} />
      </div>
    );
  }
}

export default App;
