import { IReactionPublic, action, computed, flow, observable, reaction } from 'mobx';

import AsyncStore from '../utils/AsyncStore';

import { LEGEND, LEGEND_DARK } from '../../components/constants';

import {
  fetchInternalParameters,
  getVersion,
  searchNodeCluster,
  searchNodes,
} from 'utils/services/api/common';
import {
  getAnalyticalRoles,
  getBusinessRoles,
  getChangeReason,
  getDocumentIdentities,
  getIntegrals,
  getReasonInvalid,
  getСountries,
} from 'utils/services/api/dictionaries';

import { version } from '../../../package.json';

import {
  ClusterNodesType,
  CountriesType,
  DocumentIdentitiesType,
  IChangeReasonList,
  IntegralsIdentitiesType,
  InvalidReasonType,
  NodeType,
  NodesType,
  RoleListType,
} from 'DataTypes';
import { RootStoreType } from '../rootStore';

/**
 * Хранилище общей информации для приложения
 */
class CommonStore extends AsyncStore {
  RootStore: RootStoreType;

  constructor(RootStore: RootStoreType) {
    super();
    this.RootStore = RootStore;

    reaction(() => this.appTheme, this.handleThemeToggle);
  }

  /**
   * Тема приложения
   */
  @observable appTheme = localStorage.theme || 'light';

  @observable legendInfo: any = [];
  @observable legendInfoDark: any = [];

  /**
   * Версия приложения
   */
  @observable appVersion = {
    ppvsaVersion: '',
    matrixVersion: '',
    webVersion: version,
  };

  /**
   * Список аналитических ролей
   */
  @observable analyticalRoles: RoleListType = {
    isLoading: false,
    order: [],
  };

  /**
   * Список бизнес ролей
   */
  @observable businessRoles: RoleListType = {
    isLoading: false,
    order: [],
  };

  /**
   * Список узлов
   */
  @observable nodes: NodesType = {
    error: null,
    pending: false,
    order: [],
    noResults: false,
  };

  /**
   * Список узлов для поиска внутри графа
   */
  @observable nodesGraph: NodesType = {
    error: null,
    pending: false,
    order: [],
    noResults: false,
  };

  /**
   * Список узлов для поиска внутри кластера
   */
  @observable nodesCluster: ClusterNodesType = {
    error: null,
    pending: false,
    order: [],
    fullInformation: [],
    noResults: false,
    noResultsLabel: false,
  };

  /**
   * Список статусов связи
   */
  @observable invalidReasons: InvalidReasonType = {
    isLoading: false,
    order: [],
  };

  /**
   * Список причин корректировки для бизнес и аналитической ролей, связей
   */
  @observable changeReasons: IChangeReasonList = {
    isLoading: false,
    order: [],
  };

  /**
   * Список типов документов
   */
  @observable documentIdentities: DocumentIdentitiesType = {
    isLoading: false,
    order: [],
  };

  /**
   * Список стран
   */
  @observable countries: CountriesType = {
    isLoading: false,
    order: [],
  };

  @observable integrals: IntegralsIdentitiesType = {
    isLoading: false,
    order: [],
  };

  // Искомое значение для поиска НП внутри кластера
  @observable searchCluster: any = '';

  @action
  setSearchCluster = (data: any) => {
    this.searchCluster = data;
  };

  // "Развернуть все связи" в детализации кластера
  @observable showAllClusterLinks: boolean = true;
  @observable openDetailedCluster: boolean = false;
  @observable showAllClusterLinksCheck: boolean = false;

  @observable analyticalRolesIcons: any;
  @observable businessRolesIcons: any;
  @observable businessRolesTitle: any;

  @observable enableDossierButton: boolean = true;

  @action
  setShowAllClusterLinks = () => {
    this.showAllClusterLinks = !this.showAllClusterLinks;
    this.showAllClusterLinksCheck = true;
  };

  @action
  setShowAllClusterLinksCheck = () => {
    this.showAllClusterLinksCheck = false;
  };

  @action
  setShowAllClusterLinksOptional = (data: boolean) => {
    this.showAllClusterLinks = data;
  };

  @observable isShowBeginningWork = false;

  @action setIsShowBeginningWork = () => {
    this.isShowBeginningWork = !this.isShowBeginningWork;
  };

  @action closeBeginningWork = () => {
    this.isShowBeginningWork = false;
  };

  @action openBeginningWork = () => {
    this.isShowBeginningWork = true;
  };

  /**
   * Метод запрашивает версию приложения
   */
  fetchVersion = flow(function* (this: CommonStore) {
    this.state = 'pending';

    try {
      const result = yield getVersion();
      const ppvsaVersion = result.VsaVersion;
      const matrixVersion = result.RoleMatrixVersion;

      this.appVersion = {
        ...this.appVersion,
        ppvsaVersion,
        matrixVersion,
      };

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  @action filterAnalyticalRoles() {
    this.analyticalRoles = {
      ...this.analyticalRoles,
      order: this.analyticalRoles.order.filter(
        ({ role_id }) => role_id === 1 || role_id === 2 || role_id === 3,
      ),
      isLoading: false,
    };
  }

  /**
   * Метод запрашивает список аналитических ролей
   */
  fetchAnalyticalRoles = flow(function* (this: CommonStore) {
    this.analyticalRoles = {
      ...this.analyticalRoles,
      isLoading: true,
    };
    this.state = 'pending';

    try {
      const roles = yield getAnalyticalRoles();
      this.analyticalRoles = {
        ...this.analyticalRoles,
        order: roles,
        isLoading: false,
      };

      this.analyticalRolesIcons = this.analyticalRoles.order.reduce(
        (obj, item) =>
          Object.assign(obj, {
            [item.brief]: {
              icon: URL.createObjectURL(
                new Blob([item.icon], { type: 'image/svg+xml;charset=utf-8' }),
              ),
            },
          }),
        {},
      );

      const filterRoles = roles.filter((element: any) => element.used_in_vsa);

      this.legendInfo.unshift({ name: 'Аналитические роли', order: filterRoles });
      this.legendInfo.forEach((data: any) => {
        if (data.name === 'Аналитические роли') {
          for (let obj of data.order) {
            if (obj.used_in_vsa) {
              obj.icon = this.analyticalRolesIcons[obj.brief].icon;
              obj.options = { className: 'itemImageBig' };
            }
            delete obj['brief'];
          }
        }
      });

      this.legendInfoDark.unshift({ name: 'Аналитические роли', order: filterRoles });
      this.legendInfoDark.forEach((data: any) => {
        if (data.name === 'Аналитические роли') {
          for (let obj of data.order) {
            if (obj.used_in_vsa) {
              obj.icon = this.analyticalRolesIcons[obj.brief].icon;
              obj.options = { className: 'itemImageBig' };
            }
            delete obj['brief'];
          }
        }
      });

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод запрашивает список бизнес ролей
   */
  fetchBusinessRoles = flow(function* (this: CommonStore) {
    this.businessRoles = {
      ...this.businessRoles,
      isLoading: true,
    };
    this.state = 'pending';

    try {
      const roles = yield getBusinessRoles();
      this.businessRoles = {
        ...this.businessRoles,
        order: roles,
        isLoading: false,
      };

      this.businessRolesIcons = this.businessRoles.order.reduce(
        (obj, item) =>
          Object.assign(obj, {
            [item.brief]: {
              icon: URL.createObjectURL(
                new Blob([item.icon], { type: 'image/svg+xml;charset=utf-8' }),
              ),
            },
          }),
        {},
      );

      this.businessRolesTitle = this.businessRoles.order.reduce(
        (obj, item) =>
          Object.assign(obj, {
            [item.brief]: {
              description: item.value,
              icon: URL.createObjectURL(
                new Blob([item.icon], { type: 'image/svg+xml;charset=utf-8' }),
              ),
            },
          }),
        {},
      );

      this.legendInfo.unshift({ name: 'Бизнес-роли', order: roles });
      this.legendInfo.forEach((data: any) => {
        if (data.name === 'Бизнес-роли') {
          for (let obj of data.order) {
            obj['icon'] = obj['brief'];
            obj.icon = this.businessRolesIcons[obj.icon].icon;
            obj.options = { className: 'itemImageBusiness' };
            delete obj['brief'];
          }
        }
      });
      this.legendInfoDark.unshift({ name: 'Бизнес-роли', order: roles });
      this.legendInfoDark.forEach((data: any) => {
        if (data.name === 'Бизнес-роли') {
          for (let obj of data.order) {
            obj['icon'] = obj['brief'];
            obj.icon = this.businessRolesIcons[obj.icon].icon;
            obj.options = { className: 'itemImageBusiness' };
            delete obj['brief'];
          }
        }
      });

      this.legendInfo = this.legendInfo.concat(LEGEND);
      this.legendInfoDark = this.legendInfoDark.concat(LEGEND_DARK);

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод запрашивает справочник статусов связи
   */
  fetchInvalidReasons = flow(function* (this: CommonStore) {
    this.invalidReasons = {
      ...this.invalidReasons,
      isLoading: true,
    };
    this.state = 'pending';

    try {
      const reasons = yield getReasonInvalid();
      this.invalidReasons = {
        ...this.invalidReasons,
        order: reasons,
        isLoading: false,
      };

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод запрашивает справочник причин корректировки для бизнес и аналитической ролей, связей
   */
  fetchChangeReason = flow(function* (this: CommonStore) {
    this.changeReasons = {
      ...this.changeReasons,
      isLoading: true,
    };
    this.state = 'pending';

    try {
      const reasons = yield getChangeReason();
      this.changeReasons = {
        ...this.changeReasons,
        order: reasons,
        isLoading: false,
      };

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод запрашивает список уровней рисков
   */
  fetchIntegrals = flow(function* (this: CommonStore) {
    this.integrals = {
      ...this.integrals,
      isLoading: true,
    };
    this.state = 'pending';

    try {
      const integrals = yield getIntegrals();
      this.integrals = {
        ...this.integrals,
        order: integrals,
        isLoading: false,
      };

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод изменяет цветовую тему приложения
   */
  @action setTheme = (theme: string) => {
    localStorage.theme = theme;
    this.appTheme = theme;
  };

  /**
   * Метод очищает список найденных узлов
   */
  @action resetNodesOrder = (): void => {
    this.nodes.order = [];
    this.nodes.noResults = false;
    this.nodes.error = null;
  };

  /**
   * Метод уставливает значение loader для поиска узлов в true
   */
  @action nodesPendingTrue = (): void => {
    this.nodes.pending = true;
  };

  /**
   * Метод уставливает значение loader для поиска узлов в false
   */
  @action nodesPendingFalse = (): void => {
    this.nodes.pending = false;
  };

  /**
   * Метод уставливает значение loader для поиска узлов внутри графа в true
   */
  @action nodesGraphPendingTrue = (): void => {
    this.nodesGraph.pending = true;
  };

  /**
   * Метод уставливает значение loader для поиска узлов внутри графа в false
   */
  @action nodesGraphPendingFalse = (): void => {
    this.nodesGraph.pending = false;
  };

  /**
   * Метод очищает список найденных узлов в рамках одного графа
   */
  @action resetNodesGraphOrder = (): void => {
    this.nodesGraph.order = [];
    this.nodesGraph.noResults = false;
  };

  /**
   * Метод уставливает значение loader для поиска узлов внутри кластера в true
   */
  @action nodesClusterPendingTrue = (): void => {
    this.nodesCluster.pending = true;
  };

  /**
   * Метод уставливает значение loader для поиска узлов внутри кластера в false
   */
  @action nodesClusterPendingFalse = (): void => {
    this.nodesCluster.pending = false;
  };

  /**
   * Метод очищает список найденных узлов в рамках одного кластера
   */
  @action resetNodesClusterOrder = (): void => {
    this.nodesCluster.order = [];
    this.nodesCluster.fullInformation = [];
    this.nodesCluster.noResults = false;
  };

  @action resetNoResultsLabel = (): void => {
    this.nodesCluster.noResultsLabel = false;
  };

  /**
   * Метод получения узлов
   *
   * @param {string} text - текст для поиска
   */
  searchNodesByText = flow(function* (this: CommonStore, text: string) {
    this.nodes = {
      ...this.nodes,
      pending: true,
      error: null,
    };

    try {
      let nodes: NodeType[] = yield searchNodes(text, false);
      if (nodes.length === 0) {
        this.nodes = {
          ...this.nodes,
          noResults: true,
          pending: false,
        };
      } else {
        this.nodes = {
          ...this.nodes,
          order: nodes,
          noResults: false,
          pending: false,
        };
      }
    } catch (err) {
      this.nodes = {
        ...this.nodes,
        error: err as Error,
      };
    }
  });

  /**
   * Метод получения узлов внутри графа
   *
   * @param {string} text - текст для поиска
   */
  searchNodesByTextInGraph = flow(function* (this: CommonStore, text: string, graphID: string) {
    this.nodesGraph = {
      ...this.nodesGraph,
      pending: true,
    };

    try {
      let nodes: NodeType[] = yield searchNodes(text, true, graphID);
      if (nodes.length === 0) {
        this.nodesGraph = {
          ...this.nodesGraph,
          noResults: true,
          pending: false,
        };
      } else {
        this.nodesGraph = {
          ...this.nodesGraph,
          order: nodes,
          noResults: false,
          pending: false,
        };
      }
    } catch (err) {
      this.nodesGraph = {
        ...this.nodesGraph,
      };
    }
  });

  /**
   * Метод получения узлов внутри кластера
   *
   * @param {any} id - идентификатор графа
   * @param {any} origId - идентификатор кластера
   * @param {any} text - текст для поиска
   */
  searchNodesCluster = flow(function* (
    this: CommonStore,
    id: any,
    origId: any,
    text: any,
    showNPWithLink: boolean,
  ) {
    this.nodesCluster = {
      ...this.nodesCluster,
    };
    try {
      let nodes = yield searchNodeCluster(id, origId, text, showNPWithLink);
      this.nodesCluster = {
        ...this.nodesCluster,
        fullInformation: nodes,
      };
    } catch (err) {
      this.nodesCluster = {
        ...this.nodesCluster,
        error: err,
      };
    }
  });

  @action
  searchNodesClusterDynamically = (searchValue: any) => {
    this.nodesCluster = {
      ...this.nodesCluster,
      pending: true,
      error: false,
    };
    try {
      let nodes = this.RootStore.GraphStore.detailedData.cluster;
      let participantsInfo: any[] = [];
      if (nodes.length === 0) {
        this.nodesCluster = {
          ...this.nodesCluster,
          noResults: true,
          pending: false,
          noResultsLabel: true,
        };
      } else {
        nodes.type_groups.forEach((subtype_groups: any) => {
          subtype_groups.subtype_groups.forEach((value_groups: any) => {
            value_groups.value_groups.forEach((participants: any) => {
              participants.participants.forEach((child: any) => {
                participantsInfo.push(child);
              });
            });
          });
        });
        let unique = participantsInfo.filter(
          (participant, index, self) =>
            index ===
            self.findIndex((t) => t.inn === participant.inn && t.name === participant.name),
        );
        unique = unique.filter(
          (participant) =>
            participant.inn.includes(searchValue) ||
            participant.name.toLowerCase().includes(searchValue.toLowerCase()),
        );
        if (unique.length === 0) {
          this.nodesCluster = {
            ...this.nodesCluster,
            noResults: true,
            pending: false,
            noResultsLabel: true,
          };
        } else {
          this.nodesCluster = {
            ...this.nodesCluster,
            order: unique,
            noResults: false,
            pending: false,
            noResultsLabel: false,
          };
        }
      }
    } catch (err) {
      this.nodesCluster = {
        ...this.nodesCluster,
        error: err,
      };
    }
  };

  /**
   * Калбэк при смене цветовой темы
   */
  handleThemeToggle = (theme: string, prevTheme: IReactionPublic) => {
    const diagramStore = this.RootStore.GraphStore.DiagramStore;
    diagramStore.diagram!.grid = diagramStore.grid;
    diagramStore.changeDirection();
  };

  /*
   * Метод запрашивает справочник типов документов
   */
  fetchDocumentIdentities = flow(function* (this: CommonStore) {
    this.documentIdentities = {
      ...this.documentIdentities,
      isLoading: true,
    };
    this.state = 'pending';

    try {
      const documents = yield getDocumentIdentities();
      this.documentIdentities = {
        ...this.documentIdentities,
        order: documents,
        isLoading: false,
      };

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод запрашивает справочник стран
   */
  fetchCountries = flow(function* (this: CommonStore) {
    this.countries = {
      ...this.countries,
      isLoading: true,
    };
    this.state = 'pending';

    try {
      const country = yield getСountries();
      this.countries = {
        ...this.countries,
        order: country,
        isLoading: false,
      };

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод запрашивает информацию для отображения кнопки "Передача изменений в Досье"
   */
  fetchInternalParameters = flow(function* (this: CommonStore) {
    this.state = 'pending';

    try {
      const parameters = yield fetchInternalParameters();
      this.enableDossierButton = parameters[0].value === 'true';

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод получения списка аналитических ролей
   */
  @computed get analyticalRolesList() {
    return this.analyticalRoles.order;
  }

  @action getAnalyticalRoleByName = (name: string) => {
    return this.analyticalRolesList.find((role) => role.value === name);
  };

  @action getAnalyticalRoleById = (id: number) => {
    return this.analyticalRolesList.find((role) => role.role_id === id);
  };

  /**
   * Метод получения списка бизнес ролей
   */
  @computed get businessRolesList() {
    return this.businessRoles.order;
  }

  @action getBusinessRoleByBrief = (brief: string) => {
    return this.businessRolesList.find((role) => role.brief === brief);
  };

  @action getBusinessRoleByName = (name: string) => {
    return this.businessRolesList.find((role) => role.value === name);
  };

  @action getBusinessRoleById = (id: number) => {
    return this.businessRolesList.find((role) => role.role_id === id);
  };

  /**
   * Метод получения списка бизнес ролей
   */
  @computed get integralsList() {
    return this.integrals.order;
  }

  @computed get integralsListSearchNP() {
    return this.integralsList?.map((item) => ({
      name: item.description,
      value: item.id,
    }));
  }

  @computed get businessRolesListSearchNP() {
    const listForFilter = this.businessRolesList?.map((item) => ({
      name: item.value,
      value: item.role_id,
    }));

    //@ts-ignore
    listForFilter.push({ name: 'Не определена', value: 0 });
    return listForFilter;
  }

  /**
   * Метод получения статусов связи
   */
  @computed get invalidReasonsList() {
    return this.invalidReasons.order;
  }

  /**
   * Метод получения причин корректировки для бизнес и аналитической ролей, связей
   */
  @computed get changeReasonsList() {
    return this.changeReasons.order;
  }

  @action getChangeReasonById = (id: number) => {
    return this.changeReasonsList.find((reason) => reason.reason_id === id);
  };

  @action getChangeReasonByName = (name: string) => {
    return this.changeReasonsList.find((reason) => reason.name === name);
  };

  /**
   * Метод получения списка типов документов
   */
  @computed get documentIdentitiesList() {
    return this.documentIdentities.order;
  }

  /**
   * Метод получения списка стран
   */
  @computed get countriesList() {
    return this.countries.order;
  }
}

export type CommonStoreType = CommonStore;
export default CommonStore;
