export const getCurrentDate = (format: 'roman' | 'arab' = 'arab') => {
  const quarters = {
    '1': 'I',
    '2': 'II',
    '3': 'III',
    '4': 'IV',
  };

  const date = new Date();
  const month = date.getMonth() + 1;
  const quarter = (Math.ceil(month / 3) + '') as '1' | '2' | '3' | '4';

  return {
    day: date.getDate() + '',
    month: month + '',
    year: date.getFullYear() + '',
    quarter: format === 'roman' ? quarters[quarter] : quarter,
  };
};

/**
 * получение года и квартала по правилу:
 *  IV квартал предыдущего года –  должен отражаться с 26.01 текущего года;
 *  I квартал текущего года – должен отражаться с 26.04 текущего года;
 *  II квартал текущего года – должен отражаться с 26.07 текущего года;
 *  III квартал текущего года – должен отражаться с 26.10 текущего года.
 *
 * @param {'roman' | 'arab'} format Формат квартала, римский или арабский
 * @returns { { year:string, quarter: string } } Объект с годом и кварталом
 */

export const getDefaultPeriod = (format: 'roman' | 'arab' = 'arab') => {
  const currentDate = new Date();
  const currentYear = currentDate.getFullYear();

  if (
    currentDate >= new Date(`${currentYear}-01-26`) &&
    currentDate < new Date(`${currentYear}-04-26`)
  ) {
    return {
      year: currentDate.getFullYear() - 1 + '',
      quarter: format === 'roman' ? 'IV' : '4',
    };
  } else if (
    currentDate >= new Date(`${currentYear}-04-26`) &&
    currentDate < new Date(`${currentYear}-07-26`)
  ) {
    return {
      year: currentDate.getFullYear() + '',
      quarter: format === 'roman' ? 'I' : '1',
    };
  } else if (
    currentDate >= new Date(`${currentYear}-07-26`) &&
    currentDate < new Date(`${currentYear}-10-26`)
  ) {
    return {
      year: currentDate.getFullYear() + '',
      quarter: format === 'roman' ? 'II' : '2',
    };
  } else {
    const currentMonth = currentDate.getMonth() + 1;

    return {
      year: currentDate.getFullYear() - ([10, 11, 12].includes(currentMonth) ? 0 : 1) + '',
      quarter: format === 'roman' ? 'III' : '3',
    };
  }
};
