import React, { useState, useEffect } from 'react';
import { extendedDamagePropTypes } from './ExtendedDamage.props';
import styles from './ExtendedDamage.module.scss';
import { ReactComponent as ChevronUp } from 'assets/icons/chevron_up.svg';
import InputWithIcon from '../../InputWithIcon';
import { formatNumberToLocaleRu, formatLocaleRuToString } from 'utils/ui/formatNumbers';
import { KEY_CODES } from 'components/constants/keyCodes';

interface ExtendedSearchFiledProps {
  /** Функция ввода суммы потенциального ущерба */
  onInput: (v: string) => void;
  theme?: string;
  id: string;
  amountOfDamage: any;
}

const ExtendedDamage = ({ onInput, id, theme, amountOfDamage }: ExtendedSearchFiledProps) => {
  const [value, setValue] = useState(amountOfDamage);
  const [isDisabled, setIsDisabled] = useState(true);
  const wrapperRef = React.createRef<HTMLDivElement>();

  useEffect(() => {
    if (wrapperRef && wrapperRef.current) {
      const el = wrapperRef.current;
      el.addEventListener('keydown', handleEnter);
    }
  });

  const handleEnter = (e: KeyboardEvent) => {
    if (e.which === KEY_CODES.ENTER || e.keyCode === KEY_CODES.ENTER) {
      inputRequest();
    }
  };

  const onChange = (v: string) => {
    setValue(formatNumberToLocaleRu(v));
    setIsDisabled(false);
  };

  const inputRequest = () => {
    onInput(formatLocaleRuToString(`${value}`));
    setIsDisabled(true);
  };

  return (
    <div ref={wrapperRef} className={styles.wrapper}>
      <InputWithIcon
        id={id}
        value={value ? value.toLocaleString('ru') : ''}
        onChange={onChange}
        autoFocus
        placeholder="0"
        afterIcon={
          <ChevronUp className={isDisabled ? `${styles.icon}` : `${styles.icon__active}`} />
        }
        label="Укажите сумму потенциального ущерба, ₽"
        onClickIcon={inputRequest}
        disabledButton={isDisabled}
      />
    </div>
  );
};

ExtendedDamage.propTypes = extendedDamagePropTypes;

ExtendedDamage.defaultProps = {
  onInput: () => {},
};

export default ExtendedDamage;
