/**
 * Форматирование денежной суммы
 */
export const formatMoneyDimensions = (amount: any, dimensions: string = 'rubles') => {
  if (typeof amount === 'string') {
    amount = amount.replace(/,/, '.');
  }

  if (amount == 0) {
    return null;
  } else {
    let str = amount;
    if (typeof amount === 'string') {
      str = amount.replace(/\s/g, '');
    }

    switch (dimensions) {
      case 'rubles':
        return Number(Math.trunc(Number(str)).toFixed()).toLocaleString('ru');
      case 'thousand':
        if (Number((Math.trunc(Number(str)) / 1e3).toFixed(3)) === 0) {
          return Number((Math.trunc(Number(str)) / 1e3).toPrecision(1))
            .toFixed(10)
            .replace(/\.?0+$/, '')
            .replace(/\./g, ',');
        } else {
          return Number((Math.trunc(Number(str)) / 1e3).toFixed(3)).toLocaleString('ru');
        }
      case 'million':
        if (Number((Math.trunc(Number(str)) / 1e6).toFixed(3)) === 0) {
          return Number((Math.trunc(Number(str)) / 1e6).toPrecision(1))
            .toFixed(10)
            .replace(/\.?0+$/, '')
            .replace(/\./g, ',');
        } else {
          return Number((Math.trunc(Number(str)) / 1e6).toFixed(3)).toLocaleString('ru');
        }
      case 'billion':
        if (Number((Math.trunc(Number(str)) / 1e9).toFixed(3)) === 0) {
          return Number((Math.trunc(Number(str)) / 1e9).toPrecision(1))
            .toFixed(10)
            .replace(/\.?0+$/, '')
            .replace(/\./g, ',');
        } else {
          return Number((Math.trunc(Number(str)) / 1e9).toFixed(3)).toLocaleString('ru');
        }
      default:
        return Number(Math.trunc(Number(str)).toFixed()).toLocaleString('ru');
    }
  }
};
