import * as go from 'gojs';

import ContextMenu, { Link } from '../../../utils/diagram/ContextMenu';

import create, {
  VSAFlowNDSLink,
  VSAClusterLink,
  VSABreakNDSLink,
  VSARecursiveLink,
  VSAFlowPaymentLink,
  VSAZombieLink,
} from '@fdc/vsa-gojs-lib';

import {
  DETAILED_LINK_NDS_MODAL,
  MODAL_EDIT_LINK,
} from '../../../components/modals/constants/modalNames';

import { DiagramStoreType } from '../diagramStore';
import { ModalStoreType } from '../../modalStore';
import { CommonStoreType } from '../../common/commonStore';
import { GraphStoreType } from '../graphStore';

/**
 * Класс шаблонов для связей между узлами
 *
 * go.Shape - описывает форму связи
 *
 * go.Panel - описывает позицию и размеры связи
 */
class LinkTemplates {
  DiagramStore: DiagramStoreType;
  ModalStore: ModalStoreType;
  CommonStore: CommonStoreType;
  GraphStore: GraphStoreType;
  direction: any;

  Flow_Payment: go.Link;
  Recursive: go.Link;
  Cluster_Link: go.Link;

  Zombie_Link: go.Link;

  vsaLinkContextMenuId: string;

  private commonStoreInstanceLink: ContextMenu;

  constructor(
    DiagramStore: DiagramStoreType,
    direction: 0 | 90,
    CommonStore: CommonStoreType,
    GraphStore: GraphStoreType,
  ) {
    this.DiagramStore = DiagramStore;
    this.ModalStore = DiagramStore.GraphStore.RootStore.ModalStore;
    this.CommonStore = CommonStore;
    this.direction = direction;
    this.GraphStore = GraphStore;

    this.vsaLinkContextMenuId = this.DiagramStore.vsaLinkContextMenuId;
    this.commonStoreInstanceLink = new ContextMenu(
      new Link(DiagramStore, this.vsaLinkContextMenuId, GraphStore),
    );

    this.Flow_Payment = create(VSAFlowPaymentLink);

    this.Recursive = create(VSARecursiveLink);

    this.Cluster_Link = create(VSAClusterLink);

    this.Zombie_Link = create(VSAZombieLink, { direction: this.direction });
  }

  /**
   * Метод по открытию всплывающей подсказки ('Приняты отложенные вычеты')
   */
  _showToolTipMultipleShort = async (diagramEvent: any, obj: any) => {
    let toolTipDiv = document.getElementById('toolTipMultipleShort');
    let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
    if (toolTipDiv && pt) {
      toolTipDiv.style.left = pt.x + 15 + 'px';
      toolTipDiv.style.top = pt.y + 40 + 'px';
      toolTipDiv.style.display = 'block';
    }
  };

  /**
   * Метод по закрытию всплывающей подсказки ('Приняты отложенные вычеты')
   */
  _closeToolTipMultipleShort = async (diagramEvent: any, obj: any) => {
    let toolTipDiv = document.getElementById('toolTipMultipleShort');

    if (toolTipDiv) {
      toolTipDiv.style.display = 'none';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (декларация для узла)
   */
  _showToolTipMultipleFull = async (diagramEvent: any) => {
    const selectedNode = diagramEvent.diagram.selection.first();
    const interQuarterPeriods = selectedNode.data.inter_quarter_periods;
    const coopLinkPeriods = selectedNode.data.coopLink_periods;

    this.GraphStore.setInterQuarterPeriods(interQuarterPeriods);
    this.GraphStore.setCoopLinkPeriods(coopLinkPeriods);

    let toolTipDiv = document.getElementById('toolTipMultipleFull');
    let toolTipBackGround = document.getElementById('toolTipBackGroundMultipleFull');

    let pt = this.DiagramStore.diagram?.lastInput.viewPoint;

    let widthToolTip = window.innerWidth > 1745 ? 230 : 220;

    if (toolTipDiv && pt && toolTipBackGround) {
      if (window.innerWidth < pt.x + widthToolTip) {
        toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      } else {
        toolTipDiv.style.left = pt.x + 15 + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      }
      toolTipDiv.style.display = 'block';
      toolTipBackGround.style.display = 'block';
    }
  };

  _showToolTipLayers = async (diagramEvent: any, obj: any) => {
    this.GraphStore.RootStore.TooltipStore.setCurrentLink(obj);
    let toolTipDiv = document.getElementById('ToolTipLayers');
    let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
    if (toolTipDiv && pt) {
      toolTipDiv.style.left = pt.x + 15 + 'px';
      toolTipDiv.style.top = pt.y + 40 + 'px';
      toolTipDiv.style.display = 'block';
    }
  };

  /**
   * Метод по закрытию всплывающей подсказки ('Приняты отложенные вычеты')
   */
  _closeToolTipLayers = async (diagramEvent: any, obj: any) => {
    let toolTipDiv = document.getElementById('ToolTipLayers');

    if (toolTipDiv) {
      toolTipDiv.style.display = 'none';
    }
  };

  get Flow_NDS() {
    return create(VSAFlowNDSLink, {
      direction: this.direction,
      contextMenu: this.commonStoreInstanceLink.getContextMenu,
      theme: this.CommonStore.appTheme,
      dimensions: this.GraphStore.dimensions,
      currentDrawingMode: this.GraphStore.currentDrawingMode,
      graphExpandType: this.GraphStore.graphExpandType,
      interQuarterSearch: this.GraphStore.interQuarterSearch,
      showToolTipMultipleShort: this._showToolTipMultipleShort,
      closeToolTipMultipleShort: this._closeToolTipMultipleShort,
      showToolTipMultipleFull: this._showToolTipMultipleFull,
      showToolTipLayers: this._showToolTipLayers,
      closeToolTipLayers: this._closeToolTipLayers,
      sourceSystem: this.GraphStore.sourceSystem,
      indicators: this.GraphStore.options,
      graphFilters: this.GraphStore.filtersGraph,
    });
  }

  get Break_NDS() {
    return create(VSABreakNDSLink, {
      direction: this.direction,
      contextMenu: this.commonStoreInstanceLink.getContextMenu,
      theme: this.CommonStore.appTheme,
      dimensions: this.GraphStore.dimensions,
      currentDrawingMode: this.GraphStore.currentDrawingMode,
      graphExpandType: this.GraphStore.graphExpandType,
      interQuarterSearch: this.GraphStore.interQuarterSearch,
      showToolTipMultipleShort: this._showToolTipMultipleShort,
      closeToolTipMultipleShort: this._closeToolTipMultipleShort,
      showToolTipMultipleFull: this._showToolTipMultipleFull,
      showToolTipLayers: this._showToolTipLayers,
      closeToolTipLayers: this._closeToolTipLayers,
      sourceSystem: this.GraphStore.sourceSystem,
      indicators: this.GraphStore.options,
      graphFilters: this.GraphStore.filtersGraph,
    });
  }

  /**
   * Метод открытия связи для детализации
   */
  _onLinkDetails = (diagramEvent: any) => {
    const linkID = this.DiagramStore?.diagram?.selection?.first()?.data.orig_id;
    this.ModalStore.openModal(DETAILED_LINK_NDS_MODAL, { linkID });
  };

  /**
   * Метод открытия связи на редактирование
   */
  _onLinkEdit = (diagramEvent: any) => {
    const linkID = this.DiagramStore?.diagram?.selection?.first()?.data.orig_id;
    this.ModalStore.openModal(MODAL_EDIT_LINK, { linkID });
  };
}

export default LinkTemplates;
