import { action, observable, computed } from 'mobx';

import { FilterOptionsType } from 'FilterTypes';

/**
 * Класс фильтра
 */
abstract class Filter {
  // флаг, показывающий включен ли фильтр
  @observable keep: boolean = false;

  // изначальные опции фильтра
  initialOptions: FilterOptionsType = {};

  // опции фильтра
  @observable options: FilterOptionsType = { ...this.initialOptions };

  /**
   * Метод переключает состояние фильтра
   */
  @action toggleFilter = () => {
    this.keep = !this.keep;
  };

  /**
   * Метод проверяет все опции фильтра, если хотя бы один из дочерних фильтров включен,
   * включается и родительский фильтр.
   *
   * Значение фильтра проверяется на истинность. Если значение опции не пустая строка, не нулевое
   * число или True - включается родительский фильтр
   */
  @action keepFilter = (isAllFalse?: boolean) => {
    const { options } = this;
    for (const key in options) {
      if (!this.keep && options[key]) {
        this.keep = !this.keep;
      }
    }
    if (this.keep && isAllFalse) {
      this.keep = !this.keep;
    }
  };

  @action changeKeepFilter = () => {
    const { options } = this;
    let keep = false;

    for (const key in options) {
      if (options[key]) {
        keep = true;
      }
    }

    this.keep = keep;
  };

  /**
   * Метод проверяет динамические опции фильтра, если хотя бы один из них включен,
   * то включается и родительский фильтр
   */
  @action keepFilterDynamic = (isChecked: boolean, isAllFalse?: boolean) => {
    if (!this.keep && isChecked) {
      this.keep = !this.keep;
    }
    if (this.keep && isAllFalse) {
      this.keep = !this.keep;
    }
  };

  /**
   * Метод устанавливает булевские опции на false
   */
  @action setOptionsToFalse = () => {
    const { options } = this;
    for (const key in options) {
      if (!this.keep) {
        options[key] = false;
      }
    }
  };

  /** Метод устанавливает булевские опции на true */
  @action.bound setOptionsToTrue = () => {
    const { options } = this;
    for (const key in options) {
      if (typeof options[key] === 'boolean') {
        options[key] = true;
      }
    }
    if (!this.keep) {
      this.toggleFilter();
    }
  };

  /** Метод устанавливает булевские опции на false */
  @action.bound resetFilters = () => {
    const { options } = this;
    for (const key in options) {
      if (typeof options[key] === 'boolean') {
        options[key] = false;
      }
    }
    if (this.keep) {
      this.toggleFilter();
    }
  };

  /**
   * Метод изменяет значение опции по ключу
   *
   * @param {string} key - ключ объекта
   * @param {any} value - значение для записи
   */
  @action changeOption = (key: string, value: any) => {
    if (key in this.options) {
      this.options = {
        ...this.options,
        [key]: value,
      };
    } else {
      console.warn(`Change filter option error! No option with given key '${key}' is found.`);
    }
  };

  @action changeOptionArray = (key: string, value: any) => {
    if (key in this.options) {
      let arrayFilter;
      if (this.options[key].some((item: any) => item === value)) {
        arrayFilter = this.options[key].filter((item: any) => item !== value);
      } else {
        arrayFilter = [...this.options[key], value];
      }
      this.options = {
        ...this.options,
        [key]: arrayFilter,
      };
    } else {
      console.warn(`Change filter option error! No option with given key '${key}' is found.`);
    }
  };

  /**
   * Метод сбрасывает настройки фильтра
   */
  @action reset = (): void => {
    this.keep = false;
    this.options = this.initialOptions;
  };

  /**
   * Метод обновляет опции фильтра
   *
   * @param {object} options - объект опций
   * @param {string} keepOptionName - имя опции для обновления состояния фильтра
   */
  @action updateOptions = (options: any, keepOptionName: string = '') => {
    this.keep =
      options[keepOptionName] === undefined ? false : options[keepOptionName] || this.keep;

    const initialOptions: { [key: string]: any } = {};

    for (const key in options) {
      initialOptions[key] = options[key] || this.initialOptions[key];
    }

    this.options = {
      ...initialOptions,
    };
  };

  /**
   * Метод возвращает опции фильтра
   */
  @computed get filterOptions() {
    return this.options;
  }
}

export default Filter;
