import { observer } from 'mobx-react';
import React, { useState } from 'react';
import ReactTooltip from 'react-tooltip';
import Search from '../../Search';
import Dimensions from './Dimensions';
import styles from './HeaderSearch.module.scss';
import { headerSearchPropTypes } from './HeaderSearch.props';
import { ReactComponent as CloseIcon } from './icons/close.svg';
import { ReactComponent as SaveIcon } from './icons/save.svg';
import { ReactComponent as UndoIcon } from './icons/undo.svg';

interface HeaderSearchProps {
  /** Функция поиска */
  onSearch: (value: string) => void;
  /** Закрытие меню */
  menuCallback?: () => void;
  undoCallback?: () => void;
  onSave: () => void;
  theme?: string;
  showAllScheme?: boolean;
  canUndo?: boolean;
  canSave?: boolean;
}

const HeaderSearch = ({
  onSearch,
  theme,
  menuCallback,
  undoCallback,
  onSave,
  showAllScheme,
  canUndo,
  canSave,
}: HeaderSearchProps) => {
  const [value, setValue] = useState('');

  const onChange = (v: string) => {
    setValue(v);
  };

  const searchRequest = () => {
    onSearch(value);
  };

  return (
    <>
      <div className={styles.header}>
        <h2>Основное меню</h2>
        <button
          onClick={canUndo ? undoCallback : undefined}
          data-active={canUndo}
          data-for="menu-header-tooltip"
          data-tip="Отменить"
        >
          <UndoIcon />
        </button>
        <button
          onClick={canSave ? onSave : undefined}
          data-active={canSave}
          data-for="menu-header-tooltip"
          data-tip="Сохранить"
        >
          <SaveIcon />
        </button>
        <button onClick={menuCallback} data-for="menu-header-tooltip" data-tip="Закрыть">
          <CloseIcon />
        </button>
        <ReactTooltip
          id="menu-header-tooltip"
          place="bottom"
          type="dark"
          effect="solid"
          delayShow={400}
          className={styles.reactTooltip}
        />
      </div>
      <div className={styles.wrapper}>
        {showAllScheme && (
          <div>
            <Search
              value={value}
              withSearchIcon
              placeholder={'Найти схему'}
              onChange={onChange}
              onSearch={searchRequest}
              wrapperStyle={styles.search}
              theme={theme}
            />
          </div>
        )}
        <div>
          <Dimensions />
        </div>
      </div>
    </>
  );
};

HeaderSearch.propTypes = headerSearchPropTypes;

HeaderSearch.defaultProps = {
  onSearch: () => {},
};

export default observer(HeaderSearch);
