import React, { PureComponent } from 'react';
import styles from './PeriodData.module.scss';

import TimePeriodPicker from '../../../components/TimePeriodPicker';

import { inject, observer } from 'mobx-react';

import { YEARS, QUARTERS } from '../../../components/constants';
import { CommonStoreType } from 'stores/common/commonStore';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import ReactTooltip from 'react-tooltip';

interface PeriodDataProps {
  periodDataFilter?: any;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент по выбору периода учёта данных
 */
@inject('periodDataFilter', 'commonStore', 'graphStore')
@observer
class PeriodData extends PureComponent<PeriodDataProps> {
  render() {
    const { periodDataFilter, commonStore, graphStore } = this.props;

    const periodFrom = periodDataFilter.options?.nds3_period_from?.split(';') || ['', ''];
    const periodTo = periodDataFilter.options?.nds3_period_to?.split(';') || ['', ''];

    const quarterFrom = periodFrom[0];
    const quarterTo = periodTo[0];

    const yearFrom = periodFrom[1];
    const yearTo = periodTo[1];

    return (
      <div data-tip={'React-tooltip'} data-for={'periodToolTip'}>
        <TimePeriodPicker
          wrapperStyle={styles.picker}
          fromQuarterValue={quarterFrom}
          fromYearValue={yearFrom}
          toQuarterValue={quarterTo}
          toYearValue={yearTo}
          onFromValueChange={periodDataFilter.changePeriodFrom}
          onToValueChange={periodDataFilter.changePeriodTo}
          quarters={QUARTERS}
          years={YEARS}
          label={'Период учета данных'}
          theme={commonStore?.appTheme}
          title={'Период отображения данных связей'}
          disabled={graphStore?.interQuarterSearch}
        />
        {graphStore?.interQuarterSearch && (
          <ReactTooltip
            place="right"
            type="light"
            effect="float"
            backgroundColor={'#EDEDED'}
            textColor={'#565656'}
            id="periodToolTip"
          >
            <div className={styles.divTooltip}>
              Смена <b>периода учета данных</b> недоступна. Включен{' '}
              <b>режим межквартального сопоставления</b>
            </div>
          </ReactTooltip>
        )}
      </div>
    );
  }
}

export default PeriodData;
