import React from 'react';

import BurgerMenu from 'components/BurgerMenu';

import { inject, observer } from 'mobx-react';

import {
  CommonStoreType,
  DiagramStoreType,
  FilterStoreType,
  GraphStoreType,
  ModalStoreType,
  UIStoreType,
  UserGraphsStoreType,
} from 'StoreTypes';
import NodesReportStore from 'stores/nodesReportStore';
import { MODAL_POTENTIAL_DAMAGE } from '../../components/modals/constants/modalNames';
import TaxpayersFilter from '../../stores/filters/taxpayersFilter';
import clusterStore from '../../stores/graph/graphStore/clusterStore';
import { IndicatorsStoreType } from '../../stores/indicators/indicatorsStore';

export interface MenuContainerProps {
  /** Хранилище состояние интерфейса */
  uiStore?: UIStoreType;
  /** Хранилище пользовательских графов */
  userGraphsStore?: UserGraphsStoreType;
  /** Хранилище модальных окон */
  modalStore?: ModalStoreType;
  /** Хранилище диаграммы */
  diagramStore?: DiagramStoreType;
  /** Общее хранилище */
  commonStore?: CommonStoreType;
  /** Хранилище фильтров */
  filterStore?: FilterStoreType;
  /** Хранилище графа */
  graphStore?: GraphStoreType;
  /** Хранилище индикаторов */
  indicatorsStore?: IndicatorsStoreType;
  /** Хранилище кластеров */
  clusterStore?: clusterStore;
  taxpayersFilter?: TaxpayersFilter;
  nodesReportStore?: NodesReportStore;
}

@inject(
  'uiStore',
  'userGraphsStore',
  'modalStore',
  'diagramStore',
  'commonStore',
  'filterStore',
  'graphStore',
  'indicatorsStore',
  'clusterStore',
  'taxpayersFilter',
  'nodesReportStore',
)
@observer
export default class MenuContainer extends React.PureComponent<MenuContainerProps> {
  /** Метод поиска */
  onSearch = (v: string) => {
    const { fetchScheme } = this.props.graphStore!;
    const { setFilterClusterFalse } = this.props.clusterStore!;
    const { indicatorsStore, taxpayersFilter, commonStore } = this.props;

    commonStore?.closeBeginningWork();

    // Для того, чтобы не подтягивались фильтры по индикаторам от предыдущего графа
    indicatorsStore?.showAllFilters();
    indicatorsStore?.filter();
    taxpayersFilter?.resetFiltersAndSearch();

    setFilterClusterFalse();
    if (v.includes('-')) {
      fetchScheme(v, 'itais');
    } else {
      fetchScheme(v);
    }
  };

  /** Метод ввода суммы потенциального ущерба */
  onInputDamage = async (v: string) => {
    const { graphStore } = this.props;

    await graphStore?.savePotentialDamage({ amount_of_damage: v }, graphStore?.mainVertexId);
  };

  /**
   * Метод запрашивает выбранный граф
   *
   * @param {string} id - идентификатор графа
   */
  onOpenGraph = async (id: string) => {
    const { graphStore, filterStore } = this.props;

    try {
      if (graphStore.showFilters) {
        await filterStore?.getGraphFilters(id);
      }
      await graphStore.fetchGraph(id);
    } catch (err) {}
  };

  /**
   * Метод, вызывающийся при передаче изменений (схема)
   */
  _sendChanges = (): void => {
    const { graphStore } = this.props;
    graphStore?.fetchChangeSchema();
  };

  /**
   * Метод, вызывающийся при передаче изменений (РАВП)
   */
  _sendChangesMDAB = (): void => {
    const { graphStore, modalStore } = this.props;

    graphStore?.fetchVerifySchemaMDAB();
  };

  /**
   * Метод, вызывающийся при передаче изменений (граф)
   */
  _sendGraph = (): void => {
    const { graphStore } = this.props;
    if (graphStore.showCreateKAO || graphStore.showCreateKNP) {
      graphStore?.fetchSendGraph(graphStore.controlArea);
    }
  };

  _backToDossier = async () => {
    const { graphStore } = this.props;
    const { schemeInfo } = graphStore;
    let schemeID = schemeInfo.origin_id;

    // @ts-ignore
    if (typeof window.CefSharp === 'object') {
      // @ts-ignore
      await CefSharp.BindObjectAsync('unifiedClientIntegration');
      // @ts-ignore
      unifiedClientIntegration.executeFunction(
        'Vsa2Domain',
        'GoToDossierBySchemeId',
        JSON.stringify({
          schema_id: schemeID,
          use_type: schemeInfo.use_type,
          control_area: schemeInfo.control_area,
        }),
      );
    } else {
      if (schemeInfo.use_type === 'TEST') {
        if (schemeInfo.control_area === 'KAO') {
          window.location.assign(`/inias/csc/gws/static/EiapDvWeb/kao/test/schema/` + schemeID);
        } else {
          window.location.assign(
            `/inias/csc/gws/static/EiapDvWeb/registry/test/schema/` + schemeID,
          );
        }
      }
      if (schemeInfo.use_type === 'PROD') {
        if (schemeInfo.control_area === 'KAO') {
          window.location.assign(`/inias/csc/gws/static/EiapDvWeb/kao/schema/` + schemeID);
        } else {
          window.location.assign(`/inias/csc/gws/static/EiapDvWeb/registry/schema/` + schemeID);
        }
      }
    }
  };

  _backToRAVP = async () => {
    const { graphStore } = this.props;
    const { mdabSchemaInfo } = graphStore;
    let value = mdabSchemaInfo.tos_schema_id
      ? { TypologySchemeId: mdabSchemaInfo.tos_schema_id }
      : { DelictId: mdabSchemaInfo.delict_id, ManualSchemeId: mdabSchemaInfo.mdab_schema_id };

    // @ts-ignore
    if (typeof window.CefSharp === 'object') {
      // @ts-ignore
      await CefSharp.BindObjectAsync('unifiedClientIntegration');
      // @ts-ignore
      unifiedClientIntegration.executeFunction(
        'Vsa2Domain',
        'ShowMdabSchemaCard',
        JSON.stringify(value),
      );
    } else {
      window.location.assign(
        `/inias/csc/gws/static/IASCB_MDABWeb/delicts/` +
          mdabSchemaInfo.delict_id +
          '/schemes/' +
          mdabSchemaInfo.mdab_schema_id +
          '/description',
      );
    }
  };

  _backToITAIS = async () => {
    const { graphStore } = this.props;
    const { itaisSchemeInfo } = graphStore;
    let schemeID = itaisSchemeInfo.id;

    // @ts-ignore
    if (typeof window.CefSharp === 'object') {
      // @ts-ignore
      await CefSharp.BindObjectAsync('unifiedClientIntegration');
      // @ts-ignore
      unifiedClientIntegration.executeFunction(
        'Vsa2Domain',
        'GoToItaisBySchemeId',
        JSON.stringify({
          itais_scheme_id: schemeID,
        }),
      );
    } else {
      window.location.assign(`/inias/csc/gws/static/IASCB_ITAISWeb/schemes/` + schemeID);
    }
  };

  _createAskNds = async () => {
    const { graphStore } = this.props;
    // @ts-ignore
    if (typeof window.CefSharp === 'object') {
      // @ts-ignore
      await CefSharp.BindObjectAsync('unifiedClientIntegration');
      // @ts-ignore
      unifiedClientIntegration.executeFunction(
        'Vsa2Domain',
        'VsaAskNds2CreateGraph',
        String(
          '',
        ),
      );
    } else {
      await graphStore.createSchemeAskNds(
        String(
          '',
        ),
      );
    }
  };

  _dimensionsChange = () => {
    const { graphStore } = this.props;
    graphStore?.changeIsCollapsedDimensions(true);
  };

  _potentialDamage = () => {
    const { modalStore } = this.props;

    modalStore?.openModal(MODAL_POTENTIAL_DAMAGE);
  };

  _undoOperation = () => {
    const { graphStore } = this.props;
    graphStore?.undoOperation();
  };

  render() {
    const {
      menuExtended,
      toggleLegendState,
      toggleFiltersState,
      toggleIndicatorsState,
      toggleMenuState,
      toggleTaxpayerSettingsState,
      toggleChainsIsOpen,
    } = this.props.uiStore!;
    const { listIsEmpty, lastGraphsList } = this.props.userGraphsStore!;
    const {
      openAddNewNodeModal,
      openReferenceModal,
      openSaveGraphModal,
      openUserGraphsModal,
      openSearchNodeGraph,
      openPickPeriodModal,
      openTreeReportCallback,
      openPrintSchemeCallback,
    } = this.props.modalStore!;
    const { direction } = this.props.diagramStore!;
    const { appTheme, enableDossierButton, setIsShowBeginningWork } = this.props.commonStore!;
    const {
      periodIsActive,
      AccountabilityFilter,
      CoherenceFilter,
      CooperationFilter,
      CustomLinksFilter,
      GraphChangeFilter,
      TypologyFilter,
      TaxpayersFilter,
    } = this.props.filterStore!;
    const { SpecialOperationFilter, ResultNDFilter, GapsDebtFilter, LinksFilter } =
      this.props.indicatorsStore!;
    const {
      toggleDrawingMode,
      setVerticalDirection,
      setHorizontalDirection,
      toggleVisibleNodesMode,
      freeSearch,
      visibleNodes,
      showTransformMode,
      graphTransformMode,
      showTreeReport,
      currentDrawingMode,
      showFreeSearch,
      showSendChanges,
      showCreateScheme,
      schemeId,
      showCreateKAO,
      showCreateKNP,
      showAddNP,
      showSearchNP,
      showChangeFilters,
      showFilters,
      showFormalReport,
      showListGraphs,
      showCreateGraph,
      listErrors,
      dimensions,
      showHideNP,
      showAllScheme,
      isTestGraph,
      graphOwner,
      scheme,
      setScheme,
      isFromScheme,
      ableEdit,
      isPeriod,
      isCollapsedDimensions,
      listErrorsType,
      isPreviousVersionExists,
      sourceSystem,
      amountOfDamage,
      previouslyFetchedGraphId,
    } = this.props.graphStore!;

    const { getReport } = this.props.nodesReportStore!;

    return (
      <>
        <BurgerMenu
          extended={menuExtended}
          direction={direction}
          isHideNode={visibleNodes}
          periodIsActive={periodIsActive}
          onSearch={this.onSearch}
          onInputDamage={this.onInputDamage}
          userGraphsEmpty={listIsEmpty}
          userGraphsList={lastGraphsList}
          freeSearch={freeSearch}
          onBeginWork={setIsShowBeginningWork}
          onSelectGraph={this.onOpenGraph}
          pickPeriodCallback={openPickPeriodModal}
          onUserGraphsClick={openUserGraphsModal}
          filtersCallback={toggleFiltersState}
          taxpayerSettingsCallback={toggleTaxpayerSettingsState}
          chainsCallback={toggleChainsIsOpen}
          menuCallback={toggleMenuState}
          transferCallback={toggleDrawingMode}
          addNodeCallback={openAddNewNodeModal}
          verticalViewCallback={setVerticalDirection}
          horizontalViewCallback={setHorizontalDirection}
          legendCallback={toggleLegendState}
          integralAssessmentCallback={() => {}}
          hiddenNodesCallback={() => toggleVisibleNodesMode()}
          referenceCallback={openReferenceModal}
          sendChanges={this._sendChanges}
          sendChangesMDAB={this._sendChangesMDAB}
          sendGraph={this._sendGraph}
          onSave={openSaveGraphModal}
          searchNodeGraphCallback={openSearchNodeGraph}
          treeReportCallback={openTreeReportCallback}
          printSchemeCallback={openPrintSchemeCallback}
          theme={appTheme}
          indicatorsCallback={toggleIndicatorsState}
          showTransformMode={showTransformMode}
          graphTransformMode={graphTransformMode}
          showTreeReport={showTreeReport}
          currentDrawingMode={currentDrawingMode}
          showFreeSearch={showFreeSearch}
          showSendChanges={showSendChanges}
          showCreateScheme={showCreateScheme}
          schemeId={schemeId}
          showCreateKAO={showCreateKAO}
          showCreateKNP={showCreateKNP}
          showAddNP={showAddNP}
          showSearchNP={showSearchNP}
          showFilters={showFilters}
          showFormalReport={showFormalReport}
          showListGraphs={showListGraphs}
          showCreateGraph={showCreateGraph}
          showChangeFilters={showChangeFilters}
          listErrors={listErrors}
          listErrorsType={listErrorsType}
          dimensions={dimensions}
          showHideNP={showHideNP}
          showAllScheme={showAllScheme}
          isTestGraph={isTestGraph}
          backToDossier={this._backToDossier}
          backToRAVP={this._backToRAVP}
          graphOwner={graphOwner}
          scheme={scheme}
          setSchemeCallback={setScheme}
          isFromScheme={isFromScheme}
          ableEdit={ableEdit}
          isPeriod={isPeriod}
          SpecialOperationFilter={SpecialOperationFilter}
          ResultNDFilter={ResultNDFilter}
          GapsDebtFilter={GapsDebtFilter}
          LinksFilter={LinksFilter}
          AccountabilityFilter={AccountabilityFilter}
          CoherenceFilter={CoherenceFilter}
          CooperationFilter={CooperationFilter}
          CustomLinksFilter={CustomLinksFilter}
          GraphChangeFilter={GraphChangeFilter}
          TaxpayersFilter={TaxpayersFilter}
          TypologyFilter={TypologyFilter}
          isCollapsedDimensions={isCollapsedDimensions}
          dimensionsChange={this._dimensionsChange}
          potentialDamageCallback={this._potentialDamage}
          undoCallback={this._undoOperation}
          isPreviousVersion={isPreviousVersionExists}
          sourceSystem={sourceSystem}
          backToITAIS={this._backToITAIS}
          enableDossierButton={enableDossierButton}
          amountOfDamage={amountOfDamage}
          nodesReportCallback={getReport}
          showNodesReport={previouslyFetchedGraphId && previouslyFetchedGraphId !== '0'}
        />
      </>
    );
  }
}
