import React from 'react';

/**
 * Компонент высшего порядка, добавляющий раскрытие и скрытие тела компонента
 */
const withDisclosure = <P extends object>(Component: React.ComponentType<P>) =>
  class WithDescription extends React.Component<P> {
    state = {
      isOpen: false,
    };

    /**
     * Метод переключает состояние тела компонента
     */
    _toggleOpenState = () => {
      this.setState({
        isOpen: !this.state.isOpen,
      });
    };

    render() {
      const { ...props } = this.props;
      const { isOpen } = this.state;

      return (
        <Component isOpen={isOpen} toggleOpenState={this._toggleOpenState} {...(props as P)} />
      );
    }
  };

export default withDisclosure;
