import { action, computed, observable } from 'mobx';
import Filter from '../utils/Filter';

import { IndicatorsStoreType } from './indicatorsStore';

/**
 * Хранилище фильтра Операции особого контроля
 */
class SpecialOperationFilter extends Filter {
  IndicatorsStore: IndicatorsStoreType;

  constructor(IndicatorsStore: IndicatorsStoreType) {
    super();
    this.IndicatorsStore = IndicatorsStore;

    this.checkAllTrue();
  }

  @observable keep = true;

  @observable isAllTrue = false;

  @observable changeStatic: boolean = false;

  // опции фильтра
  initialOptions = {
    // Расхождения по разделу 11
    Ook_Mis11Amount: true,
    // Контрольные соотношения
    Ook_KsVatAmount: true,
    // Сомнительные начисления
    Ook_DoubtfulAmount: true,
    // СФ до 2015г.
    Ook_Inv2015Ch8Amount: true,
    // КВО 17
    Ook_Oc17Amount: true,
    // КВО 23
    Ook_Oc23Amount: true,
    // Расхождения после завершения КНП
    Ook_MisAfterKnpAmount: true,
    // Расхождения после завершения КНП
    Ook_Inv2015Ch11Amount: true,
    // КВО 45
    Ook_Oc45Amount: true,
    // Дробление СФ
    Ook_SplittingAmount: true,
    // Журналы, в том числе не в составе НД
    Ook_JournalsNotInDeclAmount: true,
    // Журнал выставленных СФ без полученных СФ
    Ook_JournalExposedWithoutReceivedInvoices: true,
    // Несопоставленные суммы в части 2 Журнала
    Ook_UnmatchedAmountsInSecondPart: true,
    // Отрицательные операции в разделе 9 НД по НДС
    Ook_NegativeOperationsInChapter9: true,
    // КВО 33
    Ook_OperationCode33Amount: true,
    // КВО 34
    Ook_OperationCode34Amount: true,
    // КВО 36
    Ook_OperationCode36Amount: true,
  };

  @observable isAllFalse: boolean = false;

  /** Метод переключает состояние фильтра */
  @action toggleFilter = () => {
    this.keep = !this.keep;

    if (this.keep === false) {
      this.setOptionsToFalse();
      this.IndicatorsStore.showAll = false;
    } else {
      this.setOptionsToTrue();
      this.IndicatorsStore.checkAllTrue();
    }

    this.isChange();
    this.checkAllTrue();
    this.IndicatorsStore.checkChanges();
    this.IndicatorsStore.filterGraph();
  };

  // Происходили ли изменения среди  фильтров
  @action isChange = () => {
    this.changeStatic = false;
    Object.keys(this.options).forEach((item: any) => {
      if (this.options[item] !== this.IndicatorsStore.options[item]) {
        this.changeStatic = true;
      }
    });
    this.IndicatorsStore.isSpecialOperationFilter = this.changeStatic;
    this.checkAllTrue();
  };

  /**
   * Метод переключает опцию
   */
  @action toggleIndicator = (option: string) => {
    this.changeOption(option, !this.options[option]);
    this.checkAllFalse();
    if (!this.options[option]) {
      this.IndicatorsStore.showAll = false;
    } else {
      this.IndicatorsStore.checkAllTrue();
    }
    this.keepFilter(this.isAllFalse);
    this.isChange();
    this.checkAllTrue();
    this.IndicatorsStore.checkChanges();
    this.IndicatorsStore.filterGraph();
  };

  @action checkAllFalse = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_special_operation') {
        othersOptions[item] = this.options[item];
      } else {
        othersOptions[item] = false;
      }
    });
    this.isAllFalse = Object.keys(othersOptions).every(
      (item: any) => othersOptions[item] === false,
    );
  };

  @action checkAllTrue = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_special_operation') {
        othersOptions[item] = this.options[item];
      } else {
        othersOptions[item] = false;
      }
    });
    this.isAllTrue = Object.keys(othersOptions).every((item: any) => othersOptions[item] === true);
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
      is_special_operation: this.keep,
    };
  }
}

export default SpecialOperationFilter;
