import { getDetailedNodeData } from './graph';
import { DetailedNodeResponse } from 'ApiModel';

export default abstract class CacheProxy {
  private static viewedNodes = new Map<string, DetailedNodeResponse>();

  static getDetailedNodeData = async (
    graphId: string,
    nodeId: string,
  ): Promise<DetailedNodeResponse> => {
    if (CacheProxy.viewedNodes.has(graphId + nodeId)) {
      return CacheProxy.viewedNodes.get(graphId + nodeId);
    }

    const data = await getDetailedNodeData(graphId, nodeId);

    CacheProxy.viewedNodes.set(graphId + nodeId, data);

    return data;
  };
}
