import { DIRECTION_VERTICAL, DIRECTION_HORIZONTAL } from '@fdc/vsa-gojs-lib';
import * as go from 'gojs';

import { BusinessRoleIcons } from '@fdc/vsa-gojs-lib';
import { commodityFlow } from '../../../utils/commodityFlow';

import create, {
  VSAGroupNode,
  VSAClusterNode,
  VSAVirtualGroupNode,
  VSANodeRound,
} from '@fdc/vsa-gojs-lib';

import ContextMenu, {
  Node,
  Group,
  Cluster,
  VirtualGroup,
} from '../../../utils/diagram/ContextMenu';

import {
  DETAILED_CLUSTER_MODAL,
  DETAILED_NODE_MODAL,
} from '../../../components/modals/constants/modalNames';

import { GraphStoreType } from '../graphStore';
import { DiagramStoreType } from '../diagramStore';
import { ModalStoreType } from '../../modalStore';
import { FilterStoreType } from '../../filters/filterStore';
import { CommonStoreType } from '../../common/commonStore';
import { IndicatorsStoreType } from '../../indicators/indicatorsStore';
import CacheProxy from '../../../utils/services/api/cacheProxy';

import { SPECIAL_INN } from '../../../components/constants';

/**
 * Класс шаблонов для узлов
 *
 * go.Shape - описывает фигуру узла
 *
 * go.Panel - описывает позицию и размеры узла
 *
 * go.TextBlock - описывает текст внутри узла
 */
class NodeTemplatesStore {
  GraphStore: GraphStoreType;
  DiagramStore: DiagramStoreType;
  ModalStore: ModalStoreType;
  FilterStore: FilterStoreType;
  CommonStore: CommonStoreType;
  IndicatorsStore: IndicatorsStoreType;

  private graphDirection: typeof DIRECTION_HORIZONTAL | typeof DIRECTION_VERTICAL = 0;

  vsaNodeContextMenuId: string;
  vsaGroupContextMenuId: string;
  vsaClusterContextMenuId: string;
  vsaVirtualGroupContextMenuId: string;

  private commonStoreInstanceNode: ContextMenu;
  private commonStoreInstanceCluster: ContextMenu;
  private commonStoreInstanceGroup: ContextMenu;
  private commonStoreInstanceVirtualGroup: ContextMenu;

  constructor(
    DiagramStore: DiagramStoreType,
    getDirection: 0 | 90,
    FilterStore: FilterStoreType,
    GraphStore: GraphStoreType,
    CommonStore: CommonStoreType,
    IndicatorsStore: IndicatorsStoreType,
  ) {
    this.graphDirection = getDirection;

    this.GraphStore = GraphStore;
    this.DiagramStore = DiagramStore;
    this.ModalStore = DiagramStore.GraphStore.RootStore.ModalStore;
    this.FilterStore = FilterStore;
    this.CommonStore = CommonStore;
    this.IndicatorsStore = IndicatorsStore;

    this.vsaNodeContextMenuId = this.DiagramStore.vsaNodeContextMenuId;
    this.vsaGroupContextMenuId = this.DiagramStore.vsaGroupContextMenuId;
    this.vsaClusterContextMenuId = this.DiagramStore.vsaClusterContextMenuId;
    this.vsaVirtualGroupContextMenuId = this.DiagramStore.vsaVirtualGroupContextMenuId;

    this.commonStoreInstanceNode = new ContextMenu(
      new Node(DiagramStore, this.vsaNodeContextMenuId, FilterStore, GraphStore),
    );
    this.commonStoreInstanceCluster = new ContextMenu(
      new Cluster(DiagramStore, this.vsaClusterContextMenuId, GraphStore),
    );
    this.commonStoreInstanceGroup = new ContextMenu(
      new Group(DiagramStore, this.vsaGroupContextMenuId, GraphStore),
    );
    this.commonStoreInstanceVirtualGroup = new ContextMenu(
      new VirtualGroup(DiagramStore, this.vsaVirtualGroupContextMenuId, FilterStore, GraphStore),
    );
  }

  isRiskClick = false;

  /**
   * Метод создания группы. Отрабатывает при создании группы из контекст меню
   *
   * @param {any} diagramEvent - событие диаграммы
   */
  _createGroupFromNode = async (): Promise<void> => {
    const selected = this.DiagramStore.selectedNodes;

    if (selected) {
      await this.GraphStore.createGroupFromNode(selected);
      this.GraphStore.fetchGraph();
    }
  };

  /**
   * Метод создания группы. Отрабатывает при создании группы при перетаскивании одного узла на
   * другой.
   *
   * @param { string[] } nodesIds - идентификаторы узлов
   */
  _createGroupFromNodes = async (nodesIds: Array<string>): Promise<void> => {
    const groupOrIds = [...this.DiagramStore.selectedNodes, ...nodesIds];

    await this.GraphStore.createGroupFromNode(groupOrIds);
  };

  /**
   * Метод выхода узла из группы.
   *
   * @param {any} diagramEvent - событие диаграммы
   */
  _nodeOutGroup = async (diagramEvent: any): Promise<void> => {
    const selectedNode = diagramEvent.diagram.selection.first();
    const orId = selectedNode.data.orig_id;

    if (orId) {
      await this.GraphStore.nodeOutOfGroup(orId);
      this.GraphStore.fetchGraph();
    }
  };

  /**
   * Метод выхода узла из виртуальной группы.
   *
   * @param {any} diagramEvent - событие диаграммы
   */
  _nodeOutVirtualGroup = async (diagramEvent: any): Promise<void> => {
    const selectedNode = diagramEvent.diagram.selection.first();
    const orId = selectedNode.data.orig_id;

    if (orId) {
      await this.GraphStore.removeFromVirtualGroup(orId);
      this.GraphStore.fetchGraph();
    }
  };

  /**
   * Метод добавляет узел к группе после перетаскивания
   *
   * @param {any} diagramEvent - событие диаграммы
   * @param {any} group - группа
   */
  _finishDrop = async (
    diagramEvent: any,
    group: any,
    diagram?: any,
    dataID?: any,
  ): Promise<void> => {
    let selectedNode = null;
    let groupId = null;

    if (diagramEvent) {
      selectedNode = diagramEvent.diagram.selection.first();
    } else {
      selectedNode = diagram.selection.first();
    }

    const orId = selectedNode.data.orig_id;
    if (group) {
      groupId = group.data.orig_id;
    } else {
      groupId = dataID;
    }

    if (orId) {
      this._createGroupFromNodes([groupId]);
    }
  };

  /**
   * Метод добавляет связь из узла в узел
   *
   * @param {any} diagramEvent - событие диаграммы
   * @param {any} group - группа
   */
  _addLink = async (diagramEvent: any, group: any): Promise<void> => {};

  /**
   * Метод создания группы. Отрабатывает при создании группы из контекст меню
   *
   * @param {any} diagramEvent - событие диаграммы
   */
  _addNodeToGroup = async (orId: string, groupId: string): Promise<void> => {
    if (orId.length) {
      await this.GraphStore.addNodeToGroup([orId], groupId);
      this.GraphStore.fetchGraph();
    }
  };

  /**
   * Метод открытия узла для детализации
   */
  _onNodeDetails = (diagramEvent: any) => {
    const nodeData = diagramEvent.diagram.selection.first().data;
    this.ModalStore.openModal(DETAILED_NODE_MODAL, nodeData);
  };

  /**
   * Метод открытия карточки кластера для детализации
   */
  _onClusterDetails = (diagramEvent: any) => {
    this.ModalStore.openModal(DETAILED_CLUSTER_MODAL, diagramEvent);
  };

  /**
   * Метод по открытию всплывающей подсказки (индикаторы для узла)
   */
  _showToolTipIndicators = async (diagramEvent: any, obj: any) => {
    const indicatorsTop = obj.part.data.indicators_top;
    const isTraceRisk = obj.part.data.node.has_trace_risks;

    const krsbFilter = indicatorsTop.filter((item: any) => item.name === 'Krsb_Indicator');
    const payMonth = krsbFilter[0]?.pay_month;

    this.GraphStore.setIndicatorsInfo(indicatorsTop);
    this.GraphStore.setPayMonth(payMonth);
    this.GraphStore.setIsTraceRisk(isTraceRisk);

    const detailedNode = await CacheProxy.getDetailedNodeData(
      this.GraphStore.previouslyFetchedGraphId,
      obj.part.data.orig_id,
    );

    this.GraphStore.setDeclarationState(detailedNode.declaration_state);

    let toolTipDiv = document.getElementById('toolTipIndicators');
    let toolTipBackGround = document.getElementById('toolTipBackGround');

    let pt = this.DiagramStore.diagram?.lastInput.viewPoint;

    let widthToolTip = window.innerWidth > 1745 ? 335 : 305;

    if (toolTipDiv && pt && toolTipBackGround) {
      if (window.innerWidth < pt.x + widthToolTip) {
        toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      } else {
        toolTipDiv.style.left = pt.x + 15 + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      }
      toolTipDiv.style.display = 'block';
      toolTipBackGround.style.display = 'block';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (декларация для узла)
   */
  _showToolTipDeclaration = async (diagramEvent: any, obj: any) => {
    const indicatorsBottom = obj.part.data.indicators_bottom;

    this.GraphStore.setDeclarationInfo(indicatorsBottom);

    let toolTipDiv = document.getElementById('toolTipDeclaration');
    let toolTipBackGround = document.getElementById('toolTipBackGroundDeclaration');

    let pt = this.DiagramStore.diagram?.lastInput.viewPoint;

    let widthToolTip = window.innerWidth > 1745 ? 285 : 275;

    if (toolTipDiv && pt && toolTipBackGround) {
      if (window.innerWidth < pt.x + widthToolTip) {
        toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      } else {
        toolTipDiv.style.left = pt.x + 15 + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      }
      toolTipDiv.style.display = 'block';
      toolTipBackGround.style.display = 'block';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (данные из журналов)
   */
  _showToolTipJournal = async (diagramEvent: any, obj: any) => {
    const indicatorsBottom = obj.part.data.indicators_bottom;

    this.GraphStore.setDeclarationInfo(indicatorsBottom);

    let toolTipDiv = document.getElementById('toolTipJournal');
    let toolTipBackGround = document.getElementById('toolTipBackGroundJournal');

    let pt = this.DiagramStore.diagram?.lastInput.viewPoint;

    let widthToolTip = window.innerWidth > 1745 ? 285 : 275;

    if (toolTipDiv && pt && toolTipBackGround) {
      if (window.innerWidth < pt.x + widthToolTip) {
        toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      } else {
        toolTipDiv.style.left = pt.x + 15 + 'px';
        toolTipDiv.style.top = pt.y + 20 + 'px';
      }
      toolTipDiv.style.display = 'block';
      toolTipBackGround.style.display = 'block';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (наименование аналитической роли)
   */
  _showToolTipAnalytic = async (diagramEvent: any, obj: any) => {
    const selectedNode = obj.part.data.node.analytic_role;
    const specialINN = SPECIAL_INN;
    const inn = obj.part.data.node.inn;
    const name = obj.part.data.node.short_name;
    if (selectedNode) {
      const roleInfo = this.CommonStore?.analyticalRolesList.find(
        (role) => role.brief === selectedNode,
      );
      this.GraphStore.setAnalyticName(roleInfo?.value);
      let toolTipDiv = document.getElementById('toolTipAnalytic');
      let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
      let widthToolTip = window.innerWidth > 1745 ? 285 : 275;

      if (toolTipDiv && pt) {
        if (window.innerWidth < pt.x + widthToolTip) {
          toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        } else {
          toolTipDiv.style.left = pt.x + 15 + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        }
        toolTipDiv.style.display = 'block';
      }
    }
    if (specialINN.some((innSpecial: any) => innSpecial === inn)) {
      this.GraphStore.setAnalyticName(name);
      let toolTipDiv = document.getElementById('toolTipAnalytic');
      let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
      let widthToolTip = window.innerWidth > 1745 ? 285 : 275;

      if (toolTipDiv && pt) {
        if (window.innerWidth < pt.x + widthToolTip) {
          toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        } else {
          toolTipDiv.style.left = pt.x + 15 + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        }
        toolTipDiv.style.display = 'block';
      }
    }
  };

  /**
   * Метод по закрытию всплывающей подсказки (наименование аналитической роли)
   */
  _closeToolTipAnalytic = async (diagramEvent: any, obj: any) => {
    let toolTipDiv = document.getElementById('toolTipAnalytic');

    if (toolTipDiv) {
      toolTipDiv.style.display = 'none';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (наименование бизнес-роли)
   */
  _showToolTipBusiness = async (diagramEvent: any, obj: any) => {
    const selectedNode = obj.part.data.node.business_role;

    if (selectedNode && selectedNode in BusinessRoleIcons) {
      const roleInfo = this.CommonStore?.businessRolesList.find(
        (role) => role.brief === selectedNode,
      );
      this.GraphStore.setBusinessName(roleInfo?.value);

      let toolTipDiv = document.getElementById('toolTipBusiness');

      let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
      let widthToolTip = window.innerWidth > 1745 ? 285 : 275;

      if (toolTipDiv && pt) {
        if (window.innerWidth < pt.x + widthToolTip) {
          toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        } else {
          toolTipDiv.style.left = pt.x + 15 + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        }
        toolTipDiv.style.display = 'block';
      }
    }
  };

  /**
   * Метод по закрытию всплывающей подсказки (наименование бизнес-роли)
   */
  _closeToolTipBusiness = async (diagramEvent: any, obj: any) => {
    let toolTipDiv = document.getElementById('toolTipBusiness');

    if (toolTipDiv) {
      toolTipDiv.style.display = 'none';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (наименование НП)
   */
  _showToolTipName = async (diagramEvent: any, obj: any) => {
    const selectedNode = obj.part.data.node.values[0];
    if (
      obj.metrics.arrText.length === (this.graphDirection === 0 ? 4 : 2) &&
      obj.metrics.arrText[this.graphDirection === 0 ? 3 : 1].includes('...')
    ) {
      //@ts-ignore
      this.GraphStore.setNameNP(selectedNode);

      let toolTipDiv = document.getElementById('toolTipName');

      let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
      let widthToolTip = window.innerWidth > 1745 ? 285 : 275;

      if (toolTipDiv && pt) {
        if (window.innerWidth < pt.x + widthToolTip) {
          toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        } else {
          toolTipDiv.style.left = pt.x + 15 + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        }
        toolTipDiv.style.display = 'block';
      }
    }
  };

  /**
   * Метод по закрытию всплывающей подсказки (наименование бизнес-роли)
   */
  _closeToolTipName = async (diagramEvent: any, obj: any) => {
    let toolTipDiv = document.getElementById('toolTipName');

    if (toolTipDiv) {
      toolTipDiv.style.display = 'none';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (количество узлов в виртуальной группе)
   */
  _showToolTipVirtualGroup = async (diagramEvent: any, obj: any) => {
    const selectedNode = obj.part.data;
    if (selectedNode.virtual_group_count > selectedNode.virtual_group_real_count) {
      this.GraphStore.setCountVirtualGroup(selectedNode.virtual_group_count);
      this.GraphStore.setRealCountVirtualGroup(selectedNode.virtual_group_real_count);

      let toolTipDiv = document.getElementById('toolTipVirtualGroup');

      let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
      let widthToolTip = window.innerWidth > 1745 ? 285 : 275;

      if (toolTipDiv && pt) {
        if (window.innerWidth < pt.x + widthToolTip) {
          toolTipDiv.style.left = pt.x - (widthToolTip + 15) + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        } else {
          toolTipDiv.style.left = pt.x + 15 + 'px';
          toolTipDiv.style.top = pt.y + 20 + 'px';
        }
        toolTipDiv.style.display = 'block';
      }
    }
  };

  /**
   * Метод по закрытию всплывающей подсказки (наименование бизнес-роли)
   */
  _closeToolTipVirtualGroup = async (diagramEvent: any, obj: any) => {
    let toolTipDiv = document.getElementById('toolTipVirtualGroup');

    if (toolTipDiv) {
      toolTipDiv.style.display = 'none';
    }
  };

  /**
   * Метод по открытию всплывающей подсказки (информация НП)
   */
  _showToolTipTaxpayer = (obj: any) => {
    const node = obj.part as go.Node;

    if (node) {
      let pt = this.DiagramStore.diagram?.lastInput.viewPoint;
      let widthToolTip = 500;
      let left,
        top = 0;

      if (pt) {
        if (window.innerWidth < pt.x + widthToolTip) {
          left = pt.x - (widthToolTip + 5);
          top = pt.y - 70;
        } else {
          left = pt.x + 5;
          top = pt.y - 70;
        }
      }

      this.GraphStore.changeTaxpayerTooltipIsVisible(true, node, left, top);
    }
  };

  _closeToolTipTaxpayer = () => {
    this.GraphStore.changeTaxpayerTooltipIsVisible(false);
  };

  _toolTip = go.GraphObject.make(go.HTMLInfo, {
    show: this._showToolTipTaxpayer,
    hide: this._closeToolTipTaxpayer,
  });

  /**
   * Метод раскрытия контрагентов
   */
  _nodeOpenNP = async () => {
    const nodeData = this.DiagramStore.diagram?.selection.first()!.data;

    const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    if (!zombie && this.GraphStore.ableEdit && nodeData.node.is_visible) {
      const orig_id = nodeData.orig_id;

      if (
        this.GraphStore.graphTransformMode === 'ModeV3' &&
        orig_id !== this.GraphStore.mainVertexId
      ) {
        const response = this.GraphStore.filtersGraph;

        //@ts-ignore
        if (
          (this.GraphStore.graphExpandType === 'Nds' ||
            this.GraphStore.graphExpandType === 'Contract' ||
            commodityFlow.some((item) => this.GraphStore.graphExpandType.includes(item))) &&
          this.GraphStore.showOpenLinks
        ) {
          this.GraphStore.schemeForward([orig_id]);
        }
        //@ts-ignore
        else if (this.GraphStore.graphExpandType === 'Abd' && this.GraphStore.showOpenLinks) {
          this.GraphStore.payersRecipients([orig_id], 'recipients');
        }
      }
    }
  };

  /**
   * Выделение вершины на графе
   */
  _selectVertex = (e: any, node: any) => {
    const zombie = node?.data.indicators_bottom?.some((item: any) => item.name === 'Zombie');
    if (!zombie) {
      const diagram = node.diagram;
      diagram?.startTransaction('highlight');
      diagram?.clearHighlighteds();
      this._selectNodesOut(node);
      this._selectLinksOut(node);
      diagram?.commitTransaction('highlight');
    }
  };

  _selectNodesOut = (x: any) => {
    x.findNodesOutOf().each((node: any) => {
      node.isHighighted = true;
      if (node.data.orig_id !== this.GraphStore.mainVertexId) {
        this._selectNodesOut(node);
      }
    });
  };

  _selectLinksOut = (x: any) => {
    x.findLinksOutOf().each((link: any) => {
      const zombie = link.toNode.data.indicators_bottom?.some(
        (item: any) => item.name === 'Zombie',
      );
      if (!zombie) link.isHighlighted = true;
      if (link.toNode.data.orig_id !== this.GraphStore.mainVertexId) {
        this._selectLinksOut(link.toNode);
      }
    });
  };

  /**
   * Кластер диаграммы
   */
  get Cluster() {
    return create(VSAClusterNode, {
      direction: this.graphDirection,
      contextMenu: this.commonStoreInstanceCluster.getContextMenu,
      currentDrawingMode: this.GraphStore.currentDrawingMode,
    });
  }

  /**
   * Узел диаграммы
   */
  get Node() {
    return create(VSANodeRound, {
      direction: this.graphDirection,
      contextMenu: this.commonStoreInstanceNode.getContextMenu,
      onFinishDrop:
        this.GraphStore.showGroupNP &&
        !this.GraphStore.visibleNodes &&
        this.GraphStore.sourceSystem !== 'ITAIS'
          ? this._finishDrop
          : null,
      toolTip: this._toolTip,

      showToolTipIndicators: this._showToolTipIndicators,
      showToolTipDeclaration: this._showToolTipDeclaration,

      showToolTipJournal: this._showToolTipJournal,

      showToolTipAnalytic: this._showToolTipAnalytic,
      closeToolTipAnalytic: this._closeToolTipAnalytic,

      showToolTipBusiness: this._showToolTipBusiness,
      closeToolTipBusiness: this._closeToolTipBusiness,

      showToolTipName: this._showToolTipName,
      closeToolTipName: this._closeToolTipName,

      theme: this.CommonStore.appTheme,
      graphTransformMode: this.GraphStore.graphTransformMode,
      showGroupNP: this.GraphStore.showGroupNP,

      onDoubleClick:
        !this.GraphStore.visibleNodes && this.GraphStore.sourceSystem !== 'ITAIS'
          ? this._nodeOpenNP
          : null,
      click: this._selectVertex,

      indicators: this.GraphStore.options,

      isScheme: this.GraphStore.scheme,
      ableEdit: this.GraphStore.ableEdit,
      visibleNodes: this.GraphStore.visibleNodes,

      analyticRolesList: this.CommonStore.analyticalRolesIcons,
      businessRolesList: this.CommonStore.businessRolesIcons,

      sourceSystem: this.GraphStore.sourceSystem,
      graphFilters: this.GraphStore.filtersGraph,
    });
  }

  /**
   * Группа узлов
   */
  get Group() {
    return create(VSAGroupNode, {
      direction: this.graphDirection,
      contextMenu: this.commonStoreInstanceGroup.getContextMenu,
      onFinishDrop:
        this.GraphStore.showGroupNP && !this.GraphStore.visibleNodes ? this._finishDrop : null,
      onDoubleClick: !this.GraphStore.visibleNodes ? this._nodeOutGroup : null,
      theme: this.CommonStore.appTheme,
      graphTransformMode: this.GraphStore.graphTransformMode,
      showGroupNP: this.GraphStore.showGroupNP,

      showToolTipAnalytic: this._showToolTipAnalytic,
      closeToolTipAnalytic: this._closeToolTipAnalytic,

      isScheme: this.GraphStore.scheme,
      ableEdit: this.GraphStore.ableEdit,
    });
  }

  /**
   * Виртуальная группа
   */
  get VirtualGroup() {
    return create(VSAVirtualGroupNode, {
      direction: this.graphDirection,
      contextMenu: this.commonStoreInstanceVirtualGroup.getContextMenu,
      onDoubleClick: !this.GraphStore.visibleNodes ? this._nodeOutVirtualGroup : null,
      theme: this.CommonStore.appTheme,
      indicators: this.GraphStore.options,

      showToolTipVirtualGroup: this._showToolTipVirtualGroup,
      closeToolTipVirtualGroup: this._closeToolTipVirtualGroup,
    });
  }
}

export type NodeTemplatesStoreType = NodeTemplatesStore;
export default NodeTemplatesStore;
