import React from 'react';
import styles from './Indicators.module.scss';

import GapsDebt from './GapsDebt';
import SpecialOperation from './SpecialOperation';
import ResultND from './ResultND';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'stores/common/commonStore';
import { GraphStoreType } from '../../stores/graph/graphStore';
import { IndicatorsStoreType } from '../../stores/indicators/indicatorsStore';
import Search from '../../components/Search';
import { ReactComponent as CheckboxAll } from '../../components/Dropdown/assets/checkboxAll.svg';
import { ReactComponent as CheckboxNone } from '../../components/Dropdown/assets/checkboxNone.svg';
import Badge from 'components/Badge';

interface IndicatorsProps {
  uiStore?: any;
  indicatorsStore?: IndicatorsStoreType;
  gapsDebtFilter?: any;
  specialOperationFilter?: any;
  resultNDFilter?: any;
  linksFilter?: any;
  periodDataFilter?: any;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

interface IndicatorsState {
  searchValue: any;
  showAll: boolean;
}

/**
 * Компонент фильтра по Числовым значениям индикаторов
 */
@inject(
  'uiStore',
  'indicatorsStore',
  'gapsDebtFilter',
  'specialOperationFilter',
  'resultNDFilter',
  'linksFilter',
  'periodDataFilter',
  'commonStore',
  'graphStore',
)
@observer
class Indicators extends React.PureComponent<IndicatorsProps, IndicatorsState> {
  state = {
    searchValue: '',
    showAll: true,
  };

  /**
   * Метод закрывающий фильтр
   */
  _toggleFilters = (): void => {
    const { uiStore } = this.props;
    uiStore?.toggleIndicatorsState();
  };

  /**
   * Метод фильтрации
   */
  _onFilter = async () => {
    const { indicatorsStore, graphStore } = this.props;

    indicatorsStore?.filter();

    await graphStore?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, true);
  };

  /**
   * Метод сбрасывает настройки фильтров
   */
  _onReset = async () => {
    const { indicatorsStore, graphStore } = this.props;

    indicatorsStore?.resetFilters();
    indicatorsStore?.filter();
    await graphStore?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, true);
  };

  /**
   * Метод показывает все настройки фильтров
   */
  _onShowAll = async () => {
    const { indicatorsStore, graphStore } = this.props;
    indicatorsStore?.setShowAll();
    if (!indicatorsStore?.showAll) {
      this._onReset();
    } else {
      indicatorsStore?.showAllFilters();
      indicatorsStore?.filter();
      await graphStore?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, true);
    }
  };

  /**
   * Метод изменяет значение поля ввода
   */
  _onChange = (value: string): void => {
    this.setState({ searchValue: value.toLowerCase() });
  };

  /**
   * Метод отправляет запрос на поиск
   */
  _onSearch = (): void => {};

  /**
   * Метод очищает поле ввода
   */
  _onResetSearch = (): void => {
    this.setState({ searchValue: '' });
  };

  render() {
    const { gapsDebtFilter, specialOperationFilter, resultNDFilter, commonStore, indicatorsStore } =
      this.props;
    const { searchValue } = this.state;
    const { options: optionsGapsDebt } = gapsDebtFilter;
    const { options: optionsSpecialOperation } = specialOperationFilter;
    const { options: optionsResultNDF } = resultNDFilter;

    const optionsLength =
      Object.values(optionsGapsDebt).filter((item) => item).length +
      Object.values(optionsSpecialOperation).filter((item) => item).length +
      Object.values(optionsResultNDF).filter((item) => item).length;

    const GapsDebtArray = [
      '1/3 от суммы задолженности',
      '2/3 от суммы задолженности',
      '100% задолженности',
      'Разрыв по НДС',
      'Доля разрыва',
    ];
    const SpecialOperationArray = [
      'Расхождения по разделу 11',
      'Контрольные соотношения',
      'Сомнительные начисления',
      'СФ до 2015г. в разделе 8',
      'СФ до 2015г. в разделе 11',
      'Журнал выставленных СФ без полученных СФ',
      'КВО 17',
      'КВО 23',
      'КВО 45',
      'Дробление СФ',
      'Расхождения после завершения КНП',
    ];
    const ResultNDArray = [
      'Декларация к уплате',
      'Декларация к возмещению',
      'Нулевая декларация',
      'Декларация не представлена',
    ];

    return (
      <>
        <Badge count={optionsLength} styleWrapper={styles.badge} onReset={this._onReset} />
        <div className={styles.wrapper} id="Indicators">
          <Search
            wrapperStyle={styles.search}
            value={searchValue}
            onChange={this._onChange}
            onSearch={this._onSearch}
            autoSearchTimeout={700}
            placeholder={'Найти индикаторы'}
            autoFocus
            onReset={this._onResetSearch}
            withSearchIcon
            theme={commonStore?.appTheme}
          />
          <div className={styles.buttons}>
            <span className={styles.itemAllLabel} onClick={this._onShowAll}>
              {indicatorsStore?.showAll ? (
                <CheckboxAll className={styles.checkboxIcon} />
              ) : (
                <CheckboxNone className={styles.checkboxIcon} />
              )}
              <span>Выбрать все</span>
            </span>
            <span className={styles.clearLabel} onClick={this._onReset}>
              Очистить
            </span>
          </div>
          <div className={styles.container}>
            <ul>
              {GapsDebtArray.some((elem) => elem.toLowerCase().includes(searchValue)) && (
                <GapsDebt
                  onToggleCheck={gapsDebtFilter.toggleFilter}
                  isActive={gapsDebtFilter.keep}
                  search={searchValue}
                  isOpenSearch={
                    searchValue.length > 0
                      ? GapsDebtArray.some((elem) => elem.toLowerCase().includes(searchValue))
                      : false
                  }
                  isAllCheck={gapsDebtFilter.isAllTrue}
                />
              )}
              {SpecialOperationArray.some((elem) => elem.toLowerCase().includes(searchValue)) && (
                <SpecialOperation
                  onToggleCheck={specialOperationFilter.toggleFilter}
                  isActive={specialOperationFilter.keep}
                  search={searchValue}
                  isOpenSearch={
                    searchValue.length > 0
                      ? SpecialOperationArray.some((elem) =>
                          elem.toLowerCase().includes(searchValue),
                        )
                      : false
                  }
                  isAllCheck={specialOperationFilter.isAllTrue}
                />
              )}
              {ResultNDArray.some((elem) => elem.toLowerCase().includes(searchValue)) && (
                <ResultND
                  onToggleCheck={resultNDFilter.toggleFilter}
                  isActive={resultNDFilter.keep}
                  search={searchValue}
                  isOpenSearch={
                    searchValue.length > 0
                      ? ResultNDArray.some((elem) => elem.toLowerCase().includes(searchValue))
                      : false
                  }
                  isAllCheck={resultNDFilter.isAllTrue}
                />
              )}
            </ul>
          </div>
        </div>
      </>
    );
  }
}

export default Indicators;
