import React from 'react';
import { IModalTopBarProps } from './ModalTopBar.props';
import styles from './ModalTopBar.module.scss';

const ModalTopBar = ({ children, className, ...props }: IModalTopBarProps) => {
  return (
    <div className={`${styles.topBar} ${className}`} {...props}>
      {children}
    </div>
  );
};

export { ModalTopBar };
