import { action, computed, observable, autorun } from 'mobx';
import Filter from '../utils/Filter';

import { FilterStoreType } from './filterStore';
import { GraphStoreType } from '../graph/graphStore';

import { commodityFlow } from '../../utils/commodityFlow';

/**
 * Хранилище фильтра кооперации
 */
class CooperationFilter extends Filter {
  FilterStore: FilterStoreType;
  GraphStore: GraphStoreType;

  constructor(FilterStore: FilterStoreType, GraphStore: GraphStoreType) {
    super();
    this.FilterStore = FilterStore;
    this.GraphStore = GraphStore;

    autorun(() => {
      const {
        is_ask_nds2,
        purchases_from,
        purchases_to,
        sales_from,
        sales_to,
        credit_from,
        credit_to,
        debet_from,
        debet_to,
        is_cooperation,
        is_abd,
        shareVatDeduct_from,
        shareVatDeduct_to,
        shareVatCalc_from,
        shareVatCalc_to,
        is_trace,
        is_property,
        is_product_flow,
        is_mark,
        clear_deduction_ratio_from,
        clear_deduction_ratio_to,
        schema_deduction_ratio_from,
        schema_deduction_ratio_to,
        vat_gap_from,
        vat_gap_to,
        is_contract,
      } = this.FilterStore.options;
      this.updateOptions(
        {
          is_ask_nds2,
          purchases_from,
          purchases_to,
          sales_from,
          sales_to,
          credit_from,
          credit_to,
          debet_from,
          debet_to,
          is_cooperation,
          is_abd,
          shareVatDeduct_from,
          shareVatDeduct_to,
          shareVatCalc_from,
          shareVatCalc_to,
          is_trace,
          is_property,
          is_product_flow,
          clear_deduction_ratio_from,
          clear_deduction_ratio_to,
          schema_deduction_ratio_from,
          schema_deduction_ratio_to,
          is_mark,
          vat_gap_from,
          vat_gap_to,
          is_contract,
        },
        'is_cooperation',
      );
      this.checkAllTrue();
      this.checkAllTrueProductFlow();
    });
  }

  @observable keep = false;

  @observable isAllFalse = false;
  @observable isAllTrue = false;
  @observable isAllTrueProductFlow = false;

  @observable optionsProductFlow = ['is_trace', 'is_property', 'is_mark'];

  initialOptionsNDS = {
    // Сумма покупок от
    purchases_from: 0,
    // Сумма покупок до
    purchases_to: '',
    // Сумма продаж от
    sales_from: 0,
    // Сумма продаж до
    sales_to: '',
    // Доля НДС к вычету от
    shareVatDeduct_from: 0,
    // Доля НДС к вычету до
    shareVatDeduct_to: '',
    // Доля НДС исчисленный от
    shareVatCalc_from: 0,
    // Доля НДС исчисленный до
    shareVatCalc_to: '',
    // Доля чистых вычетов по НДС от
    clear_deduction_ratio_from: 0,
    // Доля чистых вычетов по НДС до
    clear_deduction_ratio_to: '',
    // Доля схемных вычетов по НДС от
    schema_deduction_ratio_from: 0,
    // Доля схемных вычетов по НДС до
    schema_deduction_ratio_to: '',
    // Сумма разрыва по НДС от
    vat_gap_from: 0,
    // Сумма разрыва по НДС до
    vat_gap_to: '',
  };

  initialOptionsABD = {
    // Сумма кредита от
    credit_from: 0,
    // Сумма кредита до
    credit_to: '',
    // Сумма дебета от
    debet_from: 0,
    // Сумма дебета до
    debet_to: '',
  };

  // опции фильтра
  initialOptions = {
    ...this.initialOptionsABD,
    ...this.initialOptionsNDS,
    // АСК НДС-2
    is_ask_nds2: false,
    // АБД (движение ДС)
    is_abd: false,
    // Товарный поток
    is_product_flow: false,
    // Слой данных ПП "Прослеживаемость"
    is_trace: false,
    // Слой данных ПП "Собственность"
    is_property: false,
    // Слой данных ГИС МТ
    is_mark: false,
    // Слой данных ГК
    is_contract: false,
  };

  /** Метод переключает состояние фильтра */
  @action toggleFilter = () => {
    this.keep = !this.keep;
    if (!this.keep) {
      this.reset();
      this.setOptionsToTrueCooperationExpandType(
        this.GraphStore.showABD,
        this.GraphStore.showNDS,
        this.GraphStore.graphExpandType,
        this.GraphStore.sourceSystem,
      );
    } else {
      this.setOptionsToTrueCooperation(
        this.GraphStore.showABD,
        this.GraphStore.showNDS,
        this.GraphStore.interQuarterSearch,
        this.GraphStore.sourceSystem,
      );
    }
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /** Метод устанавливает булевские опции на true */
  @action setOptionsToTrueCooperation = (
    showABD?: boolean,
    showNDS?: boolean,
    interQuarterSearch?: boolean,
    sourceSystem?: string,
  ) => {
    const { options } = this;
    for (const key in options) {
      if (typeof options[key] === 'boolean') {
        if (showABD && !interQuarterSearch) {
          options['is_abd'] = true;
        }
        if (showNDS) {
          options['is_ask_nds2'] = true;
        }
        options['is_contract'] = true;
        if (!interQuarterSearch) {
          options['is_product_flow'] = true;
          this.setOptionsProductFlow(true);
        }
      }
    }
    if (!this.keep) {
      this.keep = !this.keep;
    }
  };

  /** Метод устанавливает булевские опции на true */
  @action setOptionsToTrueCooperationExpandType = (
    showABD?: any,
    showNDS?: any,
    graphExpandType?: any,
    sourceSystem?: string,
  ) => {
    const { options } = this;
    for (const key in options) {
      if (typeof options[key] === 'boolean') {
        if (showABD && graphExpandType === 'Abd' && sourceSystem !== 'MDAB') {
          options['is_abd'] = true;
        }
        if (showNDS && graphExpandType === 'Nds' && sourceSystem !== 'MDAB') {
          options['is_ask_nds2'] = true;
        }
        if (graphExpandType === 'Contract' && sourceSystem !== 'MDAB') {
          options['is_contract'] = true;
        }
        if (graphExpandType === 'Trace' && sourceSystem !== 'MDAB') {
          options['is_product_flow'] = true;
          options['is_trace'] = true;
        }
        if (graphExpandType === 'Property' && sourceSystem !== 'MDAB') {
          options['is_product_flow'] = true;
          options['is_property'] = true;
        }
        if (graphExpandType === 'Mark' && sourceSystem !== 'MDAB') {
          options['is_product_flow'] = true;
          options['is_mark'] = true;
        }
        if (
          commodityFlow.some((item) => graphExpandType.includes(item)) &&
          graphExpandType.split(',').length > 1 &&
          sourceSystem !== 'MDAB'
        ) {
          options['is_product_flow'] = true;
          this.setOptionsProductFlow(true);
        }
      }
    }
    if (!this.keep && sourceSystem !== 'MDAB') {
      this.keep = !this.keep;
    }
  };

  /**
   * Метод переключает значение АСК НДС-2
   */
  @action toggleAskNds2 = () => {
    if (!this.options.is_ask_nds2) {
      this.FilterStore.togglePeriodActiveState(true);
    } else if (!this.options.is_abd) {
      this.FilterStore.togglePeriodActiveState(false);
    }

    this.options = {
      ...this.options,
      is_ask_nds2: !this.options.is_ask_nds2,
    };

    if (!this.options.is_ask_nds2) {
      this.options = {
        ...this.options,
        ...this.initialOptionsNDS,
      };
    }

    this.checkAllFalse();
    this.checkAllTrue();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод переключает значение АБД
   */
  @action toggleNp = () => {
    if (!this.options.is_abd) {
      this.FilterStore.togglePeriodActiveState(true);
    } else if (!this.options.is_ask_nds2) {
      this.FilterStore.togglePeriodActiveState(false);
    }

    this.options = {
      ...this.options,
      is_abd: !this.options.is_abd,
    };

    if (!this.options.is_abd) {
      this.options = {
        ...this.options,
        ...this.initialOptionsABD,
      };
    }

    this.checkAllFalse();
    this.checkAllTrue();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод переключает значение ГК
   */
  @action toggleContract = () => {
    if (!this.options.is_contract) {
      this.FilterStore.togglePeriodActiveState(true);
    } else if (!this.options.is_abd) {
      this.FilterStore.togglePeriodActiveState(false);
    }

    this.options = {
      ...this.options,
      is_contract: !this.options.is_contract,
    };

    if (!this.options.is_contract) {
      this.options = {
        ...this.options,
      };
    }

    this.checkAllFalse();
    this.checkAllTrue();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  @action setOptionsProductFlow = (data: boolean) => {
    this.optionsProductFlow.forEach((item: any) => this.changeOption(item, data));
  };

  @action checkProductFlowFalse = () => {
    return this.optionsProductFlow.every((item: any) => this.options[item] === false);
  };

  /** Метод переключает состояние фильтра (Товарный поток) */
  @action toggleFilterProductFlow = () => {
    this.options.is_product_flow = !this.options.is_product_flow;
    if (this.options.is_product_flow) {
      this.options['is_trace'] = true;
      this.options['is_property'] = true;
      this.options['is_mark'] = true;
    } else {
      this.options['is_trace'] = false;
      this.options['is_property'] = false;
      this.options['is_mark'] = false;
    }
    this.checkAllFalse();
    this.checkAllTrue();
    this.checkAllTrueProductFlow();
    this.keepFilter(this.isAllFalse);
    this.setOptionsProductFlow(this.options.is_product_flow);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  // Изменение опций в "Товарный поток"
  @action.bound
  toggleProductFlowCommon = (option: string) => {
    this.changeOption(option, !this.options[option]);
    this.changeOption('is_product_flow', !this.checkProductFlowFalse());
    this.checkAllFalse();
    this.checkAllTrue();
    this.checkAllTrueProductFlow();
    this.keepFilter(this.isAllFalse);
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  @action checkValue = (value: number) => {
    let valueTransform = value;
    if (value.toString().includes('.')) {
      // @ts-ignore
      valueTransform = valueTransform.toString().replace('.', '');
    }
    if (isNaN(valueTransform)) {
      // @ts-ignore
      valueTransform = valueTransform.toString().replace(/[^\d]/g, '');
    }

    return Number(valueTransform);
  };

  /**
   * Метод изменяет сумму покупок от
   *
   * @param {number} value - значение для записи
   */
  @action changePurchasesFrom = (value: number) => {
    this.changeOption('purchases_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму покупок до
   *
   * @param {number} value - значение для записи
   */
  @action changePurchasesTo = (value: number) => {
    this.changeOption('purchases_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму продаж от
   *
   * @param {number} value - значение для записи
   */
  @action changeSalesFrom = (value: number) => {
    this.changeOption('sales_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму продаж до
   *
   * @param {number} value - значение для записи
   */
  @action changeSalesTo = (value: number) => {
    this.changeOption('sales_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю вычета НДС от
   *
   * @param {number} value - значение для записи
   */
  @action changeShareVatDeductFrom = (value: number) => {
    this.changeOption('shareVatDeduct_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю вычета НДС до
   *
   * @param {number} value - значение для записи
   */
  @action changeShareVatDeductTo = (value: number) => {
    this.changeOption('shareVatDeduct_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю НДС исчисленного от
   *
   * @param {number} value - значение для записи
   */
  @action changeShareVatCalcFrom = (value: number) => {
    this.changeOption('shareVatCalc_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю НДС исчисленного до
   *
   * @param {number} value - значение для записи
   */
  @action changeShareVatCalcTo = (value: number) => {
    this.changeOption('shareVatCalc_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму кредита от
   *
   * @param {number} value - значение для записи
   */
  @action changeCreditFrom = (value: number) => {
    this.changeOption('credit_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму кредита до
   *
   * @param {number} value - значение для записи
   */
  @action changeCreditTo = (value: number) => {
    this.changeOption('credit_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму дебета от
   *
   * @param {number} value - значение для записи
   */
  @action changeDebetFrom = (value: number) => {
    this.changeOption('debet_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму дебета до
   *
   * @param {number} value - значение для записи
   */
  @action changeDebetTo = (value: number) => {
    this.changeOption('debet_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю чистых вычетов по НДС от
   *
   * @param {number} value - значение для записи
   */
  @action changeClearDeductionRatioFrom = (value: number) => {
    this.changeOption('clear_deduction_ratio_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю чистых вычетов по НДС до
   *
   * @param {number} value - значение для записи
   */
  @action changeClearDeductionRatioTo = (value: number) => {
    this.changeOption('clear_deduction_ratio_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю схемных вычестов по НДС от
   *
   * @param {number} value - значение для записи
   */
  @action changeSchemaDeductionRatioFrom = (value: number) => {
    this.changeOption('schema_deduction_ratio_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет долю схемных вычестов по НДС до
   *
   * @param {number} value - значение для записи
   */
  @action changeSchemaDeductionRatioTo = (value: number) => {
    this.changeOption('schema_deduction_ratio_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму разрыва по НДС от
   *
   * @param {number} value - значение для записи
   */
  @action changeVatGapFrom = (value: number) => {
    this.changeOption('vat_gap_from', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод изменяет сумму разрыва по НДС до
   *
   * @param {number} value - значение для записи
   */
  @action changeVatGapTo = (value: number) => {
    this.changeOption('vat_gap_to', this.checkValue(value));
    this.isChangeStatic();
    this.FilterStore.checkChanges();
  };

  // Происходили ли изменения среди фильтров
  @action isChangeStatic = () => {
    this.FilterStore.isCooperationChange = false;
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_cooperation') {
        if (
          this.options[item] !=
          (this.FilterStore.options[item] == undefined ? '' : this.FilterStore.options[item])
        ) {
          this.FilterStore.isCooperationChange = true;
        }
      }
    });
  };

  // Все ли фильтры отключены
  @action checkAllFalse = () => {
    this.isAllFalse =
      this.options['is_abd'] === false &&
      this.options['is_ask_nds2'] === false &&
      this.options['is_product_flow'] === false &&
      this.options['is_contract'] === false;
  };

  @action checkAllTrue = () => {
    this.isAllTrue =
      this.options['is_abd'] === true &&
      this.options['is_ask_nds2'] === true &&
      this.options['is_product_flow'] === true &&
      this.options['is_contract'] === true;
  };

  @action checkAllTrueProductFlow = () => {
    this.isAllTrueProductFlow =
      this.options['is_trace'] === true &&
      this.options['is_property'] === true &&
      this.options['is_mark'] === true;
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
      is_cooperation: this.keep,
    };
  }
}

export default CooperationFilter;
