import { IPagination } from '@fdc/vsa-react-lib';
import { IChains, IChainsRequest, ILink, ISearchListParams } from 'ApiModel';
import { Statuses } from 'constants/statuses';
import { action, computed, configure, decorate, flow, observable, runInAction } from 'mobx';
import { RootStoreType } from 'stores/rootStore';
import { toNumber } from 'utils';
import { flowTypes } from 'utils/flowTypes';
import { getQuarterRoman } from 'utils/getPeriodNumbers';
import { checkChainsJob, createScheme, searchChains } from 'utils/services/api/common';
import { KeysOfType } from 'utils/types/utils/keysOfType';
import { getDefaultPeriod } from 'utils/ui/getCurrentDate';
import GraphStore from '../graph/graphStore/graphStore';

const { quarter, year } = getDefaultPeriod('roman');

const initialResult: IChains = {
  link_type: null,
  nds_chains: [],
  abd_chains: [],
  goods_chains: [],
  contract_chains: [],
};

const initialFormData = {
  seller_inn: '',
  buyer_inn: '',
  period_from: quarter + ';' + year,
  quarter_from: quarter,
  year_from: year,
  period_to: quarter + ';' + year,
  quarter_to: quarter,
  year_to: year,
  steps: '3',
  link_type: 'АСК НДС',
  vat_calc_from: '',
  vat_calc_to: '',
  vat_deduct_from: '',
  vat_deduct_to: '',
  debit_min: '',
  debit_max: '',
  credit_min: '',
  credit_max: '',
  is_mark: false,
  is_property: false,
  is_trace: false,
  cost_min: '',
  cost_max: '',
  contract_min: '',
  contract_max: '',
};

type FormDataType = typeof initialFormData;

const typesMap = {
  'АСК НДС': { name: 'nds', code: 1 },
  АБД: { name: 'abd', code: 4 },
  'Товарный поток': { name: 'goods', code: 0 },
  Госконтракты: { name: 'contract', code: 64 },
};

const typesNamesMap = {
  nds: 'АСК НДС',
  abd: 'АБД',
  goods: 'Товарный поток',
  contract: 'Госконтракты',
};

type TypesNamesMapType = typeof typesNamesMap;

configure({ enforceActions: 'observed' });

class ChainsStore {
  graphStore: GraphStore;
  rootStore: RootStoreType;

  constructor(rootStore: RootStoreType) {
    this.rootStore = rootStore;
    this.graphStore = rootStore.GraphStore;
  }

  page: number = 1;
  formIsCollapsed: boolean = false;
  interval: number | undefined;
  searchState?: number = -1;
  searchResult: IChains = initialResult;
  searchCount?: number = 0;
  searchWarning?: string | null = '';
  requestId: string = '';

  pagination: IPagination = {
    page: 0,
    count: 10,
    total: 0,
  };

  jobId: number = 0;

  formData: FormDataType = initialFormData;

  changeFormData = (name: KeysOfType<FormDataType, string>, value: string) => {
    if (name === 'link_type') {
      this.formData.is_mark = false;
      this.formData.is_property = false;
      this.formData.is_trace = false;
    }

    this.formData[name] = value;
  };

  changePeriod = (name: 'from' | 'to', value: string) => {
    const [quarter, year] = value.split(';');

    this.formData[('period_' + name) as KeysOfType<FormDataType, string>] = value;
    this.formData[('quarter_' + name) as KeysOfType<FormDataType, string>] = quarter;
    this.formData[('year_' + name) as KeysOfType<FormDataType, string>] = year;
  };

  changeNumberFormData = (name: KeysOfType<FormDataType, string>, value: number | null) => {
    if (!isNaN(Number(value))) {
      this.formData[name] = value ? Number(value).toLocaleString('ru-RU') : '';
    }
  };

  changeBooleanFormData = (name: KeysOfType<FormDataType, boolean>) => {
    this.formData[name] = !this.formData[name];
  };

  setPage = (value: number) => {
    this.page = value;
    this.setFormIsCollapsed(value === 2);
  };

  setFormIsCollapsed = (value: boolean) => {
    this.formIsCollapsed = value;
  };

  formatDate = (date: number) => {
    const dateArr = String(-date + '').split('');
    const quarter = getQuarterRoman(dateArr.pop() || '');
    const year = dateArr.join('');

    return { quarter, year };
  };

  formatFormData = (params: ISearchListParams) => {
    const { quarter: startQuarter, year: startYear } = this.formatDate(params.yq_min);
    const { quarter: endQuarter, year: endYear } = this.formatDate(params.yq_max);

    return {
      seller_inn: params?.start[0]?.[1] || '',
      buyer_inn: params?.finish[0]?.[1] || '',
      period_from: startQuarter + ';' + startYear,
      quarter_from: startQuarter,
      year_from: startYear,
      period_to: endQuarter + ';' + endYear,
      quarter_to: endQuarter,
      year_to: endYear,
      steps: params.steps + '',
      link_type: typesNamesMap[params.link_type as keyof TypesNamesMapType],
      vat_calc_from: params.vat8total_min ? params.vat8total_min.toLocaleString('ru-RU') : '',
      vat_calc_to: params.vat8total_max ? params.vat8total_max.toLocaleString('ru-RU') : '',
      vat_deduct_from: params.vat9total_min ? params.vat9total_min.toLocaleString('ru-RU') : '',
      vat_deduct_to: params.vat9total_max ? params.vat9total_max.toLocaleString('ru-RU') : '',
      debit_min: params.debit_min ? params.debit_min.toLocaleString('ru-RU') : '',
      debit_max: params.debit_max ? params.debit_max.toLocaleString('ru-RU') : '',
      credit_min: params.credit_min ? params.credit_min.toLocaleString('ru-RU') : '',
      credit_max: params.credit_max ? params.credit_max.toLocaleString('ru-RU') : '',
      is_mark: params.mark || false,
      is_property: params.prop || false,
      is_trace: params.trace || false,
      cost_min: params.cost_min ? params.cost_min.toLocaleString('ru-RU') : '',
      cost_max: params.cost_max ? params.cost_max.toLocaleString('ru-RU') : '',
      contract_min: params.contract_min ? params.contract_min.toLocaleString('ru-RU') : '',
      contract_max: params.contract_max ? params.contract_max.toLocaleString('ru-RU') : '',
    };
  };

  setSearchIsActive = flow(function* (
    this: ChainsStore,
    jobId: number,
    searchId: number,
    state: number,
    params: ISearchListParams,
  ) {
    this.searchState = state;

    this.formData = this.formatFormData(params);

    try {
      yield this.checkChainsJob(jobId, 0, 10);

      this.setIsCollapsed(true);
      this.rootStore.UIStore.setChainsIsOpen(true);

      if (this.searchState === Statuses.IN_STACK || this.searchState === Statuses.IN_PROCESS) {
        this.interval = this.setSearchJobInterval(jobId, 0, 10);
      }
    } catch (err) {
      this.graphStore.onError(err);
      this.searchState = -1;
    }
  });

  setSearchIsComplited = flow(function* (
    this: ChainsStore,
    jobId: number,
    searchId: number,
    state: number,
    params: ISearchListParams,
  ) {
    this.setIsCollapsed(false);
    this.setFormIsCollapsed(true);
    this.searchState = Statuses.IN_PROCESS;
    this.formData = this.formatFormData(params);

    try {
      yield this.checkChainsJob(jobId, 0, 10, false);
    } catch (err) {
      this.graphStore.onError(err);
      this.searchState = -1;
    }
  });

  changePagination = flow(function* (this: ChainsStore, pagination: IPagination) {
    this.searchState = Statuses.IN_PROCESS;

    try {
      yield this.checkChainsJob(this.jobId, pagination.page, pagination.count, false);
    } catch (err) {
      this.graphStore.onError(err);
      this.searchState = -1;
    }
  });

  resetSearch = () => {
    this.rootStore.UIStore.openDialog({
      title: '',
      opened: true,
      type: 'confirm',
      text: 'Результаты поиска будут удалены.',
      additionalText: '',
      okText: 'OK',
      cancelText: 'Отмена',
      onConfirm: () =>
        runInAction(() => {
          this.page = 1;
          this.formIsCollapsed = false;
          this.searchState = -1;
          this.searchResult = initialResult;
          this.searchWarning = '';
          this.formData = initialFormData;
          this.clearSearchJobInterval();
        }),
      onCancel: () => {},
    });
  };

  submitForm = async () => {
    const requestBody: IChainsRequest = {
      seller_inn: this.formData.seller_inn,
      buyer_inn: this.formData.buyer_inn,
      period_from: this.formData.period_from,
      period_to: this.formData.period_to,
      steps: toNumber(this.formData.steps, 2),
      link_type: typesMap[this.formData.link_type as keyof typeof typesMap].name,
      vat_calc_from: toNumber(this.formData.vat_calc_from, 0),
      vat_calc_to: toNumber(this.formData.vat_calc_to),
      vat_deduct_from: toNumber(this.formData.vat_deduct_from, 0),
      vat_deduct_to: toNumber(this.formData.vat_deduct_to),
      debit_min: toNumber(this.formData.debit_min, 0),
      debit_max: toNumber(this.formData.debit_max),
      credit_min: toNumber(this.formData.credit_min, 0),
      credit_max: toNumber(this.formData.credit_max),
      cost_min: toNumber(this.formData.cost_min, 0),
      cost_max: toNumber(this.formData.cost_max),
      contract_min: toNumber(this.formData.contract_min, 0),
      contract_max: toNumber(this.formData.contract_max),
      is_mark: this.formData.is_mark,
      is_property: this.formData.is_property,
      is_trace: this.formData.is_trace,
    };

    this.searchChains(requestBody);
  };

  searchChains = flow(function* (this: ChainsStore, bodyOptions: IChainsRequest) {
    this.searchState = Statuses.IN_STACK;

    try {
      const { job_id } = yield* flowTypes(searchChains(bodyOptions));

      this.setIsCollapsed(true);

      yield this.checkChainsJob(job_id, 0, 10);
      this.rootStore.SearchListStore.fetchSearchList();

      if (this.searchState === Statuses.IN_STACK || this.searchState === Statuses.IN_PROCESS) {
        this.interval = this.setSearchJobInterval(job_id, 0, 10);
      }
    } catch (err) {
      this.graphStore.onError(err);
      this.searchState = -1;
    }
  });

  checkChainsJob = flow(function* (
    this: ChainsStore,
    jobId: number,
    reqOffset: number,
    pageSize: number,
    showAlert: boolean = true,
  ) {
    try {
      const { items, offset, page_size, total_rows, request_id } = yield* flowTypes(
        checkChainsJob(jobId, reqOffset, pageSize),
      );
      const { state, chains, warning } = items[0];

      if (state === undefined) {
        this.searchState = -1;
        this.clearSearchJobInterval();
        this.rootStore.SearchListStore.fetchSearchList();
      }

      if (state === 2) {
        this.searchResult = chains;
        this.searchWarning = warning;
        this.requestId = request_id || '';
        this.clearSearchJobInterval();
        this.rootStore.SearchListStore.fetchSearchList();
        this.setFormIsCollapsed(true);

        this.pagination = {
          page: offset,
          count: page_size,
          total: total_rows,
        };

        this.jobId = jobId;

        if (showAlert) {
          this.rootStore.UIStore.openDialog({
            title: '',
            opened: true,
            type: 'confirm',
            text: `Поиск цепочек завершен.${!this.hasResult ? ' Цепочки не найдены' : ''}`,
            additionalText: '',
            okText: 'Перейти',
            cancelText: 'Закрыть',
            onConfirm: () => {
              this.rootStore.ModalStore.closeModals();
              this.rootStore.UIStore.setChainsIsOpen(true);
              this.setIsCollapsed(false);
            },
            onCancel: () => this.rootStore.UIStore.setChainsIsOpen(false),
          });
        }
      }

      if (state === 3) {
        this.searchWarning = warning;
        this.clearSearchJobInterval();
        this.rootStore.SearchListStore.fetchSearchList();
      }

      if (this.searchState === Statuses.IN_STACK && state === Statuses.IN_PROCESS) {
        this.rootStore.SearchListStore.fetchSearchList();
      }

      this.searchState = state;
    } catch (e) {
      this.graphStore.onError(e);
      this.clearSearchJobInterval();
      this.rootStore.SearchListStore.fetchSearchList();
      this.searchState = Statuses.ERROR;
    }
  });

  createScheme = async (links: ILink[]) => {
    let types = [];

    if (this.formData.link_type === 'Товарный поток') {
      this.formData.is_mark && types.push(32);
      this.formData.is_property && types.push(16);
      this.formData.is_trace && types.push(8);
    } else {
      types = [typesMap[this.formData.link_type as keyof typeof typesMap]?.code];
    }

    const request = {
      internalId: this.requestId,
      expand_type: { type: 1, types },
      drawMode: 2,
      links: links.map((link) => ({
        seller_inn: link.seller_inn,
        seller_kppEffective: link.seller_kpp_effective,
        seller_fid: link.seller_fid,
        buyer_inn: link.buyer_inn,
        buyer_kppEffective: link.buyer_kpp_effective,
        buyer_fid: link.buyer_fid,
        year_quarter: link.year_quarter,
      })),
    };

    try {
      return await createScheme(request);
    } catch (e) {
      this.graphStore.onError(e);
    }
  };

  goToScheme = async (links: ILink[]) => {
    this.rootStore.CommonStore.closeBeginningWork();
    this.setIsCollapsed(true);
    this.graphStore.statePending();

    const scheme = await this.createScheme(links);

    if (scheme?.GraphId) {
      await this.graphStore.fetchGraph(scheme.GraphId);
    }

    this.graphStore.stateDone();
  };

  get hasResult() {
    return (
      this.searchResult.nds_chains.length > 0 ||
      this.searchResult.abd_chains.length > 0 ||
      this.searchResult.goods_chains.length > 0 ||
      this.searchResult.contract_chains.length > 0
    );
  }

  setSearchJobInterval = (jobId: number, offset: number, pageSize: number) => {
    this.clearSearchJobInterval();

    return setInterval(async () => {
      await this.checkChainsJob(jobId, offset, pageSize);
    }, 5000) as unknown as number;
  };

  clearSearchJobInterval = () => {
    clearInterval(this.interval);
  };

  isCollapsed = false;

  setIsCollapsed = (value: boolean) => {
    this.isCollapsed = value;
  };
}

decorate(ChainsStore, {
  formData: observable,
  searchState: observable,
  searchResult: observable,
  searchCount: observable,
  searchWarning: observable,
  isCollapsed: observable,
  page: observable,
  formIsCollapsed: observable,
  requestId: observable,

  hasResult: computed,

  setSearchJobInterval: action,
  clearSearchJobInterval: action,
  goToScheme: action,
  createScheme: action,
  setFormIsCollapsed: action,
  resetSearch: action,
  setPage: action,
  setIsCollapsed: action,
  changePeriod: action,
  changeFormData: action,
  changeNumberFormData: action,
  changeBooleanFormData: action,
  setSearchIsActive: action.bound,
  checkChainsJob: action.bound,
  searchChains: action.bound,
  changePagination: action.bound,
});

export default ChainsStore;
