import React, { PureComponent } from 'react';
import styles from './ToolTipJournal.module.scss';

import { inject, observer } from 'mobx-react';
import { GraphStoreType } from '../../stores/graph/graphStore';

import { ReactComponent as Declaration10Icon } from './assets/declaration10.svg';
import { ReactComponent as CloseIcon } from './assets/close.svg';

type ToolTipJournalProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
};

type ToolTipJournalState = {
  graphID: any;
};

/**
 * Компонент всплывающей подсказки по "Данные из журналов"
 */
@inject('graphStore')
@observer
class ToolTipJournal extends PureComponent<ToolTipJournalProps, ToolTipJournalState> {
  /**
   * Метод отрисовывает элементы на странице
   */

  _onClose = (): void => {
    let toolTipDiv = document.getElementById('toolTipJournal');
    let toolTipBackGround = document.getElementById('toolTipBackGroundJournal');

    if (toolTipDiv && toolTipBackGround) {
      toolTipDiv.style.display = 'none';
      toolTipBackGround.style.display = 'none';
    }
  };

  render() {
    const { graphStore, id } = this.props;

    return (
      <>
        <div className={styles.background} id="toolTipBackGroundJournal" onClick={this._onClose} />
        <div className={styles.container} id={id}>
          <div className={styles.header} data-draggable>
            <div></div>
            <div onClick={this._onClose}>
              <CloseIcon className={styles.close_icon} />
            </div>
          </div>
          <div className={styles.data_container}>
            {graphStore?.declarationInfo?.map((item: any) => (
              <div className={styles.containerDebt}>
                <div className={styles.iconTitle}>
                  <div className={styles.icon}>
                    {item.name === 'DeclState_10' && (
                      <Declaration10Icon className={styles.iconStyle} />
                    )}
                  </div>
                  <div className={styles.title}>
                    {item.name === 'DeclState_10' && 'Данные из журналов'}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </>
    );
  }
}

export default ToolTipJournal;
