import { observable, computed, action } from 'mobx';

/**
 * Класс для асинхронных хранилищ
 */
class AsyncStore {
  // состояние хранилища
  @observable state: 'done' | 'pending' | 'error' | null = null;

  // состояние процесса очередей
  @observable stateJob: number = 2;
  // интервал вызова функции очереди
  @observable interval: any;

  @observable isLoadingDiagram: boolean = false;

  // данные об ошибке
  @observable requestError: Error | null = null;

  /**
   * Метод обрабатывает ошибку
   */
  @action onError = (error: any) => {
    this.state = 'error';
    this.requestError = {
      ...error,
      ...error.error,
    };
  };

  /**
   * Метод возвращает состояние 'done'
   */
  @computed get done() {
    return this.state === 'done' ? true : false;
  }

  /**
   * Метод возвращает состояние 'pending'
   */
  @computed get pending() {
    return this.state === 'pending' ? true : false;
  }

  /**
   * Метод возвращает состояние 'error'
   */
  @computed get error() {
    return this.state === 'error' ? true : false;
  }
}

export default AsyncStore;
