import { INotification } from 'DataTypes';
import { ReactComponent as ArrowIcon } from 'assets/icons/arrow_round.svg';
import { ReactComponent as CloseIcon } from 'assets/icons/close_round.svg';
import errorIcon from 'assets/icons/error.png';
import Button from 'components/Button';
import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import moment from 'moment';
import { QRCodeCanvas } from 'qrcode.react';
import React, { useState } from 'react';
import { formatDateDetailedLink } from 'utils/ui/formatDateDetailedLink';
import styles from './Warning.module.scss';

interface WarningProps {
  errors: INotification[];
}

/**
 * Компонент оповещения об ошибке
 */
const Warning = observer(({ errors }: WarningProps) => {
  const [index, setIndex] = useState(0);
  const [isCopied, setIsCopied] = useState(errors.map(() => false));

  const { notificationStore } = useStores();
  const { removeNotifications } = notificationStore;

  const generateLog = () => {
    const { request_id, payload, url, ...error } = errors[index];

    if (url) {
      return `${url}\n${payload || ''}`;
    }

    return `${request_id}\n${error.code || ''}\n${error.message || ''}\n${
      error.description || ''
    }\n${error.search_params || ''}\n${error.error_time || ''}\n${error.username || ''}`;
  };

  /**
   * Скачивание лога ошибок
   */
  const downloadLog = () => {
    const fileName = `${moment(new Date()).locale('ru').format('DD_MM_YYYY_HH_mm_ss')}_errors.log`;
    const log = generateLog();
    const element = document.createElement('a');
    const file = new Blob([log], { type: 'text/plain' });
    element.href = URL.createObjectURL(file);
    element.download = fileName;
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();

    setIsCopied((e) => {
      const arr = [...e];
      arr[index] = true;
      return arr;
    });
  };

  function onClose() {
    removeNotifications(errors.map((e) => e.id));
  }

  const { message, description, request_id, exec_time, status, url, payload, ...error } =
    errors[index];

  const isInfo = error.type === 'info';
  const today = new Date(),
    date =
      (today.getDate() < 10 ? '0' + today.getDate() : today.getDate()) +
      '.' +
      (today.getMonth() + 1 < 10 ? '0' + (today.getMonth() + 1) : today.getMonth() + 1) +
      '.' +
      today.getFullYear().toString().substr(-2) +
      ' ' +
      (today.getHours() < 10 ? '0' + today.getHours() : today.getHours()) +
      ':' +
      (today.getMinutes() < 10 ? '0' + today.getMinutes() : today.getMinutes()) +
      ':' +
      (today.getSeconds() < 10 ? '0' + today.getSeconds() : today.getSeconds());

  const valueQRCode = generateLog();

  return (
    <div className={styles.wrapper}>
      <div className={styles.header}>
        <div className={styles.header_title}>Ошибка</div>
        <div className={styles.header_subtitle}>
          {/* Информационное сообщение */}
          {isInfo ? (
            <div className={styles.header_info} title={'Информация'}>
              Информация
            </div>
          ) : (
            <>
              {/* Ошибка */}
              <img src={errorIcon} className={styles.header_icon} alt="Error" />
              <span
                className={styles.header_info}
                title={message || (status !== 504 ? 'Техническая ошибка' : 'Таймаут')}
              >
                {(message && message.length > 140 ? message?.slice(0, 140) + '...' : message) ||
                  (status !== 504 ? 'Техническая ошибка' : 'Таймаут')}
              </span>
            </>
          )}
        </div>
        <button className={styles.closeButton} onClick={onClose}>
          <CloseIcon />
        </button>
      </div>
      <div className={styles.body}>
        {isInfo ? (
          <>
            {/* Информационное сообщение */}
            <div className={styles.hint}>{message ?? ''}</div>
            <div className={styles.hint}>{description ?? ''}</div>
          </>
        ) : (
          <>
            {/* Ошибка */}
            <div className={styles.content}>
              <div className={styles.content_left}>
                <div className={styles.title}>Данные об ошибке</div>
                <div className={styles.info}>
                  <div className={styles.description}>
                    <span>{exec_time ? formatDateDetailedLink(exec_time) : date}</span>
                    <span>{description}</span>
                  </div>
                  <div className={styles.text}>
                    {(message === 'Таймаут' || status === 504) && (
                      <>
                        <span className={styles.hint}>URL: {url}</span>
                        <p>
                          {payload &&
                            (payload.length > 30 ? payload.slice(0, 30) + ' ...' : payload)}
                        </p>
                      </>
                    )}
                    {request_id && (
                      <>
                        <span className={styles.hint}>ID запроса: </span>
                        <p>{request_id}</p>
                      </>
                    )}
                  </div>
                </div>
              </div>
              <div className={styles.content_QRCode}>
                <QRCodeCanvas value={valueQRCode} size={186} />
              </div>
            </div>
            {errors.length > 1 && (
              <div className={styles.buttons}>
                <button onClick={() => setIndex((i) => i - 1)} data-disabled={!index}>
                  <ArrowIcon />
                </button>
                <button
                  onClick={() => setIndex((i) => i + 1)}
                  data-disabled={index >= errors.length - 1}
                >
                  <ArrowIcon />
                </button>
                <div>
                  Ошибки: <b>{index + 1}</b>/{errors.length}
                </div>
              </div>
            )}
          </>
        )}
      </div>
      <div className={styles.footer}>
        {!isInfo && (
          <div className={styles.footer__left}>
            <Button onClick={() => downloadLog()}>
              {isCopied[index] ? 'Скачано!' : 'Скачать лог ошибки'}
            </Button>
          </div>
        )}
        <div className={styles.footer__right}>
          <Button onClick={onClose}>Закрыть</Button>
        </div>
      </div>
    </div>
  );
});

export default Warning;
