import { INFO_CODES } from 'constants/notification';
import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import React from 'react';
import { Info, Warning } from '../../components/notifications';
import styles from './NotificationList.module.scss';
import { INotification } from 'DataTypes';

/**
 * Компонент списка нотификаций
 */
const NotificationList = observer(() => {
  const {
    notificationStore: { notifications, removeNotifications },
  } = useStores();

  if (!notifications.length) {
    return null;
  }

  const isInfo = (e: INotification) => (e.code && INFO_CODES[e.code]) || e.type === 'info';
  
  const info = notifications.filter((e) => isInfo(e));
  const errors = notifications.filter((e) => !isInfo(e));

  return (
    <>
      {info.length > 0 && (
        <div className={styles.wrapper}>
          <div className={styles.list}>
            {info.map((item, index) => (
              <Info
                key={'notification-list-item' + index}
                note={item}
                onClose={() => removeNotifications([item.id])}
              />
            ))}
          </div>
        </div>
      )}
      {errors.length > 0 && <div className={styles.wrapper}>{<Warning errors={errors} />}</div>}
    </>
  );
});

export default NotificationList;
