import { IPagination } from '@fdc/vsa-react-lib';
import {
  IClusterData,
  IClusterSubtype,
  IClusterType,
  IDetailedClusterLink,
  IDetailedLinkCommon,
  IDetailedLinkCommonItem,
  ISaveLinkForm,
} from 'ApiModel';
import { ClusterTaxpayer } from 'DataTypes';
import { observable, action, decorate, flow, computed } from 'mobx';
import GraphStore from 'stores/graph/graphStore/graphStore';
import { flowTypes } from 'utils/flowTypes';
import { uploadDoc } from 'utils/services/api/common';
import {
  getClusterLink,
  getDetailedCluster,
  getDetailedLinkPeriod,
  getNPCluster,
  postClusterFilters,
  postLinkCluster,
  saveClusterLink,
} from 'utils/services/api/graph';

/**
 * Хранилище для работы с кластерами
 */
class ClusterStore {
  graphStore: GraphStore;

  constructor(graphStore: GraphStore) {
    this.graphStore = graphStore;
  }

  allIsChecked = true;
  isFilterCluster = false;
  clusterTaxpayers: ClusterTaxpayer[] = [];
  selectedTaxpayers: string[] = [];
  clusterData: IClusterData | null = null;
  linkData: IDetailedClusterLink | null = null;
  commonData: IDetailedLinkCommonItem[] = [];

  pagination: IPagination = {
    page: 0,
    count: 10,
    total: 0,
  };

  showLinksBetweenTaxpayers = false;

  changeShowLinksBetweenTaxpayers = () => {
    this.showLinksBetweenTaxpayers = !this.showLinksBetweenTaxpayers;
  };

  setFilterClusterFalse = () => {
    this.isFilterCluster = false;
  };

  changeSelectedTaxpayers = (taxpayer: string) => {
    if (this.selectedTaxpayers.some((item) => item === taxpayer)) {
      this.selectedTaxpayers = this.selectedTaxpayers.filter((item) => item !== taxpayer);
    } else {
      this.selectedTaxpayers.push(taxpayer);
    }

    if (this.selectedTaxpayers.length < 2) {
      this.showLinksBetweenTaxpayers = false;
    } else {
      this.showLinksBetweenTaxpayers = true;
    }
  };

  clearSelectedTaxpayers = () => {
    this.selectedTaxpayers = [];
  };

  getClusterData = flow(function* (
    this: ClusterStore,
    clusterId: string,
    graphId = this.graphStore.previouslyFetchedGraphId,
    withFilter?: boolean,
  ) {
    this.graphStore.state = 'pending';

    try {
      this.clusterData = yield getDetailedCluster(
        graphId,
        clusterId,
        withFilter
          ? {
              taxPayers: this.selectedTaxpayers,
              showOtherTaxPayers: this.showLinksBetweenTaxpayers,
            }
          : undefined,
      );

      this.checkAll(true);

      this.graphStore.state = 'done';
    } catch (err) {
      this.graphStore.onError(err);
    }
  });

  checkLink = (typeId: number, subtypeId: number, linkId: string) => {
    const type = this.clusterData?.types.find((type) => type.type_id === typeId);
    const subtype = type?.subtypes.find((subtype) => subtype.subtype_id === subtypeId);
    const link = subtype?.links?.find((link) => link.link_id === linkId);

    if (link && subtype && type) {
      link.checked = !link?.checked;

      this.changeSubtype(subtype, type, true);
    }
  };

  checkCommon = (typeId: number, subtypeId: number, inn: string) => {
    const type = this.clusterData?.types.find((type) => type.type_id === typeId);
    const subtype = type?.subtypes.find((subtype) => subtype.subtype_id === subtypeId);
    const common = subtype?.common_items?.find((item) => item.common_inn === inn);

    if (common && subtype && type) {
      common.checked = !common?.checked;

      this.changeSubtype(subtype, type);
    }
  };

  checkSubtype = (typeId: number, subtypeId: number) => {
    const type = this.clusterData?.types.find((type) => type.type_id === typeId);
    const subtype = type?.subtypes.find((subtype) => subtype.subtype_id === subtypeId);

    if (subtype && type) {
      this.checkAllSubtype(subtype, !subtype?.checked);
      subtype.checked = !subtype?.checked;

      this.changeType(type);
    }
  };

  changeSubtype = (subtype: IClusterSubtype, type: IClusterType, fromLink = false) => {
    const hasChecked = !fromLink
      ? subtype?.common_items?.some((item) => item.checked)
      : subtype?.links?.some((link) => link.checked);

    subtype.checked = hasChecked;
    this.changeType(type);
  };

  checkType = (typeId: number) => {
    const type = this.clusterData?.types.find((type) => type.type_id === typeId);

    if (type) {
      this.checkAllType(type, !type?.checked);
      type.checked = !type?.checked;

      if (!type.checked) {
        this.allIsChecked = false;
      } else {
        this.allIsChecked = !this.hasUnchecked;
      }
    }
  };

  changeType = (type: IClusterType) => {
    const hasChecked = type?.subtypes.some((subtype) => subtype.checked);
    type.checked = hasChecked;

    if (!type.checked) {
      this.allIsChecked = false;
    } else {
      this.allIsChecked = !this.hasUnchecked;
    }
  };

  checkAll = (checked: boolean) => {
    this.allIsChecked = checked;
    this.clusterData?.types.forEach((type) => {
      type.checked = checked;

      type.subtypes.forEach((subtype) => {
        subtype.checked = checked;

        subtype.common_items?.forEach((item) => {
          item.checked = checked;
        });

        subtype.links?.forEach((link) => {
          link.checked = checked;
        });
      });
    });
  };

  get hasUnchecked() {
    let hasUnchecked = false;

    this.clusterData?.types.forEach((type) => {
      if (!type.checked) hasUnchecked = true;

      type.subtypes.forEach((subtype) => {
        if (!subtype.checked) hasUnchecked = true;

        subtype.common_items?.forEach((item) => {
          if (!item.checked) hasUnchecked = true;
        });

        subtype.links?.forEach((link) => {
          if (!link.checked) hasUnchecked = true;
        });
      });
    });

    return hasUnchecked;
  }

  checkAllSubtype = (subtype: IClusterSubtype, checked: boolean) => {
    subtype.common_items?.forEach((item) => {
      item.checked = checked;

      item.links.forEach((link) => {
        link.checked = checked;
      });
    });

    subtype.links?.forEach((link) => {
      link.checked = checked;
    });
  };

  checkAllType = (type: IClusterType, checked: boolean) => {
    type.subtypes?.forEach((subtype) => {
      this.checkAllSubtype(subtype, !subtype?.checked);
      subtype.checked = checked;
    });
  };

  setClusterFilters = flow(function* (
    this: ClusterStore,
    clusterIndex: number,
    graphId = this.graphStore.previouslyFetchedGraphId,
  ) {
    this.graphStore.state = 'pending';

    try {
      const filtered_types: number[] = [];
      const filtered_subtypes: number[] = [];
      const filtered_edges: string[] = [];

      this.clusterData?.types.forEach((type) => {
        if (!type.checked) {
          filtered_types.push(type.type_id);
        }

        type.subtypes.forEach((subtype) => {
          if (!subtype.checked) {
            filtered_subtypes.push(subtype.subtype_id);
          }

          subtype.common_items?.forEach((item) => {
            if (!item.checked) {
              item.links.forEach((link) => {
                filtered_edges.push(link.link_id);
              });
            }
          });

          subtype.links?.forEach((link) => {
            if (!link.checked) {
              filtered_edges.push(link.link_id);
            }
          });
        });
      });

      const { GraphId } = yield postClusterFilters(graphId, clusterIndex, {
        cluster_index: clusterIndex,
        search_filter: {
          taxPayers: this.selectedTaxpayers,
          showOtherTaxPayers: this.showLinksBetweenTaxpayers,
        },
        filtered_types,
        filtered_subtypes,
        filtered_edges,
      });

      this.graphStore.updateGraphId(GraphId);
      this.graphStore.updateGraph();
    } catch (err) {
      this.graphStore.onError(err);
    } finally {
      this.graphStore.state = 'done';
    }
  });

  getClusterTaxpayers = flow(function* (
    this: ClusterStore,
    clusterId: string,
    graphId = this.graphStore.previouslyFetchedGraphId,
  ) {
    this.graphStore.state = 'pending';

    try {
      this.clusterTaxpayers = yield getNPCluster(graphId, clusterId);
      this.graphStore.state = 'done';
    } catch (err) {
      this.graphStore.onError(err);
    }
  });

  getDetailedLink = flow(function* (
    this: ClusterStore,
    linkId: string,
    pageNumber: number,
    graphId = this.graphStore.previouslyFetchedGraphId,
  ) {
    this.graphStore.state = 'pending';

    try {
      const linkData = yield* flowTypes(getClusterLink(graphId, linkId, pageNumber));

      this.linkData = linkData;

      const {
        periods: { items, offset, page_size, total_rows },
      } = linkData;

      this.commonData = items;

      this.pagination = {
        page: offset,
        count: page_size,
        total: total_rows,
      };

      this.graphStore.state = 'done';
    } catch (err) {
      this.graphStore.onError(err);
    }
  });

  saveLink = flow(function* (
    this: ClusterStore,
    data: ISaveLinkForm,
    graphId = this.graphStore.previouslyFetchedGraphId,
  ) {
    this.graphStore.state = 'pending';

    try {
      const docIds = [];

      if (data.files?.length > 0) {
        for (const doc of data.files) {
          if (doc.stream) {
            try {
              yield uploadDoc(doc.file_name, doc.stream).then((response: any) => {
                docIds.push(response.document_id);
              });
            } catch (err) {
              this.graphStore.onError(err);
            }
          } else {
            docIds.push(doc.document_id);
          }
        }
      }

      const { GraphId, EdgeId } = yield* flowTypes(
        saveClusterLink(graphId, {
          link_id: data.linkId,
          invalid_reason: data.invalidReason ? Number(data.invalidReason) : null,
          change_reason: data.editReason ? Number(data.editReason) : null,
          info_source: data.source,
          description: data.comment,
          attach_documents: docIds,
        }),
      );

      this.getDetailedLink(EdgeId, 0);

      this.graphStore.updateGraphId(GraphId);
      this.graphStore.updateGraph();

      this.graphStore.state = 'done';
    } catch (err) {
      this.graphStore.onError(err);
    }
  });
}

decorate(ClusterStore, {
  clusterData: observable,
  clusterTaxpayers: observable,
  selectedTaxpayers: observable,
  showLinksBetweenTaxpayers: observable,
  linkData: observable,
  commonData: observable,
  allIsChecked: observable,

  hasUnchecked: computed,

  changeType: action,
  changeSubtype: action,
  checkLink: action,
  checkCommon: action,
  checkSubtype: action,
  checkType: action,
  checkAllSubtype: action,
  checkAllType: action,
  checkAll: action,
  changeShowLinksBetweenTaxpayers: action,
  changeSelectedTaxpayers: action,
  clearSelectedTaxpayers: action,
  getClusterData: action.bound,
  getClusterTaxpayers: action.bound,
  getDetailedLink: action.bound,
  setClusterFilters: action.bound,
  saveLink: action.bound,
});

export default ClusterStore;
