import React, { PureComponent } from 'react';
import styles from './ToolTipMultipleShort.module.scss';

import { observer } from 'mobx-react';

type ToolTipMultipleShortProps = {
  [key: string]: any;
  id: string;
};

/**
 * Компонент всплывающей подсказки по отображению отложенных вычетов (надпись)
 */
@observer
class ToolTipMultipleShort extends PureComponent<ToolTipMultipleShortProps> {
  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { id } = this.props;

    return (
      <>
        <div className={styles.container} id={id}>
          <div className={styles.data_container}>Приняты отложенные вычеты</div>
        </div>
      </>
    );
  }
}

export default ToolTipMultipleShort;
