import React from 'react';

import Collapsed from './Collapsed';
import Extended from './Extended';

interface MenuProps {
  /** Элементы меню */
  children: React.ReactNode[] | React.ReactNode;
  /** Показывает развернуто ли меню */
  extended?: boolean;
  /** CSS класс контейнера развернутого меню */
  extendedMenuContainerClassName?: string;
  /** CSS класс списка свернутого меню */
  collapsedListClassName?: string;
  /** CSS класс элемента свернутого меню*/
  collapsedItemClassName?: string;
  /** CSS класс списка развернутого меню*/
  extendedListClassName?: string;
  /** CSS класс  элемента развернутого меню */
  extendedItemClassName?: string;
}

/** Компонент меню */
const Menu = ({ extended, extendedMenuContainerClassName, children }: MenuProps) => {
  return extended ? (
    <Extended className={extendedMenuContainerClassName}>{children}</Extended>
  ) : (
    <Collapsed>{children}</Collapsed>
  );
};

Menu.defaultProps = {
  extended: false,
  headerComponent: null,
  footerComponent: null,
  collapsedListClassName: '',
  collapsedItemClassName: '',
  extendedMenuContainerClassName: '',
  extendedListClassName: '',
  extendedItemClassName: '',
};

export default Menu;
