import { flow, action } from 'mobx';
import AsyncStore from '../../utils/AsyncStore';

import { API_URL_CERT, API_ATTACH } from 'utils/services/constants';

import { readParamsFromURL } from 'utils/getParamsFromURL';
import { getForScreenGraphURL } from 'utils/getForScreenGraphURL';
import { getForCreateAskNds } from 'utils/getForCreateAskNds';
import { getForCreateITAIS } from 'utils/getForCreateITAIS';

import { v4 as uuidv4 } from 'uuid';

import * as go from 'gojs';

//@ts-ignore
import domtoimage from 'dom-to-image';

import {
  postChangeDirection,
  postDrawingMode,
  postChangeRoles,
  getSchemeBack,
  getSchemeForward,
  postCopyGraph,
  postCreateGraph,
  getDetailedCluster,
  postCreateDifferentGraph,
  getPayersRecipients,
  getGoToDnpByFid,
  postPotentialDamage,
  getNPCluster,
  postFilteredNP,
  getFilteredNP,
} from 'utils/services/api/graph';
import { editGraph, downloadDoc, uploadDoc } from 'utils/services/api/common';

import { GraphStoreType } from 'StoreTypes';

/** Хранилище для узлов графа */
export default class GraphCommonStore extends AsyncStore {
  constructor(private readonly GraphStore: GraphStoreType) {
    super();
  }

  /** Обработка тестового графа */
  @action.bound
  setIsTestGraph = (value: boolean) => {
    this.GraphStore.isTestGraph = value;
  };
  @action.bound
  checkIfGraphIsTestAndSetValue = (graphId: string) => {
    if (graphId.length < 1) {
      return null;
    }

    if (graphId[0] === '-') {
      this.GraphStore.setIsTestGraph(true);
    } else {
      this.GraphStore.setIsTestGraph(false);
    }
  };

  /**
   * Функция изменяет информацию о графе
   *
   * @param {string} [id] - идентификатор графа
   */
  @action.bound
  editGraph = flow(function* (
    this: GraphCommonStore,
    id = this.GraphStore.previouslyFetchedGraphId,
    graph: any,
  ) {
    this.GraphStore.state = 'pending';

    try {
      yield editGraph(id, graph);
      if (this.GraphStore.previouslyGraphId === '0') {
        this.GraphStore.previouslyGraphId = id;
        this.GraphStore.updateGraph(true);
      }
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на сохранение направления графа
   *
   * @param {string} graphId - идентификатор графа
   * @param {string} direction - направление графа (0 - вертикально, 90 - горизонтально)
   */
  @action.bound
  saveDirection = flow(function* (
    this: GraphCommonStore,
    direction: 0 | 90,
    graphId = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield postChangeDirection(graphId, direction);

      this.GraphStore.updateGraphId(GraphId);

      yield this.GraphStore.updateGraph().then(() => {
        // Позиционирование к корневому узлу
        let targetNode: go.Part | null | undefined;

        targetNode = this.GraphStore.DiagramStore.diagram
          ?.findNodesByExample({ key: this.GraphStore.mainVertexId })
          .first()?.part;

        if (targetNode) {
          this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
        }
      });
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод смены режима построения графа
   */
  @action toggleDrawingMode = async () => {
    try {
      if (this.GraphStore.currentDrawingMode === 'from_buyer' || null) {
        await this.changeDrawingMode('from_seller');
      } else {
        await this.changeDrawingMode('from_buyer');
      }
      await this.GraphStore.updateGraph().then(() => {
        // Позиционирование к корневому узлу
        let targetNode: go.Part | null | undefined;

        targetNode = this.GraphStore.DiagramStore.diagram
          ?.findNodesByExample({ key: this.GraphStore.mainVertexId })
          .first()?.part;

        if (targetNode) {
          this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
        }
      });
    } catch (err) {}
  };

  /**
   * Метод делает POST запрос по переключению режима построения графа
   *
   * @param {string} mode - направление графа (from_buyer - от покупателя; from_seller от продавца)
   * @param {string} id - идентификатор графа
   */
  @action.bound changeDrawingMode = flow(function* (
    this: GraphCommonStore,
    mode: string,
    id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield postDrawingMode(id, { mode });

      this.GraphStore.state = 'done';
      this.GraphStore.updateGraphId(GraphId);
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает POST запрос на изменение роли (бизнес/аналитической) узла
   *
   * @param {string} changes - изменённая информация о роли
   * @param {string} node_id - идентификатор узла
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound changeRoles = flow(function* (
    this: GraphCommonStore,
    changes: object,
    node_id: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const newData: any = { ...changes };

      //Загрузка документов для аналитической роли
      if (newData.attach_docs_role?.length > 0) {
        let docIds = [];

        for (const doc of newData.attach_docs_role) {
          if (doc.stream) {
            try {
              yield uploadDoc(doc.file_name, doc.stream).then((response: any) => {
                docIds.push(response.document_id);
              });
            } catch (err) {
              this.GraphStore.onError(err);
            }
          } else {
            docIds.push(doc.document_id);
          }
        }

        newData.attach_docs_role = docIds;
      }

      //Загрузка документов для бизнес-роли
      if (newData.attach_docs_biz_role?.length > 0) {
        let docIds = [];
        for (const doc of newData.attach_docs_biz_role) {
          if (doc.stream) {
            try {
              yield uploadDoc(doc.file_name, doc.stream).then((response: any) => {
                docIds.push(response.document_id);
              });
            } catch (err) {
              this.GraphStore.onError(err);
            }
          } else {
            docIds.push(doc.document_id);
          }
        }
        newData.attach_docs_biz_role = docIds;
      }

      let new_node_id;

      yield postChangeRoles(graph_id, node_id, newData).then((res: any) => {
        new_node_id = res.node_id;
        this.GraphStore.updateGraphId(res.graph_id);
        this.GraphStore.fetchGraph(res.graph_id).then(() => {
          // Позиционирование к узлу, у которого изменяется роль
          let targetNode: go.Part | null | undefined;

          targetNode = this.GraphStore.DiagramStore.diagram
            ?.findNodesByExample({ key: res.node_id })
            .first()?.part;

          if (targetNode) {
            this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
          }
        });
      });

      this.GraphStore.state = 'done';
      return new_node_id;
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает GET запрос на раскрытие схемы назад
   *
   * @param {string} node_id - идентификатор узла
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound schemeBack = flow(function* (
    this: GraphCommonStore,
    node_id: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';
    let nodeId = node_id;
    try {
      yield getSchemeBack(graph_id, node_id).then((res) => {
        this.GraphStore.updateGraphId(res.GraphId);
        this.GraphStore.fetchGraph(res.GraphId).then(() => {
          // Позиционирование к узлу
          let targetNode: go.Part | null | undefined;

          targetNode = this.GraphStore.DiagramStore.diagram
            ?.findNodesByExample({ key: nodeId.toString() })
            .first()?.part;

          if (targetNode) {
            this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
            targetNode.isSelected = true;
          }
        });
      });
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод делает GET запрос на раскрытие схемы вперёд
   *
   * @param {string} node_id - идентификатор узла
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound schemeForward = flow(function* (
    this: GraphCommonStore,
    node_id: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';
    let nodeId = node_id;

    try {
      yield getSchemeForward(graph_id, node_id).then((res) => {
        this.GraphStore.updateGraphId(res.GraphId);
        this.GraphStore.fetchGraph(res.GraphId).then(() => {
          // Позиционирование к узлу
          let targetNode: go.Part | null | undefined;

          targetNode = this.GraphStore.DiagramStore.diagram
            ?.findNodesByExample({ key: nodeId.toString() })
            .first()?.part;

          if (targetNode) {
            this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
            targetNode.isSelected = true;
          }
        });
      });
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод по сохранению копии графа
   *
   * @param {string} node_id - идентификатор узла
   * @param {string} name - наименование для сохранённой копии графа
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound copyGraph = flow(function* (
    this: GraphCommonStore,
    name: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.state = 'pending';

    try {
      yield postCopyGraph(graph_id, { name });
      yield this.GraphStore.fetchGraph(graph_id);
      yield this.GraphStore.RootStore.UserGraphsStore.fetchUserGraphs();

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод по получению документа
   *
   * @param {string} document_id - идентификатор документа
   * @param {string} filename - имя документа
   */

  @action.bound getUploadedDocument = flow(function* (
    this: GraphCommonStore,
    document_id: string,
    filename: string,
  ) {
    if (!document_id) return;

    this.GraphStore.state = 'pending';

    try {
      yield downloadDoc(document_id)
        .then((response) => new Blob([response]))
        .then((blob) => {
          let url = window.URL.createObjectURL(blob);
          let a = document.createElement('a');
          a.href = url;
          a.download = filename;
          document.body.appendChild(a);
          a.click();
          a.remove();
        });

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод по получению Проекта акта
   *
   * @param {string} docTitle - наименование документа
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound getFormalReference = flow(function* (
    this: GraphCommonStore,
    docTitle: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    yield fetch(API_URL_CERT + '/formalizedCert/graph/' + graph_id, {
      method: 'GET',
    }).then((response) => {
      if (response.status !== 406 && response.status !== 401)
        response.blob().then((blob) => {
          let url = window.URL.createObjectURL(blob);
          let a = document.createElement('a');
          a.href = url;
          a.download = docTitle;
          document.body.appendChild(a);
          a.click();
          a.remove();
        });
      else
        response.text().then((error) =>
          this.GraphStore.onError({
            name: 'error',
            message: error,
          }),
        );
      this.GraphStore.stateDone();
    });
  });

  /** Перейти в ДНП */
  @action.bound
  goToDnpByFid = flow(function* (this: GraphCommonStore, fid: string, subject_type: string) {
    this.GraphStore.state = 'pending';
    try {
      yield getGoToDnpByFid(fid, subject_type);
    } catch (err) {
      this.GraphStore.onError(err);
    }

    this.GraphStore.state = 'done';
  });

  /** Метод запроса графа при инициализации */
  @action.bound
  fetchInitialGraph = async () => {
    try {
      const windowLocationHref = window.location.href;
      const urlGraphId = readParamsFromURL(windowLocationHref).graph_id || null;
      const urlUploadGraphImage = getForScreenGraphURL(windowLocationHref) || null;
      const urlCreateAskNds = getForCreateAskNds(windowLocationHref) || null;
      const urlCreateITAIS = getForCreateITAIS(windowLocationHref) || null;

      // Установка фильтров по индикаторам для перехода из ссылки
      this.GraphStore?.RootStore.fetchGraphIndicators();

      if (urlUploadGraphImage) {
        await this.GraphStore.fetchScheme(urlUploadGraphImage as string);

        let schemeString = urlUploadGraphImage ? urlUploadGraphImage.toString() : '';

        const diagram = document.querySelector('.diagram-component') as HTMLElement;

        // позиция и зум графа перед скрином
        const oldPositionX = this.GraphStore.DiagramStore.diagram?.position.x;
        const oldPositionY = this.GraphStore.DiagramStore.diagram?.position.y;
        const oldScale = this.GraphStore.DiagramStore.diagram?.scale;

        // размеры для контейнера, равные размеру графа
        diagram.style.height = this.GraphStore.DiagramStore.diagram?.documentBounds.height + 'px';
        diagram.style.width = this.GraphStore.DiagramStore.diagram?.documentBounds.width + 'px';

        this.GraphStore.DiagramStore.diagram?.requestUpdate();

        setTimeout(() => {
          // позиция для графа в левый верхний угол и зум 1
          let position =
            this.GraphStore.DiagramStore.direction === 0
              ? new go.Point(-60, -35)
              : new go.Point(-35, -35);
          let scale = 1;
          this.GraphStore.DiagramStore.setPositionScale(position, scale);
        }, 1);

        setTimeout(() => {
          // скрин всего контейнера
          domtoimage
            .toPng(diagram, {
              width: this.GraphStore.DiagramStore.diagram?.documentBounds.width,
              height: this.GraphStore.DiagramStore.diagram?.documentBounds.height,
              style: {
                left: 0,
                top: 0,
              },
            })
            .then(async (dataUrl: any) => {
              // ширина и высота для контейнера к 100%
              diagram.style.height = '';
              diagram.style.width = '';
              this.GraphStore.DiagramStore.diagram?.requestUpdate();

              setTimeout(() => {
                // позиция графа, которая была до скрина
                let position = new go.Point(oldPositionX, oldPositionY);
                let scale = oldScale;
                this.GraphStore.DiagramStore.setPositionScale(position, scale);
              }, 5000);

              dataUrl = dataUrl.substr(22);

              await this.getUploadGraphImage(dataUrl, schemeString);
            })
            .catch((error: any) => {
              setTimeout(() => {
                // позиция графа, которая была до скрина
                let position = new go.Point(oldPositionX, oldPositionY);
                let scale = oldScale;
                this.GraphStore.DiagramStore.setPositionScale(position, scale);
              }, 5000);
              console.log(error);
            });
        }, 5000);
      } else {
        if (urlCreateITAIS) {
          await this.GraphStore.fetchDiagram(urlCreateITAIS as string, null, false, 'itais');
        } else if (urlCreateAskNds) {
          await this.GraphStore?.createSchemeAskNds(urlCreateAskNds);
        } else if (urlGraphId) {
          if (urlGraphId.toString().includes('-')) {
            await this.GraphStore.fetchDiagram(urlGraphId as string);
          } else {
            await this.GraphStore.fetchScheme(urlGraphId as string);
          }
          if (this.GraphStore.showFilters) {
            await this.GraphStore?.RootStore.fetchGraphLayers();
          }
        } else {
          if (this.GraphStore.showFreeSearch) {
            this.GraphStore?.RootStore.CommonStore.openBeginningWork();
          }
        }
      }
    } catch (err) {}
  };

  /**
   * Метод по свободному поиску
   */
  @action freeSearch = async () => {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = await postCreateGraph();
      await this.GraphStore.fetchGraph(GraphId);
      if (this.GraphStore.showFilters) {
        await this.GraphStore.RootStore.fetchGraphLayers();
      }
      action(() => {
        this.GraphStore.state = 'done';
      });
    } catch (err) {
      this.GraphStore.onError(err);
    }
  };

  /**
   * Метод создания графа для начала работы
   */
  @action.bound
  beginingWork = flow(function* (this: GraphCommonStore, params: any) {
    this.GraphStore.statePending();
    try {
      const GraphID = yield postCreateDifferentGraph(params);
      const GraphId = GraphID.GraphId;

      this.GraphStore.RootStore.CommonStore?.setIsShowBeginningWork();
      this.GraphStore.RootStore.CommonStore?.resetNodesOrder();
      this.GraphStore.RootStore.BeginningWorkStore?.resetNpSearch();
      this.GraphStore.RootStore.BeginningWorkStore?.changeTabIndex(0);

      if (this.GraphStore.previouslyFetchedGraphId === '0') {
        yield this.GraphStore.fetchGraph(GraphId);
      } else {
        // @ts-ignore
        if (typeof window.CefSharp === 'object') {
          this.GraphStore.statePending();
          // @ts-ignore
          yield CefSharp.BindObjectAsync('unifiedClientIntegration');
          // @ts-ignore
          const resultInfo = yield unifiedClientIntegration.executeFunction(
            'Vsa2Domain',
            'VsaGetGraphByGraphId',
            JSON.stringify({
              graph_id: GraphId,
            }),
          );
          if (resultInfo) {
            this.GraphStore.stateDone();
          } else {
            this.onError({ name: 'Error', message: 'Невозможно построить схему' });
            this.GraphStore.stateDone();
          }
        } else {
          window.open(`/inias/csc/gws/static/VSAWebIntegration/?graph_id=${GraphId}`);
        }
      }

      if (this.GraphStore.showFilters) {
        yield this.GraphStore.RootStore.fetchGraphLayers();
      }

      this.GraphStore.stateDone();
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /** Метод получения детальной информации кластера */
  @action.bound
  fetchDetailedCluster = flow(function* (
    this: GraphCommonStore,
    clusterId: string,
    graphId = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const result = yield getDetailedCluster(graphId, clusterId);

      this.GraphStore.detailedData = {
        ...this.GraphStore.detailedData,
        cluster: {
          ...result,
        },
      };
      this.GraphStore.clusterFilter(result);
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод по получению Отчёта "Дерево связей"
   *
   * @param {string} image - изображение графа
   * @param {string} docTitle - название отчёта
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound getTreeReport = flow(function* (
    this: GraphCommonStore,
    image: any,
    docTitle: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    yield fetch(API_URL_CERT + '/relationshipTree/graph/' + graph_id, {
      method: 'POST',
      body: JSON.stringify({ data: image }),
      headers: {
        'Content-Type': 'image/png',
        'X-Request-ID': uuidv4(),
      },
    }).then((response) => {
      if (response.status !== 406 && response.status !== 401)
        response.blob().then((blob) => {
          let url = window.URL.createObjectURL(blob);
          let a = document.createElement('a');
          a.href = url;
          a.download = docTitle;
          document.body.appendChild(a);
          a.click();
          a.remove();
        });
      else
        response.text().then((error) =>
          this.GraphStore.onError({
            name: 'error',
            message: error,
          }),
        );
      this.GraphStore.stateDone();
    });
  });

  /**
   * Метод по генерации схемы в формате pdf ("Напечатать схему")
   *
   * @param {string} image - изображение графа
   * @param {string} docTitle - название отчёта
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound getPrintScheme = flow(function* (
    this: GraphCommonStore,
    image: any,
    docTitle: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    yield fetch(API_URL_CERT + '/graphPicture/graph/' + graph_id, {
      method: 'POST',
      body: JSON.stringify({ data: image }),
      headers: {
        'Content-Type': 'image/png',
        'X-Request-ID': uuidv4(),
      },
    }).then((response) => {
      if (response.status !== 406 && response.status !== 401)
        response.blob().then((blob) => {
          let url = window.URL.createObjectURL(blob);
          let a = document.createElement('a');
          a.href = url;
          a.download = docTitle;
          document.body.appendChild(a);
          a.click();
          a.remove();
        });
      else
        response.text().then((error) =>
          this.GraphStore.onError({
            name: 'error',
            message: error,
          }),
        );
      this.GraphStore.stateDone();
    });
  });

  /**
   * Метод делает GET запрос на плательщиков/получателей АБД
   *
   * @param {string} node_id - идентификатор узла
   * @param {string} graph_id - идентификатор графа
   * @param {string} payersRecipients - плателищик/получатель
   */
  @action.bound payersRecipients = flow(function* (
    this: GraphCommonStore,
    node_id: string,
    payersRecipients: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';
    let nodeId = node_id;

    try {
      yield getPayersRecipients(graph_id, node_id, payersRecipients).then((res) => {
        this.GraphStore.updateGraphId(res.GraphId);
        this.GraphStore.fetchGraph(res.GraphId).then(() => {
          // Позиционирование к узлу
          let targetNode: go.Part | null | undefined;

          targetNode = this.GraphStore.DiagramStore.diagram
            ?.findNodesByExample({ key: nodeId.toString() })
            .first()?.part;

          if (targetNode) {
            this.GraphStore.DiagramStore.diagram?.commandHandler.scrollToPart(targetNode);
            targetNode.isSelected = true;
          }
        });
      });
      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /**
   * Метод по получению Табличного представления кластера
   *
   * @param {string} origID - идентификатор кластера
   * @param {string} graph_id - идентификатор графа
   */
  @action.bound getExportCluster = flow(function* (
    this: GraphCommonStore,
    origID: string,
    docTitle: string,
    graph_id = this.GraphStore.previouslyGraphId,
  ) {
    this.GraphStore.state = 'pending';

    yield fetch(API_URL_CERT + '/clusterTableView/graph/' + graph_id + '/cluster/' + origID, {
      method: 'GET',
    })
      .then((response) => response.blob())
      .then((blob) => {
        let url = window.URL.createObjectURL(blob);
        let a = document.createElement('a');
        a.href = url;
        a.download = docTitle;
        document.body.appendChild(a);
        a.click();
        a.remove();
        this.GraphStore.stateDone();
      });
  });

  /**
   * Метод по получению скрина графа
   *
   * @param {string} image - изображение графа
   * @param {string} scheme_id - идентификатор схемы
   */
  @action.bound getUploadGraphImage = flow(function* (
    this: GraphCommonStore,
    image: any,
    scheme_id: any,
  ) {
    this.GraphStore.state = 'pending';

    yield fetch(API_ATTACH + '/uploadGraphImage/' + scheme_id, {
      method: 'POST',
      body: JSON.stringify({ data: image }),
      headers: {
        'Content-Type': 'image/png',
        'X-Request-ID': uuidv4(),
      },
    }).then((response) => {
      if (response.status === 406)
        response.text().then((error) =>
          this.GraphStore.onError({
            name: 'error',
            message: error,
          }),
        );
      this.GraphStore.stateDone();
    });
  });

  /** Метод по сохранению потенциального ущерба */
  @action.bound
  fetchPotentialDamage = flow(function* (
    this: GraphCommonStore,
    potentialDamage: any,
    nodeId: any,
    graphId = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      const { GraphId } = yield postPotentialDamage(graphId, nodeId, potentialDamage);

      this.GraphStore.updateGraphId(GraphId);
      this.GraphStore.updateGraph();

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });

  /** Метод получения всех НП в кластере */
  @action.bound
  fetchNPCluster = flow(function* (
    this: GraphCommonStore,
    clusterId: string,
    graphId = this.GraphStore.previouslyFetchedGraphId,
  ) {
    this.GraphStore.state = 'pending';

    try {
      this.GraphStore.NPCluster = yield getNPCluster(graphId, clusterId);

      this.GraphStore.state = 'done';
    } catch (err) {
      this.GraphStore.onError(err);
    }
  });
}
