import React from 'react';

import { IPanelGroupProps } from './PanelGroup.props';

import styles from './PanelGroup.module.scss';

const PanelGroup = ({ className, children, ...props }: IPanelGroupProps) => {
  return (
    <div className={`${className} ${styles.group}`} {...props}>
      {children}
    </div>
  );
};

export { PanelGroup };
