import React, { MouseEventHandler } from 'react';
import { dropdownPropTypes } from './Dropdown.props';
import styles from './Dropdown.module.scss';

import Icon from '../Icon';
import ReactTooltip from 'react-tooltip';

import { CrossIcon } from '@fdc/vsa-react-lib';

import { CommonStoreType } from 'stores/common/commonStore';

import { ReactComponent as CheckboxAll } from './assets/checkboxAll.svg';
import { ReactComponent as CheckboxSome } from './assets/checkboxSome.svg';
import { ReactComponent as CheckboxNone } from './assets/checkboxNone.svg';
import { ReactComponent as CheckboxAllDark } from './assets/checkboxAllDark.svg';
import { ReactComponent as CheckboxSomeDark } from './assets/checkboxSomeDark.svg';
import { ReactComponent as CheckboxNoneDark } from './assets/checkboxNoneDark.svg';
import Search from '../Search';
import { inject, observer } from 'mobx-react';

interface DropdownProps {
  items?: string[];
  itemsArray?: any;
  onSelect: (item: string) => void;
  onSearch?: (search: string) => void;
  onChangeOptionsVisible?: (value?: boolean) => void;

  label?: any;
  value?: string;
  placeholder?: string;
  disabled?: boolean;

  dropdownStyle?: string;
  labelStyle?: string;
  containerStyle?: string;
  mainContainerStyle?: string;
  valueStyle?: string;
  iconStyle?: string;
  iconContainerStyle?: string;
  listStyle?: string;
  itemStyle?: string;
  listWrapper?: string;

  isPeriod?: boolean;
  title?: string;

  isToolTip?: boolean;
  toolTipID?: string;
  toolTipText?: string;

  isAll?: boolean;
  isCheckbox?: boolean;
  checked?: string[];
  checkedMap?: any;
  isCheckedSum?: boolean;
  isNewStyle?: boolean;

  showOnlySelected?: boolean;

  isArray?: boolean;

  onLiveSearch?: (value: string) => void;
  liveSearchValue?: string;

  [key: string]: any;

  commonStore?: CommonStoreType;
}

interface DropdownState {
  isOpen: boolean;
  isCheckAll: boolean;
}

/**
 * Компонент выпадающего меню
 *
 * @param { string[] } items - массив элементов выпадающего меню
 * @param {func} onSelect - функция вызывающяяся при выборе элемента
 * @param {string} [value] - выбранный элемент
 * @param {string} [lable] - подпись к выпадающеиму меню
 * @param {boolean} [disabled] - флаг показывающий состояние меню
 * @param {boolean} isToolTip - наличие всплывающей подсказки
 * @param {string} toolTipText - текст всплывающей подсказки
 * @param {string} toolTipID - id всплывающей подсказки
 */
@inject('commonStore')
@observer
class Dropdown extends React.PureComponent<DropdownProps, DropdownState> {
  static propTypes = dropdownPropTypes;

  static defaultProps = {
    value: '',
    disabled: false,
    label: '',
  };

  state = { isOpen: false, isCheckAll: false };

  dropdownRef: any = React.createRef();
  inputRef: any = React.createRef();
  sumRef: any = React.createRef();
  openingTimeout: any = null;

  /**
   * Метод устанавливает обработчик события при появлении компонента
   */
  componentDidMount() {
    this.openingTimeout = setTimeout(() => {
      window.addEventListener('click', this._onAwayClick);
    }, 100);
  }

  /**
   * Метод удаляет обработчик события при удалении компонента со страницы
   */
  componentWillUnmount() {
    clearTimeout(this.openingTimeout);
    window.removeEventListener('click', this._onAwayClick);
  }

  /**
   * Метод обрабатывает события клика за пределами выпадающего меню
   */
  _onAwayClick = (event: any): void => {
    if (event?.target?.parentNode?.parentNode && !this.dropdownRef.current.contains(event.target)) {
      this._handleClose();
    }
  };

  /**
   * Метод обрабатывает открытие выпадающего меню
   */
  _handleOpen = (): void => {
    const {
      state: { isOpen },
    } = this;

    if (isOpen) {
      return;
    }

    this.setState({
      isOpen: true,
    });
  };

  /**
   * Метод обрабатывает закрытие выпадающего меню
   */
  _handleClose = (e?: any): void => {
    const { isOpen } = this.state;
    const { onChangeOptionsVisible } = this.props;

    if (
      isOpen &&
      (!e ||
        (!this?.inputRef?.current?.contains(e.target) &&
          !this?.sumRef?.current?.contains(e.target)))
    ) {
      this.setState({
        isOpen: false,
      });

      onChangeOptionsVisible && onChangeOptionsVisible(false);
    }
  };

  /**
   * Метод обрабатывает событие выбора
   */
  _onSelect = (value: string) => {
    const { onSelect, isAll } = this.props;
    onSelect(value);
    if (value === 'All') {
      this.setState({
        isCheckAll: true,
      });
    } else {
      this.setState({
        isCheckAll: false,
      });
    }

    if (!isAll) {
      this._handleClose();
    }
  };

  /**
   * Метод обнуляет значение поиска
   */
  _onReset: MouseEventHandler<SVGSVGElement | HTMLSpanElement> = (e) => {
    e.stopPropagation();

    const { onSelect } = this.props;
    onSelect('RESET_ACTION');
  };

  /**
   * Метод по выбору всех значений
   */
  _onSelectAll = (): void => {
    const { onSelect } = this.props;

    onSelect('ALL_CHECK_ACTION');
  };

  /**
   * Метод отрисовывает элементы выпадающего меню
   */
  _renderItems = () => {
    const {
      items,
      itemStyle,
      isAll,
      itemsArray,
      value,
      checked,
      isNewStyle,
      isArray,
      commonStore,
    } = this.props;
    const { isCheckAll } = this.state;

    return (
      <>
        {isAll ? (
          <>
            {isNewStyle ? (
              <div className={`${styles.itemAllContainer} ${itemStyle}`}>
                <span className={styles.itemAllLabel} onClick={this._onSelectAll}>
                  {checked?.length === itemsArray?.length ? (
                    commonStore?.appTheme === 'dark' ? (
                      <CheckboxAllDark className={styles.checkboxIconItem} />
                    ) : (
                      <CheckboxAll className={styles.checkboxIconItem} />
                    )
                  ) : checked?.length ? (
                    commonStore?.appTheme === 'dark' ? (
                      <CheckboxSomeDark className={styles.checkboxIcon} />
                    ) : (
                      <CheckboxSome className={styles.checkboxIcon} />
                    )
                  ) : commonStore?.appTheme === 'dark' ? (
                    <CheckboxNoneDark className={styles.checkboxIconItem} />
                  ) : (
                    <CheckboxNone className={styles.checkboxIconItem} />
                  )}
                  <span>Выбрать все</span>
                </span>
                <span className={styles.clearLabel} onClick={this._onReset}>
                  Очистить
                </span>
              </div>
            ) : (
              <li
                className={`${styles.itemAll} ${itemStyle}`}
                onClick={() => this._onSelect('All')}
              >
                Выбрать все
              </li>
            )}
            <hr className={styles.line} />
            <div className={styles.containerItems}>
              {itemsArray?.map((item: any) => (
                <div
                  key={item.value}
                  className={styles.containerItem}
                  onClick={() => this._onSelect(item.value)}
                >
                  {checked?.includes(item.value) || isCheckAll ? (
                    commonStore?.appTheme === 'dark' ? (
                      <CheckboxAllDark className={styles.checkboxIconItem} />
                    ) : (
                      <CheckboxAll className={styles.checkboxIconItem} />
                    )
                  ) : commonStore?.appTheme === 'dark' ? (
                    <CheckboxNoneDark className={styles.checkboxIconItem} />
                  ) : (
                    <CheckboxNone className={styles.checkboxIconItem} />
                  )}
                  <li className={`${styles.item} ${itemStyle}`}>{item.name}</li>
                </div>
              ))}
            </div>
          </>
        ) : isArray ? (
          itemsArray?.map((item: any) => (
            <div
              key={item.value}
              className={styles.containerItem}
              onClick={() => this._onSelect(item)}
            >
              <li className={`${styles.item} ${itemStyle}`}>{item.name}</li>
            </div>
          ))
        ) : (
          items?.map((item: string, index: number) => (
            <div className={styles.containerItem} onClick={() => this._onSelect(item)}>
              <li key={'dropdown-item-' + index} className={`${styles.item} ${itemStyle}`}>
                {item}
              </li>
            </div>
          ))
        )}
      </>
    );
  };

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      props: {
        commonStore,
        value,
        placeholder,
        label,
        dropdownStyle,
        labelStyle,
        containerStyle,
        valueStyle,
        iconContainerStyle,
        listWrapper,
        iconStyle,
        listStyle,
        isPeriod,
        disabled,
        theme,
        title,
        isToolTip,
        toolTipID,
        toolTipText,
        checked,
        isAll,
        checkedMap,
        itemsArray,
        isCheckedSum,
        searchValue,
        onSearch,
        showOnlySelected,
        onChangeOptionsVisible,
        onLiveSearch,
        isNewStyle,
        liveSearchValue = '',
        mainContainerStyle,
      },
      state: { isOpen },
    } = this;

    return (
      <>
        <div
          className={`${
            mainContainerStyle || (isPeriod ? styles.periodContainer : styles.mainContainer)
          }`}
        >
          {label && <span className={`${labelStyle || styles.label}`}>{label}</span>}
          <div className={`${dropdownStyle || styles.dropdown}`} ref={this.dropdownRef}>
            <div
              className={`${disabled && styles.disabled} ${containerStyle || ''} ${
                styles.container
              }`}
              onClick={(e) => {
                return !disabled ? (isOpen ? this._handleClose(e) : this._handleOpen()) : false;
              }}
              title={title}
              data-tip={isToolTip ? 'React-tooltip' : ''}
              data-for={isToolTip ? toolTipID : null}
            >
              {!isAll ? (
                <span
                  className={`${
                    disabled && isPeriod ? styles.valueDisabled : valueStyle || styles.value
                  } ${!value && styles.placeholder}`}
                >
                  {value || placeholder}
                </span>
              ) : !isCheckedSum ? (
                <div>
                  {checked?.length === itemsArray?.length ? (
                    <span
                      className={`${
                        disabled && isPeriod ? styles.valueDisabled : valueStyle || styles.value
                      }`}
                    >
                      {' '}
                      Все
                    </span>
                  ) : (
                    checked?.map((item: any) => (
                      <span
                        className={`${
                          disabled && isPeriod ? styles.valueDisabled : valueStyle || styles.value
                        }`}
                      >
                        {' '}
                        {checkedMap.get(item)}
                      </span>
                    ))
                  )}
                </div>
              ) : (
                <div className={styles.inputContainer}>
                  <input
                    className={styles.input}
                    type="text"
                    onChange={(e) => onSearch && onSearch(e.target.value)}
                    value={searchValue}
                    placeholder={'Выберите значения'}
                    ref={this.inputRef}
                  />
                  {checked?.length !== 0 && (
                    <div
                      className={`${styles.checkedSum} ${
                        showOnlySelected && styles.checkedSumActive
                      }`}
                      onClick={() => onChangeOptionsVisible && onChangeOptionsVisible()}
                      ref={this.sumRef}
                    >
                      <span className={styles.checkedSumLabel}>{checked?.length}</span>
                      <CrossIcon className={styles.reset} onClick={this._onReset} />
                    </div>
                  )}
                </div>
              )}
              <div>
                {isOpen ? (
                  <div className={`${iconContainerStyle || styles.iconContainer}`}>
                    <Icon name="arrow_up" className={`${iconStyle || styles.icon}`} theme={theme} />
                  </div>
                ) : (
                  <div className={`${iconContainerStyle || styles.iconContainer}`}>
                    <Icon
                      name="arrow_down"
                      className={`${iconStyle || styles.icon}`}
                      theme={theme}
                    />
                  </div>
                )}
              </div>
            </div>
            {isOpen && (
              <div className={`${listWrapper || styles.listWrapper}`}>
                {onLiveSearch && (
                  <div className={styles.search}>
                    <Search
                      wrapperStyle={styles.searchInput}
                      onChange={(value) => onLiveSearch(value)}
                      value={liveSearchValue}
                      withSearchIcon
                      placeholder={'Найти значения'}
                    />
                  </div>
                )}
                <div
                  className={`${listStyle || styles.list} ${isNewStyle && styles.withoutScroll}`}
                >
                  {this._renderItems()}
                </div>
              </div>
            )}
          </div>
        </div>
        {isToolTip ? (
          <ReactTooltip place="top" type="dark" effect="float" id={toolTipID} delayShow={200}>
            <span className={styles.spanTooltip}>{toolTipText}</span>
          </ReactTooltip>
        ) : (
          ''
        )}
      </>
    );
  }
}

export default Dropdown;
