import React, { PureComponent } from 'react';
import { resultNDPropTypes } from './ResultND.props';
import styles from './ResultND.module.scss';
import filterStyles from '../common/Filter.module.scss';

import Icon from '../../../components/Icon';

import Option from '../Option';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';

import { ReactComponent as DeclarationPayment } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declarationPayment.svg';
import { ReactComponent as DeclarationPaymentDark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declarationPaymentDark.svg';
import { ReactComponent as DeclarationReturn } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declarationReturn.svg';
import { ReactComponent as DeclarationReturnDark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declarationReturnDark.svg';
import { ReactComponent as Declaration0 } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declaration0.svg';
import { ReactComponent as Declaration0Dark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declaration0Dark.svg';
import { ReactComponent as DeclarationNull } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declarationNull.svg';
import { ReactComponent as DeclarationNullDark } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/declarationNullDark.svg';
import { GraphStoreType } from '../../../stores/graph/graphStore';

interface ResultNDProps {
  isActive: boolean;
  onToggleCheck: () => void;
  resultNDFilter?: any;
  isOpen?: boolean;
  toggleOpenState?: () => void;

  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;

  search?: any;
  isOpenSearch?: boolean;
}

/**
 * Компонент блока Результат по НД для фильтров Индикаторов
 */
@inject('resultNDFilter', 'commonStore', 'graphStore')
@observer
class ResultND extends PureComponent<ResultNDProps> {
  static propTypes = resultNDPropTypes;

  render() {
    const {
      isOpen,
      toggleOpenState,
      resultNDFilter,
      commonStore,
      search,
      graphStore,
      isOpenSearch,
    } = this.props;

    return (
      <li className={`${styles.wrapper} ${filterStyles.wrapper}`}>
        <div className={`${styles.header} ${filterStyles.header}`}>
          <span
            className={`${styles.headerLabel} ${filterStyles.headerLabel}`}
            onClick={toggleOpenState}
          >
            Результат по НД
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {(isOpen || isOpenSearch) && (
          <div className={styles.container}>
            {'Декларация к уплате'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.option}
                  isActive={resultNDFilter.options?.DeclState_1}
                  onToggleCheck={() => resultNDFilter.toggleIndicator('DeclState_1')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          resultNDFilter.options?.DeclState_1 ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'DeclState_1' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount.DeclState_1
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <DeclarationPayment className={styles.icon} />
                      ) : (
                        <DeclarationPaymentDark className={styles.icon} />
                      )}
                      <span>Декларация к уплате</span>
                    </div>
                  }
                />
              </div>
            )}
            {'Декларация к возмещению'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.option}
                  isActive={resultNDFilter.options?.DeclState_2}
                  onToggleCheck={() => resultNDFilter.toggleIndicator('DeclState_2')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          resultNDFilter.options?.DeclState_2 ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'DeclState_2' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount.DeclState_2
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <DeclarationReturn className={styles.icon} />
                      ) : (
                        <DeclarationReturnDark className={styles.icon} />
                      )}
                      <span>Декларация к возмещению</span>
                    </div>
                  }
                />
              </div>
            )}
            {'Нулевая декларация'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.option}
                  isActive={resultNDFilter.options?.DeclState_3}
                  onToggleCheck={() => resultNDFilter.toggleIndicator('DeclState_3')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          resultNDFilter.options?.DeclState_3 ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'DeclState_3' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount.DeclState_3
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <Declaration0 className={styles.icon} />
                      ) : (
                        <Declaration0Dark className={styles.icon} />
                      )}
                      <span>Нулевая декларация</span>
                    </div>
                  }
                />
              </div>
            )}
            {'Декларация не представлена'.toLowerCase().includes(search) && (
              <div className={styles.optionContainer}>
                <Option
                  className={styles.option}
                  isActive={resultNDFilter.options?.DeclState_0}
                  onToggleCheck={() => resultNDFilter.toggleIndicator('DeclState_0')}
                  value={
                    <div className={styles.optionDiv}>
                      <span
                        className={
                          resultNDFilter.options?.DeclState_0 ? styles.countActive : styles.count
                        }
                      >
                        (
                        {'DeclState_0' in graphStore?.indicatorsCount
                          ? graphStore?.indicatorsCount.DeclState_0
                          : 0}
                        )
                      </span>
                      {commonStore?.appTheme === 'light' ? (
                        <DeclarationNull className={styles.icon} />
                      ) : (
                        <DeclarationNullDark className={styles.icon} />
                      )}
                      <span>Декларация не представлена</span>
                    </div>
                  }
                />
              </div>
            )}
          </div>
        )}
      </li>
    );
  }
}

export default ResultND;
