import ApiServices from './ApiServices';

import {
  SearchResponse,
  GraphResponse,
  IChainsRequest,
  IChainsResponse,
  IChainsJobResponse,
  ISearchListResponse,
  ICreateSchemeRequest,
  ICreateSchemeResponse,
  IGenerateNodesReportResponse,
  IGetNodesReportResponse,
} from 'ApiModel';

/**
 * Функция делает GET запрос для получения версии приложения
 */
export const getVersion = async (): Promise<GraphResponse> => {
  return await ApiServices.get<GraphResponse>(`/version`);
};

/**
 * Функция делает GET запрос на поиск узлов
 *
 * @param {string} text - текст запроса
 * @param {boolean} inScheme - искать внутри графа
 * @param {graphId} text - идентификатор графа
 *
 * @return {Promise} - узлы
 */
export const searchNodes = async (
  text: string,
  inScheme: boolean,
  graphId?: string,
): Promise<SearchResponse> => {
  return await ApiServices.get<SearchResponse>(
    `/search-np/${text}?inScheme=${inScheme}&graphId=${graphId}`,
  );
};

/**
 * Функция делает GET запрос для получения списка графов пользователя
 */
export const fetchUserGraphs = async (): Promise<GraphResponse> => {
  return await ApiServices.get<GraphResponse>(`/graphs`);
};

/**
 * Функция делает DELETE запрос на удаление графа
 *
 * @param {string} id - идентификатор графа
 */
export const deleteGraph = async (id: string): Promise<GraphResponse> => {
  return await ApiServices.delete<GraphResponse>(`/graph/${id}`);
};

/**
 * Функций делает POST запрос на изменения информации о графе
 *
 * @param {string} id - идентификатор графа
 * @param {object} graph - иноформация о графа
 */
export const editGraph = async (id: string, graph: any): Promise<GraphResponse> => {
  return await ApiServices.post<GraphResponse>(`/graph/${id}`, graph);
};

/**
 * Функций делает POST запрос на загрузку документа
 *
 * @param {string} filename - имя файла
 * @param {Stream} file - файл в виде потока байтов типа Stream
 */
export const uploadDoc = async (filename: string, file: File): Promise<GraphResponse> => {
  return await ApiServices.post<GraphResponse>(
    `/VSA2AttachDocumentApiV1/upload-doc/${filename}`,
    file,
    {
      'Content-Type': 'application/json',
    },
    true,
  );
};

/**
 * Функций делает GET запрос на получение документа
 *
 * @param {string} id - идентификатор документа
 */
export const downloadDoc = async (document_id: string): Promise<GraphResponse> => {
  return await ApiServices.get<BlobPart>(
    `/VSA2AttachDocumentApiV1/get-uploaded-doc/${document_id}`,
    true,
    'arraybuffer',
    {
      'Content-Type': 'application/octet-stream',
    },
  );
};

/**
 * Функция делает POST запрос на поиск НП в кластере
 *
 * @param {any} id - идентификатор графа
 * @param {any} origId - индентификатор кластера
 * @param {any} filter - строка поиска
 * @param {any} showNPWithLink - показать связи с выбранными НП
 */
export const searchNodeCluster = async (
  id: any,
  origId: any,
  filter: any,
  showNPWithLink: boolean,
): Promise<GraphResponse> => {
  return await ApiServices.post<GraphResponse>(`/graph/${id}/cluster/${origId}`, {
    taxPayers: filter,
    showOtherTaxPayers: !showNPWithLink,
  });
};

/**
 * Функция делает GET запрос для получения параметра, отвечающего за отображение кнопки "Передача изменений в Досье"
 */
export const fetchInternalParameters = async (): Promise<GraphResponse> => {
  return await ApiServices.get<GraphResponse>(`/VSA2DictionaryApiV1/internal-parameters`, true);
};

export const searchChains = async (request: IChainsRequest): Promise<IChainsResponse> => {
  return await ApiServices.post<IChainsResponse>(`/search-chains`, request);
};

export const checkChainsJob = async (
  jobId: number,
  offset: number,
  pageSize: number,
): Promise<IChainsJobResponse> => {
  return await ApiServices.get<IChainsJobResponse>(
    `/search-chains-result/${jobId}?pageSize=${pageSize}&offset=${offset}`,
    false,
    'json',
    {},
    false,
    false,
    false,
    true,
  );
};

export const fetchSearchList = async (): Promise<ISearchListResponse[]> => {
  return await ApiServices.get<ISearchListResponse[]>(`/VSA2BackgroundTasks/api/v1/tasks `, true);
};

export const createScheme = async (
  request: ICreateSchemeRequest,
): Promise<ICreateSchemeResponse> => {
  return await ApiServices.post<ICreateSchemeResponse>(`/graph/chains/create`, request);
};

export const generateNodesReport = async (
  graphId: string,
): Promise<IGenerateNodesReportResponse> => {
  return await ApiServices.get<IGenerateNodesReportResponse>(
    `/VSA2ReportsApiV1/graph/${graphId}/expanded-nodes/async`,
    true,
  );
};

export const getNodesReport = async (graphId: string): Promise<IGetNodesReportResponse> => {
  return await ApiServices.get<IGetNodesReportResponse>(
    `/VSA2ReportsApiV1/graph/${graphId}/expanded-nodes`,
    true,
  );
};
