import { TaxpayerType } from 'DataTypes';
import Button from 'components/Button';
import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';
import { CommonStoreType } from 'stores/common/commonStore';
import { GraphStoreType } from 'stores/graph/graphStore';
import mainStyles from '../BeginningWork.module.scss';
import { SearchNp } from '../SearchNp/SearchNp';
import styles from './FreeSearchTab.module.scss';

import Dropdown from '../../../Dropdown';

import Spinner from 'components/Spinner';
import BeginningWorkStore from '../../../../stores/modalData/beginningWorkStore';
import { parseCooperationDate } from '../../../../utils/parseCooperationDate';
import { Col, Row } from '../../../Grid';
import MoneySpreadPicker from '../../../MoneySpreadPicker';
import TimePeriodPicker from '../../../TimePeriodPicker';
import { QUARTERS, YEARS } from '../../../constants';
import SearchResult from '../SearchResult';

interface IProps {
  graphStore?: GraphStoreType;
  commonStore?: CommonStoreType;
  beginningWorkStore?: BeginningWorkStore;
  paramsDNP?: any;
}

interface IState {
  np?: TaxpayerType;
  expandType?: string;

  filterIsFill?: boolean;
}

/**
 * Компонент вкладка свободного поиска
 **
 */
@inject('graphStore', 'commonStore', 'beginningWorkStore')
@observer
export class FreeSearchTab extends Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);
  }

  closeFreeSearch = () => {
    const { commonStore } = this.props;

    commonStore?.setIsShowBeginningWork();
  };

  render() {
    const { commonStore, graphStore, beginningWorkStore } = this.props;

    const {
      npSearchState,
      npSearchResult,
      npSearchCount,
      npSearchWarning,
      selectedNp,
      filterIsOpen,
      options,
      handleSelectNp,
      changeFilterVisible,
      resetAdvancedSearch,
      changeIntegrals,
      changeBusinessRoles,
      changeOption,
      filterIsFill,
      resetFilter,
      handleSubmit,
      goToNextTab,
      changePeriodFrom,
      changePeriodTo,
    } = beginningWorkStore!;

    const { period_from, period_to } = options;

    const { quarterFrom, quarterTo, yearFrom, yearTo } = parseCooperationDate({
      nds3_period_from: period_from,
      nds3_period_to: period_to,
    });

    return (
      <>
        <div className={mainStyles.row}>
          <div className={mainStyles.container}>
            <SearchNp
              onSelectNp={handleSelectNp}
              onOpenFilter={changeFilterVisible}
              filterIsOpen={filterIsOpen}
              filterIsFill={filterIsFill}
            />

            {filterIsOpen && (
              <>
                {(npSearchState === 0 || npSearchState === 1) && (
                  <Spinner className={styles.spinner} text="Поиск результатов..." />
                )}

                {npSearchState === 2 && (
                  <SearchResult
                    onSelectNp={handleSelectNp}
                    onSearchReset={resetAdvancedSearch}
                    items={npSearchResult}
                    count={npSearchCount}
                    warning={npSearchWarning}
                  />
                )}

                {npSearchState === 3 && <div className={styles.warning}>{npSearchWarning}</div>}

                {npSearchState === -1 && (
                  <div className={styles.filterContainer}>
                    <Row className={`${styles.filterRow} ${styles.headerFilter}`}>
                      <span>Расширенный поиск</span>
                    </Row>

                    <Row className={styles.filterRow} aligment="end">
                      <Col size={12}>
                        <TimePeriodPicker
                          wrapperStyle={styles.picker}
                          fromQuarterValue={quarterFrom}
                          fromYearValue={yearFrom}
                          toQuarterValue={quarterTo}
                          toYearValue={yearTo}
                          onFromValueChange={changePeriodFrom}
                          onToValueChange={changePeriodTo}
                          quarters={QUARTERS}
                          years={YEARS}
                          label={'Период учета данных'}
                          theme={commonStore?.appTheme}
                          disabled={graphStore?.interQuarterSearch}
                          labelFrom={'С'}
                          labelTo={'по'}
                          placeholderFrom="Кв."
                          placeholderTo="Год"
                        />
                      </Col>
                    </Row>

                    <Row className={styles.filterRow} aligment="end">
                      <Col size={3}>
                        <Dropdown
                          dropdownStyle={styles.dropdown}
                          mainContainerStyle={styles.dropdownMainContainer}
                          itemsArray={commonStore?.integralsListSearchNP}
                          onSelect={changeIntegrals}
                          value={options['integrals']}
                          isArray
                          label={'СУР'}
                          theme={commonStore?.appTheme}
                          placeholder="Выберите значение"
                        />
                      </Col>

                      <Col size={9}>
                        <Dropdown
                          dropdownStyle={styles.dropdown}
                          mainContainerStyle={styles.dropdownMainContainer}
                          itemsArray={commonStore?.businessRolesListSearchNP}
                          onSelect={changeBusinessRoles}
                          value={options['businessRole']}
                          isArray
                          label={'Бизнес роль'}
                          theme={commonStore?.appTheme}
                          placeholder="Выберите значение"
                        />
                      </Col>
                    </Row>

                    <Row className={styles.filterRow} aligment="end">
                      <Col size={6}>
                        <MoneySpreadPicker
                          wrapperStyle={styles.picker}
                          fromValue={
                            options['vat_deduct_from']
                              ? Number(options['vat_deduct_from']).toLocaleString('ru')
                              : ''
                          }
                          toValue={
                            options['vat_deduct_to']
                              ? Number(options['vat_deduct_to']).toLocaleString('ru')
                              : ''
                          }
                          onChangeFromValue={(value) => changeOption(value, 'vat_deduct_from')}
                          onChangeToValue={(value) => changeOption(value, 'vat_deduct_to')}
                          label={'Сумма НДС к вычету, ₽'}
                          title={'Диапазон сумм НДС к вычету'}
                          inputStyle={styles.input}
                        />
                      </Col>

                      <Col size={6}>
                        <MoneySpreadPicker
                          wrapperStyle={styles.picker}
                          fromValue={
                            options['vat_calc_from']
                              ? Number(options['vat_calc_from']).toLocaleString('ru')
                              : ''
                          }
                          toValue={
                            options['vat_calc_to']
                              ? Number(options['vat_calc_to']).toLocaleString('ru')
                              : ''
                          }
                          onChangeFromValue={(value) => changeOption(value, 'vat_calc_from')}
                          onChangeToValue={(value) => changeOption(value, 'vat_calc_to')}
                          label={'Сумма НДС исчисленный, ₽'}
                          title={'Диапазон сумм НДС исчисленный'}
                          inputStyle={styles.input}
                        />
                      </Col>
                    </Row>

                    <Row className={styles.filterRow} aligment="end">
                      <Col size={4}>
                        <MoneySpreadPicker
                          wrapperStyle={styles.picker}
                          fromValue={
                            options['ook_total_from']
                              ? Number(options['ook_total_from']).toLocaleString('ru')
                              : ''
                          }
                          toValue={
                            options['ook_total_to']
                              ? Number(options['ook_total_to']).toLocaleString('ru')
                              : ''
                          }
                          onChangeFromValue={(value) => changeOption(value, 'ook_total_from')}
                          onChangeToValue={(value) => changeOption(value, 'ook_total_to')}
                          label={'Сумма ООК, ₽'}
                          title={'Диапазон сумм ООК'}
                          inputStyle={styles.input}
                        />
                      </Col>

                      <Col size={4}>
                        <MoneySpreadPicker
                          wrapperStyle={styles.picker}
                          fromValue={
                            options['emp_count_from']
                              ? Number(options['emp_count_from']).toLocaleString('ru')
                              : ''
                          }
                          toValue={
                            options['emp_count_to']
                              ? Number(options['emp_count_to']).toLocaleString('ru')
                              : ''
                          }
                          onChangeFromValue={(value) => changeOption(value, 'emp_count_from')}
                          onChangeToValue={(value) => changeOption(value, 'emp_count_to')}
                          label={'Среднесписочная численность'}
                          inputStyle={styles.input}
                        />
                      </Col>

                      <Col size={4}>
                        <MoneySpreadPicker
                          wrapperStyle={styles.picker}
                          fromValue={options['sectoral_rate_from']}
                          toValue={options['sectoral_rate_to']}
                          onChangeFromValue={(value) => changeOption(value, 'sectoral_rate_from')}
                          onChangeToValue={(value) => changeOption(value, 'sectoral_rate_to')}
                          label={'TG, %'}
                          inputStyle={styles.input}
                          isPercent
                        />
                      </Col>
                    </Row>

                    <Row className={`${styles.filterRow} ${styles.noteFilter}`}>
                      <span>
                        Результаты поиска могут содержать не более 100 строк. Для более точных
                        результатов уточните критерии поиска! <br />
                        Результаты поиска удаляются через 1 день после завершения поиска.
                      </span>
                    </Row>

                    <Row className={`${styles.filterRow} ${styles.filterButtons}`}>
                      <Button onClick={resetFilter} mode="transparent">
                        Сбросить все
                      </Button>

                      <Button onClick={handleSubmit}>Применить</Button>
                    </Row>
                  </div>
                )}
              </>
            )}
          </div>
        </div>

        <div
          className={`${mainStyles.row} ${styles.buttons} ${npSearchState === 2 && styles.sticky}`}
        >
          <div className={`${mainStyles.container} ${styles.buttons__container}`}>
            <Button onClick={this.closeFreeSearch} mode="transparent">
              Отменить
            </Button>

            <Button
              onClick={goToNextTab}
              disabled={!selectedNp || !graphStore?.showFreeSearch || !graphStore?.showCreateGraph}
              title={
                graphStore?.showFreeSearch && graphStore?.showCreateGraph
                  ? selectedNp
                    ? ''
                    : 'Выберите НП, используя кнопку "Найти"'
                  : 'Недоступно для данной роли'
              }
            >
              Перейти к настройкам графа
            </Button>
          </div>
        </div>
      </>
    );
  }
}
