import React, { useState } from 'react';
import { observer } from 'mobx-react';

import { useStores } from 'hooks';

import Link from '../Link';

import { IChainProps } from './Chain.props';
import { TaxpayerType } from 'DataTypes';

import { ReactComponent as ArrowIcon } from 'assets/icons/arrow.svg';
import { ReactComponent as SchemeIcon } from 'assets/icons/scheme.svg';

import styles from './Chain.module.scss';

const Taxpayer = ({ taxpayer }: { taxpayer: TaxpayerType }) => {
  const date = String(-(taxpayer?.date || 0))?.split('') || [];
  const quarter = date.pop() || '';
  const year = date.join('');

  return (
    <div className={styles.taxpayer}>
      <div className={styles.row}>
        <span className={styles.col}>
          <span className={styles.label}>ИНН:</span>
          <span className={styles.value}>{taxpayer?.inn || '-'}</span>
        </span>

        <span className={styles.col}>
          <span className={styles.label}>КПП:</span>
          <span className={styles.value}>{taxpayer?.kpp || '-'}</span>
        </span>
      </div>

      <div className={styles.row}>
        <span className={styles.col}>
          <span className={styles.label}>
            {quarter} кв. {year}
          </span>
        </span>
      </div>

      <div className={styles.row}>
        <span className={styles.col}>
          <span className={styles.value}>{taxpayer?.name || '-'}</span>
        </span>
      </div>
    </div>
  );
};

const Chain = ({ className, chain, type, ...props }: IChainProps) => {
  const {
    chainsStore: { goToScheme },
  } = useStores();

  const [opened, setOpened] = useState<boolean>(false);

  const { links } = chain;

  const vatCalculatedSumm = links.reduce((a, link) => a + (link.vat_calculated_total || 0), 0);
  const vatDeductionSumm = links.reduce((a, link) => a + (link.vat_deduction_total || 0), 0);
  const creditSumm = links.reduce((a, link) => a + (link.credit_sum || 0), 0);
  const debitSumm = links.reduce((a, link) => a + (link.debit_sum || 0), 0);
  const goodsSumm = links.reduce((a, link) => a + (link.goods_sum || 0), 0);
  const contractSumm = links.reduce((a, link) => a + (link.contract_sum || 0), 0);

  const seller = {
    inn: links[0].seller_inn || '-',
    kpp: links[0].seller_kpp || '-',
    name: links[0].seller_name || '-',
    date: links[0].year_quarter,
  };

  const buyer = {
    inn: links[links.length - 1].buyer_inn || '-',
    kpp: links[links.length - 1].buyer_kpp || '-',
    name: links[links.length - 1].buyer_name || '-',
    date: links[links.length - 1].year_quarter,
  };

  return (
    <div className={`${className} ${styles.chain} ${opened && styles.opened}`} {...props}>
      <div className={styles.header}>
        <div className={styles.arrow} onClick={() => setOpened(!opened)}>
          <ArrowIcon />
        </div>

        <div className={styles.info}>
          <div className={styles.number}>{chain.number}</div>
          <div className={styles.type}>{type}</div>
        </div>

        <div className={styles.taxpayers}>
          <Taxpayer taxpayer={seller} />
          <Taxpayer taxpayer={buyer} />
        </div>

        <div className={styles.action}>
          <SchemeIcon onClick={() => goToScheme(links)} />
        </div>
      </div>

      {opened && (
        <div className={styles.links}>
          {links.map((link, i) => (
            <Link
              key={`link${chain.number}${i}`}
              link={{
                ...link,
                vatCalculatedSumm,
                vatDeductionSumm,
                creditSumm,
                debitSumm,
                goodsSumm,
                contractSumm,
              }}
              number={i + 1}
            />
          ))}
        </div>
      )}
    </div>
  );
};

export default observer(Chain);
