import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import React, { useState } from 'react';
import copyToClipboard from 'utils/ui/copyToClipboard';

import { ENotificationCodes, INFO_CODES } from 'constants/notification';
import { IInfoProps } from './Info.props';

import Button from 'components/Button';
import { Modal, ModalBody, ModalFooter, ModalHeader } from 'components/modals/Modal';

import { ReactComponent as CopyIcon } from 'assets/icons/copy.svg';
import { ReactComponent as SearchIcon } from 'assets/icons/search.svg';

import styles from './Info.module.scss';

const Info = observer(({ className, note, onClose, ...props }: IInfoProps) => {
  const {
    commonStore: { appTheme },
  } = useStores();

  const [isCopied, setIsСopied] = useState(false);

  async function onCopyToClipboard() {
    await copyToClipboard(note.request_id || '');
    setIsСopied(true);
    setTimeout(() => setIsСopied(false), 2000);
  }

  return (
    <Modal className={`${styles.info} ${className}`} {...props}>
      <ModalHeader onClose={onClose}>
        <div className={styles.header}>
          {note.code && INFO_CODES[note.code] == ENotificationCodes.NO_RESULT && (
            <span className={styles.header__icon}>
              <SearchIcon />
            </span>
          )}

          <span>{note.message}</span>
        </div>
      </ModalHeader>

      <ModalBody withFooter={true}>
        <div className={styles.container}>
          <div className={styles.description}>{note.description}</div>

          {note.exec_time && (
            <div>
              Дата и время поиска: {new Date(note.exec_time)?.toLocaleString()?.replaceAll(',', '')}
            </div>
          )}

          <div className={styles.request}>
            ID запроса:
            <div className={styles.request__id}>
              {note.request_id}

              <div className={styles.copy}>
                {isCopied && (
                  <span className={styles.copy__info}>ID запроса скопирован в буфер обмена</span>
                )}
                <Button mode="white" size="small" onClick={onCopyToClipboard} isIcon>
                  <CopyIcon />
                </Button>
              </div>
            </div>
          </div>
        </div>
      </ModalBody>

      <ModalFooter>
        <div className={styles.footer}>
          <Button mode="secondary" onClick={onClose}>
            Закрыть
          </Button>
        </div>
      </ModalFooter>
    </Modal>
  );
});

export default Info;
