import React, { useState } from 'react';
import { userCardPropTypes } from './UserCard.props';
import styles from './UserCard.module.scss';
import { SCREEN_MODES } from '../../stores/constants/screenModes';
import ReactTooltip from 'react-tooltip';

export interface UserCardProps {
  /** Имя пользователя */
  name: string;
  /** Аватар пользователя */
  avatar?: string;
  /** CSS класс контейнера */
  className?: string;
  /** Наименование должности*/
  postName: string;
  /** Код подразделеения */
  subDivCode: string;
  /** Наименование подразделения */
  subDivName: string;
  /** Экран */
  screenMode: string;
}

/** Компонент карточки пользователя */
const UserCard = ({
  name,
  avatar,
  className,
  postName,
  subDivCode,
  subDivName,
  screenMode,
}: UserCardProps) => {
  const [isOpen, toggleOpen] = useState(false);

  let postNameLength = 0;
  let subDivNameLength = 0;

  if (screenMode === SCREEN_MODES.FULL.name) {
    postNameLength = 29;
    subDivNameLength = 29;
  } else if (screenMode === SCREEN_MODES.MIDDLE.name) {
    postNameLength = 25;
    subDivNameLength = 25;
  } else if (screenMode === SCREEN_MODES.MEDIUM.name) {
    postNameLength = 30;
    subDivNameLength = 30;
  }

  return (
    <div
      className={`${styles.wrapper} ${className}`}
      onClick={isOpen ? () => toggleOpen(false) : () => toggleOpen(true)}
    >
      <div className={styles.userName}>
        {Boolean(avatar) ? (
          <img className={styles.avatar} src={avatar} alt={'Изображение профиля'} />
        ) : (
          <div className={styles.avatar} />
        )}
        {postName?.length > postNameLength ||
        (subDivCode ? subDivCode.length : 0) + subDivName?.length + 3 > subDivNameLength ? (
          <>
            <span className={styles.name} data-for={'toolTipInfoUser'} data-tip={'React-tooltip'}>
              {name || 'Неизвестный пользователь'}
            </span>
            <ReactTooltip
              place="bottom"
              type="info"
              effect="float"
              id={'toolTipInfoUser'}
              delayShow={200}
            >
              <div className={styles.spanTooltip}>
                <span>{postName ? postName : ''}</span>
                <span>
                  {(subDivCode ? subDivCode + ' - ' : '') + (subDivName ? subDivName : '')}
                </span>
              </div>
            </ReactTooltip>
          </>
        ) : (
          <span className={styles.name}>{name || 'Неизвестный пользователь'}</span>
        )}
        {/* <Icon name={isOpen ? 'arrow_up' : 'arrow_down'} className={styles.arrow} /> */}
      </div>
      <span className={styles.postName}>
        {postName
          ? postName.length > postNameLength
            ? postName.slice(0, postNameLength) + '...'
            : postName
          : ''}
      </span>
      <span className={styles.postName}>
        {(subDivCode ? subDivCode + ' - ' : '') +
          (subDivName
            ? (subDivCode ? subDivCode.length : 0) + subDivName.length + 3 > subDivNameLength
              ? subDivName.slice(0, subDivNameLength) + '...'
              : subDivName
            : '')}
      </span>
    </div>
  );
};

UserCard.propTypes = userCardPropTypes;

UserCard.defaultProps = {
  name: 'Неизвестный пользователь',
  avatar: '',
  className: '',
  postName: '',
  subDivCode: '',
  subDivName: '',
};

export default UserCard;
