import React, { useEffect, useRef, useState } from 'react';
import { MenuItem, menuItemPropTypes } from '../../Menu.props';
import styles from './CollapsedMenuItem.module.scss';

import { KEY_CODES } from '../../../constants/keyCodes';

import ReactTooltip from 'react-tooltip';

interface CollapsedMenuItemProps extends MenuItem {}

const CollapsedMenuItem = ({
  label,
  iconComponent,
  activeIconComponent,
  isActive,
  callback,
  collapsedMenuExtendableComponent,
  collapsedClassName,
  collapsedIconClassName,
  collapsedExtendedContainerClassName,
  collapsedIconContainerClassName,
  title,
  isDisabled,
  separateNextWithLineCollapse,
  idExtendedComponent,
}: CollapsedMenuItemProps) => {
  /** Состояние активное состояние для иконки */
  const [isActiveState, toggleActive] = useState(isActive);
  /** Состояние открытости элемента */
  const [isOpen, toggleOpen] = useState(false);

  /** Ref-ссылка на элемент */
  const elementRef = React.createRef<HTMLDivElement>();
  const liRef = useRef<HTMLLIElement>(null);

  /**
   * Функция обрабатывает нажатие клавиш
   *
   * При нажатии ESC - закрывается расширенный компонент
   */
  const handleKeyDown = (e: KeyboardEvent) => {
    if (e.which === KEY_CODES.ESC || e.keyCode === KEY_CODES.ESC) {
      toggleOpen(false);
    }
    if (e.which === KEY_CODES.ENTER || e.keyCode === KEY_CODES.ENTER) {
      if (idExtendedComponent === 'ExtendedDamage') {
        toggleOpen(false);
      }
    }
  };

  /**
   * Функция обрабатывает нажатие мышки
   *
   * Закрывает развернутый компонент, если клик мышкой произошел не в его области
   */
  const handleMouseClick = (e: any) => {
    if (elementRef && !elementRef.current?.contains(e.target)) {
      toggleOpen(false);
    }

    if (idExtendedComponent === 'ExtendedDamage') {
      if (e.target.nodeName !== 'INPUT') {
        toggleOpen(false);
        callback?.();
      }
    }
  };

  /** Установка обработчиков событий */
  useEffect(() => {
    if (isOpen) {
      window.addEventListener('keydown', handleKeyDown);
      window.addEventListener('click', handleMouseClick);
    } else {
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('click', handleMouseClick);
    }

    return () => {
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('click', handleMouseClick);
    };
  });

  const Icon = iconComponent;
  const ActiveIcon = activeIconComponent;
  const ExtendedComponent = collapsedMenuExtendableComponent;

  /**
   * Функция обрабатывает клик по элементу
   *
   * Если элемент имеет расширенный компонент, то функция открывает его,
   * иначе вызывает переданный callback и устанавливает активное состояние элемента
   */
  const handleClick = () => {
    if (ExtendedComponent) {
      toggleOpen(true);
      callback?.();
    } else {
      callback?.();
      toggleActive(!isActiveState);
    }
  };

  return (
    <>
      <li
        ref={liRef}
        key={'collapsed-menu-item-' + label}
        className={`${styles.item} ${collapsedClassName} ${isDisabled ? `${styles.disabled}` : ''}`}
        onClick={(e) => {
          !isDisabled && handleClick();
        }}
      >
        <div
          className={`${styles.iconContainer} 
            ${collapsedIconContainerClassName}`}
          data-tip={'React-tooltip'}
          data-for={'collapsed-menu-item-tooltip-' + label}
        >
          {Icon && isActive ? (
            ActiveIcon ? (
              <ActiveIcon
                className={`${styles.icon} ${styles.activeIcon} ${collapsedIconClassName}`}
                stroke="white"
                color="white"
              />
            ) : (
              <Icon
                className={`${styles.icon} ${styles.activeIcon} ${collapsedIconClassName}`}
                stroke="white"
                color="white"
              />
            )
          ) : (
            Icon && <Icon className={`${styles.icon} ${collapsedIconClassName}`} />
          )}
          {title && (
            <ReactTooltip
              place="right"
              type="dark"
              effect="solid"
              id={'collapsed-menu-item-tooltip-' + label}
              delayShow={400}
              className={styles.reactTooltip}
            >
              {typeof title === 'string'
                ? title
                : Object.keys(title).map((item, i) => (
                    <li key={i} className={styles.liTooltip}>
                      <span>{title[item]}</span>
                    </li>
                  ))}
            </ReactTooltip>
          )}
        </div>
      </li>
      {ExtendedComponent && isOpen && (
        <div
          ref={elementRef}
          className={`${styles.extendedContainer} ${collapsedExtendedContainerClassName}`}
          style={{ top: (liRef.current?.getBoundingClientRect().top || 0) - 69 }}
        >
          {<ExtendedComponent />}
        </div>
      )}
      {separateNextWithLineCollapse && <li className={`${styles.line}`} />}
    </>
  );
};

CollapsedMenuItem.propTypes = menuItemPropTypes.isRequired;

CollapsedMenuItem.defaultProps = {
  activeIconComponent: null,
  hasActiveState: false,
  isActive: false,
  collapsedMenuExtendableComponent: null,
  iconComponent: null,
  collapsedExtendedContainerClassName: '',
  collapsedIconContainerClassName: '',
};

export default CollapsedMenuItem;
