import { computed, observable, autorun, action } from 'mobx';
import Filter from '../utils/Filter';

import { FilterStoreType } from './filterStore';

/**
 * Хранилище фильтров изменения графа
 */
class GraphChangeFilter extends Filter {
  FilterStore: FilterStoreType;

  constructor(FilterStore: FilterStoreType) {
    super();
    this.FilterStore = FilterStore;

    // функция срабатывает, когда изменяется одна из переданных зависимостей,
    // в данным случае this.FilterStore.options.
    autorun(() => {
      const { show_changes } = this.FilterStore.options;
      this.updateOptions({ show_changes }, 'show_changes');
    });
  }

  @observable keep = false;

  /** Метод переключает состояние фильтра */
  @action toggleFilter = () => {
    this.keep = !this.keep;
    if ((this.FilterStore.options['show_changes'] || false) !== this.keep) {
      this.FilterStore.isGraphChange = true;
    } else {
      this.FilterStore.isGraphChange = false;
    }
    this.FilterStore.checkChanges();
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
      show_changes: this.keep,
    };
  }
}

export default GraphChangeFilter;
