import React, { ReactNode } from 'react';
import { actionButtonPropTypes } from './ActionButton.props';
import styles from './ActionButton.module.scss';

type ActionButtonProps = {
  onClick: () => void;
  label: any;
  isSecondary?: boolean;
  buttonStyle?: string;
  labelClassName?: string;
  disabled?: boolean;
  title?: string;
  icon?: ReactNode;
  isFrontIcon?: boolean;
};

/**
 * Компонент кнопки действия в детализации НП
 *
 * @param {func} onClick - функция выщывающаяся при клике
 * @param {string} label - надпись на кнопке
 * @param {boolean} isSecondary - флаг показывающий нужно ли применять стили второстепенной
 * кнопки
 * @param {string} title - всплывающая подсказка
 */
const ActionButton = (props: ActionButtonProps) => {
  const {
    label,
    onClick,
    isSecondary,
    labelClassName,
    buttonStyle,
    disabled,
    title,
    icon,
    isFrontIcon,
  } = props;

  return (
    <button
      className={`${styles.button} ${buttonStyle} ${
        isSecondary ? styles.secondary : styles.primary
      } ${disabled ? styles.disabled : ''}`}
      onClick={onClick}
      disabled={disabled}
      title={title}
    >
      {isFrontIcon && icon && <span className={styles.icon}>{icon}</span>}
      <span className={`${styles.label} ${labelClassName}`}>{label}</span>
      {!isFrontIcon && icon && <span className={styles.icon}>{icon}</span>}
    </button>
  );
};

ActionButton.propTypes = actionButtonPropTypes;

ActionButton.defaultProps = {
  isSecondary: false,
  buttonStyle: '',
};

export default ActionButton;
