import React from 'react';
import { moneySpreadPickerPropTypes } from './MoneySpreadPicker.props';
import styles from './MoneySpreadPicker.module.scss';

import Input from '../Input';

interface MoneySpreadPickerProps {
  fromValue: string;
  toValue: string;
  onChangeFromValue: (value: number) => void;
  onChangeToValue: (value: number) => void;
  label?: string;
  wrapperStyle?: string;
  title?: string;
  disabled?: boolean;
  isPercent?: boolean;
  isSmall?: boolean;

  inputStyle?: string;
  inputPercentStyle?: string;
}

/**
 * Компонент выбора денежного промежутка
 */
class MoneySpreadPicker extends React.PureComponent<MoneySpreadPickerProps> {
  static propTypes = moneySpreadPickerPropTypes;

  /**
   * Функция обработчик ввода суммы, проверяет, содержатся ли в вводимых данных не только цифры,
   * и пропускает данные для дальнейшей обработки
   *
   * @param {React.BaseSyntheticEvent} event - событие ввода
   * @param {func} onChange - функция для обработки ввода
   */
  _onChange = (event: React.BaseSyntheticEvent, onChange: (value: any) => void) => {
    const { value } = event.target;

    onChange(value?.replace(/\s/g, ''));
  };

  /**
   * Функция обработчик ввода суммы от
   */
  _onChangeFromValue = (event: React.BaseSyntheticEvent) => {
    const { onChangeFromValue } = this.props;
    this._onChange(event, onChangeFromValue);
  };

  /**
   * Функция обработчик ввода суммы до
   */
  _onChangeToValue = (event: React.BaseSyntheticEvent) => {
    const { onChangeToValue } = this.props;
    this._onChange(event, onChangeToValue);
  };

  render() {
    const {
      fromValue = '',
      toValue = '',
      label,
      wrapperStyle,
      title,
      disabled,
      isPercent,
      isSmall,
      inputPercentStyle,
      inputStyle,
    } = this.props;

    return (
      <div className={`${styles.wrapper} ${wrapperStyle}`}>
        {label && (
          <span className={styles.headerLabel} title={title}>
            {label}
          </span>
        )}
        <div className={`${styles.container} ${isSmall && styles.small}`}>
          <div className={styles.picker}>
            <Input
              value={fromValue}
              label={'От'}
              inputStyle={
                isPercent
                  ? parseInt(fromValue?.replace(/\s/g, '')) > 100
                    ? `${inputPercentStyle} ${styles.inputPercent} ${styles.error}`
                    : `${inputPercentStyle} ${styles.inputPercent}`
                  : `${inputStyle} ${styles.input}`
              }
              wrapperStyle={styles.inputContainer}
              labelStyle={styles.inputLabel}
              onChange={this._onChangeFromValue}
              title={title}
              disabled={disabled}
              maxLength={isPercent ? 3 : undefined}
              placeholder="0"
            />
          </div>
          <div className={styles.picker}>
            <Input
              value={toValue}
              label={'до'}
              inputStyle={
                isPercent
                  ? parseInt(toValue?.replace(/\s/g, '')) > 100
                    ? `${inputPercentStyle} ${styles.inputPercent} ${styles.error}`
                    : parseInt(fromValue?.replace(/\s/g, '')) >
                      parseInt(toValue?.replace(/\s/g, ''))
                    ? ` ${inputPercentStyle} ${styles.inputPercent} ${styles.error}`
                    : `${inputPercentStyle} ${styles.inputPercent}`
                  : parseInt(fromValue?.replace(/\s/g, '')) > parseInt(toValue?.replace(/\s/g, ''))
                  ? `${inputStyle} ${styles.input} ${styles.error}`
                  : `${inputStyle} ${styles.input}`
              }
              wrapperStyle={styles.inputContainer}
              labelStyle={styles.inputLabel}
              onChange={this._onChangeToValue}
              title={title}
              disabled={disabled}
              maxLength={isPercent ? 3 : undefined}
              placeholder="0"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default MoneySpreadPicker;
