import { configure, action, decorate, flow } from 'mobx';
import GraphStore from './graph/graphStore/graphStore';
import { RootStoreType } from 'stores/rootStore';
import { flowTypes } from 'utils/flowTypes';
import { generateNodesReport } from 'utils/services/api/common';

configure({ enforceActions: 'observed' });

class NodesReportStore {
  graphStore: GraphStore;
  rootStore: RootStoreType;

  constructor(rootStore: RootStoreType) {
    this.rootStore = rootStore;
    this.graphStore = rootStore.GraphStore;
  }

  getReport = flow(function* (this: NodesReportStore) {
    const graphId = this.graphStore.previouslyFetchedGraphId;
    this.graphStore.statePending();

    try {
      yield* flowTypes(generateNodesReport(graphId));

      this.rootStore.UIStore.openDialog({
        title: 'Началось формирование документа',
        opened: true,
        type: 'confirm',
        text: `Выгруженный документ будет находиться в разделе "Документы".`,
        additionalText: 'Файл хранится 48 часов.',
        okText: 'Перейти в "Документы"',
        cancelText: 'Закрыть',
        dialogClassName: 'dialog_nodes_report',
        onConfirm: async () => {
          // @ts-ignore
          if (typeof window.CefSharp === 'object') {
            // @ts-ignore
            await CefSharp.BindObjectAsync('unifiedClientIntegration');
            // @ts-ignore
            unifiedClientIntegration.executeFunction(
              'IASCBCommonDomain',
              'openActivityDocuments',
              JSON.stringify({}),
            );
          } else {
            window.open('/inias/csc/gws/static/iascbcommonweb/', '_blank');
          }
        },
        onCancel: () => {},
      });
    } catch (err) {
      this.graphStore.onError(err);
    } finally {
      this.graphStore.stateDone();
    }
  });
}

decorate(NodesReportStore, {
  getReport: action.bound,
});

export default NodesReportStore;
