import { configure, observable, action, decorate, flow, computed } from 'mobx';
import { postFilteredNP, getFilteredNP } from 'utils/services/api/graph';
import { TaxpayerType } from 'DataTypes';
import GraphStore from '../graph/graphStore/graphStore';
import { RootStoreType } from 'stores/rootStore';
import { readParamsFromURL } from '../../utils/getParamsFromURL';
import { getQuarterRoman } from 'utils/getPeriodNumbers';
import { ISearchListParams } from 'ApiModel';
import { getDefaultPeriod } from 'utils/ui/getCurrentDate';
import { toNumber } from 'utils';

configure({ enforceActions: 'observed' });

const { inn, name, quarter, year } = readParamsFromURL(window.location.href) as Record<
  string,
  string
>;

const initialOptions = {
  period_from: `${getDefaultPeriod('roman').quarter};${getDefaultPeriod('roman').year}`,
  period_to: `${getDefaultPeriod('roman').quarter};${getDefaultPeriod('roman').year}`,
  integrals: '',
  integralsID: '',
  businessRole: '',
  businessRoleID: '',
  vat_deduct_from: '',
  vat_deduct_to: '',
  vat_calc_from: '',
  vat_calc_to: '',
  ook_total_from: '',
  ook_total_to: '',
  emp_count_from: '',
  emp_count_to: '',
  sectoral_rate_from: '',
  sectoral_rate_to: '',
};

class BeginningWorkStore {
  graphStore: GraphStore;
  rootStore: RootStoreType;

  constructor(rootStore: RootStoreType) {
    this.rootStore = rootStore;
    this.graphStore = rootStore.GraphStore;
  }

  npSearchState: number = -1;
  npSearchResult: TaxpayerType[] = [];
  npSearchCount: number = 0;
  npSearchWarning: string = '';
  interval: number | undefined;
  tabIndex: number = inn ? 1 : 0;
  selectedNp: TaxpayerType | null = inn ? { inn, name, quarter, year } : null;
  filterIsOpen: boolean = false;
  formData: any = null;

  setFilterIsOpen = (value: boolean) => {
    this.filterIsOpen = value;
  };

  changeTabIndex = (index: number) => {
    this.tabIndex = index;
  };

  setSelectedNp = (np: TaxpayerType | null) => {
    this.selectedNp = np;
  };

  resetNpSearch = () => {
    this.npSearchState = -1;
    this.npSearchResult = [];
    this.npSearchWarning = '';
    this.selectedNp = null;
  };

  formatDate = (date: number) => {
    const dateArr = String(-date).split('');
    const quarter = getQuarterRoman(dateArr.pop() || '');
    const year = dateArr.join('');

    return { quarter, year };
  };

  formatFormData = (params: ISearchListParams) => {
    const { quarter: startQuarter, year: startYear } = this.formatDate(params.yq[0]);
    const { quarter: endQuarter, year: endYear } = this.formatDate(params.yq[params.yq.length - 1]);

    return {
      period_from: startQuarter + ';' + startYear,
      period_to: endQuarter + ';' + endYear,
      integralsID: params.sur ? params.sur[0] + '' : '',
      businessRoleID: params.business_role ? params.business_role[0] + '' : '',
      vat_calc_from: params.calc_min ? params.calc_min.toLocaleString('ru-RU') : '',
      vat_calc_to: params.calc_max ? params.calc_max.toLocaleString('ru-RU') : '',
      vat_deduct_from: params.deduct_min ? params.deduct_min.toLocaleString('ru-RU') : '',
      vat_deduct_to: params.deduct_max ? params.deduct_max.toLocaleString('ru-RU') : '',
      ook_total_from: params.ook_min ? params.ook_min.toLocaleString('ru-RU') : '',
      ook_total_to: params.ook_max ? params.ook_max.toLocaleString('ru-RU') : '',
      emp_count_from: params.emp_min ? params.emp_min.toLocaleString('ru-RU') : '',
      emp_count_to: params.emp_max ? params.emp_max.toLocaleString('ru-RU') : '',
      sectoral_rate_from: params.tg_min ? params.tg_min.toLocaleString('ru-RU') : '',
      sectoral_rate_to: params.tg_max ? params.tg_max.toLocaleString('ru-RU') : '',
    };
  };

  setSearchIsActive = flow(function* (
    this: BeginningWorkStore,
    jobId: number,
    searchId: number,
    state: number,
    params: ISearchListParams,
  ) {
    this.changeTabIndex(0);
    this.openFilter();
    this.npSearchState = state;
    this.formData = this.formatFormData(params);

    try {
      yield this.fetchFilteredNP(jobId, searchId);

      this.rootStore.CommonStore.openBeginningWork();

      if (this.npSearchState === 0 || this.npSearchState === 1) {
        this.interval = this.setSearchJobInterval(jobId, searchId);
      }
    } catch (err) {
      this.graphStore.onError(err);
      this.npSearchState = -1;
    }
  });

  setSearchIsComplited = flow(function* (
    this: BeginningWorkStore,
    jobId: number,
    searchId: number,
    state: number,
    params: ISearchListParams,
  ) {
    this.changeTabIndex(0);
    this.openFilter();
    this.npSearchState = 1;
    this.formData = this.formatFormData(params);

    try {
      yield this.fetchFilteredNP(jobId, searchId);
    } catch (err) {
      this.graphStore.onError(err);
      this.npSearchState = -1;
    }
  });

  fetchFilteredNP = flow(function* (this: BeginningWorkStore, jobId: number, searchId: number) {
    try {
      const { state, taxpayers, warning, count } = yield getFilteredNP(jobId, searchId);

      if (state === undefined) {
        this.npSearchState = -1;
        this.clearSearchJobInterval();
        this.rootStore.SearchListStore.fetchSearchList();
      }

      if (state === 2) {
        this.npSearchResult = taxpayers;
        this.npSearchCount = count;
        this.npSearchWarning = warning;
        this.clearSearchJobInterval();
        this.rootStore.SearchListStore.fetchSearchList();
      }

      if (state === 3) {
        this.npSearchWarning = warning;
        this.clearSearchJobInterval();
        this.rootStore.SearchListStore.fetchSearchList();
      }

      if (this.npSearchState === 0 && state === 1) {
        this.rootStore.SearchListStore.fetchSearchList();
      }

      this.npSearchState = state;
    } catch (e) {
      this.graphStore.onError(e);
      this.clearSearchJobInterval();
    }
  });

  /** Метод поиска НП по фильтрации */
  searchFilteredNP = flow(function* (this: BeginningWorkStore, bodyOptions: any) {
    this.npSearchState = 0;

    try {
      const { job_id, search_id } = yield postFilteredNP(bodyOptions);

      yield this.fetchFilteredNP(job_id, search_id);
      this.rootStore.SearchListStore.fetchSearchList();

      if (this.npSearchState === 0 || this.npSearchState === 1) {
        this.interval = this.setSearchJobInterval(job_id, search_id);
      }
    } catch (err) {
      this.graphStore.onError(err);
      this.npSearchState = -1;
    }
  });

  setSearchJobInterval = (jobId: number, searchId: number) => {
    this.clearSearchJobInterval();

    return setInterval(async () => {
      await this.fetchFilteredNP(jobId, searchId);
    }, 5000) as unknown as number;
  };

  clearSearchJobInterval = () => {
    clearInterval(this.interval);
  };

  options: Record<string, string> = initialOptions;

  changeOption = (value: any, key: any) => {
    if (key in this.options) {
      this.options = {
        ...this.options,
        [key]: value,
      };
    } else {
      console.warn(`Change filter option error! No option with given key '${key}' is found.`);
    }
  };

  get filterIsFill() {
    return Object.keys(this.options).some(
      (key: string) => this.options[key as string] && key !== 'period_from' && key !== 'period_to',
    );
  }

  /**
   * Метод изменяет налогоплотильщика
   */
  handleSelectNp = (np: TaxpayerType) => {
    this.setSelectedNp(this.isEqualNp(this.selectedNp, np) ? null : np);
  };

  isEqualNp = (np1: TaxpayerType | null, np2: TaxpayerType | null) => {
    return (
      np1?.inn === np2?.inn &&
      np1?.kpp === np2?.kpp &&
      np1?.quarter === np2?.quarter &&
      np1?.year === np2?.year &&
      np1?.ufns_code === np2?.ufns_code
    );
  };

  /**
   * Метод обрабатывает событие "Submit"
   */
  handleSubmit = async () => {
    const bodyOptions = {
      ...this.options,
      vat_deduct_from: toNumber(this.options.vat_deduct_from, 0),
      vat_calc_from: toNumber(this.options.vat_calc_from, 0),
      ook_total_from: toNumber(this.options.ook_total_from, 0),
      emp_count_from: toNumber(this.options.emp_count_from, 0),
      sectoral_rate_from: toNumber(this.options.sectoral_rate_from, 0),
      vat_deduct_to: toNumber(this.options.vat_deduct_to),
      vat_calc_to: toNumber(this.options.vat_calc_to),
      ook_total_to: toNumber(this.options.ook_total_to),
      emp_count_to: toNumber(this.options.emp_count_to),
      sectoral_rate_to: toNumber(this.options.sectoral_rate_to),
      business_roles: this.options.businessRoleID ? [this.options.businessRoleID] : [],
      risk_levels: this.options.integralsID ? [this.options.integralsID] : [],
    };

    this.searchFilteredNP(bodyOptions);
  };

  resetAdvancedSearch = () => {
    this.resetNpSearch();
    this.setFilterIsOpen(true);
  };

  resetFilter = () => {
    this.options = initialOptions;
  };

  changePeriodFrom = (period_from: string) => {
    this.options['period_from'] = period_from;
  };

  changePeriodTo = (period_to: string) => {
    this.options['period_to'] = period_to;
  };

  changeIntegrals = (value: any) => {
    this.options['integrals'] = value.name;
    this.options['integralsID'] = value.value;
  };

  changeBusinessRoles = (value: any) => {
    this.options['businessRole'] = value.name;
    this.options['businessRoleID'] = value.value;
  };

  changeFilterVisible = () => {
    this.setSelectedNp(null);
    this.setFilterIsOpen(!this?.filterIsOpen);
  };

  openFilter = () => {
    this.setSelectedNp(null);
    this.setFilterIsOpen(true);
  };

  goToNextTab = () => {
    this.changeTabIndex(1);
  };
}

decorate(BeginningWorkStore, {
  npSearchState: observable,
  npSearchResult: observable,
  npSearchWarning: observable,
  tabIndex: observable,
  selectedNp: observable,
  filterIsOpen: observable,
  options: observable,

  filterIsFill: computed,

  setSearchJobInterval: action,
  clearSearchJobInterval: action,
  changeFilterVisible: action,
  changeOption: action,
  handleSelectNp: action,
  handleSubmit: action,
  resetAdvancedSearch: action,
  resetFilter: action,
  changePeriodFrom: action,
  changePeriodTo: action,
  changeIntegrals: action,
  changeBusinessRoles: action,
  openFilter: action,
  goToNextTab: action,
  changeTabIndex: action,
  setSelectedNp: action,
  resetNpSearch: action,
  fetchFilteredNP: action.bound,
  searchFilteredNP: action.bound,
  setFilterIsOpen: action,
});

export default BeginningWorkStore;
