import * as go from 'gojs';
import { observable, computed, action } from 'mobx';
import { DIRECTION_HORIZONTAL, DIRECTION_VERTICAL, DESIGN_NODE } from '@fdc/vsa-gojs-lib';

import ZoomStore from './zoomStore';

import NodeTemplatesStore from './templates/nodeTemplatesStore';
import LinkTemplatesStore from './templates/linkTemplatesStore';

import { Vertex, Edge } from 'GraphModel';
import { ModalStoreType } from '../modalStore';
import GraphStore from './graphStore';
import CommonStore from 'stores/common';

import { MODAL_CREATE_LINK } from '../../components/modals/constants/modalNames';
import FilterStore from '../filters/filterStore';
import { v4 as uuidv4 } from 'uuid';
import IndicatorsStore from '../indicators';
import { DraggingTool } from 'utils/diagram/Tools/DraggingTool';

/**
 * Хранилище диаграммы
 */
class DiagramStore {
  ModalStore: ModalStoreType;
  GraphStore: GraphStore;
  ZoomStore: ZoomStore;
  CommonStore: CommonStore;
  FilterStore: FilterStore;
  IndicatorsStore: IndicatorsStore;

  // объект диаграммы
  @observable diagram: go.Diagram | null = null;

  // ЛКМ по зомби-узлу
  @observable zombieClick: boolean = true;
  // key зомби-узла
  @observable zombieKey: string = '';
  // key временной связи для зомби
  @observable keyTemporaryLink: string = '';
  // Временная связь для зомби-узла
  @observable zombieLink: any;
  // Информация по нескольким кластерам
  @observable clusterInfo: any;

  nodeTemplates: {
    vertical: NodeTemplatesStore;
    horizontal: NodeTemplatesStore;
  };

  linkTemplates: {
    vertical: LinkTemplatesStore;
    horizontal: LinkTemplatesStore;
  };

  vsaNodeContextMenuId = 'vsa-node-context-menu';
  vsaGroupContextMenuId = 'vsa-group-context-menu';
  vsaClusterContextMenuId = 'vsa-cluster-context-menu';
  vsaLinkContextMenuId = 'vsa-link-context-menu';
  vsaVirtualGroupContextMenuId = 'vsa-virtual-group-context-menu';

  constructor(GraphStore: GraphStore) {
    this.ModalStore = GraphStore.RootStore.ModalStore;
    this.GraphStore = GraphStore;
    this.ZoomStore = new ZoomStore(this);
    this.CommonStore = GraphStore.RootStore.CommonStore;
    this.FilterStore = new FilterStore(GraphStore.RootStore);
    this.IndicatorsStore = new IndicatorsStore(GraphStore.RootStore);

    this.nodeTemplates = {
      vertical: new NodeTemplatesStore(
        this,
        DIRECTION_VERTICAL,
        this.FilterStore,
        this.GraphStore,
        this.CommonStore,
        this.IndicatorsStore,
      ),
      horizontal: new NodeTemplatesStore(
        this,
        DIRECTION_HORIZONTAL,
        this.FilterStore,
        this.GraphStore,
        this.CommonStore,
        this.IndicatorsStore,
      ),
    };

    // Шаблоны связей
    this.linkTemplates = {
      vertical: new LinkTemplatesStore(this, DIRECTION_VERTICAL, this.CommonStore, this.GraphStore),
      horizontal: new LinkTemplatesStore(
        this,
        DIRECTION_HORIZONTAL,
        this.CommonStore,
        this.GraphStore,
      ),
    };
  }

  /** Выбранные узла */
  @observable selectedNodes: string[] = [];
  @action.bound
  setSelectedNodes = (selectedNodes: string[]) => {
    if (selectedNodes !== null) {
      this.selectedNodes = selectedNodes;
    }
  };

  @action.bound
  resetSelectedNodes = () => {
    this.selectedNodes = [];
  };

  @action.bound
  selectionEventListener = (event: go.DiagramEvent) => {
    const selectionArr = event.diagram?.selection.toArray();
    const filterSelectionArr = selectionArr.filter(
      (element) => element.category !== 'VirtualGroup',
    );
    this.setSelectedNodes(
      filterSelectionArr.map((item) =>
        this.GraphStore.graphTransformMode === 'ModeV3' &&
        item.data.indicators_bottom?.some((element: any) => element.name === 'Zombie')
          ? item.data.key
          : item.data.orig_id,
      ),
    );
  };

  /** Нажатие клавиши CTRL */
  @observable ctrlPressed = false;
  @action.bound
  setCtrlPressed = (value?: boolean) => {
    value ? (this.ctrlPressed = value) : (this.ctrlPressed = !this.ctrlPressed);
  };

  // Направление диаграммы (90 - Горизонтально / 0 - Вертикально)
  @observable direction: typeof DIRECTION_HORIZONTAL | typeof DIRECTION_VERTICAL | string =
    DIRECTION_HORIZONTAL;

  // Дополнительная информация о диаграмме
  @observable diagramInfo: object | null = null;

  // Узлы
  @observable vertexes: Vertex[] | null = null;

  // Связи
  @observable edges: Edge[] | null = null;

  // Виды узлов
  nodes: Array<string> = ['Cluster', 'Node', 'Group', 'VirtualGroup'];

  // Виды связей
  links: Array<string> = [
    'Flow_NDS',
    'Break_NDS',
    'Recursive',
    'Cluster_Link',
    'Flow_Payment',
    'Zombie_Link',
  ];

  // Выделенный узел (Узел для детализации)
  @observable viewedNode: go.Node | null = null;
  // Не скрытые узлы
  @observable visibleNodes: Array<string> = [];

  @computed get currentDiagram() {
    return this.diagram;
  }

  /**
   * Метод получения всех узлов, включая клонов
   */
  @computed get nodesWithClones(): Array<Vertex> {
    const nodes: Array<Vertex> = this.vertexes ? [...this.vertexes] : [];

    this.edges?.forEach((edge: Edge) => {
      const startNode = nodes.find((node) => node.PK === edge.SID);
      const endNode = nodes.find((node) => node.PK === edge.BID);

      if (edge.SID === edge.BID || endNode?.Level - startNode?.Level > 1) {
        nodes.push({ ...endNode, PK: endNode?.PK + '_clone', isClone: true });
      }
    });

    return nodes;
  }

  /**
   * Метод возвращает все узлы
   */
  @computed get graphNodes(): Array<Vertex> {
    return this.nodesWithClones.map((node) => node);
  }

  /**
   * Метод возвращает корневой узел
   */
  @computed get rootNode(): Vertex | null {
    return this.graphNodes.find((node) => node.Level === 0) || null;
  }

  /**
   * Метод возвращает все связи графа
   */
  @computed get graphEdges(): Array<Edge> {
    return this.edges?.map((edge) => edge) || [];
  }

  /**
   * Метод возвращает расположения данных внутри диаграммы
   */
  @computed get contentAlignment(): go.Spot {
    return this.direction === DIRECTION_VERTICAL ? go.Spot.Left : go.Spot.Center;
  }

  /**
   * Метод возвращает отступы для диаграммы
   */
  @computed get diagramPadding(): go.Margin {
    return new go.Margin(100, 50, 250, 170);
  }

  /**
   * Метод возвращает сетку диаграммы в зависимости от направления просмотра
   * (Горизонтально / Вертикально)
   */
  @computed get grid(): go.Panel {
    const $ = go.GraphObject.make;
    const gridShape = this.direction === DIRECTION_VERTICAL ? 'BarV' : 'BarH';
    const gridInterval = 2;
    let gridCellSize;
    let gridHeight;

    if (DESIGN_NODE === 'Round') {
      gridCellSize =
        this.direction === DIRECTION_VERTICAL ? new go.Size(600, 10) : new go.Size(10, 306);
      gridHeight = this.direction === DIRECTION_VERTICAL ? 600 : 306;
    } else {
      gridCellSize =
        this.direction === DIRECTION_VERTICAL ? new go.Size(600, 10) : new go.Size(10, 306);
      gridHeight = this.direction === DIRECTION_VERTICAL ? 600 : 306;
    }

    return $(
      go.Panel,
      'Grid',
      {
        gridCellSize: gridCellSize,
      },
      $(go.Shape, gridShape, {
        fill:
          this.CommonStore.appTheme === 'light'
            ? 'rgba(178,199,226,0.31)'
            : 'rgba(30, 40, 50, 0.5)',
        interval: gridInterval,
        height: gridHeight,
      }),
    );
  }

  /**
   * Метод устанавливает сетку для диаграммы
   */
  private setGrid = (): void => {
    if (this.diagram) {
      this.diagram.contentAlignment = this.contentAlignment;
      this.diagram.padding = this.diagramPadding;
      this.diagram.grid = this.grid;
    }
  };

  @action setPositionScale = (position: any, scale: any) => {
    if (this.diagram) {
      this.diagram.position = position;
      this.diagram.scale = scale;
    }
  };

  /**
   * Метод инициализации диаграммы
   */
  @action initDiagram = (): go.Diagram => {
    const $ = go.GraphObject.make;

    const diagram = $(go.Diagram, {
      'undoManager.isEnabled': true,
      'animationManager.isEnabled': false,
      'animationManager.duration': 200,
      initialAutoScale: go.Diagram.UniformToFill,
      initialContentAlignment: go.Spot.Center,
      // "hasHorizontalScrollBar": false,
      // "hasVerticalScrollBar": false,
      contentAlignment: go.Spot.Center,
      padding: this.diagramPadding,
      'grid.visible': true,
      allowDelete: false,
      allowMove: true,
      toolTipDuration: Infinity,
      allowLink: true,
      hoverDelay: 300,
      allowCopy: false,
      draggingTool: new DraggingTool(),
    });

    const forelayer: any = diagram.findLayer('Foreground');
    diagram.addLayerBefore($(go.Layer, { name: 'ClusterLayer' }), forelayer);
    diagram.addLayerBefore($(go.Layer, { name: 'GroupLayer' }), forelayer);
    diagram.addLayerBefore($(go.Layer, { name: 'NodeLayer' }), forelayer);

    diagram.grid = this.grid;

    // Для создания новой связи - стиль новой связи
    let templink = $(
      go.Link,
      { layerName: 'Tool' },
      $(go.Shape, {
        strokeWidth: 1,
        stroke: '#0B346A',
      }),
      $(go.Shape, {
        fill: '#0B346A',
        stroke: '#0B346A',
        toArrow: 'Triangle',
      }),
    );

    // Для создания новой связи - стиль области узла, к которой идёт связь
    let temptonode = $(
      go.Node,
      { layerName: 'Tool' },
      $(go.Shape, 'Circle', {
        stroke: '#0B346A',
        strokeWidth: 1,
        fill: null,
        portId: '',
        width: 36,
        height: 36,
      }),
    );

    // Для создания новой связи - стиль области узла, в которую идёт связь
    let tempfromnode = $(
      go.Node,
      { layerName: 'Tool' },
      $(go.Shape, 'Circle', {
        stroke: '#0B346A',
        strokeWidth: 1,
        fill: null,
        portId: '',
        width: 36,
        height: 36,
      }),
    );

    // Если нет операции 'Оп-СозданиеСвязи' или 'Оп-ПросмотрСпискаСвязей', то не рисовать линию
    diagram.addDiagramListener('GainedFocus', (e) => {
      diagram.toolManager.linkingTool.temporaryToNode =
        this.GraphStore.showTypesLinks &&
        this.GraphStore.showCreateLink &&
        this.GraphStore.ableEdit &&
        this.GraphStore.filtersGraph.is_user_link &&
        !this.GraphStore.visibleNodes
          ? temptonode
          : $(go.Node);
      diagram.toolManager.linkingTool.temporaryFromNode =
        this.GraphStore.showTypesLinks &&
        this.GraphStore.showCreateLink &&
        this.GraphStore.ableEdit &&
        this.GraphStore.filtersGraph.is_user_link &&
        !this.GraphStore.visibleNodes
          ? tempfromnode
          : $(go.Node);
      diagram.toolManager.linkingTool.temporaryLink =
        this.GraphStore.showTypesLinks &&
        this.GraphStore.showCreateLink &&
        this.GraphStore.ableEdit &&
        this.GraphStore.filtersGraph.is_user_link &&
        !this.GraphStore.visibleNodes
          ? templink
          : $(go.Link);
    });

    diagram.addDiagramListener('LinkDrawn', (e) => {
      if (
        this.GraphStore.showTypesLinks &&
        this.GraphStore.showCreateLink &&
        !this.GraphStore.scheme &&
        this.GraphStore.ableEdit &&
        this.GraphStore.filtersGraph.is_user_link &&
        !this.GraphStore.visibleNodes
      ) {
        const fromNode = e.subject.fromNode.data;
        const toNode = e.subject.toNode.data;

        const zombieFromNode = fromNode.indicators_bottom?.some(
          (item: any) => item.name === 'Zombie',
        );
        const zombieToNode = toNode.indicators_bottom?.some((item: any) => item.name === 'Zombie');

        if (!(zombieFromNode || zombieToNode)) {
          this.ModalStore?.openModal(MODAL_CREATE_LINK, { fromNode, toNode });
        }
      }
      diagram.currentTool.doCancel();
    });

    diagram.addDiagramListener('ObjectSingleClicked', (e) => {
      const node = e.subject.part.data;
      const diagramInfo = e.subject.diagram;
      diagram.startTransaction('noHighlighteds');
      diagram.clearHighlighteds();
      diagram.commitTransaction('noHighlighteds');
      if (node) {
        const zombie = node.indicators_bottom?.some((item: any) => item.name === 'Zombie');

        // Если был совершён переход на другой зомби-узел
        if (this.zombieKey !== node.key) {
          this.zombieClick = true;
          this.zombieKey = node.key;
          diagram.startTransaction('removeLink');
          // @ts-ignore
          diagram.model.removeLinkData(this.zombieLink);
          diagram.commitTransaction('removeLink');
        }

        if (zombie) {
          if (this.zombieClick) {
            diagramInfo?.nodes.each((n: any) => {
              if (
                n.data.category === 'Node' &&
                !n.data.indicators_bottom?.some((item: any) => item.name === 'Zombie')
              ) {
                if (node.key !== n.data.key && node.orig_id === n.data.orig_id) {
                  this.keyTemporaryLink = uuidv4();
                  this.zombieLink = {
                    from: node.key,
                    to: n.data.orig_id,
                    key: this.keyTemporaryLink,
                    category: 'Zombie_Link',
                  };
                  diagram.startTransaction('addNewLink');
                  // @ts-ignore
                  diagram.model.addLinkData(this.zombieLink);
                  diagram.commitTransaction('addNewLink');
                }
              }
            });
          } else {
            diagram.startTransaction('removeLink');
            // @ts-ignore
            diagram.model.removeLinkData(this.zombieLink);
            diagram.commitTransaction('removeLink');
          }
          this.zombieClick = !this.zombieClick;
        }
      }
    });

    diagram.nodeTemplateMap.clear();
    diagram.linkTemplateMap.clear();

    this.nodes.forEach((node) => {
      if (this.direction === DIRECTION_VERTICAL) {
        // @ts-ignore
        diagram.nodeTemplateMap.add(node, this.nodeTemplates.horizontal[node]);
      } else {
        // @ts-ignore
        diagram.nodeTemplateMap.add(node, this.nodeTemplates.vertical[node]);
      }
    });

    this.links.forEach((link) => {
      if (this.direction === DIRECTION_VERTICAL) {
        // @ts-ignore
        diagram.linkTemplateMap.add(link, this.linkTemplates.horizontal[link]);
      } else {
        // @ts-ignore
        diagram.linkTemplateMap.add(link, this.linkTemplates.vertical[link]);
      }
    });

    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      diagram.addDiagramListener('LayoutCompleted', () => {
        console.log('diagram layout completed');
      });
    }

    this.diagram = diagram;

    diagram.addDiagramListener('ChangedSelection', this.selectionEventListener);

    diagram.addDiagramListener('BackgroundSingleClicked', (e) => {
      diagram.startTransaction('noHighlighteds');
      diagram.clearHighlighteds();
      diagram.commitTransaction('noHighlighteds');
    });

    return diagram;
  };

  /**
   * Метод изменяет направление просмотра графа
   */
  public changeDirection = async (): Promise<void> => {
    if (this.diagram) {
      this.setGrid();

      this.nodes.forEach((node) => {
        if (this.direction === DIRECTION_VERTICAL) {
          // @ts-ignore
          this.diagram?.nodeTemplateMap.add(node, this.nodeTemplates.horizontal[node]);
        } else {
          // @ts-ignore
          this.diagram?.nodeTemplateMap.add(node, this.nodeTemplates.vertical[node]);
        }
      });
      this.links.forEach((link) => {
        if (this.direction === DIRECTION_VERTICAL) {
          // @ts-ignore
          this.diagram?.linkTemplateMap.add(link, this.linkTemplates.horizontal[link]);
        } else {
          // @ts-ignore
          this.diagram?.linkTemplateMap.add(link, this.linkTemplates.vertical[link]);
        }
      });

      this.diagram.rebuildParts();
    }
  };

  /**
   * Метод устанавливает горизонтальный режим просмотра графа
   */
  @action setHorizontalDirection = async (): Promise<void> => {
    this.direction = DIRECTION_VERTICAL;
    this.changeDirection();
  };

  /**
   * Метод устанавливает вертикальный режим просмотра графа
   */
  @action setVerticalDirection = async (): Promise<void> => {
    this.direction = DIRECTION_HORIZONTAL;
    this.changeDirection();
  };

  /**
   * Метод записывает в массив информацию по нескольким кластерам, находящимся в одной точке
   */
  @action setClusterInfo = async (data: any): Promise<void> => {
    this.clusterInfo = data;
  };

  @observable selectedItem = '';

  @action setSelectedItem = (item: string) => {
    this.selectedItem = item;
  };
}

export type DiagramStoreType = DiagramStore;
export default DiagramStore;
