import React, { HTMLAttributes } from 'react';
import { observer } from 'mobx-react';

import { useStores } from '../../hooks';

import Button from 'components/Button';
import Form from './components/Form';
import Loader from './components/Loader';
import Result from './components/Result';

import { ReactComponent as ArrowIcon } from 'assets/icons/arrow.svg';

import styles from './Chains.module.scss';

const Chains = observer(() => {
  const {
    chainsStore: {
      searchState,
      submitForm,
      formData,
      isCollapsed,
      setIsCollapsed,
      formIsCollapsed,
      setFormIsCollapsed,
      searchWarning,
    },
    uiStore: { setChainsIsOpen },
  } = useStores();

  const {
    seller_inn,
    buyer_inn,
    period_from,
    period_to,
    link_type,
    is_mark,
    is_property,
    is_trace,
  } = formData;

  const formIsFilled =
    !!seller_inn &&
    !!buyer_inn &&
    !!period_from &&
    !!period_to &&
    (link_type === 'Товарный поток' ? is_mark || is_property || is_trace : true);

  const applyIsDisabled = !formIsFilled || searchState !== -1;

  return (
    <div className={`${styles.wrapper} ${isCollapsed && styles.isCollapsed}`}>
      {isCollapsed ? (
        <div
          className={`${styles.indicator} ${searchState === 2 && styles.finished} ${
            searchState === 3 && styles.error
          } ${(searchState === 0 || searchState === 1) && styles.processed}`}
          onClick={() => setIsCollapsed(false)}
        >
          <div className={styles.text}>
            {searchState === 2
              ? 'Поиск завершен'
              : searchState === 0 || searchState === 1
              ? 'Поиск в процессе..'
              : searchState === 3
              ? 'Ошибка поиска'
              : 'Поиск цепочек'}
          </div>
          <div className={styles.progress}></div>
        </div>
      ) : (
        <div className={styles.container}>
          <div className={styles.header}>Поиск цепочек между налогоплательщиками</div>

          <div className={styles.body}>
            {(searchState === 2 || searchState === 1) && (
              <div className={styles.result__header}>
                <div className={styles.result__title}>Результаты поиска цепочек НП</div>

                <div
                  className={`${styles.showForm} ${!formIsCollapsed && styles.opened}`}
                  onClick={() => setFormIsCollapsed(!formIsCollapsed)}
                >
                  <span>{formIsCollapsed ? 'Показать' : 'Скрыть'} запрос</span>
                  <ArrowIcon />
                </div>
              </div>
            )}

            {!formIsCollapsed && <Form disabled={searchState !== -1} />}

            {searchState === 0 && <Loader />}

            {searchState === 3 && <div className={styles.warning}>{searchWarning}</div>}

            {(searchState === 2 || searchState === 1) && <Result />}
          </div>

          <div className={styles.buttons}>
            <div className={styles.buttons__left}>
              <Button mode="secondary" onClick={() => setChainsIsOpen(false)}>
                Назад
              </Button>
            </div>

            {searchState !== 2 && searchState !== 3 && (
              <div className={styles.buttons__right}>
                <Button onClick={submitForm} disabled={applyIsDisabled}>
                  Найти цепочки НП
                </Button>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
});

export default Chains;
