import React from 'react';
import styles from './ModalBody.module.scss';
import { IModalBodyProps } from './ModalBody.props';

const ModalBody = ({
  children,
  className,
  withHeader,
  withFooter,
  size = 'medium',
  ...props
}: IModalBodyProps) => {
  return (
    <div
      className={`${styles.body} ${styles[size]} ${className} ${withHeader && styles.withHeader} ${
        withFooter && styles.withFooter
      }`}
      {...props}
    >
      {children}
    </div>
  );
};

export { ModalBody };
