import React from 'react';

import { ICheckboxGroupProps } from './CheckboxGroup.props';
import Checkbox from '../Checkbox';

import styles from './CheckboxGroup.module.scss';

const CheckboxGroup = ({
  className,
  children,
  items,
  onCheckboxChange,
  onAllCheckboxChange,
  itemsClassName,
  ...props
}: ICheckboxGroupProps) => {
  return (
    <div className={`${className} ${styles.group}`} {...props}>
      {items && onAllCheckboxChange && (
        <div className={styles.all}>
          <Checkbox
            className={`${styles.checkbox} ${styles.small}`}
            checked={!items.some(({ checked }) => !checked)}
            name="check_all"
            onChange={onAllCheckboxChange}
            isAll={true}
            dimension="small"
          >
            Включить / выключить все
          </Checkbox>

          <div className={styles.total}>
            Выбрано {items.filter(({ checked }) => checked).length} из {items.length}
          </div>
        </div>
      )}

      {items && onCheckboxChange && (
        <div className={`${styles.items} ${itemsClassName}`}>
          {items.map((item) => {
            return (
              <Checkbox
                className={`${styles.checkbox} ${styles.small}`}
                checked={item.checked}
                disabled={item.disabled}
                key={item.name}
                name={item.name}
                onChange={(e) => onCheckboxChange(e, item.name)}
                dimension="small"
              >
                {item.label}
              </Checkbox>
            );
          })}
        </div>
      )}

      {children}
    </div>
  );
};

export default CheckboxGroup;
