import Button from 'components/Button';
import React, { HTMLAttributes, useLayoutEffect, useState } from 'react';
import { IDialog } from 'stores/uiStore';
import styles from './Dialog.module.scss';

interface IDialogProps extends HTMLAttributes<HTMLDivElement> {
  options: IDialog;
  withOverlay?: boolean;
}

const Dialog = ({ className, options, withOverlay = true, ...props }: IDialogProps) => {
  const [opened, setOpened] = useState(options.opened);

  useLayoutEffect(() => {
    setOpened(options.opened);
  }, [options]);

  const onConfirm = () => {
    options.onConfirm && options.onConfirm();
    setOpened(false);
  };

  const onCancel = () => {
    options.onCancel && options.onCancel();
    setOpened(false);
  };

  if (!opened) return null;

  return (
    <div
      className={`${className} ${styles.wrapper} ${withOverlay && styles.overlay} ${
        options.dialogClassName
      }`}
      {...props}
    >
      <div className={styles.dialog}>
        {options.title && <div className={styles.title}>{options.title}</div>}
        <div className={styles.text}>{options.text}</div>
        {options.additionalText && (
          <div className={`${styles.additional} additional_text`}>{options.additionalText}</div>
        )}
        <div className={styles.buttons}>
          {options.type === 'confirm' && (
            <Button onClick={onConfirm}>{options.okText || 'OK'}</Button>
          )}
          <Button mode={options.type === 'confirm' ? 'transparent' : 'primary'} onClick={onCancel}>
            {options.cancelText || 'Отмена'}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default Dialog;
