import React, { PureComponent } from 'react';
import styles from './ToolTipDeclaration.module.scss';

import { inject, observer } from 'mobx-react';
import { GraphStoreType } from '../../stores/graph/graphStore';

import { ReactComponent as Declaration0Icon } from './assets/declaration0.svg';
import { ReactComponent as DeclarationPaymentIcon } from './assets/declarationPayment.svg';
import { ReactComponent as DeclarationReturnIcon } from './assets/declarationReturn.svg';
import { ReactComponent as DeclarationNullIcon } from './assets/declarationNull.svg';

import { ReactComponent as CloseIcon } from './assets/close.svg';

type ToolTipDeclarationProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
};

type ToolTipDeclarationState = {
  graphID: any;
};

/**
 * Компонент всплывающей подсказки по индикаторам о декларации (узел)
 */
@inject('graphStore')
@observer
class ToolTipDeclaration extends PureComponent<ToolTipDeclarationProps, ToolTipDeclarationState> {
  /**
   * Метод отрисовывает элементы на странице
   */

  _onClose = (): void => {
    let toolTipDiv = document.getElementById('toolTipDeclaration');
    let toolTipBackGround = document.getElementById('toolTipBackGroundDeclaration');

    if (toolTipDiv && toolTipBackGround) {
      toolTipDiv.style.display = 'none';
      toolTipBackGround.style.display = 'none';
    }
  };

  render() {
    const { graphStore, id } = this.props;

    return (
      <>
        <div
          className={styles.background}
          id="toolTipBackGroundDeclaration"
          onClick={this._onClose}
        />
        <div className={styles.container} id={id}>
          <div className={styles.header} data-draggable>
            <span className={styles.header_text} data-draggable>
              Результат по НД
            </span>
            <div onClick={this._onClose}>
              <CloseIcon className={styles.close_icon} />
            </div>
          </div>
          <div className={styles.data_container}>
            {graphStore?.declarationInfo?.map((item: any) => (
              <div className={styles.containerDebt}>
                <div className={styles.iconTitle}>
                  <div className={styles.icon}>
                    {item.name === 'DeclState_1' && <DeclarationPaymentIcon />}
                    {item.name === 'DeclState_2' && <DeclarationReturnIcon />}
                    {item.name === 'DeclState_3' && <Declaration0Icon />}
                    {item.name === 'DeclState_0' && <DeclarationNullIcon />}
                  </div>
                  <div className={styles.title}>
                    {item.name === 'DeclState_1' && 'Декларация к уплате'}
                    {item.name === 'DeclState_2' && 'Декларация к возмещению'}
                    {item.name === 'DeclState_3' && 'Нулевая декларация'}
                    {item.name === 'DeclState_0' && 'Декларация не представлена'}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </>
    );
  }
}

export default ToolTipDeclaration;
