import { observer } from 'mobx-react';
import React, { useEffect, useState } from 'react';
import { useStores } from '../../hooks';

import { DetailedNodeResponse } from 'ApiModel';
import { ITooltipTaxpayerProps } from './TooltipTaxpayer.props';

import CacheProxy from '../../utils/services/api/cacheProxy';
import Loader from '../Loader/Loader';
import { Modal, ModalBody, ModalHeader } from '../modals/Modal';
import styles from './TooltipTaxpayer.module.scss';

const TooltipTaxpayer = observer(({ className, children, ...props }: ITooltipTaxpayerProps) => {
  const {
    tooltipStore: { taxpayerSettingItems, taxpayerSettingRequiredItems },
    graphStore: {
      taxpayerTooltipPosition,
      previouslyFetchedGraphId,
      taxpayerTooltipNodeId,
      changeTaxpayerTooltipIsVisible,
    },
    commonStore: { appTheme },
    periodDataFilter,
  } = useStores();

  const ndsPeriod = periodDataFilter?.options?.nds3_period_to?.split(';');
  const avgHeadcountPeriod = ndsPeriod ? `${ndsPeriod[0]} кв. ${ndsPeriod[1]} г.` : '-';

  const { x, y } = taxpayerTooltipPosition;

  const [data, setData] = useState<Record<string, DetailedNodeResponse>>({});
  const [isLoading, setIsloading] = useState<boolean>(false);

  useEffect(() => {
    const graphId = previouslyFetchedGraphId || '';

    const fetchData = async () => {
      setIsloading(true);

      const data = await CacheProxy.getDetailedNodeData(graphId, taxpayerTooltipNodeId);
      setData({ ...data });

      setIsloading(false);
    };

    fetchData();
  }, []);

  const closeTooltip = () => {
    changeTaxpayerTooltipIsVisible(false);
  };

  return (
    <div
      id="TooltipTaxpayer"
      className={`${className} ${styles.tooltip}`}
      style={{ left: x + 'px', top: y + 'px' }}
      {...props}
    >
      <Modal>
        <ModalHeader size="small" onClose={closeTooltip}>
          Детализация узла
        </ModalHeader>

        <ModalBody size="small">
          {isLoading && (
            <div className={styles.loader}>
              <Loader />
            </div>
          )}

          <div className={styles.body}>
            {taxpayerSettingRequiredItems
              .filter((item: any) => item.checked)
              .map((item: any, i: number) => (
                <div key={`TooltipTaxpayerRequired${i}`} className={styles.item}>
                  <span className={styles.label}>{item.label}</span>
                  {item.prepare(data[item.name as string])}
                </div>
              ))}

            {taxpayerSettingItems
              .filter((item: any) => item.checked)
              .map((item: any, i: number) => (
                <div key={`TooltipTaxpayer${i}`} className={styles.item}>
                  <span className={styles.label}>{item.label}</span>
                  {item.name === 'avgHeadcountPeriod' || item.name === 'avgHeadcountPeriodNDFL'
                    ? avgHeadcountPeriod
                    : item.prepare(data[(item.parentName || item.name) as string])}
                </div>
              ))}
          </div>
        </ModalBody>
      </Modal>
    </div>
  );
});

export default TooltipTaxpayer;
