import { observable, action } from 'mobx';
import { GraphStoreType } from 'StoreTypes';

/** Хранилище данных для графа */
class GraphDataStore {
  constructor(private readonly GraphStore: GraphStoreType) {}

  /** Детализированная информация связей */
  @observable
  linkDetailedData: any[] = [];

  /** Метод добавляет детализированную информацию связи */
  @action.bound
  addLinkDetailedData = (link: any) => {
    this.linkDetailedData = [/*...this.linkDetailedData*/ ...link];
  };

  /**
   * Метод удаляет детализированную информацию связи
   *
   * @param {string} id - идентификатор связи
   */
  @action.bound
  removeLinkDetailedData = (id: string) => {
    this.linkDetailedData = this.linkDetailedData.filter((link) => link.link_id !== id);
  };

  /**
   * Метод возвращает детализированную информацию связи по id
   *
   * @param {string} id - идентификатор связи
   */
  @action.bound
  getLinkDetailedData = (id: string) => {
    return this.linkDetailedData.find((link) => link.link_id === id);
  };

  /** Метод очищает массив детализированной информации связи */
  @action.bound
  resetLinkDetailedData = () => {
    this.linkDetailedData = [];
  };
}

export type GraphDataStoreType = GraphDataStore;
export default GraphDataStore;
