import { action, computed, observable, autorun } from 'mobx';
import Filter from '../utils/Filter';

import { FilterStoreType } from './filterStore';

/**
 * Хранилище фильтра подконтрольности
 */
class AccountabilityFilter extends Filter {
  FilterStore: FilterStoreType;

  constructor(FilterStore: FilterStoreType) {
    super();
    this.FilterStore = FilterStore;

    // функция срабатывает, когда изменяется одна из переданных зависимостей,
    // в данным случае this.FilterStore.options
    autorun(() => {
      const { is_control } = this.FilterStore.options;
      this.updateOptions(
        {
          is_control,
        },
        'is_control',
      );
      this.checkAllTrue();
    });
  }

  @observable keep = false;

  @observable changeStatic: boolean = false;
  @observable changeDynamic: boolean = false;

  @observable isAllFalse: boolean = false;
  @observable isAllTrue: boolean = false;

  /** Метод переключает состояние фильтра */
  @action toggleFilter = () => {
    this.keep = !this.keep;
    if (this.keep === false) {
      this.setOptionsToFalse();
      this.FilterStore.controlDynamicOptions.forEach((item: any) => {
        item.is_checked = false;
      });
    } else {
      this.setOptionsToTrue();
      this.FilterStore.controlDynamicOptions.forEach((item: any) => {
        if ('is_disabled' in item && item.is_disabled) {
          item.is_checked = false;
        } else {
          item.is_checked = true;
        }
      });
    }
    this.isChangeStatic();
    this.isChangeDynamic();
    this.isChange();
    this.checkAllTrue();
    this.FilterStore.checkChanges();
  };

  /**
   * Метод переключает динамически приходящие опции
   */
  @action toggleControlDynamicOptions = (id: any) => {
    this.FilterStore.controlDynamicOptions.forEach((item: any) => {
      if (id === item.link_subtype_id) {
        item.is_checked = !item.is_checked;
        this.checkAllFalse();
        this.checkAllTrue();
        this.keepFilterDynamic(item.is_checked, this.isAllFalse);
      }
    });
    this.isChangeStatic();
    this.isChangeDynamic();
    this.isChange();
    this.FilterStore.checkChanges();
  };

  // Происходили ли изменения среди статических фильтров
  @action isChangeStatic = () => {
    this.changeStatic = false;
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_control') {
        if (this.options[item] !== this.FilterStore.options[item]) {
          this.changeStatic = true;
        }
      }
    });
  };

  // Происходили ли изменения среди динамических фильтров
  @action isChangeDynamic = () => {
    this.changeDynamic = false;
    this.FilterStore.controlDynamicOptions.forEach((item: any) => {
      this.FilterStore.initialControlDynamicOptions.forEach((element: any) => {
        if (item.link_subtype_id === element.link_subtype_id) {
          if (item.is_checked !== element.is_checked) {
            this.changeDynamic = true;
          }
        }
      });
    });
  };

  // Общая информация об изменении Подконтрольности на основе двух видов фильтров
  @action isChange = () => {
    this.FilterStore.isAccountabilityChange = this.changeStatic || this.changeDynamic;
  };

  // Все ли фильтры (статические и динамические) отключены
  @action checkAllFalse = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    Object.keys(this.options).forEach((item: any) => {
      if (item !== 'is_control') {
        othersOptions[item] = this.options[item];
      } else {
        othersOptions[item] = false;
      }
    });
    this.isAllFalse =
      Object.keys(othersOptions).every((item: any) => othersOptions[item] === false) &&
      this.FilterStore.controlDynamicOptions.every((element: any) => element.is_checked === false);
  };

  @action checkAllTrue = () => {
    let othersOptions: any = {};
    Object.assign(othersOptions, this.options);
    delete othersOptions['is_control'];
    this.isAllTrue =
      Object.keys(othersOptions).every((item: any) => othersOptions[item] === true) &&
      this.FilterStore.controlDynamicOptions.every((element: any) => element.is_checked === true);
    if (this.isAllTrue) {
      this.keep = true;
    }
  };

  /**
   * Метод возвращает опции фильтра для запроса на фильтрацию
   */
  @computed get getFilterOptions() {
    return {
      ...this.options,
      is_control: this.keep,
    };
  }
}

export default AccountabilityFilter;
