/**
 * Функция для считывания значения из url (переход из ЕКП, АСК-НДС2)
 *
 * @param {string} url - URL
 */
export const getForCreateAskNds = (url: string) => {
  const searchValue = 'askNds2Model';
  if (url.includes(searchValue)) {
    const paramsStartsAt = url.indexOf(searchValue);
    const searchURL = url.slice(paramsStartsAt + (searchValue.length + 1), url.length);
    return searchURL;
  } else {
    return false;
  }
};
