import { inject, observer } from 'mobx-react';
import React, { Component, DetailedHTMLProps, SVGAttributes } from 'react';
import CommonStore from 'stores/common';

interface IProps extends DetailedHTMLProps<SVGAttributes<SVGSVGElement>, SVGSVGElement> {
  title?: string;
  commonStore?: CommonStore;
}

@inject('commonStore')
@observer
export class ThemeSwitch extends Component<IProps> {
  render() {
    const { title, commonStore, ...props } = this.props;
    const isLight = commonStore?.appTheme === 'light';

    return (
      <svg
        width="1em"
        onClick={() => {
          commonStore?.setTheme(isLight ? 'dark' : 'light');
        }}
        {...props}
        xmlns="http://www.w3.org/2000/svg"
      >
        <title>{title}</title>

        <circle cx={isLight ? '14' : '50'} cy="14" r="14" fill="#c8b7b780" />

        <path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M47.2916 4.28886C42.8747 7.78508 42.1093 14.2336 45.586 18.6785C49.0626 23.1235 55.4728 23.8921 59.8898 20.3959C56.631 20.8012 52.377 19.0748 49.5889 15.51C46.8007 11.9453 46.1325 7.37868 47.2916 4.28886Z"
          fill="white"
          stroke="#0B346A"
          strokeWidth="0.0174595"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M14.1759 19.6215C17.062 19.6215 19.4017 17.2753 19.4017 14.3811C19.4017 11.4869 17.062 9.14062 14.1759 9.14062C11.2898 9.14062 8.9502 11.4869 8.9502 14.3811C8.9502 17.2753 11.2898 19.6215 14.1759 19.6215Z"
          fill="#F6C000"
        />
        <path
          d="M14.1592 8.14293V4.24609"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M7.78216 14.4805H3.89624"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M9.571 9.88438L6.82324 7.12891"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M18.7443 9.88437L21.4921 7.12891"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M6.82329 21.8336L9.57104 19.0781"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M21.4924 21.8336L18.7446 19.0781"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M24.4224 14.4805H20.5365"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
        <path
          d="M14.1592 24.7718V20.875"
          stroke="#F6C000"
          strokeMiterlimit="1.5"
          strokeLinejoin="round"
        />
      </svg>
    );
  }
}
