import React from 'react';
import CollapsedMenuItem from '../Collapsed/Item';
import styles from './List.module.scss';
import { IListProps } from './List.props';

const List = ({ className, children, isExtend, itemClassName, ...props }: IListProps) => (
  <ul
    className={`${className} ${styles.list} ${isExtend ? styles.extended : styles.collapsed}`}
    {...props}
  >
    {isExtend
      ? children
      : React.Children.map(children, (child: any) => {
          if (child.props.hideInCollapsed) {
            return null;
          }

          return <CollapsedMenuItem {...child.props} itemClassName={itemClassName} />;
        })}
  </ul>
);

export default List;
