import { observable, computed, action, flow } from 'mobx';

import AsyncStore from '../utils/AsyncStore';

import AccountabilityFilter from './accountabilityFilter';
import CoherenceFilter from './coherenceFilter';
import CooperationFilter from './cooperationFilter';
import CustomLinksFilter from './customLinksFilter';
import GraphChangeFilter from './graphChangeFilter';
import TypologyFilter from './typologyFilter';
import PeriodDataFilter from './periodDataFilter';
import TaxpayersFilter from './taxpayersFilter';

import { filterGraph } from '../../utils/services/api/graph';

import { FilterOptions } from 'StoreTypes';
import { GraphStoreType } from '../graph/graphStore';
import { RootStoreType } from '../rootStore';
import { CommonStoreType } from '../common/commonStore';

/**
 * Хранилище фильтров
 */
class FilterStore extends AsyncStore {
  GraphStore: GraphStoreType;
  CommonStore: CommonStoreType;
  AccountabilityFilter: AccountabilityFilter;
  CoherenceFilter: CoherenceFilter;
  CooperationFilter: CooperationFilter;
  CustomLinksFilter: CustomLinksFilter;
  GraphChangeFilter: GraphChangeFilter;
  TypologyFilter: TypologyFilter;
  PeriodDataFilter: PeriodDataFilter;
  TaxpayersFilter: TaxpayersFilter;

  /**
   * Конструктор собирает все фильтры
   */
  constructor(RootStore: RootStoreType) {
    super();

    this.GraphStore = RootStore.GraphStore;
    this.CommonStore = RootStore.CommonStore;

    this.AccountabilityFilter = new AccountabilityFilter(this);
    this.CoherenceFilter = new CoherenceFilter(this);
    this.CooperationFilter = new CooperationFilter(this, this.GraphStore);
    this.CustomLinksFilter = new CustomLinksFilter(this);
    this.GraphChangeFilter = new GraphChangeFilter(this);
    this.TypologyFilter = new TypologyFilter(this, this.GraphStore);
    this.PeriodDataFilter = new PeriodDataFilter(this);
    this.TaxpayersFilter = new TaxpayersFilter(this, this.CommonStore, this.GraphStore);
  }

  @observable options: FilterOptions = {};

  @observable periodIsActive: boolean = false;

  @observable coherenceDynamicOptions: any = [];
  @observable controlDynamicOptions: any = [];

  @observable initialCoherenceDynamicOptions: any = [];
  @observable initialControlDynamicOptions: any = [];

  // Изменения по подконтрольности
  @observable isAccountabilityChange: boolean = false;
  // Изменения по согласованности
  @observable isCoherenceChange: boolean = false;
  // Изменения по кооперации
  @observable isCooperationChange: boolean = false;
  // Изменения по пользовательским связям
  @observable isCustomLinksChange: boolean = false;
  // Изменения по изменениям графа
  @observable isGraphChange: boolean = false;
  // Изменения по периоду учёта данных
  @observable isPeriodDataChange: boolean = false;
  // Изменения по связям, выявленными типологией
  @observable isTypologyFilter: boolean = false;
  // Изменения по налогоплательщикам
  @observable isTaxpayersChange: boolean = false;

  @observable isChangeFilter: boolean = false;

  /** Метод переключения состояния активного состояние периода */
  @action.bound
  togglePeriodActiveState = (value?: boolean) => {
    if (value) {
      this.periodIsActive = value;
    } else {
      this.periodIsActive = !this.periodIsActive;
    }
  };

  /**
   * Метод делает запрос на фильтры
   */
  @action.bound
  getGraphFilters = () => {
    this.options = this.GraphStore.filtersGraph;
    this.isChangeFilter = false;
    this.coherenceDynamicOptions = this.GraphStore.coherence_subtypes;
    this.initialCoherenceDynamicOptions = JSON.parse(
      JSON.stringify(this.GraphStore.coherence_subtypes),
    );
    this.controlDynamicOptions = this.GraphStore.control_subtypes;
    this.initialControlDynamicOptions = JSON.parse(
      JSON.stringify(this.GraphStore.control_subtypes),
    );
  };

  @action.bound
  checkChanges = () => {
    this.isChangeFilter =
      this.isAccountabilityChange ||
      this.isCoherenceChange ||
      this.isCooperationChange ||
      this.isCustomLinksChange ||
      this.isGraphChange ||
      this.isPeriodDataChange ||
      this.isTypologyFilter ||
      this.isTaxpayersChange;
  };

  /**
   * Метод делает запрос на установку фильтров
   *
   * @param {string} id - идентификатор графа
   */
  filter = flow(function* (this: FilterStore, id = this.GraphStore.previouslyFetchedGraphId) {
    this.state = 'pending';
    this.GraphStore.state = 'pending';

    try {
      const options = this.getFiltersOptions();
      const { GraphId } = yield filterGraph(id, options);

      this.GraphStore.updateGraphId(GraphId);
      yield this.GraphStore.updateGraph();
      this.isChangeFilter = false;
      this.isAccountabilityChange = false;
      this.isCoherenceChange = false;
      this.isCooperationChange = false;
      this.isCustomLinksChange = false;
      this.isGraphChange = false;
      this.isPeriodDataChange = false;
      this.isTypologyFilter = false;
      this.isTaxpayersChange = false;
    } catch (err) {
      this.onError(err);
    }

    this.GraphStore.state = 'done';
  });

  /**
   * Метод возвращает опции всех фильтров для запроса на фильтрацию
   */
  @action getFiltersOptions = () => {
    const {
      AccountabilityFilter,
      CoherenceFilter,
      CooperationFilter,
      CustomLinksFilter,
      GraphChangeFilter,
      TypologyFilter,
      PeriodDataFilter,
      TaxpayersFilter,
      coherenceDynamicOptions,
      controlDynamicOptions,
    } = this;

    let options = {};

    const PeriodDataFilterOptions = PeriodDataFilter.getFilterOptions;
    options = {
      ...options,
      ...PeriodDataFilterOptions,
    };

    if (AccountabilityFilter.keep) {
      const AccountabilityFilterOptions = AccountabilityFilter.getFilterOptions;
      options = {
        ...options,
        ...AccountabilityFilterOptions,
        control_subtypes: [...controlDynamicOptions],
      };
    } else {
      options = {
        ...options,
        control_subtypes: [...controlDynamicOptions],
      };
    }

    if (CoherenceFilter.keep) {
      const CoherenceFilterOptions = CoherenceFilter.getFilterOptions;
      options = {
        ...options,
        ...CoherenceFilterOptions,
        coherence_subtypes: [...coherenceDynamicOptions],
      };
    } else {
      options = {
        ...options,
        coherence_subtypes: [...coherenceDynamicOptions],
      };
    }

    if (CooperationFilter.keep) {
      const CooperationFilterOptions = CooperationFilter.getFilterOptions;
      options = {
        ...options,
        ...CooperationFilterOptions,
      };
    }

    if (CustomLinksFilter.keep) {
      const CustomLinksFilterOptions = CustomLinksFilter.getFilterOptions;
      options = {
        ...options,
        ...CustomLinksFilterOptions,
      };
    }

    if (TaxpayersFilter.keep) {
      const TaxpayersFilterOptions = TaxpayersFilter.getFilterOptions;
      options = {
        ...options,
        ...TaxpayersFilterOptions,
      };
    }

    if (GraphChangeFilter.keep) {
      const GraphChangeFilterOptions = GraphChangeFilter.getFilterOptions;
      options = {
        ...options,
        ...GraphChangeFilterOptions,
      };
    }

    if (TypologyFilter.keep) {
      const TypologyFilterOptions = TypologyFilter.getFilterOptions;
      options = {
        ...options,
        ...TypologyFilterOptions,
      };
    }

    return options;
  };

  /**
   * Метод сбрасывает настройки фильтров
   */
  @action resetFilters = (showABD?: any, showNDS?: any, graphExpandType?: any) => {
    const {
      AccountabilityFilter,
      CoherenceFilter,
      CooperationFilter,
      CustomLinksFilter,
      GraphChangeFilter,
      PeriodDataFilter,
      TypologyFilter,
      TaxpayersFilter,
    } = this;

    AccountabilityFilter.setOptionsToTrue();
    CoherenceFilter.setOptionsToTrue();
    CooperationFilter.reset();
    CooperationFilter.setOptionsToTrueCooperationExpandType(
      showABD,
      showNDS,
      graphExpandType,
      this.GraphStore.sourceSystem,
    );
    CustomLinksFilter.reset();
    GraphChangeFilter.setOptionsToTrue();
    PeriodDataFilter.setOptionsToTrue();
    TypologyFilter.resetFilters();
    TaxpayersFilter.resetFiltersAndSearch();
    this.isChangeFilter = false;
    this.periodIsActive = false;

    this.filter();
  };

  /**
   * Метод возвращает список опций
   */
  @computed get publicOptions() {
    return this.options;
  }
}

export type FilterStoreType = FilterStore;
export default FilterStore;
