import { ReactComponent as ChangeI } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/change_i.svg';
import { ReactComponent as ChangeK } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/change_k.svg';
import { ReactComponent as ChangeN } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/change_n.svg';
import { ReactComponent as ChangeU } from '@fdc/vsa-gojs-lib/lib/assets/icons/indicators/change_u.svg';
import React, { Fragment } from 'react';
import styles from './GoIcons.module.scss';

type Status = 'N' | 'K' | 'U' | 'I';

interface StatusesIconsProps {
  statuses: Status[];
  iconClassname?: string;
  size?: string | number;
  height?: string | number;
  width?: string | number;
}

export const StatusesIcons = ({
  statuses,
  size,
  width = 20,
  height = 20,
  iconClassname,
}: StatusesIconsProps) => {
  return (
    <>
      {statuses?.length > 0 &&
        statuses.map((status, i) => {
          switch (status) {
            case 'N':
              return (
                <ChangeN
                  key={status}
                  className={`${styles.icon} ${iconClassname}`}
                  height={size || height}
                  width={size || width}
                />
              );
            case 'K':
              return (
                <ChangeK
                  key={status}
                  className={`${styles.icon} ${iconClassname}`}
                  height={size || height}
                  width={size || width}
                />
              );
            case 'U':
              return (
                <ChangeU
                  key={status}
                  className={`${styles.icon} ${iconClassname}`}
                  height={size || height}
                  width={size || width}
                />
              );
            case 'I':
              return (
                <ChangeI
                  key={status}
                  className={`${styles.icon} ${iconClassname}`}
                  height={size || height}
                  width={size || width}
                />
              );
            default:
              return <Fragment key={i} />;
          }
        })}
    </>
  );
};
