export type Quarter = 'I' | 'II' | 'III' | 'IV';

export const getQuarterNumber = (quarter: Quarter) => {
  const quarters = {
    I: 1,
    II: 2,
    III: 3,
    IV: 4,
  };

  return quarters[quarter];
};

export const getQuarterRoman = (quarter: string) => {
  const quarters = {
    '1': 'I',
    '2': 'II',
    '3': 'III',
    '4': 'IV',
  };

  return quarters?.[quarter as '1' | '2' | '3' | '4'] || '';
};

export const getPeriodNumbers = (period: string) => {
  const dateArray = period?.split(';');

  return {
    year: Number(dateArray[1]),
    quarter: getQuarterNumber(dateArray[0] as Quarter),
  };
};
