import React from 'react';
import { optionPropTypes } from './Option.props';
import filterStyles from '../common/Filter.module.scss';

interface OptionProps {
  value: any;
  onToggleCheck: () => void;
  className?: string;
  isActive?: boolean;
}

/**
 * Компонент опции фильтров
 */
const Option = ({ value, className }: OptionProps) => {
  return <span className={`${filterStyles.optionLabel} ${className}`}>{value}</span>;
};

Option.propTypes = optionPropTypes;

Option.defaultProps = {
  className: '',
  isActive: false,
};

export default Option;
