    super();

    this.RootStore = RootStore;
    this.UserGraphsStore = RootStore.UserGraphsStore;
    this.DiagramStore = new DiagramStore(this);
    this.GraphDataStore = new GraphDataStore(this);
    this.GroupDetailStore = new GroupDetailStore(this);

    this.ClusterStore = new ClusterStore(this);
    this.GraphCommonStore = new GraphCommonStore(this);
    this.GraphNodesStore = new GraphNodesStore(this);
    this.GraphLinksStore = new GraphLinksStore(this);

    this.ModalStore = new ModalStore(this);

    this.setIsTestGraph = this.GraphCommonStore.setIsTestGraph;
    this.checkIfGraphIsTestAndSetValue = this.GraphCommonStore.checkIfGraphIsTestAndSetValue;
    this.editGraph = this.GraphCommonStore.editGraph;
    this.saveDirection = this.GraphCommonStore.saveDirection;
    this.toggleDrawingMode = this.GraphCommonStore.toggleDrawingMode;
    this.changeDrawingMode = this.GraphCommonStore.changeDrawingMode;
    this.changeRoles = this.GraphCommonStore.changeRoles;
    this.schemeBack = this.GraphCommonStore.schemeBack;
    this.schemeForward = this.GraphCommonStore.schemeForward;
    this.copyGraph = this.GraphCommonStore.copyGraph;
    this.getFormalReference = this.GraphCommonStore.getFormalReference;
    this.fetchInitialGraph = this.GraphCommonStore.fetchInitialGraph;
    this.freeSearch = this.GraphCommonStore.freeSearch;
    this.beginingWork = this.GraphCommonStore.beginingWork;
    this.fetchDetailedCluster = this.GraphCommonStore.fetchDetailedCluster;
    this.getUploadedDocument = this.GraphCommonStore.getUploadedDocument;
    this.getTreeReport = this.GraphCommonStore.getTreeReport;
    this.payersRecipients = this.GraphCommonStore.payersRecipients;
    this.goToDnpByFid = this.GraphCommonStore.goToDnpByFid;
    this.getExportCluster = this.GraphCommonStore.getExportCluster;
    this.savePotentialDamage = this.GraphCommonStore.fetchPotentialDamage;
    this.fetchNPCluster = this.GraphCommonStore.fetchNPCluster;
    this.getPrintScheme = this.GraphCommonStore.getPrintScheme;

    this.createNode = this.GraphNodesStore.createNode;
    this.hideNodes = this.GraphNodesStore.hideNodes;
    this.showNodes = this.GraphNodesStore.showNodes;
    this.setHiddenNodesMode = this.GraphNodesStore.setHiddenNodesMode;
    this.toggleVisibleNodesMode = this.GraphNodesStore.toggleVisibleNodesMode;
    this.createGroupFromNode = this.GraphNodesStore.createGroupFromNode;
    this.nodeOutOfGroup = this.GraphNodesStore.nodeOutOfGroup;
    this.removeFromVirtualGroup = this.GraphNodesStore.removeFromVirtualGroup;
    this.addNodeToGroup = this.GraphNodesStore.addNodeToGroup;
    this.fetchDetailedNodeData = this.GraphNodesStore.fetchDetailedNodeData;
    this.focusNode = this.GraphNodesStore.focusNode;
    this.createNodeFL = this.GraphNodesStore.createNodeFL;

    this.createNewLink = this.GraphLinksStore.createNewLink;
    this.editLink = this.GraphLinksStore.editLink;
