  @observable interQuarterSearch: boolean = false;
  // Вычеты по кварталам (всплывающая информация для связи)
  @observable interQuarterPeriods: any = [];
  // Квартал (всплывающая информация для связи)
  @observable coopLinkPeriods: any = [];

  // Потенциальный ущерб
  @observable amountOfDamage: any = '';

