type Params = {
  [key: string]: string | number | boolean;
};

/**
 * Функция считывает все параметры из URL и возвращает их в объекте вида { параметр : значение }
 *
 * @param {string} url - URL
 */
export const readParamsFromURL = (url: string) => {
  const params: Params = {};

  let currentParamKey = '';
  let currentParamValue = '';
  let paramKeyPassed = false;

  const paramsStartsAt = url.indexOf('?');
  for (let i = paramsStartsAt + 1; i < url.length; i++) {
    if (url[i] === '?') {
      throw new Error('URL is not valid.');
    }

    if (url[i] === '&' || i + 1 === url.length) {
      if (i + 1 === url.length) {
        currentParamValue += url[i];
      }

      params[currentParamKey] = currentParamValue;

      currentParamKey = '';
      currentParamValue = '';
      paramKeyPassed = false;

      continue;
    }

    if (paramKeyPassed) {
      currentParamValue += url[i];
    } else {
      if (url[i] === '=') {
        paramKeyPassed = true;
        continue;
      }
      currentParamKey += url[i];
    }
  }

  return params;
};
