import { observable, action, computed, flow } from 'mobx';
import AsyncStore from './utils/AsyncStore';

import { sortByProp } from '../utils/sortByProp';

import { fetchUserGraphs, deleteGraph } from '../utils/services/api/common';

import { UserGraph } from 'DataTypes';
import { RootStoreType } from 'StoreTypes';

import moment from 'moment';
import { IPagination } from '@fdc/vsa-react-lib';

/**
 * Хранилище для раздела 'Мои графы'
 */
class UserGraphs extends AsyncStore {
  RootStore: RootStoreType;

  constructor(RootStore: RootStoreType) {
    super();

    this.RootStore = RootStore;
  }

  // количество элементов для отображения последних графов
  lastGraphListLength = 20;

  // список графов пользователя
  @observable initialUserGraphs: UserGraph[] = [];
  @observable userGraphs: UserGraph[] = [];
  @observable filteredUserGraphs: UserGraph[] = [];

  @observable searchValue = '';

  @observable currentSort = {
    prop: 'updated',
    reversed: false,
  };

  // флаг показывающий, пуст ли список графов пользователя
  @observable listIsEmpty = false;

  // флаг показывающий применять ли сортировку для списка
  @observable applySort = false;
  // флаг показывающий применять ли поиск для списка
  @observable applySearch = false;

  // список отсортированных графов
  @observable sortedUserGraphs: UserGraph[] = [];

  // список графов из поиска
  @observable searchedUserGraphs: UserGraph[] = [];

  @observable showList = false;

  @observable pagination: IPagination = {
    page: 0,
    count: 10,
    total: 0,
  };

  /**
   * Метод отправляет запрос на удаление графа по ID
   *
   * @param {string} graphId - идентификатор графа
   */
  deleteGraph = flow(function* (this: UserGraphs, id: string) {
    this.state = 'pending';

    try {
      yield deleteGraph(id);

      this.state = 'done';
      this.removeGraph(id);
    } catch (err) {
      this.onError(err);
    }
  });

  /**
   * Метод делает запрос для получения графов пользователя
   */
  fetchUserGraphs = flow(function* (this: UserGraphs) {
    this.state = 'pending';

    try {
      const result: UserGraph[] = yield fetchUserGraphs();
      this.initialUserGraphs = result;
      this.updateGraphs();

      if (!result.length) {
        this.listIsEmpty = true;
      } else {
        this.listIsEmpty = false;
      }

      this.state = 'done';
    } catch (err) {
      this.onError(err);
    }
  });

  @action changePagination = (pagination: IPagination) => {
    this.pagination = pagination;
    this.setPagination();
  };

  /**
   * Метод поиска графа из списка
   *
   * Ищет вхождение текста в любое из свойств объекта
   *
   * @param {string} text - текст для поиска
   */
  @action search = (text: string): void => {
    this.searchValue = text;
    this.updateGraphs();
  };

  /**
   * Метод сортирует графы по заданному свойству
   *
   * @param {string} prop - свойство для сортировки
   * @param {boolean} reversed - флаг показывающий направление для сортировки
   */
  @action sort = (prop: string, reversed: boolean = false): void => {
    this.currentSort = { prop, reversed };
    this.updateGraphs();
  };

  @action setFiltered = (): void => {
    this.filteredUserGraphs =
      this.searchValue.length > 0
        ? this.initialUserGraphs.filter((item: UserGraph) =>
            item.name.toLowerCase().includes(this.searchValue.toLowerCase()),
          )
        : this.initialUserGraphs;
  };

  @action setSorted = (): void => {
    const sortOrder = this.currentSort.reversed ? -1 : 1;

    let sortedList;
    if (this.currentSort.prop === 'updated') {
      sortedList = this.filteredUserGraphs
        .slice()
        .sort((a, b) =>
          moment(a[this.currentSort.prop], 'DD.MM.YYYY hh:mm:ss').isBefore(
            moment(b[this.currentSort.prop], 'DD.MM.YYYY hh:mm:ss'),
          )
            ? 1 * sortOrder
            : -1 * sortOrder,
        );
    } else if (this.currentSort.prop === 'name') {
      sortedList = this.filteredUserGraphs.slice().sort((a, b) =>
        a[this.currentSort.prop].localeCompare(b[this.currentSort.prop], undefined, {
          numeric: true,
          sensitivity: 'base',
        }) === 1 || 0
          ? 1 * sortOrder
          : -1 * sortOrder,
      );
    } else {
      sortedList = sortByProp<UserGraph>(this.initialUserGraphs, this.currentSort.prop, sortOrder);
    }

    this.filteredUserGraphs = sortedList;
  };

  @action setPagination = () => {
    this.userGraphs = this.filteredUserGraphs.slice(
      this.pagination.page * this.pagination.count,
      (this.pagination.page + 1) * this.pagination.count,
    );
  };

  @action updateGraphs = () => {
    this.setFiltered();
    this.setSorted();
    this.resetPagination();
    this.setPagination();
  };

  /**
   * Метод удаляет граф из списка
   *
   * @param {string} id - идентификатор графа
   */
  @action removeGraph = (id: string) => {
    this.userGraphs = this.userGraphs.filter((item: UserGraph) => item.graph_id !== id);
  };

  /**
   * Метод изменяет название графа
   *
   * @param {string} id - идентификатор графа
   * @param {string} name - название графа
   */
  @action renameGraph = (id: string, name: string) => {
    this.userGraphs = this.userGraphs.map((item: UserGraph) => {
      if (item.graph_id === id) {
        return {
          ...item,
          name,
        };
      }

      return item;
    });
  };

  /**
   * Метод сбрасывает сортировку
   */
  @action resetSort = () => {
    this.sort('updated', false);
    this.updateGraphs();
  };

  /**
   * Метод сбрасывает поиск
   */
  @action resetSearch = () => {
    this.search('');
    this.updateGraphs();
  };

  @action resetPagination = () => {
    this.pagination = {
      ...this.pagination,
      page: 0,
      total: this.filteredUserGraphs.length,
    };
  };

  /**
   * Метод переключаем режим показа списка графов в меню
   */
  @action showGraphsList = () => {
    this.showList = true;
  };

  /**
   * Метод переключаем режим показа списка графов в меню
   */
  @action hideGraphsList = () => {
    this.showList = false;
  };

  /**
   * Метод возвращает список графов для отображения
   */
  @computed get graphList() {
    if (this.applySearch && this.applySort) {
      return this.searchedUserGraphs;
    }

    if (this.applySearch) {
      return this.searchedUserGraphs;
    }

    if (this.applySort) {
      return this.sortedUserGraphs;
    }

    return this.userGraphs
      .slice()
      .sort((a, b) =>
        moment(a.updated, 'DD.MM.YYYY hh:mm:ss').isBefore(moment(b.updated, 'DD.MM.YYYY hh:mm:ss'))
          ? 1
          : -1,
      );
  }

  /**
   * Метод возвращает список последних графов
   */
  @computed get lastGraphsList() {
    const sortedGraphs = this.userGraphs
      .slice(0, this.lastGraphListLength)
      .sort((a, b) =>
        moment(a.updated, 'DD.MM.YYYY hh:mm:ss').isBefore(moment(b.updated, 'DD.MM.YYYY hh:mm:ss'))
          ? 1
          : -1,
      );
    return sortedGraphs;
  }
}

export type UserGraphsStoreType = UserGraphs;
export default UserGraphs;
