/**
 * Форматирование денежной суммы (для Детализации Узла)
 */
export const formatMoneyDetailedNode = (amount: string | number, withCurrency = false) => {
  if (typeof amount === 'string') {
    amount = +amount.replace(/,/, '.');
  }

  const numberFormat = new Intl.NumberFormat('ru-RU', {
    style: 'currency',
    maximumFractionDigits: 0,
    minimumFractionDigits: 0,
    currency: 'rub',
  });

  if (amount === null || amount == 0) {
    return 'нет данных';
  } else {
    const str = numberFormat.format(amount);
    return withCurrency ? str : str.slice(0, str.length - 1);
  }
};
