import React, { PureComponent } from 'react';
import { typologyPropTypes } from './Typology.props';
import filterStyles from '../common/Filter.module.scss';
import styles from './Typology.module.scss';

import Icon from '../../../components/Icon';
import Option from '../Option';
import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';
import { GraphStoreType } from '../../../stores/graph/graphStore';

interface TypologyProps {
  isActive: boolean;
  onToggleCheck: () => void;
  toggleOpenState?: () => void;
  typologyFilter?: any;
  isOpen?: boolean;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент блока пользовательских связей для фильтров
 */
@inject('typologyFilter', 'commonStore', 'graphStore')
@observer
class Typology extends PureComponent<TypologyProps> {
  static propTypes = typologyPropTypes;

  render() {
    const { isOpen, toggleOpenState, typologyFilter, commonStore, graphStore } = this.props;

    return (
      <li className={`${filterStyles.wrapper} ${styles.wrapper}`}>
        <div className={filterStyles.header}>
          <span className={filterStyles.headerLabel} onClick={toggleOpenState}>
            Связи по схеме
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {isOpen && (
          <div className={styles.container}>
            <div className={styles.optionContainer}>
              <Option
                className={styles.option}
                isActive={typologyFilter.options?.is_cooperation_from_typology}
                onToggleCheck={
                  graphStore?.showChangeFilters &&
                  (!graphStore?.schemeId ||
                    (graphStore?.schemeId && graphStore?.sourceSystem === 'MDAB')) &&
                  typologyFilter.toggleCooperationNdsFromTypology
                }
                value={'Кооперация в схеме'}
                title={
                  graphStore?.schemeId && graphStore?.sourceSystem !== 'MDAB'
                    ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                    : 'Признак отображения связей вида Кооперация по данным схемы, выявленной Типологией'
                }
                isAllCheck
              />
            </div>
            <div className={styles.optionContainer}>
              <Option
                className={styles.option}
                isActive={typologyFilter.options.is_control_from_typology}
                onToggleCheck={graphStore?.showChangeFilters && typologyFilter.toggleFilterControl}
                value={'Подконтрольность'}
                title={
                  'Признак отображения связей вида Подконтрольность по данным схемы, выявленной Типологией'
                }
                isAllCheck
              />
            </div>
            <div className={styles.optionContainer}>
              <Option
                className={styles.option}
                isActive={typologyFilter.options.is_coherence_from_typology}
                onToggleCheck={
                  graphStore?.showChangeFilters && typologyFilter.toggleFilterCoherence
                }
                value={'Согласованность'}
                title={
                  'Признак отображения связей вида Согласованность по данным схемы, выявленной Типологией'
                }
                isAllCheck
              />
            </div>
            <div className={styles.optionContainer}>
              <Option
                className={styles.option}
                isActive={typologyFilter.options?.is_dependence_from_typology}
                onToggleCheck={
                  graphStore?.showChangeFilters && typologyFilter.toggleDependenceFromTypology
                }
                value={'Взаимозависимость в схеме'}
                title={
                  'Признак отображения связей вида Взаимозависимость по данным схемы, выявленной Типологией'
                }
                isAllCheck
              />
            </div>
          </div>
        )}
      </li>
    );
  }
}

export default Typology;
