import * as go from 'gojs';
import { action, computed, observable, reaction } from 'mobx';

import DiagramStore from './diagramStore';

/**
 * Хранилище состояния масштаба графа
 */
class ZoomStore {
  DiagramStore: DiagramStore;

  constructor(DiagramStore: DiagramStore) {
    this.DiagramStore = DiagramStore;

    reaction(() => this.scale, this.setScale);
  }

  @observable zoom = 100;

  /**
   * Метод инициализирует позицию и размеры диаграммы
   */
  @action initOverview = () => {
    const overview = go.GraphObject.make(go.Overview, {
      contentAlignment: go.Spot.Center,
      computePixelRatio: () => {
        shape.strokeWidth = 12;
        return window.devicePixelRatio;
      },
    });
    const shape = overview.box.elt(0) as go.Shape;
    shape.figure = 'Rectangle';
    shape.stroke = '#006CE8';
    return overview;
  };

  /**
   * Метод устанавливает значения масштаба диаграммы
   *
   * @param {number} value - масштаб
   */
  @action setZoom = (value: number): void => {
    let zoomToSet = value;
    if (zoomToSet < 1) {
      zoomToSet = 1;
    }

    if (zoomToSet > 200) {
      zoomToSet = 200;
    }

    this.zoom = value;

    if (this.DiagramStore?.diagram) {
      this.DiagramStore.diagram.scale = this.zoom / 100;
    }
  };

  /**
   * Метод устанавливает масштаб диаграммы
   */
  @action setScale = () => {
    if (this.DiagramStore.diagram) {
      this.zoom = this.DiagramStore.diagram.scale * 100;
    }
  };

  /**
   * Метод устанавливает масштаб на весь экран
   */
  @action setZoomToFit = () => {
    if (this.DiagramStore?.diagram) {
      this.DiagramStore?.diagram.zoomToFit();
      this.zoom = this.DiagramStore?.diagram.scale * 100;
    }
  };

  /**
   * Метод возвращает масштаб диаграммы
   */
  @computed get scale() {
    return this.DiagramStore.diagram?.scale;
  }
}

export type ZoomStoreType = ZoomStore;
export default ZoomStore;
