import { ReactComponent as CheckboxAllIcon } from 'assets/icons/checkboxAll.svg';
import { ReactComponent as CheckboxIcon } from 'assets/icons/checkboxSome.svg';
import React from 'react';
import styles from './Checkbox.module.scss';
import { ICheckboxProps } from './Checkbox.props';

const Checkbox = ({
  className,
  children,
  title,
  checked,
  disabled,
  dimension = 'medium',
  mode = 'primary',
  isAll,
  ...props
}: ICheckboxProps) => {
  return (
    <label
      title={title}
      className={`${styles.container} ${styles[dimension]} ${disabled && styles.disabled} ${
        styles[mode]
      } ${checked && styles.checked} ${className}`}
    >
      <input
        {...props}
        checked={checked}
        type="checkbox"
        className={styles.input}
        disabled={disabled}
      />

      <div className={`${styles.icon} ${checked && styles.checked} ${disabled && styles.disabled}`}>
        {checked ? isAll ? <CheckboxAllIcon /> : <CheckboxIcon /> : null}
      </div>

      {children && (
        <div className={`${styles.label} ${isAll && styles.isAll} ${disabled && styles.disabled}`}>
          {children}
        </div>
      )}
    </label>
  );
};

export default Checkbox;
