import { RootStoreType, UserGraphsStoreType } from 'StoreTypes';
import { action, observable } from 'mobx';
import { ReactNode } from 'react';
import { SCREEN_MODES } from './constants/screenModes';

/**
 * Хранилище состояния интерфейса приложения
 */
export interface IDialog {
  title?: string;
  type?: string;
  opened?: boolean;
  text: ReactNode;
  additionalText?: string;
  okText?: string;
  cancelText?: string;
  dialogClassName?: string;
  onConfirm?: () => void;
  onCancel?: () => void;
}

const initialDialog: IDialog = {
  title: '',
  type: 'alert',
  opened: false,
  text: '',
  additionalText: '',
  okText: 'OK',
  cancelText: 'Отмена',
  dialogClassName: '',
  onConfirm: () => {},
  onCancel: () => {},
};

class UIStore {
  RootStore: RootStoreType;
  UserGraphsStore: UserGraphsStoreType;

  constructor(RootStore: RootStoreType) {
    this.RootStore = RootStore;
    this.UserGraphsStore = RootStore.UserGraphsStore;
  }

  // флаг для бургер меню
  @observable menuExtended = false;

  // флаг для легенды
  @observable legendIsOpen = false;

  // флаг для фильтров
  @observable filtersIsOpen = false;

  // флаг для настроек тултипа НП
  @observable taxpayerSettingsIsOpen = false;

  // режим экрана
  @observable screenMode: string = this.layout();

  // флаг для фильтра на индикаторы
  @observable indicatorsIsOpen = false;

  @observable chainsIsOpen = false;

  // флаг для открытия легенды из свернутого меню
  legendOpenedFromCollapsedMenu = false;

  @observable dialog = initialDialog;

  @action openDialog = (params: IDialog) => {
    this.dialog = { ...params, opened: true };
  };

  @action closeDialog = () => {
    this.dialog = initialDialog;
  };

  /**
   * Метод переключает состояние меню
   */
  @action toggleMenuState = (): void => {
    if (this.filtersIsOpen) {
      this.filtersIsOpen = false;
    }

    if (this.indicatorsIsOpen) {
      this.indicatorsIsOpen = false;
    }

    this.taxpayerSettingsIsOpen = false;

    this.menuExtended = !this.menuExtended;
  };

  /**
   * Метод переключает состояние легенды
   */
  @action toggleLegendState = (): void => {
    this.legendIsOpen = !this.legendIsOpen;
  };

  /**
   * Метод переключает состояние фильтра на индикаторы
   */
  @action toggleIndicatorsState = (): void => {
    if (this.menuExtended) {
      this.indicatorsIsOpen = !this.indicatorsIsOpen;
    }
  };

  /**
   * Метод переключает состояние фильтров
   */
  @action toggleFiltersState = (): void => {
    this.filtersIsOpen = !this.filtersIsOpen;
  };

  /**
   * Метод переключает состояние настроек тултипа НП
   */
  @action toggleTaxpayerSettingsState = (): void => {
    this.taxpayerSettingsIsOpen = !this.taxpayerSettingsIsOpen;
  };

  @action toggleChainsIsOpen = (): void => {
    if (this.RootStore.ChainsStore.isCollapsed) {
      this.RootStore.ChainsStore.setIsCollapsed(false);
      this.chainsIsOpen = true;
    } else {
      this.chainsIsOpen = !this.chainsIsOpen;
    }
  };

  @action setChainsIsOpen = (value: boolean): void => {
    this.chainsIsOpen = value;
  };

  /**
   * Метод изменяет режим верстки
   */
  @action setScreenMode = (): void => {
    this.screenMode = this.layout();
  };

  /**
   * Метод возвращает режим верстки
   */
  @action layout() {
    const userScreenWidth = window.innerWidth;

    if (userScreenWidth >= SCREEN_MODES.FULL.minSize) {
      return SCREEN_MODES.FULL.name;
    } else if (userScreenWidth >= SCREEN_MODES.MIDDLE.minSize) {
      return SCREEN_MODES.MIDDLE.name;
    } else if (userScreenWidth >= SCREEN_MODES.MEDIUM.minSize) {
      return SCREEN_MODES.MEDIUM.name;
    } else {
      return SCREEN_MODES.MINOR.name;
    }
  }
}

export type UIStoreType = UIStore;
export default UIStore;
