import React, { PureComponent } from 'react';
import { diagramPropTypes } from './Diagram.props';
import './Diagram.scss';

import Loader from '../Loader';
import { ReactDiagram } from 'gojs-react';

import { Diagram as DiagramType } from 'gojs';
import { GraphStoreType } from 'StoreTypes';
import { inject, observer } from 'mobx-react';

interface DiagramProps {
  // диаграмма
  diagram: DiagramType;

  // массив узлов
  nodeDataArray: any[];

  // массив связей
  linkDataArray: any[];

  // показывает грузиться ли граф
  isGraphLoading: boolean;

  // показывает грузятся ли справочники
  isDictsLoading: boolean;

  // пропускать ли обновления диарграммы
  skipsDiagramUpdate?: boolean;

  // функция для инициализации диарграммы
  initDiagram: () => DiagramType;

  // хранилище графов
  graphStore?: GraphStoreType;

  // состояние загрузки
  stateLoading?: string;
}

/**
 * Компонент диаграммы
 */
@inject('graphStore')
@observer
class Diagram extends PureComponent<DiagramProps> {
  static propTypes = diagramPropTypes;

  static defaultProps = {
    skipsDiagramUpdate: false,
  };

  // Наличие водяного знака
  private isWaterMark: boolean = false;

  // Соответствие логину шаблока вида: xxxx-xx-xxx
  private isRegExp: boolean = false;

  /**
   * Метод перерисовывает обновленные части диаграммы
   */
  componentDidUpdate() {
    const { diagram, stateLoading, graphStore } = this.props;
    const { schemeInfo } = graphStore;

    if (stateLoading && !this.isWaterMark) {
      const currentLogin = schemeInfo.current_login;
      if (currentLogin.match(/^\d{4}-\d{2}-\d{3}$/)) {
        this.isRegExp = true;
      }

      // родительский div, в котором будет водяной знак
      const markContainer = document.querySelector('.diagram-component');

      // создание экземпляра водяного знака, чтобы по нему посчитать ширину, высоту и, соответственно, отступы и количесво
      const mark = document.createElement('div');
      mark.classList.add('mark');

      const markNumber = document.createElement('div');
      markNumber.classList.add('mark__number');
      markNumber.textContent = this.isRegExp ? currentLogin.slice(0, 4) : currentLogin || '';

      const markName = document.createElement('div');
      markName.classList.add('mark__name');
      markName.textContent = this.isRegExp
        ? currentLogin.slice(4).replace(/-/g, '')
        : currentLogin || '';

      mark.appendChild(markNumber);
      mark.appendChild(markName);

      markContainer?.appendChild(mark);

      // отступы и количество на основе высоты и нирины
      const markWidth = parseInt(
        // @ts-ignore
        window.getComputedStyle(document.querySelector('.mark'), null).getPropertyValue('width'),
      );

      const markHeight = parseInt(
        // @ts-ignore
        window.getComputedStyle(document.querySelector('.mark'), null).getPropertyValue('height'),
      );

      const yOffset =
        parseInt(
          window
            // @ts-ignore
            .getComputedStyle(document.querySelector('.mark__number'), null)
            .getPropertyValue('width'),
        ) * 1.6;

      const xOffset = parseInt(
        window
          // @ts-ignore
          .getComputedStyle(document.querySelector('.mark__name'), null)
          .getPropertyValue('width'),
      );

      const xNumber = window.innerWidth / (markWidth + xOffset) + 1;
      const yNumber = window.innerHeight / (markHeight + yOffset);

      mark.remove();

      // вывод рядов циклом
      for (let i = 0; i < yNumber + 10; i++) {
        const markRow = document.createElement('div');
        markRow.classList.add('mark__row');

        // и водяные знаки в рядах
        for (let j = 0; j < xNumber + 10; j++) {
          if (i !== 0 || j !== 0) {
            const mark = document.createElement('div');
            mark.classList.add('mark');
            mark.style.left = (markWidth + xOffset) * j + 'px';
            mark.style.width = markWidth + 1 + 'px';

            const markNumber = document.createElement('div');
            markNumber.classList.add('mark__number');
            markNumber.textContent = this.isRegExp ? currentLogin.slice(0, 4) : currentLogin || '';

            const markName = document.createElement('div');
            markName.classList.add('mark__name');
            markName.textContent = (this.isRegExp && currentLogin.slice(4).replace(/-/g, '')) || '';

            mark.appendChild(markNumber);
            mark.appendChild(markName);
            markRow.appendChild(mark);
          }
        }
        markRow.style.top = (markHeight + yOffset) * i + 'px';
        markContainer?.appendChild(markRow);
      }
      this.isWaterMark = true;
    }
    diagram.rebuildParts();
  }

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const {
      initDiagram,
      isGraphLoading,
      isDictsLoading,
      nodeDataArray,
      linkDataArray,
      skipsDiagramUpdate,
    } = this.props;

    return (
      <>
        {(isGraphLoading || isDictsLoading) && <Loader fullscreen />}
        <ReactDiagram
          divClassName={'diagram-component'}
          skipsDiagramUpdate={skipsDiagramUpdate!}
          onModelChange={() => {}}
          // @ts-ignore
          initDiagram={initDiagram}
          nodeDataArray={nodeDataArray}
          linkDataArray={linkDataArray}
        />
      </>
    );
  }
}

export default Diagram;
