import * as go from 'gojs';
import { inject, observer } from 'mobx-react';
import React from 'react';
import ReactTooltip from 'react-tooltip';
import { getPeriodNumbers } from 'utils/getPeriodNumbers';
import UnifiedClientFacade from 'utils/services/UnifiedClientFacade';
import {
  DETAILED_NODE_MODAL,
  MODAL_DETAILED_INDIVIDUAL_PERSON,
} from '../modals/constants/modalNames';
import './ContextMenuNode.scss';
import { ReactComponent as AgentsIcon } from './assets/agents.svg';
import { ReactComponent as AnalyticalIcon } from './assets/analytical.svg';
import { ReactComponent as BuyersIcon } from './assets/buyers.svg';
import { ReactComponent as DetailedIcon } from './assets/detailed.svg';
import { ReactComponent as GroupIcon } from './assets/group.svg';
import { ReactComponent as HideIcon } from './assets/hide.svg';
import { ReactComponent as LinkFromIcon } from './assets/link_from.svg';
import { ReactComponent as LinkToIcon } from './assets/link_to.svg';
import { ReactComponent as SellersIcon } from './assets/sellers.svg';
import { ReactComponent as TreeIcon } from './assets/tree.svg';

// interface ContextMenuNodeProps {
//   diagramStore?: DiagramStoreType;
//   modalStore?: ModalStoreType;
//   graphStore?: GraphStoreType;
//   filterStore?: FilterStoreType;
//   [key: string]: any;
// }

/**
 * Компонент контекстное меню узла
 */
@inject('graphStore', 'diagramStore', 'modalStore', 'filterStore')
@observer
class ContextMenuNode extends React.Component<any> {
  // Объект пользовательского контекст меню
  CustomContextMenuNode = go.GraphObject.make(go.HTMLInfo, {
    show: null,
    hide: null,
  });

  /**
   * Метод открытия узла для детализации
   */
  _onNodeDetails = (isAnalytic: boolean) => {
    const { diagramStore, modalStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection?.first()?.data;
    const taxpayer = diagramStore?.diagram?.selection?.first()?.data;
    const keyZombie = nodeData.key;

    if (nodeData.node.subject_type === 'FL') {
      const isShow = true;
      const disabledINN = false;
      const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

      const orig_id =
        graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

      modalStore?.openModal(MODAL_DETAILED_INDIVIDUAL_PERSON, {
        taxpayer,
        orig_id,
        isShow,
        disabledINN,
        isAnalytic,
      });
    } else {
      modalStore?.openModal(DETAILED_NODE_MODAL, { nodeData, keyZombie, isAnalytic });
    }
  };

  /**
   * Метод скрытия узла
   */
  _onHideNode = () => {
    const { diagramStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection.first()!.data;

    const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orig_id =
      graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

    const nodesOrIds =
      diagramStore.selectedNodes.length > 1 ? diagramStore.selectedNodes : [orig_id];

    graphStore?.hideNodes(nodesOrIds);
  };

  /**
   * Метод показа узла
   */
  _onShowNode = () => {
    const { diagramStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection.first()!.data;

    const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orig_id =
      graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

    const nodesOrIds =
      diagramStore.selectedNodes.length > 1 ? diagramStore.selectedNodes : [orig_id];

    graphStore?.showNodes(nodesOrIds);
  };

  /**
   * Метод создания группы. Отрабатывает при создании группы из контекст меню
   */
  _createGroupFromNode = async (): Promise<void> => {
    const { diagramStore, graphStore } = this.props;
    const selectedNode = diagramStore?.diagram?.selection.first();

    const zombie = selectedNode.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orId =
      graphStore?.graphTransformMode === 'ModeV3' && zombie
        ? selectedNode.data.key
        : selectedNode.data.orig_id;

    const groupOrIds = diagramStore.selectedNodes.length > 1 ? diagramStore.selectedNodes : [orId];

    if (orId) {
      await graphStore?.createGroupFromNode(groupOrIds);
    }
  };

  /**
   * Метод раскрытия схемы назад
   */
  _onSchemeBack = () => {
    const { diagramStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection.first()!.data;

    const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orig_id =
      graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

    graphStore?.schemeBack([orig_id]);
  };

  /**
   * Метод раскрытия схемы вперёд
   */
  _onSchemeForward = () => {
    const { diagramStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection.first()!.data;

    const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orig_id =
      graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

    graphStore?.schemeForward([orig_id]);
  };

  /**
   * Метод показа плательщиков (АБД)
   */
  _showABDPayers = () => {
    const { diagramStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection.first()!.data;

    const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orig_id =
      graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

    graphStore?.payersRecipients([orig_id], 'payers');
  };

  /**
   * Метод показа получателей (АБД)
   */
  _showABDRecipients = () => {
    const { diagramStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection.first()!.data;

    const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

    const orig_id =
      graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

    graphStore?.payersRecipients([orig_id], 'recipients');
  };

  /**
   * Метод по Построению Дерева для неосновных узлов
   */
  _onCreateTree = async () => {
    const { diagramStore, graphStore } = this.props;

    if (graphStore.filtersGraph.is_ask_nds2) {
      const nodeData = diagramStore?.diagram?.selection.first()!.data;

      const params = {
        tax_payers: [
          {
            fid: parseInt(nodeData.node.fid, 10),
            inn: nodeData.node.inn,
            kpp_effective: nodeData.node.kpp_effective,
          },
        ],
        draw_mode: {
          mode: graphStore?.currentDrawingMode,
        },
        expand_type: {
          types: [graphStore?.graphExpandType],
        },
        graph_id: graphStore?.previouslyGraphId,
        control_area: graphStore?.controlArea,
      };

      await graphStore?.fetchCreateTree(params);
    }
  };

  /**
   * Метод по Построению Дерева для нескольких узлов
   */
  _onCreateTreeNodes = async () => {
    const { diagramStore, graphStore } = this.props;

    if (graphStore.filtersGraph.is_ask_nds2) {
      const nodeData = diagramStore?.diagram?.selection.first()!.data;

      const zombie = nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie');

      const orig_id =
        graphStore?.graphTransformMode === 'ModeV3' && zombie ? nodeData.key : nodeData.orig_id;

      const nodesOrIds =
        diagramStore.selectedNodes.length > 1 ? diagramStore.selectedNodes : [orig_id];

      const result: any = [];

      nodesOrIds.map((item: any) => {
        result.push({ id: item });
      });

      const params = {
        tax_payers: result,
        draw_mode: {
          mode: graphStore?.currentDrawingMode,
        },
        expand_type: {
          types: [graphStore?.graphExpandType],
        },
        graph_id: graphStore?.previouslyGraphId,
        control_area: graphStore?.controlArea,
      };

      await graphStore?.fetchCreateTree(params);
    }
  };

  /**
   * Метод по созданию узла и связи к нему (от него) для основного режима (ModeV3)
   */
  _createLinkModeV3 = async (modeLink: string) => {
    const { modalStore, graphStore } = this.props;
    const createLinkModeV3 = true;
    if (graphStore.filtersGraph.is_user_link) {
      modalStore.openAddNewNodeModal({ createLinkModeV3, modeLink });
    }
  };

  /**
   * Функция перехода в стороннюю подсистему
   * @private
   */
  _onTransfer = async (
    functionName: string,
    context: string | Record<string, string | number>,
    errorMessage: string,
    browserLink?: string,
  ) => {
    const { graphStore } = this.props;
    graphStore?.statePending();

    try {
      await UnifiedClientFacade.executeFunction('Vsa2Domain', functionName, context, browserLink);
    } catch (e) {
      e.message = errorMessage;
      graphStore?.onError(e);
    } finally {
      graphStore?.stateDone();
    }
  };

  /**
   * Функция получения данных для перехода в карточки НП
   * @private
   */
  _getTransferToTaxpayerData = () => {
    const { diagramStore } = this.props;

    const selectedNode = diagramStore?.diagram?.selection.first();
    const { node } = selectedNode?.data || null;

    const { filterStore } = this.props;
    const { year, quarter } = getPeriodNumbers(filterStore?.options?.nds3_period_to);

    return {
      inn: node.inn,
      kppEffective: node.kpp_effective,
      year,
      quarter,
    };
  };

  /**
   * Функция "Перейти в НД НДС"
   * @private
   */
  _onGoToDeclarationByTaxPayer = () => {
    this._onTransfer(
      'GoToDeclarationByTaxPayer',
      this._getTransferToTaxpayerData(),
      'Невозможно перейти в НД НДС',
    );
  };

  /**
   * Функция "Перейти в карточку НП в АСК НДС"
   * @private
   */
  _onGoToAskNdsByTaxPayer = () => {
    const { diagramStore } = this.props;

    const selectedNode = diagramStore?.diagram?.selection.first();
    const { node } = selectedNode?.data || null;

    this._onTransfer(
      'GoToAskNdsByTaxPayer',
      {
        inn: node.inn,
        kppEffective: node.kpp_effective,
      },
      'Невозможно перейти в карточку НП в АСК НДС',
    );
  };

  /**
   * Функция "Перейти в Отложенные вычеты покупателя"
   * @private
   */
  _onGoToDeductionsByBuyerCard = () => {
    this._onTransfer(
      'GoToDeductionsByBuyerCard',
      this._getTransferToTaxpayerData(),
      'Невозможно перейти в "Отложенные вычеты покупателя"',
    );
  };

  /**
   * Функция "Перейти в Отложенные вычеты продавца"
   * @private
   */
  _onGoToDeductionsBySellerCard = () => {
    this._onTransfer(
      'GoToDeductionsBySellerCard',
      this._getTransferToTaxpayerData(),
      'Невозможно перейти в "Отложенные вычеты продавца"',
    );
  };

  /**
   * Метод по отмене предыдущего действия (закрытие контекст меню)
   */
  hideCX = () => {
    const { diagramStore } = this.props;
    if (diagramStore?.diagram?.currentTool instanceof go.ContextMenuTool) {
      diagramStore.diagram.currentTool.doCancel();
    }
  };

  /**
   * Метод открытия контекст меню
   */
  showContextMenu = (obj: any, diagram: any, tool: any) => {
    const cxElement: HTMLElement | null = document.getElementById('contextMenu');
    let hasMenuItem = false;
    function maybeShowItem(elt: any) {
      if ((elt.style.display = 'none')) {
        elt.style.display = 'block';
        hasMenuItem = true;
      }
    }
    maybeShowItem(document.getElementById('detailNode'));
    maybeShowItem(document.getElementById('createGroup'));

    maybeShowItem(document.getElementById('schemeBack'));
    maybeShowItem(document.getElementById('schemeForward'));

    const { diagramStore } = this.props;
    const selectedNode = diagramStore?.diagram?.selection.first();
    const { node } = selectedNode?.data || null;

    const showNodeItem = document.getElementById('showNode');
    const hideNodeItem = document.getElementById('hideNode');

    if (showNodeItem && hideNodeItem) {
      if (node?.is_visible) {
        showNodeItem.style.display = 'none';
        hideNodeItem.style.display = 'block';
      } else {
        showNodeItem.style.display = 'block';
        hideNodeItem.style.display = 'none';
      }
    }

    if (hasMenuItem) {
      cxElement?.classList.add('show-menu');
      const mousePt = diagram.lastInput.viewPoint;
      const leftCoordinate = mousePt.x + 7 + 'px';
      let topCoordinate;

      if (window.innerWidth > 1745) {
        topCoordinate = mousePt.y + 100 + 'px';
      } else {
        topCoordinate = mousePt.y + 55 + 'px';
      }
      cxElement?.setAttribute('style', 'left:' + leftCoordinate + ';top:' + topCoordinate);
    }
    window.addEventListener('click', this.hideCX, true);
  };

  /**
   * Метод закрытия контекст меню
   */
  hideContextMenu = () => {
    const cxElement = document.getElementById('contextMenu');
    cxElement?.classList.remove('show-menu');
    window.removeEventListener('click', this.hideCX, true);
  };

  get getContextMenu() {
    this.CustomContextMenuNode = go.GraphObject.make(go.HTMLInfo, {
      show: this.showContextMenu,
      hide: this.hideContextMenu,
    });
    return this.CustomContextMenuNode;
  }

  _onChangeMarkUp = (type: 'clear' | 'create') => {
    const { diagramStore, graphStore } = this.props;
    const nodeData = diagramStore?.diagram?.selection?.first()?.data;
    graphStore?.changeMarkup(type, nodeData.orig_id);
  };

  render() {
    const { diagramStore, graphStore } = this.props;
    const { currentGraph } = graphStore!;

    const selectedNode =
      diagramStore.selectedNodes.length === 1
        ? currentGraph.nodes.find(
            (item: { key: string }) => item.key === diagramStore.selectedNodes[0],
          )
        : false;

    const canMarkup = selectedNode?.can_markup;
    const canClearMarkup = selectedNode?.can_clear_markup;
    const isExpanded = selectedNode?.node?.is_expanded;

    return (
      <>
        <ul id={diagramStore.vsaNodeContextMenuId} className="menu">
          <li className="menu__header">Детализация</li>

          <li id="detailNode" className="menu-item" onClick={() => this._onNodeDetails(false)}>
            <span className="menu__item">
              <span className="menu__icon">
                <DetailedIcon />
              </span>
              Детальная информация
            </span>
          </li>

          <li id="analyticRole" className="menu-item" onClick={() => this._onNodeDetails(true)}>
            <span className="menu__item">
              <span className="menu__icon">
                <AnalyticalIcon />
              </span>
              Аналитическая роль
            </span>
          </li>

          <li id="createGroup" className="menu-item" onClick={this._createGroupFromNode}>
            <span className="menu__item">
              <span className="menu__icon">
                <GroupIcon />
              </span>
              Группировать
            </span>
          </li>
          <li id="hideNode" className="menu-item" onClick={this._onHideNode}>
            <span className="menu__item">
              <span className="menu__icon">
                <HideIcon />
              </span>
              {diagramStore.selectedNodes.length > 1 ? 'Скрыть узлы' : 'Скрыть узел'}
            </span>
          </li>

          <li id="showNode" className="menu-item" onClick={this._onShowNode}>
            <span className="menu__item">
              <span className="menu__icon">
                <HideIcon />
              </span>
              Показать узел
            </span>
          </li>

          <li
            id="userLinkToNP"
            className="menu-item"
            onClick={() => this._createLinkModeV3('to')}
            data-tip="React-tooltip"
            data-for="createLinkToolTip"
          >
            <span className="menu__item">
              <span className="menu__icon">
                <LinkToIcon />
              </span>
              Пользовательская связь к другому НП
            </span>
          </li>

          <li
            id="userLinkFromNP"
            className="menu-item"
            onClick={() => this._createLinkModeV3('from')}
            data-tip="React-tooltip"
            data-for="createLinkToolTip"
          >
            <span className="menu__item">
              <span className="menu__icon">
                <LinkFromIcon />
              </span>
              Пользовательская связь от другого НП
            </span>
          </li>

          {graphStore.sourceSystem === 'MDAB' && (
            <>
              <li
                id="createMarkup"
                className={`menu-item ${!canMarkup ? 'menu-item__disabled' : ''}`}
                onClick={() => canMarkup && this._onChangeMarkUp('create')}
              >
                <span className="menu__item">
                  <span className="menu__icon"></span>
                  Разметить цепочку
                </span>
              </li>
              <li
                id="clearMarkup"
                className={`menu-item ${!canClearMarkup ? 'menu-item__disabled' : ''}`}
                onClick={() => canClearMarkup && this._onChangeMarkUp('clear')}
              >
                <span className="menu__item">
                  <span className="menu__icon"></span>
                  Отменить разметку
                </span>
              </li>
            </>
          )}

          {!graphStore.filtersGraph.is_user_link && (
            <ReactTooltip
              place="bottom"
              type="dark"
              effect="float"
              id="createLinkToolTip"
              delayShow={200}
            >
              Не применен фильтр "Пользовательские связи"
            </ReactTooltip>
          )}

          <li
            id="createTree"
            className="menu-item"
            onClick={this._onCreateTree}
            data-tip="React-tooltip"
            data-for="createTreeToolTip"
          >
            <span className="menu__item">
              <span className="menu__icon">
                <TreeIcon />
              </span>
              Построить дерево
            </span>
          </li>

          <li
            id="createTreeNode"
            className="menu-item"
            onClick={this._onCreateTreeNodes}
            data-tip="React-tooltip"
            data-for="createTreeToolTip"
          >
            <span className="menu__item">
              <span className="menu__icon"></span>
              Новая схема из выбранных узлов
            </span>
          </li>

          {!graphStore.filtersGraph.is_ask_nds2 && !graphStore.filtersGraph.is_abd && (
            <ReactTooltip
              place="bottom"
              type="dark"
              effect="float"
              id="createTreeToolTip"
              delayShow={200}
            >
              Не применен фильтр "Кооперация" и "АСК НДС -2"
            </ReactTooltip>
          )}

          <li className="menu-group" id="NDS">
            {graphStore.graphExpandType === 'Nds' ? 'НДС' : 'ГК 44-ФЗ'}
          </li>

          <li id="schemeBack" className="menu-item" onClick={this._onSchemeBack}>
            <span className="menu__item">
              <span className="menu__icon">
                <SellersIcon />
              </span>
              Показать продавцов
            </span>
          </li>
          <li id="schemeForward" className="menu-item" onClick={this._onSchemeForward}>
            <span className="menu__item">
              <span className="menu__icon">
                <BuyersIcon />
              </span>
              Показать покупателей
            </span>
          </li>

          <li
            id="NDSOpenNP"
            className="menu-item"
            onClick={this._onSchemeForward}
            data-tip="React-tooltip"
            data-for="openSchemeForwardToolTip"
          >
            <span className="menu__item">
              <span className="menu__icon">
                <AgentsIcon />
              </span>
              {isExpanded ? 'Скрыть контрагентов' : 'Раскрыть контрагентов'}
            </span>
          </li>
          {graphStore.sourceSystem === 'MDAB' && !graphStore.filtersGraph.is_cooperation && (
            <ReactTooltip
              place="bottom"
              type="dark"
              effect="float"
              id="openSchemeForwardToolTip"
              delayShow={200}
            >
              Для редактирования схемы включите фильтр "Кооперация"
            </ReactTooltip>
          )}

          <li className="menu-group" id="ABD">
            АБД
          </li>

          <li id="ABDPayers" className="menu-item" onClick={this._showABDPayers}>
            <span className="menu__item">
              <span className="menu__icon">
                <SellersIcon />
              </span>
              Показать плательщиков
            </span>
          </li>
          <li id="ABDRecipients" className="menu-item" onClick={this._showABDRecipients}>
            <span className="menu__item">
              <span className="menu__icon">
                <BuyersIcon />
              </span>
              Показать получателей
            </span>
          </li>
          <li
            id="ABDOpenNP"
            className="menu-item"
            onClick={this._showABDRecipients}
            data-tip="React-tooltip"
            data-for="openSchemeForwardToolTip"
          >
            <span className="menu__item">
              <span className="menu__icon">
                <AgentsIcon />
              </span>
              {isExpanded ? 'Скрыть контрагентов' : 'Раскрыть контрагентов'}
            </span>
          </li>

          <li className="menu-group" id="Trace">
            Товарный поток
          </li>
          <li id="schemeBackTrace" className="menu-item" onClick={this._onSchemeBack}>
            <span className="menu__item">
              <span className="menu__icon">
                <SellersIcon />
              </span>
              Показать продавцов
            </span>
          </li>
          <li id="schemeForwardTrace" className="menu-item" onClick={this._onSchemeForward}>
            <span className="menu__item">
              <span className="menu__icon">
                <BuyersIcon />
              </span>
              Показать покупателей
            </span>
          </li>
          <li
            id="TraceOpenNP"
            className="menu-item"
            onClick={this._onSchemeForward}
            data-tip="React-tooltip"
            data-for="openSchemeForwardToolTip"
          >
            <span className="menu__item">
              <span className="menu__icon">
                <AgentsIcon />
              </span>
              {isExpanded ? 'Скрыть контрагентов' : 'Раскрыть контрагентов'}
            </span>
          </li>

          {typeof window.CefSharp !== 'object' && (
            <ReactTooltip place="bottom" type="dark" effect="float" id="goToAksNDS" delayShow={200}>
              Функционал доступен при работе в ЕКП
            </ReactTooltip>
          )}
          <li className="menu-group" id="AksNDS">
            АСК НДС
          </li>
          <li
            id="declarationByTaxPayer"
            className="menu-item"
            onClick={this._onGoToDeclarationByTaxPayer}
            data-tip="React-tooltip"
            data-for="goToAksNDS"
          >
            <span className="menu__item">
              <span className="menu__icon"></span>
              Декларация НДС
            </span>
          </li>
          <li
            id="AskNdsByTaxPayer"
            className="menu-item"
            onClick={this._onGoToAskNdsByTaxPayer}
            data-tip="React-tooltip"
            data-for="goToAksNDS"
          >
            <span className="menu__item">
              <span className="menu__icon"></span>
              Карточка налогоплательщика
            </span>
          </li>
          <li
            id="deductionsByBuyerCard"
            className="menu-item"
            onClick={this._onGoToDeductionsByBuyerCard}
            data-tip="React-tooltip"
            data-for="goToAksNDS"
          >
            <span className="menu__item">
              <span className="menu__icon"></span>
              Отложенные вычеты покупателя
            </span>
          </li>
          <li
            id="deductionsBySellerCard"
            className="menu-item"
            onClick={this._onGoToDeductionsBySellerCard}
            data-tip="React-tooltip"
            data-for="goToAksNDS"
          >
            <span className="menu__item">
              <span className="menu__icon"></span>
              Отложенные вычеты продавца
            </span>
          </li>
        </ul>
      </>
    );
  }
}

export default ContextMenuNode;
