import { Diagram, Overview } from 'gojs';
import { ReactOverview } from 'gojs-react';
import React, { useState } from 'react';
import InputRange, { Range } from 'react-input-range';
import styles from './DiagramMap.module.scss';
import { ReactComponent as Plus } from './icons/add-fill.svg';
import { ReactComponent as Closed } from './icons/arrow-left-s-line.svg';
import { ReactComponent as Open } from './icons/arrow-right-s-line.svg';
import { ReactComponent as Minus } from './icons/subtract-fill.svg';

interface DiagramMapProps {
  /** Масштаб */
  zoom: number;
  /** Диаграмма для наблюдения */
  diagram: Diagram | null;

  /** Пользовательский компонент для изменения масштаба */
  zoomComponent?: React.ComponentType;
  /** Пользовательский компонент для мини-карты */
  mapComponent?: React.ComponentType;

  /** Значение для прибавления или вычитания из текущего масштаба по клику на иконки */
  iconClickZoomModifier?: number;
  /** Максимальное значение масштаба */
  zoomMaxValue?: number;
  /** Минимальное значение масштаба */
  zoomMinValue?: number;
  /** Показывает, блокировать ли изменение значения при переполнении */
  restricted?: boolean;

  /** CSS класс обертки компонента */
  wrapperClassName?: string;
  /** CSS класс контейнера мини-карты */
  mapContainerClassName?: string;
  /** CSS класс мини-карты */
  mapClassName?: string;
  /** CSS класс закрытой мини-карты */
  closedMapClassName?: string;
  /** CSS класс контейнера для изменения масштаба */
  zoomContainerClassName?: string;
  /** CSS класс иконок масштаба */
  zoomIconClassName?: string;
  /** CSS класс иконки переключения режима карты */
  toggleMapClassName?: string;

  /** Функция для инициализации мини-карты */
  initOverview: () => Overview;
  /**
   * Функция для изменения масштаба
   *
   * @param {number} v - значение масштаба
   */
  setZoom: (v: number | Range) => void;
}

/**
 * Компонент мини-карты для диаграммы
 */
const DiagramMap = ({
  zoom,
  diagram,
  zoomComponent,
  mapComponent,
  iconClickZoomModifier,
  zoomMaxValue,
  zoomMinValue,
  wrapperClassName,
  mapContainerClassName,
  mapClassName,
  closedMapClassName,
  zoomContainerClassName,
  zoomIconClassName,
  toggleMapClassName,
  initOverview,
  setZoom,
  restricted,
}: DiagramMapProps) => {
  const [isOpen, toggleOpen] = useState(true);

  const handleChange = (value: number) => {
    let valueToSet = value;

    if (restricted) {
      if (value > zoomMaxValue!) {
        valueToSet = zoomMaxValue!;
      }

      if (value < zoomMinValue!) {
        valueToSet = zoomMinValue!;
      }
    }

    return setZoom(valueToSet);
  };

  return isOpen ? (
    <div className={`${styles.wrapper} ${wrapperClassName}`}>
      <div className={`${styles.mapContainer} ${mapContainerClassName}`}>
        {mapComponent || (
          <ReactOverview
            observedDiagram={diagram}
            divClassName={`${styles.map} ${mapClassName}`}
            initOverview={initOverview}
          />
        )}
      </div>
      <div className={`${styles.zoomContainer} ${zoomContainerClassName}`}>
        {zoomComponent || (
          <>
            <Minus
              className={`${styles.zoomContainer} ${zoomIconClassName}`}
              onClick={() => handleChange(zoom - iconClickZoomModifier!)}
            />
            <InputRange
              maxValue={zoomMaxValue}
              minValue={zoomMinValue}
              value={zoom}
              onChange={setZoom}
            />
            <Plus
              className={`${styles.zoomContainer} ${zoomIconClassName}`}
              onClick={() => handleChange(zoom + iconClickZoomModifier!)}
            />
          </>
        )}
      </div>
      <div
        className={`${styles.toggleMap} ${toggleMapClassName}`}
        onClick={() => toggleOpen(!isOpen)}
      >
        <Open />
      </div>
    </div>
  ) : (
    <div
      className={`${styles.toggleMap} ${closedMapClassName}`}
      onClick={() => toggleOpen(!isOpen)}
    >
      <Closed />
    </div>
  );
};

DiagramMap.defaultProps = {
  zoomComponent: null,
  mapComponent: null,
  iconClickZoomModifier: 5,
  zoomMaxValue: 200,
  zoomMinValue: 1,

  wrapperClassName: '',
  mapContainerClassName: '',
  mapClassName: '',
  closedMapClassName: '',
  zoomContainerClassName: '',
  zoomIconClassName: '',
  toggleMapClassName: '',
};

export default DiagramMap;
