import { TaxpayerType } from 'DataTypes';
import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';
import { GraphStoreType } from 'stores/graph/graphStore';
import PeriodPicker from '../PeriodPicker/PeriodPicker';

import styles from './CreateScheme.module.scss';
import mainStyles from '../BeginningWork.module.scss';
import Button from 'components/Button';
import Checkbox from '../../../Checkbox';
import Radio from 'components/Radio';
import { CommonStoreType } from 'stores/common/commonStore';

import { ReactComponent as QuestionIcon } from '../assets/question.svg';
import ReactTooltip from 'react-tooltip';
import { action } from 'mobx';
import Dropdown from '../../../Dropdown';

import { expandTypeTraceArray, expandTypeTraceMap, quarter } from './utils';
import BeginningWorkStore from '../../../../stores/modalData/beginningWorkStore';
import SearchResult from '../SearchResult/SearchResult';
import { readParamsFromURL } from '../../../../utils/getParamsFromURL';
import { getDefaultPeriod } from '../../../../utils/ui/getCurrentDate';

interface IProps {
  graphStore?: GraphStoreType;
  commonStore?: CommonStoreType;
  beginningWorkStore?: BeginningWorkStore;
  paramsDNP?: any;
}

interface IState {
  period?: string;
  mode?: string;
  np?: TaxpayerType;
  expandType?: string;
  interQuarterSearch: boolean;
  controlArea: string;
  expandTypeTrace: any;
}

const { inn, draw_mode, expand_type } = readParamsFromURL(window.location.href) as Record<
  string,
  string
>;

const { year: initialYear, quarter: initialQuarter } = getDefaultPeriod('roman');
const initialDate = `${initialQuarter} квартал;${initialYear}`;

/**
 * Компонент вкладка свободного поиска
 **
 */
@inject('graphStore', 'commonStore', 'beginningWorkStore')
@observer
export class CreateScheme extends Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {
      period: initialDate,
      mode: draw_mode || 'from_buyer',
      expandType: 'Nds',
      interQuarterSearch: false,
      controlArea: 'KNP',
      expandTypeTrace: ['Trace'],
    };
  }

  @action
  async componentDidMount() {
    const { graphStore, beginningWorkStore, commonStore } = this.props;
    if (graphStore?.showCreateKAO && graphStore?.showCreateKNP) {
      this.setState({ controlArea: 'KNP' });
    } else if (graphStore?.showCreateKAO && !graphStore?.showCreateKNP) {
      this.setState({ controlArea: 'KAO' });
    } else if (!graphStore?.showCreateKAO && graphStore?.showCreateKNP) {
      this.setState({ controlArea: 'KNP' });
    }

    if (beginningWorkStore?.selectedNp?.quarter && beginningWorkStore?.selectedNp?.year) {
      this.handleChangePeriod(
        quarter?.get(beginningWorkStore?.selectedNp?.quarter + '') +
          beginningWorkStore?.selectedNp?.year,
      );
    }

    if (inn) {
      await commonStore?.searchNodesByText(inn);

      if (commonStore?.nodes?.order?.[0]) {
        beginningWorkStore?.setSelectedNp({
          quarter: beginningWorkStore?.selectedNp?.quarter,
          year: beginningWorkStore?.selectedNp?.year,
          ...commonStore?.nodes.order[0],
        });
      }

      commonStore?.resetNodesOrder();
    }

    if (expand_type) {
      if (expand_type === 'Nds' || expand_type === 'Abd' || expand_type === 'Contract') {
        this.handleChangeExpandType(expand_type);
      } else {
        this.setState({ expandTypeTrace: expand_type?.split(',') });
        this.handleChangeExpandType('Trace');
      }
    }
  }

  /**
   * Метод изменяет значение периода
   */
  handleChangePeriod = (period: string) => {
    this.setState({ period });
  };

  /**
   * Метод изменяет значение построения графа
   */
  handleChangeMode = (mode: string) => {
    this.setState({ mode });
    if (mode === 'from_seller') {
      this.setState({ interQuarterSearch: false });
    }
  };

  /**
   * Метод изменяет налогоплотильщика
   */
  handleSelectNp = (np?: any) => {
    this.setState({ np });
  };

  /**
   * Метод изменяет значение раскрытия графа
   */
  handleChangeExpandType = (expandType: string) => {
    this.setState({ expandType });
    if (expandType !== 'Nds') {
      this.setState({ interQuarterSearch: false });
    }
  };

  /**
   * Метод изменяет значение раскрытия по товарному потоку
   */
  handleChangeTrace = (expandType: any) => {
    const { expandTypeTrace } = this.state;

    if (expandType === 'All') {
      this.setState({ expandTypeTrace: ['Trace', 'Property', 'Mark'] });
    } else {
      if (expandTypeTrace.some((item: any) => item === expandType)) {
        const arrayFilter = expandTypeTrace.filter((item: any) => item !== expandType);
        this.setState({ expandTypeTrace: arrayFilter });
      } else {
        this.setState({ expandTypeTrace: [...expandTypeTrace, expandType] });
      }
    }
  };

  /**
   * Метод изменяет значение раскрытия графа
   */
  handleChangeInterQuarterSearch = () => {
    const { interQuarterSearch } = this.state;

    this.setState({ interQuarterSearch: !interQuarterSearch });
  };

  /**
   * Метод изменяет значения области контроля
   */
  handleChangeControlArea = (controlArea: string) => {
    this.setState({ controlArea });
  };

  /**
   * Метод обрабатывает событие "Submit"
   */
  handleSubmit = () => {
    const {
      period,
      mode,
      expandType,
      interQuarterSearch,
      controlArea,
      expandTypeTrace,
    } = this.state;

    const { beginningWorkStore, commonStore } = this.props;

    const [quartalFull, year] = (period || ';').split(';');
    const quartal = quartalFull.split(' ')[0];
    this.props.graphStore?.beginingWork({
      tax_payers: [beginningWorkStore?.selectedNp],
      filter: { nds3_period_from: `${quartal};${year}` },
      draw_mode: { mode },
      expand_type: { types: expandType === 'Trace' ? expandTypeTrace : [expandType] },
      inter_quarter_search: interQuarterSearch,
      control_area: controlArea,
    });
  };

  goToBackTab = () => {
    const { beginningWorkStore } = this.props;

    beginningWorkStore?.changeTabIndex(0);
  };

  closeFreeSearch = () => {
    const { commonStore } = this.props;

    commonStore?.setIsShowBeginningWork();
  };

  render() {
    const { period, mode, np, expandType, interQuarterSearch, controlArea, expandTypeTrace } =
      this.state;
    const { commonStore, graphStore, beginningWorkStore } = this.props;

    return (
      <>
        <div className={mainStyles.row}>
          <div className={mainStyles.container}>
            <div className={styles.title}>
              <span>Выбранный налогоплательщик</span>
            </div>

            <div className={styles.np}>
              <SearchResult items={[beginningWorkStore?.selectedNp as TaxpayerType]} />
            </div>

            <div className={styles.row}>
              <PeriodPicker
                value={period}
                onChange={this.handleChangePeriod}
                theme={this.props.commonStore?.appTheme}
              />
            </div>

            <div className={styles.row}>
              <div className={styles.label}>Построение схемы</div>

              <div className={styles.inputs}>
                <Radio
                  items={[
                    { value: 'from_buyer', label: 'От покупателя' },
                    { value: 'from_seller', label: 'От продавца' },
                  ]}
                  value={mode}
                  onChange={this.handleChangeMode}
                  iconFirst
                />
              </div>
            </div>

            <div className={styles.row}>
              <div className={styles.label}>Раскрытие схемы</div>

              <div className={styles.inputs}>
                {this.props.graphStore?.showABD ? (
                  <Radio
                    items={[
                      { value: 'Nds', label: 'АСК НДС' },
                      { value: 'Abd', label: 'АБД' },
                      { value: 'Contract', label: 'ГК 44-ФЗ' },
                      { value: 'Trace', label: 'Товарный поток' },
                    ]}
                    value={expandType}
                    onChange={this.handleChangeExpandType}
                    iconFirst
                    mode="buttons"
                  />
                ) : (
                  <Radio
                    items={[
                      { value: 'Nds', label: 'По НДС' },
                      { value: 'Contract', label: 'ГК 44-ФЗ' },
                      { value: 'Trace', label: 'Товарный поток' },
                    ]}
                    value={expandType}
                    onChange={() => {}}
                    iconFirst
                    mode="buttons"
                  />
                )}

                {expandType === 'Trace' && (
                  <Dropdown
                    containerStyle={styles.dropdownContainer}
                    mainContainerStyle={styles.trace}
                    itemsArray={expandTypeTraceArray}
                    onSelect={this.handleChangeTrace}
                    theme={commonStore?.appTheme}
                    isAll
                    isCheckbox
                    checked={expandTypeTrace}
                    checkedMap={expandTypeTraceMap}
                  />
                )}
              </div>
            </div>

            <div className={styles.row}>
              <div className={styles.label}>
                Отображение отложенных вычетов НДС
                <QuestionIcon
                  data-tip="React-tooltip"
                  data-for="questionIconToolTip"
                  className={styles.question_icon}
                />
                <ReactTooltip
                  place="right"
                  type="light"
                  effect="float"
                  backgroundColor={'#EDEDED'}
                  textColor={'#565656'}
                  id="questionIconToolTip"
                >
                  <div className={styles.divTooltip}>
                    <p>
                      Режим <b>”Межквартальный поиск”</b> позволяет отобразить суммы принятых
                      вычетов НДС по периодам, в том числе отложенных.
                    </p>

                    <div>
                      Данный режим доступен только в случае одновременно выбранных параметров:
                      <ul>
                        <li>
                          Построение схемы: <b>от покупателя</b>;
                        </li>
                        <li>
                          Раскрытие схемы: <b>по НДС</b>.
                        </li>
                      </ul>
                    </div>
                  </div>
                </ReactTooltip>
              </div>

              <div
                data-tip={'React-tooltip'}
                data-for={'switchIconToolTip'}
                className={styles.inputs}
              >
                <Checkbox
                  checked={interQuarterSearch}
                  disabled={!(mode === 'from_buyer' && expandType === 'Nds')}
                  onChange={this.handleChangeInterQuarterSearch}
                >
                  Включить отображение
                </Checkbox>

                {!(mode === 'from_buyer' && expandType === 'Nds') && (
                  <ReactTooltip
                    place="left"
                    type="light"
                    effect="float"
                    backgroundColor={'#EDEDED'}
                    textColor={'#565656'}
                    id="switchIconToolTip"
                  >
                    <div className={styles.divTooltip}>
                      Режим <b>”Межквартальный поиск”</b> доступен только в случае одновременно
                      выбранных параметров:
                      <ul>
                        <li>
                          Построение схемы: <b>от покупателя</b>;
                        </li>
                        <li>
                          Раскрытие схемы: <b>по НДС</b>.
                        </li>
                      </ul>
                      Во всех остальных случаях данный режим неактивен.
                    </div>
                  </ReactTooltip>
                )}
              </div>
            </div>

            <div className={styles.row}>
              <div className={styles.label}>Область контроля</div>

              <div className={styles.inputs}>
                <Radio
                  className={styles.radio}
                  items={[
                    { value: 'KNP', label: 'КНП' },
                    { value: 'KAO', label: 'КАО' },
                  ]}
                  value={controlArea}
                  onChange={this.handleChangeControlArea}
                  iconFirst
                  disabled={
                    !this.props.graphStore?.showCreateKAO || !this.props.graphStore?.showCreateKNP
                  }
                />
              </div>
            </div>
          </div>
        </div>

        <div className={`${mainStyles.row} ${styles.buttons}`}>
          <div className={`${mainStyles.container} ${styles.buttons__container}`}>
            <div>
              <Button onClick={this.goToBackTab} mode="secondary">
                Назад
              </Button>
            </div>

            <div>
              <Button onClick={this.closeFreeSearch} mode="transparent">
                Отменить
              </Button>

              <Button
                onClick={this.handleSubmit}
                disabled={!graphStore?.showFreeSearch || !graphStore?.showCreateGraph}
                title={
                  graphStore?.showFreeSearch && graphStore?.showCreateGraph
                    ? 'Недоступно для данной роли'
                    : ''
                }
              >
                Создать новую схему
              </Button>
            </div>
          </div>
        </div>
      </>
    );
  }
}
