import React, { PureComponent } from 'react';
import styles from './Dimensions.module.scss';

import { inject, observer } from 'mobx-react';

import { ReactComponent as BackIcon } from './assets/back.svg';

import { GraphStoreType } from 'StoreTypes';

type DimensionsProps = {
  graphStore?: GraphStoreType;
};

/**
 * Компонент Разрядность (денег)
 */
@inject('graphStore')
@observer
class Dimensions extends PureComponent<DimensionsProps> {
  /**
   * Установить единицу измерения
   * @param data - единица измерения (thousand - тысячи, million - миллион, billion - миллиард)
   * @private
   */
  _setDimensions = (data: string): void => {
    const { graphStore } = this.props;

    graphStore.setDimensions(data);
    graphStore.fetchGraph(graphStore.previouslyFetchedGraphId, null, true);
  };

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { graphStore } = this.props;

    return (
      <div className={styles.container}>
        <div
          className={
            graphStore.dimensions === 'rubles'
              ? `${styles.item} ${styles.item__active}`
              : styles.item
          }
          onClick={() => this._setDimensions('rubles')}
        >
          Руб.
        </div>
        <div
          className={
            graphStore.dimensions === 'thousand'
              ? `${styles.item} ${styles.item__active}`
              : styles.item
          }
          onClick={() => this._setDimensions('thousand')}
        >
          Тыс.
        </div>
        <div
          className={
            graphStore.dimensions === 'million'
              ? `${styles.item} ${styles.item__active}`
              : styles.item
          }
          onClick={() => this._setDimensions('million')}
        >
          Млн
        </div>
        <div
          className={
            graphStore.dimensions === 'billion'
              ? `${styles.item} ${styles.item__active}`
              : styles.item
          }
          onClick={() => this._setDimensions('billion')}
        >
          Млрд
        </div>
      </div>
    );
  }
}

export default Dimensions;
