import React, { PureComponent } from 'react';
import { productFlowPropTypes } from './ProductFlow.props';
import filterStyles from '../../common/Filter.module.scss';
import styles from './ProductFlow.module.scss';

import Option from '../../Option';
import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';
import { GraphStoreType } from '../../../../stores/graph/graphStore';

import Icon from '../../../../components/Icon';

import { commodityFlow } from '../../../../utils/commodityFlow';

interface ProductFlowProps {
  isActive: boolean;
  onToggleCheck?: () => void;
  toggleOpenState?: () => void;
  cooperationFilter?: any;
  isOpen?: boolean;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент "Товарный поток" в блоке "Кооперация"
 */
@inject('cooperationFilter', 'commonStore', 'graphStore')
@observer
class ProductFlow extends PureComponent<ProductFlowProps> {
  static propTypes = productFlowPropTypes;

  render() {
    const { isOpen, toggleOpenState, commonStore, cooperationFilter, graphStore } = this.props;
    const graphExpandTypeArray = graphStore?.graphExpandType.split(',') || [];

    return (
      <li className={`${filterStyles.wrapper} ${styles.wrapper}`}>
        <div className={filterStyles.header}>
          <span
            className={filterStyles.headerLabel}
            onClick={toggleOpenState}
            title={
              commodityFlow.some((item) => graphStore?.graphExpandType.includes(item)) &&
              graphStore?.sourceSystem !== 'MDAB'
                ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                : 'Признак отображения связей по данным Товарного потока'
            }
          >
            Товарный поток
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {isOpen && (
          <div className={styles.container}>
            <div className={styles.optionContainer}>
              <Option
                className={styles.option}
                isActive={cooperationFilter.options.is_trace}
                onToggleCheck={() => {
                  graphStore?.showChangeFilters &&
                    graphStore?.graphExpandType !== 'Trace' &&
                    !(
                      graphExpandTypeArray.length > 1 &&
                      graphExpandTypeArray.some((item) => item === 'Trace')
                    ) &&
                    cooperationFilter.toggleProductFlowCommon('is_trace');
                }}
                value={'ПП "Прослеживаемость"'}
                title={
                  graphStore?.graphExpandType === 'Trace' ||
                  (graphExpandTypeArray.length > 1 &&
                    graphExpandTypeArray.some((item) => item === 'Trace'))
                    ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                    : 'ПП "Прослеживаемость"'
                }
                isAllCheck
              />
            </div>
            <div className={styles.optionContainer}>
              <Option
                className={styles.option}
                isActive={cooperationFilter.options.is_property}
                onToggleCheck={() => {
                  graphStore?.showChangeFilters &&
                    graphStore?.graphExpandType !== 'Property' &&
                    !(
                      graphExpandTypeArray.length > 1 &&
                      graphExpandTypeArray.some((item) => item === 'Property')
                    ) &&
                    cooperationFilter.toggleProductFlowCommon('is_property');
                }}
                value={'ПП "Собственность"'}
                title={
                  graphStore?.graphExpandType === 'Property' ||
                  //@ts-ignore
                  (graphExpandTypeArray.length > 1 &&
                    graphExpandTypeArray.some((item) => item === 'Property'))
                    ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                    : 'ПП "Собственность"'
                }
                isAllCheck
              />
            </div>
            <div className={styles.optionContainer}>
              <Option
                className={styles.option}
                isActive={cooperationFilter.options.is_mark}
                onToggleCheck={() => {
                  graphStore?.showChangeFilters &&
                    graphStore?.graphExpandType !== 'Mark' &&
                    !(
                      graphExpandTypeArray.length > 1 &&
                      graphExpandTypeArray.some((item) => item === 'Mark')
                    ) &&
                    cooperationFilter.toggleProductFlowCommon('is_mark');
                }}
                value={'ГИС МТ'}
                title={
                  graphStore?.graphExpandType === 'Mark' ||
                  (graphExpandTypeArray.length > 1 &&
                    graphExpandTypeArray.some((item) => item === 'Mark'))
                    ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                    : 'ГИС МТ'
                }
                isAllCheck
              />
            </div>
          </div>
        )}
      </li>
    );
  }
}

export default ProductFlow;
