import React, { PureComponent } from 'react';
import { cooperationPropTypes } from './Cooperation.props';
import styles from './Cooperation.module.scss';
import filterStyles from '../common/Filter.module.scss';

import Icon from '../../../components/Icon';

import Option from '../Option';
import MoneySpreadPicker from '../../../components/MoneySpreadPicker';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'stores/common/commonStore';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import ProductFlow from './ProductFlow';
import AskNds from './AskNds';
import Abd from './Abd';

import { commodityFlow } from '../../../utils/commodityFlow';

interface CooperationProps {
  isActive: boolean;
  onToggleCheck: () => void;
  isOpen?: boolean;
  toggleOpenState?: () => void;

  cooperationFilter?: any;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
}

/**
 * Компонент блока кооперации для фильтров
 */
@inject('cooperationFilter', 'commonStore', 'graphStore')
@observer
class Cooperation extends PureComponent<CooperationProps> {
  static propTypes = cooperationPropTypes;

  componentDidMount(): void {
    this.props.cooperationFilter?.changeKeepFilter();
  }

  render() {
    const { isOpen, toggleOpenState, cooperationFilter, commonStore, graphStore } = this.props;

    return (
      <li className={`${filterStyles.wrapper} ${styles.wrapper}`}>
        <div className={filterStyles.header}>
          <span className={filterStyles.headerLabel} onClick={toggleOpenState}>
            Кооперация
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {isOpen && (
          <div className={styles.container}>
            {!graphStore?.interQuarterSearch && (
              <div className={styles.optionContainer}>
                <ProductFlow
                  isActive={cooperationFilter.options.is_product_flow}
                  isAllCheck={cooperationFilter.isAllTrueProductFlow}
                  onToggleCheck={
                    graphStore?.showChangeFilters &&
                    (graphStore?.sourceSystem === 'MDAB' ||
                      !commodityFlow.some((item) => graphStore?.graphExpandType.includes(item))) &&
                    cooperationFilter.toggleFilterProductFlow
                  }
                  title={
                    commodityFlow.some((item) => graphStore?.graphExpandType.includes(item)) &&
                    graphStore?.sourceSystem !== 'MDAB'
                      ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                      : 'Признак отображения связей по данным Товарного потока'
                  }
                />
              </div>
            )}
            {graphStore?.showNDS && (
              <div className={styles.optionContainer}>
                <AskNds
                  isActive={cooperationFilter.options?.is_ask_nds2}
                  isAllCheck
                  onToggleCheck={
                    graphStore?.showChangeFilters &&
                    (graphStore?.sourceSystem === 'MDAB' ||
                      graphStore?.graphExpandType !== 'Nds') &&
                    cooperationFilter.toggleAskNds2
                  }
                  title={
                    graphStore?.graphExpandType === 'Nds' && graphStore?.sourceSystem !== 'MDAB'
                      ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                      : 'Признак отображения связей по данным АСК НДС-2'
                  }
                />
              </div>
            )}
            {graphStore?.showABD && !graphStore?.interQuarterSearch && (
              <>
                <div className={styles.optionContainer}>
                  <Abd
                    isActive={cooperationFilter.options?.is_abd}
                    isAllCheck
                    onToggleCheck={
                      graphStore?.showChangeFilters &&
                      (graphStore?.sourceSystem === 'MDAB' ||
                        graphStore?.graphExpandType !== 'Abd') &&
                      cooperationFilter.toggleNp
                    }
                    title={
                      graphStore?.graphExpandType === 'Abd' && graphStore?.sourceSystem !== 'MDAB'
                        ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                        : 'Признак отображения связей по данным анализа банковской деятельности'
                    }
                  />
                </div>
                <div className={styles.optionContainer}>
                  <Option
                    className={styles.sectionOption}
                    isActive={cooperationFilter.options?.is_contract}
                    onToggleCheck={
                      graphStore?.showChangeFilters &&
                      (graphStore?.sourceSystem === 'MDAB' ||
                        graphStore?.graphExpandType !== 'Contract') &&
                      cooperationFilter.toggleContract
                    }
                    value={'ГК'}
                    title={
                      graphStore?.graphExpandType === 'Contract' &&
                      graphStore?.sourceSystem !== 'MDAB'
                        ? 'Связь является основной для текущей схемы. Ее отключение невозможно'
                        : 'Признак отображения связей по данным Госконтрактов'
                    }
                    isAllCheck
                  />
                </div>
              </>
            )}
          </div>
        )}
      </li>
    );
  }
}

export default Cooperation;
