import React, { ReactNode, createRef } from 'react';
import styles from './InputWithIcon.module.scss';

interface InputWithIconProps {
  id?: string;
  value: string;
  label?: string;
  onChange: (value: any) => void;
  onClickIcon: () => void;
  inputStyle?: string;
  containerStyle?: string;
  autoFocus?: boolean;
  placeholder?: string;
  disabled?: boolean;
  wrapperStyle?: string;
  prevIcon?: ReactNode;
  afterIcon?: ReactNode;
  disabledButton?: boolean;
}

/**
 * Компонент поля с иконкой
 *
 * @param {string} id - id
 * @param {string} value - значение поля поиска
 * @param {string} label
 * @param {func} onChange - обработчик ввода
 * @param {func} onClickIcon - обработчик клик по иконке
 * @param {boolean} [autoFocus] - авто фокус на поле поиска
 * @param {string} [placeholder] - плейсхолдер
 * @param {string} [inputStyle] - стили инпута
 * @param {string} [containerStyle] - стили fieldContainer
 * @param {boolean} [disabled] - дизейбл
 * @param {string} [wrapperStyle] - стили обертки
 * @param {ReactNode} [prevIcon] - иконка, перед инпутом
 * @param {ReactNode} [afterIcon] - иконка, после инпута
 * @param {boolean} [disabledButton] - дизейбл иконки
 */
const InputWithIcon = ({
  id,
  value,
  label,
  inputStyle,
  autoFocus,
  placeholder,
  disabled = false,
  disabledButton = false,
  wrapperStyle,
  prevIcon,
  afterIcon,
  onChange,
  onClickIcon,
  containerStyle,
}: InputWithIconProps) => {
  const _onChange = (event: React.BaseSyntheticEvent): void => {
    onChange(event.target.value);
  };

  const _onClickIcon = (): void => {
    onClickIcon();
  };

  return (
    <div className={`${wrapperStyle}`}>
      {label && <span className={styles.label}>{label}</span>}
      <div className={`${styles.fieldContainer} ${containerStyle}`}>
        {prevIcon && (
          <button
            className={`${styles.button} ${styles.button__prev}`}
            onClick={_onClickIcon}
            disabled={disabledButton}
          >
            {prevIcon}
          </button>
        )}
        <input
          id={id}
          className={`${styles.input} ${inputStyle}`}
          ref={createRef<HTMLInputElement>()}
          type="string"
          autoFocus={autoFocus}
          onChange={_onChange}
          value={value}
          placeholder={placeholder}
          disabled={disabled}
        />
        {afterIcon && (
          <button
            className={`${styles.button} ${styles.button__after}`}
            onClick={_onClickIcon}
            disabled={disabledButton}
          >
            {afterIcon}
          </button>
        )}
      </div>
    </div>
  );
};

export default InputWithIcon;
