import React, { PureComponent } from 'react';
import { coherencePropTypes } from './Coherence.props';
import styles from './Coherence.module.scss';
import filterStyles from '../common/Filter.module.scss';

import Icon from '../../../components/Icon';

import Option from '../Option';

import { inject, observer } from 'mobx-react';
import { CommonStoreType } from 'StoreTypes';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import { FilterStoreType } from '../../../stores/filters/filterStore';

interface CoherenceProps {
  isActive: boolean;
  onToggleCheck: () => void;
  coherenceFilter?: any;
  isOpen?: boolean;
  toggleOpenState?: () => void;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
  filterStore?: FilterStoreType;
}

/**
 * Компонент блока согласованности для фильтров
 */
@inject('coherenceFilter', 'commonStore', 'graphStore', 'filterStore')
@observer
class Coherence extends PureComponent<CoherenceProps> {
  static propTypes = coherencePropTypes;

  render() {
    const { isOpen, toggleOpenState, coherenceFilter, commonStore, graphStore, filterStore } =
      this.props;

    return (
      <li className={`${styles.wrapper} ${filterStyles.wrapper}`}>
        <div className={`${styles.header} ${filterStyles.header}`}>
          <span
            className={`${styles.headerLabel} ${filterStyles.headerLabel}`}
            onClick={toggleOpenState}
          >
            Согласованность
          </span>
          <Icon
            className={filterStyles.toggle}
            name={isOpen ? 'arrow_up' : 'arrow_down'}
            onClick={toggleOpenState}
            theme={commonStore?.appTheme}
          />
        </div>
        {isOpen && (
          <div className={styles.container}>
            {filterStore?.coherenceDynamicOptions?.length > 0 && (
              <>
                {filterStore?.coherenceDynamicOptions.map((item: any) => (
                  <div
                    className={`${styles.optionContainer} 
                       ${'is_disabled' in item && item.is_disabled ? styles.disabled : ''}`}
                  >
                    <Option
                      className={styles.option}
                      isActive={item.is_checked}
                      onToggleCheck={() => {
                        'is_disabled' in item
                          ? graphStore?.showChangeFilters &&
                            !item.is_disabled &&
                            coherenceFilter.toggleCoherenceDynamicOptions(item.link_subtype_id)
                          : graphStore?.showChangeFilters &&
                            coherenceFilter.toggleCoherenceDynamicOptions(item.link_subtype_id);
                      }}
                      value={item.link_subtype_name}
                      title={'title' in item ? item.title : item.link_subtype_name}
                      isAllCheck
                    />
                  </div>
                ))}
              </>
            )}
          </div>
        )}
      </li>
    );
  }
}

export default Coherence;
