import React from 'react';
import { iconPropTypes } from './Icon.props';

import icons from '@fdc/vsa-react-lib/lib/assets/icons.svg';
import darkIcons from '@fdc/vsa-react-lib/lib/assets/dark_icons.svg';

type IconProps = {
  name: string;
  size?: string | number;
  height?: string | number;
  width?: string | number;
  theme?: string;
  [key: string]: any;
};

/**
 * Компонент иконки
 */
const Icon = ({ name, size, height, width, theme, ...props }: IconProps) => {
  return (
    <svg width={size || width} height={size || height} {...props}>
      <use href={`${theme === 'dark' ? darkIcons : icons}#${name}`} />
    </svg>
  );
};

Icon.propTypes = iconPropTypes;

Icon.defaultProps = {
  size: '',
  height: 12,
  width: 12,
};

export default Icon;
