import React from 'react';
import { optionPropTypes } from './Option.props';
import filterStyles from '../common/Filter.module.scss';

interface OptionProps {
  value: any;
  onToggleCheck?: () => void;
  className?: string;
  isActive?: boolean;
  title?: string;
}

/**
 * Компонент опции фильтров
 */
const Option = ({ value, className, title }: OptionProps) => {
  return (
    <span className={`${filterStyles.optionLabel} ${className}`} title={title}>
      {value}
    </span>
  );
};

Option.propTypes = optionPropTypes;

Option.defaultProps = {
  className: '',
  isActive: false,
};

export default Option;
