import React, { PureComponent } from 'react';
import { inject, observer } from 'mobx-react';

import Header from 'components/Header';
import Legend from 'components/Legend';
import NotificationList from 'components/NotificationList';

import { UIStoreType, GraphStoreType, NotificationStoreType } from 'StoreTypes';
import { CommonStoreType } from '../../stores/common/commonStore';
import { FilterStoreType } from '../../stores/filters/filterStore';

interface RootContainerStoresProps {
  /** хранилище состояния интерфейса */
  uiStore?: UIStoreType;

  /** хранилище графов */
  graphStore?: GraphStoreType;

  /** хранилище оповещений */
  notificationStore?: NotificationStoreType;

  /** хранилище общей информации */
  commonStore?: CommonStoreType;

  /** хранилище фильтров */
  filterStore?: FilterStoreType;
}

interface RootContainerProps extends RootContainerStoresProps {}

/**
 * Корневой контейнер
 */
@inject('uiStore', 'graphStore', 'notificationStore', 'commonStore', 'filterStore')
@observer
class RootContainer extends PureComponent<RootContainerProps> {
  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { screenMode, menuExtended, legendIsOpen, toggleMenuState, toggleLegendState } =
      this.props.uiStore!;

    const {
      schemeInfo,
      graphName,
      graphChange,
      schemeId,
      graphMode,
      graphOwner,
      userInfo,
      currentDrawingMode,
      dimensions,
      informationMainVertexID,
      interQuarterSearch,
      amountOfDamage,
      controlArea,
      graphExpandType,
      sourceSystem,
      mdabSchemaInfo,
      itaisSchemeInfo,
    } = this.props.graphStore!;

    const { analyticalRoles, analyticalRolesIcons } = this.props.commonStore!;

    const { options } = this.props.filterStore!;

    return (
      <>
        <NotificationList />
        <Header
          toggleMenu={toggleMenuState}
          screenMode={screenMode}
          menuExtended={menuExtended}
          schemeInfo={schemeInfo}
          graphName={graphName}
          graphChange={graphChange}
          schemeId={schemeId}
          graphOwner={graphOwner}
          graphMode={graphMode}
          periodFrom={options.nds3_period_from}
          periodTo={options.nds3_period_to}
          userInfo={userInfo}
          currentDrawingMode={currentDrawingMode}
          dimensions={dimensions}
          informationMainVertexID={informationMainVertexID}
          interQuarterSearch={interQuarterSearch}
          analyticalRoles={analyticalRoles}
          amountOfDamage={amountOfDamage}
          controlArea={controlArea}
          graphExpandType={graphExpandType}
          sourceSystem={sourceSystem}
          mdabSchemaInfo={mdabSchemaInfo}
          analyticalRolesIcons={analyticalRolesIcons}
          itaisSchemeInfo={itaisSchemeInfo}
        />
        {legendIsOpen && <Legend />}
        {/* TODO: Context Menus */}
      </>
    );
  }
}

export default RootContainer;
