import { observable, action } from 'mobx';
import { GraphStoreType } from 'StoreTypes';
import { IPagination } from '@fdc/vsa-react-lib';
import { DetailedGroupRequestParams, DetailedGroupFilter } from 'ApiModel';

/** Хранилище данных детализации группы */
class GroupDetailStore {
  constructor(private readonly GraphStore: GraphStoreType) {}

  INITIAL_PAGINATION = { page: 0, pageSize: 10 };

  /** Детализированная информация по группе */
  @observable groupDetailedData: any[] = [];
  @observable groupDetailedDataTotal = 0;

  /** Параметры группы */
  @observable params: DetailedGroupRequestParams = {
    pagination: this.INITIAL_PAGINATION,
    groupFilter: {},
  };
  @observable extract: string[] = [];
  @observable virtualExtract: any[] = [];

  /** Метод изменяет список на извлечение */
  @action changeExtract = (extract: string[]) => {
    this.extract = [...extract];
  };

  /** Метод изменяет список на извлечение */
  @action changeVirtualExtract = (virtualExtract: any[]) => {
    this.virtualExtract = [...virtualExtract];
    console.log(this.virtualExtract);
  };

  /** Метод добавляет детализированную информацию по группе */
  @action.bound
  addGroupDetailedData = (group: any, total: number) => {
    this.groupDetailedData = [...group];
    this.groupDetailedDataTotal = total;
  };

  /** Метод изменяет номер страницы */
  @action changePage = (pagination: IPagination, groupID: string, isVirtual?: boolean) => {
    if (
      this.params.pagination.page !== pagination.page ||
      this.params.pagination.pageSize !== pagination.count
    ) {
      const newParams = {
        pagination: { page: pagination.page, pageSize: pagination.count },
        groupFilter: this.params.groupFilter,
      };
      this.GraphStore.fetchDetailedGroup(groupID, newParams, isVirtual);
      this.params = newParams;
    }
  };

  /** Метод изменяет фильтры */
  @action changeFilter = (
    groupFilter: DetailedGroupFilter,
    groupID: string,
    isVirtual?: boolean,
    pageSize: number = this.params.pagination.pageSize,
  ) => {
    const newParams = { pagination: { page: 0, pageSize }, groupFilter };
    this.GraphStore.fetchDetailedGroup(groupID, newParams, isVirtual);
    this.params = newParams;
  };

  /** Метод сбрасывает фильтры и пагинацию */
  @action resetParams = (groupID: string, isVirtual?: boolean) => {
    const newParams = { pagination: this.INITIAL_PAGINATION, groupFilter: {} };
    this.GraphStore.fetchDetailedGroup(groupID, newParams, isVirtual);
    this.params = newParams;
  };

  /** Метод сбрасывает параметры стора */
  @action resetStore = () => {
    this.groupDetailedData = [];
    this.groupDetailedDataTotal = 0;
    this.params = { pagination: this.INITIAL_PAGINATION, groupFilter: {} };
    this.virtualExtract = [];
    this.extract = [];
  };
}

export default GroupDetailStore;
