import { configure, observable, action, decorate } from 'mobx';
import { RootStoreType } from 'StoreTypes';

configure({ enforceActions: 'observed' });

class IndividualPersonStore {
  RootStore: RootStoreType;

  constructor(RootStore: RootStoreType) {
    this.RootStore = RootStore;
  }

  // Серия паспорта
  series: string = '';
  // Номер паспорта
  number: string = '';

  // Наименование аналитической роли
  analiticName: string = '';

  // Основная информация о физическом лице
  data: any = {
    person_attribute: 'CountryCitizen',
  };

  // Ошибки (основная информация)
  errors: any = [];

  // Ошибки (аналитическая роль)
  errorsAnalytical: any = [];

  // Информация о том, в каких полях содержатся ошибки (основная информация)
  fieldsError: any = {};

  // Информация о том, в каких полях содержатся ошибки (аналитическая роль)
  fieldsErrorAnalytic: any = {};

  // Информация о аналитической роли
  analiticData: any = {};

  // Прикреплённые файлы (аналитическая роль)
  analiticDocument: any[] = [];

  // Модальная карточка на открытие
  isOpen: boolean = true;
  // Наименование гражданства
  citizenship_name: string = '';
  // Наименование типа документа
  document_name: string = '';

  // Ошибка по типу файла
  isValidFile: boolean = false;
  // Имя файла
  fileName: string = '';

  // Наличие ошибок (основная информация)
  isValidBasic: boolean = false;

  // Наличие ошибок (аналитическая роль)
  isValidAnalytical: boolean = false;

  // Ошибка в формат даты
  isInvalidDate: boolean = false;

  // Признак открытия модального окна с детальной информации
  setIsOpen = (): void => {
    this.isOpen = !this.isOpen;
  };

  // Установить ИНН
  setINN = (data: any): void => {
    this.data.inn = data;
  };

  // Установить ФИО
  setName = (data: any): void => {
    this.data.name = data;
  };

  // Установить место рождения
  setPlaceOfBirth = (data: any): void => {
    this.data.place_of_birth = data;
  };

  // Установить серию документа
  setSeries = (data: any): void => {
    this.data.document_series = data;
  };

  // Установить номер документа
  setNumber = (data: any): void => {
    this.data.document_number = data;
  };

  // Установить адрес места жительства
  setResidenceAddress = (data: any): void => {
    this.data.residence_address = data;
  };

  // Установить основную информацию о ФЛ
  setData = (data: any): void => {
    this.data = data;
  };

  // Установить информацию об аналитической роли ФЛ
  setAnaliticData = (data: any): void => {
    this.analiticData = data;
  };

  // Установить признак персоны
  setPersonAttribute = (data: any): void => {
    this.data.person_attribute = data;
  };

  // Установить дату рождения
  setDate = (data: any): void => {
    if (!isNaN(data)) {
      let date = new Date(data);
      date.setDate(date.getDate() + 1);
      this.data.date_of_birth = date;
      this.isInvalidDate = false;
    }
  };

  // Установить наименование гражданства
  setCitizenshipName = (data?: any): void => {
    this.citizenship_name = data;
  };

  // Установить id для гражданства
  setCitizenshipID = (data?: any): void => {
    this.data.citizenship_id = data;
  };

  // Установить наименование типа документа
  setDocumentName = (data?: any): void => {
    this.document_name = data;
  };

  // Установить id типа документа
  setDocumentID = (data?: any): void => {
    this.data.document_identity = data;
  };

  // Установить флаг "Признак, что адрес места жительства не на территории РФ"
  setNotCountryResidenceAddress = (): void => {
    this.data.not_country_residence_address = !this.data.not_country_residence_address;
  };

  // Установить наименование аналитической роли
  setAnaliticName = (data?: any): void => {
    this.analiticName = data;
  };

  // Установить id для аналитической роли
  setAnaliticID = (data?: any): void => {
    this.analiticData.role_id = data;
  };

  // Установить источник информации (аналитическая роль)
  setSource = (data?: any): void => {
    this.analiticData.role_info_source = data;
  };

  // Установить причину изменения (аналитическая роль)
  setReasonName = (data?: any): void => {
    this.analiticData.changeName = data;
  };

  setReasonID = (data: any): void => {
    this.analiticData.role_change_reason = data;
  };

  // Установить комментарий (аналитическая роль)
  setComment = (data?: any): void => {
    this.analiticData.role_description = data;
  };

  // Установить серию и номер документа
  setDocumentNumber = (series: any, number: any): void => {
    this.data.document_number = series + number;
  };

  // Установка значения Прикрепленные документы аналитической роли
  setAnaliticDocument = (documents?: any[]): void => {
    // @ts-ignore
    this.analiticDocument = documents;
  };

  // Установк документов
  setDocuments = (documents: any): void => {
    this.analiticData.attach_docs_role = documents;
  };

  // Признак создания ФЛ вручную
  setManualInput = (data: boolean): void => {
    this.data.is_manual_input = data;
  };

  // Признак наличия ошибок по типу файла
  setIsValidFile = (data: any): void => {
    this.isValidFile = data;
  };

  // Установить имя файла
  setFileName = (data: any): void => {
    this.fileName = data;
  };

  // Признак наличия ошибок (основная информация)
  setIsValidBasic = (data: any): void => {
    this.isValidBasic = data;
  };

  // Признак наличия ошибок (аналитическая роль)
  setIsValidAnalytical = (data: any): void => {
    this.isValidAnalytical = data;
  };

  // Очистить список ошибок (основная информация)
  resetValidate = (): void => {
    this.errors = [];
  };

  // Очистить список ошибок (аналитическая роль)
  resetAnalyticalValidate = (): void => {
    this.errorsAnalytical = [];
  };

  // Установить ошибку в поле (основная информация)
  setFieldsError = (field: string, data: boolean): void => {
    this.fieldsError[field] = data;
  };

  // Установить ошибку в поле (аналитическая роль)
  setFieldsAnalyticalError = (field: string, data: boolean): void => {
    this.fieldsErrorAnalytic[field] = data;
  };

  // Очистить информацию по ошибкам (основная информация)
  resetErrorInfo = (): void => {
    this.fieldsError.inn = false;
    this.fieldsError.name = false;
    this.fieldsError.date_of_birth = false;
    this.fieldsError.person_attribute = false;
    this.fieldsError.residence_address = false;
    this.fieldsError.citizenship_name = false;
    this.isInvalidDate = false;
  };

  // Очистить информацию по ошибкам (аналитическая роль)
  resetErrorAnalyticalInfo = (): void => {
    this.fieldsErrorAnalytic.role_id = false;
    this.fieldsErrorAnalytic.role_info_source = false;
    this.fieldsErrorAnalytic.role_change_reason = false;
  };

  // Очистить информацию
  resetData = (): void => {
    this.data.inn = '';
    this.data.name = '';
    this.data.citizenship_id = '';
    this.data.person_attribute = 'CountryCitizen';
    this.data.place_of_birth = '';
    this.data.date_of_birth = '';
    this.data.document_identity = '';
    this.data.document_number = '';
    this.data.document_series = '';
    this.data.residence_address = '';
    this.data.not_country_residence_address = false;
    this.data.is_manual_input = false;

    this.analiticData.role_id = '';
    this.analiticData.role_info_source = '';
    this.analiticData.role_change_reason = '';
    this.analiticData.changeName = '';
    this.analiticData.role_description = '';
    this.analiticDocument = [];
    this.analiticData.attach_docs_role = [];

    this.series = '';
    this.number = '';
    this.analiticName = '';

    this.citizenship_name = '';
    this.document_name = '';
  };
}

decorate(IndividualPersonStore, {
  data: observable,
  analiticData: observable,
  series: observable,
  number: observable,
  isOpen: observable,
  document_name: observable,
  citizenship_name: observable,
  analiticName: observable,
  analiticDocument: observable,
  isValidFile: observable,
  fileName: observable,
  isValidBasic: observable,
  isValidAnalytical: observable,
  errorsAnalytical: observable,
  errors: observable,
  fieldsError: observable,
  fieldsErrorAnalytic: observable,
  isInvalidDate: observable,

  setINN: action,
  setName: action,
  setPlaceOfBirth: action,
  setSeries: action,
  setNumber: action,
  setResidenceAddress: action,
  setPersonAttribute: action,
  setData: action,
  setDate: action,
  setNotCountryResidenceAddress: action,
  setAnaliticName: action,
  setAnaliticID: action,
  setSource: action,
  setReasonName: action,
  setComment: action,
  resetData: action,
  setDocumentNumber: action,
  setAnaliticDocument: action,
  setDocuments: action,
  setManualInput: action,
  setIsValidFile: action,
  setFileName: action,
  resetValidate: action,
  resetAnalyticalValidate: action,
  setIsValidBasic: action,
  setIsValidAnalytical: action,
  setFieldsError: action,
  resetErrorInfo: action,
  resetErrorAnalyticalInfo: action,
  setFieldsAnalyticalError: action,
  setReasonID: action,
});

export default IndividualPersonStore;
