import React from 'react';
import { IColProps } from './Col.props';
import styles from './Col.module.scss';

const Col = ({ children, className, size = 12, ...props }: IColProps) => {
  return (
    <div className={`${styles.col} ${className} ${styles['col_' + size]}`} {...props}>
      {children}
    </div>
  );
};

export { Col };
