import React, { PureComponent } from 'react';
import styles from './ToolTipName.module.scss';

import { inject, observer } from 'mobx-react';
import { GraphStoreType } from '../../stores/graph/graphStore';

type ToolTipNameProps = {
  [key: string]: any;
  id: string;
  graphStore?: GraphStoreType;
};

type ToolTipNameState = {
  graphID: any;
};

/**
 * Компонент всплывающей подсказки по наименованию НП
 */
@inject('graphStore')
@observer
class ToolTipName extends PureComponent<ToolTipNameProps, ToolTipNameState> {
  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { graphStore, id } = this.props;

    return (
      <>
        <div className={styles.container} id={id}>
          <div className={styles.data_container}>{graphStore?.nameNP}</div>
        </div>
      </>
    );
  }
}

export default ToolTipName;
