import React, { ReactNode } from 'react';
import { textPropTypes } from './Text.props';

import ToolTip from 'react-tooltip';
import { ReactComponent as InfoIcon } from './assets/info.svg';

import styles from './Text.module.scss';

type TextProps = {
  [key: string]: any;
  idForTip?: string;
  text?: string;
  explanationText?: string;
  TipComponent?: ReactNode;
  reverse?: boolean;
};

/**
 * Компонент текста для шапки
 *
 * Отрисовывает текст, его пояснение и подсказку
 *
 * @param {any} any - любые параметры для обертки
 * @param {string} text - основной текст
 * @param {string} explanationText - дополнительный текст(пояснение)
 * @param {ReactNode} TipComponent - компонент подсказки при наведении на основной текст
 */
const Text = (props: TextProps) => {
  const { idForTip, text, explanationText, TipComponent, reverse, ...rest } = props;

  return (
    <div {...rest}>
      {explanationText && (
        <span className={`${reverse ? styles.text : styles.explanation_text}`}>
          {(reverse ? text : explanationText) + ' '}
        </span>
      )}
      {TipComponent ? (
        <>
          <span className={`${text && explanationText ? styles.explanation_text : styles.text}`}>
            {text && explanationText
              ? explanationText + ' '
              : text && !explanationText
              ? text + ' '
              : ''}
            <span data-tip data-for={`header-${idForTip}`}>
              <InfoIcon className={styles.text_info} />
            </span>
          </span>
          <ToolTip id={`header-${idForTip}`} type="info" place="bottom">
            {TipComponent}
          </ToolTip>
        </>
      ) : (
        <span className={`${reverse ? styles.explanation_text : styles.text}`}>
          {(reverse ? explanationText : text) || ''}
        </span>
      )}
    </div>
  );
};

Text.propTypes = textPropTypes;

export default Text;
