import React from 'react';

import styles from './Loader.module.scss';

type LoaderProps = {
  fullscreen?: boolean;
};

/**
 * Компонент отрисовывает загрузчик
 */
const Loader = (props: LoaderProps) => {
  const { fullscreen } = props;

  return (
    <div className={!fullscreen ? '' : styles.loader}>
      <div className={styles.ldsRing}>
        <div />
        <div />
        <div />
        <div />
      </div>
    </div>
  );
};

Loader.defaultProps = {
  fullscreen: false,
};

export default Loader;
