import React from 'react';
import { dropdownItemPropTypes } from './DropdownItem.props';
import styles from './DropdownItem.module.scss';

import { MenuItem } from '../../../Menu.props';

export interface DropdownItemProps extends MenuItem {}

const DropdownItem = ({ label, callback, iconComponent }: DropdownItemProps) => {
  const Icon = iconComponent;

  return (
    <li className={styles.item} onClick={callback}>
      <div className={styles.iconContainer}>
        <Icon className={styles.icon} />
      </div>
      <span className={styles.label}>{label}</span>
    </li>
  );
};

DropdownItem.propTypes = dropdownItemPropTypes;

DropdownItem.defaultProps = {
  subLabel: '',
  items: [],
};

export default DropdownItem;
