import React, { PureComponent } from 'react';
import styles from './FilterGraphCluster.module.scss';

import { inject, observer } from 'mobx-react';

import { ReactComponent as InformationIcon } from './assets/information.svg';

import { GraphStoreType } from 'StoreTypes';
import ClusterStore from '../../stores/graph/graphStore/clusterStore';

type FilterGraphClusterProps = {
  graphStore?: GraphStoreType;
  clusterStore?: ClusterStore;
};

/**
 * Компонент "Граф перерисован с учётом фильтров кластера..."
 */
@inject('graphStore', 'clusterStore')
@observer
class FilterGraphCluster extends PureComponent<FilterGraphClusterProps> {
  /**
   * Функция "Сбросить"
   * @private
   */
  _onBack = async () => {
    const { graphStore, clusterStore } = this.props;
    graphStore.clearClusterFilter();
    clusterStore?.setFilterClusterFalse();
  };

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    return (
      <div className={`${styles.container}`}>
        <InformationIcon />
        <span className={styles.title}>
          Схема отображается с учетом выбранных фильтров кластеров.{' '}
          <span className={styles.reset} onClick={this._onBack}>
            Сбросить
          </span>
        </span>
      </div>
    );
  }
}

export default FilterGraphCluster;
