import React from 'react';
import { IInputProps } from './Input.prop';
import styles from './Input.module.scss';

const Input = ({
  label,
  labelPosition = 'left',
  wrapperStyle,
  labelStyle,
  inputStyle,
  className,
  ...props
}: IInputProps) => {
  return (
    <label className={`${styles.wrapper} ${className} ${wrapperStyle} ${styles[labelPosition]}`}>
      {label && <span className={`${styles.label} ${labelStyle}`}>{label}</span>}
      <input className={`${styles.input} ${inputStyle}`} {...props} />
    </label>
  );
};

export default Input;
